import React from 'react'
import { useHistory } from 'react-router-dom'

import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Button from '@material-ui/core/Button'

import CustomEnhancedTable from "../CustomEnhancedTable/CustomEnhancedTable";
import {DateFormatterByAbbreviation} from "../../../../../Utilities/TextFormatter";
import moment from "moment";

// Styles
import classes from './BillingAnalystTab.module.scss'

const FooterBar = props => {
  const { cancelAssigning, fetchData } = props
  const history = useHistory()

  const handleOnClickCancel = () => {
    if (history.location.hash.replace(/[#/]/g, '') === 'from_modal') {
      history.push('/#Client-Ledger-Setup')
    } else {
      // history.push('/')
      cancelAssigning()
      fetchData()
    }
  }

  return (
    <div className={classes.button}>
      <Button variant='contained' className={classes['button-negative']} onClick={handleOnClickCancel}>
        Cancel
      </Button>
      <Button variant='contained' className={classes['button-positive']} onClick={handleOnClickCancel}>
        Close
      </Button>
    </div>
  )
}

export default function BillingAnalystTab (props) {
  const { reset, cancelReset, cancelAssigning, headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore, pagination, params, fetchData, changeView } = props

  return (
    <>
    <div>
      <p><span>BILLING ANALYST:</span> <strong>{localStorage.getItem('full_name')}</strong></p>
      <p><span>STATUS:</span> <strong>Active</strong></p>
      <p><span>NO. OF ACCOUNTS:</span> <strong>{users.count}</strong></p>
    </div>

      <CustomEnhancedTable
        count={users.count}
        changeView={changeView}
        tableHeaders={headCells}
        pagination={pagination}
        search
        sort
        fetchData={fetchData}
        params={params}
        reset={reset}
        cancelReset={cancelReset}
      >
        <TableBody>
          {users.body
            ? users.body.map((item, index) => (
              <TableRow key={index}>
                <TableCell id={`analyst-number-${index}`}>{item.client.account_number}</TableCell>
                <TableCell id={`analyst-name-${index}`}>{item.client.account_name}</TableCell>
                <TableCell id={`analyst-contract-type-${index}`}>{item.contract.type}</TableCell>
                <TableCell id={`analyst-contract-type-${index}`}>{DateFormatterByAbbreviation(item.contract.effectivity)}</TableCell>
                <TableCell id={`analyst-contract-type-${index}`}>{DateFormatterByAbbreviation(item.contract.expiration)}</TableCell>
                <TableCell id={`analyst-payment-term-${index}`}>{item.contract.payment_term}</TableCell>
                <TableCell id={`analyst-billing-cycle-${index}`}>{item.billing.cycle.code} - {item.billing.cycle.name}</TableCell>
              </TableRow>
            ))
            : 'No Data'}
        </TableBody>
      </CustomEnhancedTable>
      <FooterBar cancelAssigning={cancelAssigning} fetchData={fetchData}/>
    </>
  )
}
