import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Services
import CollectionSharedServices from 'Services/Api/CollectionSharedServices'
import { useHistory } from 'react-router-dom'

// Config
const filterMenu = {
  data: [
    { value: 'this-month', text: 'This Month' },
    { value: 'this-week', text: 'This Week' },
    { value: 'today', text: 'Today' }
  ],
  default: 'this-month'
}

const columnConfig = [
  { id: 'pending-transactions-tile-table-header', name: 'Account Name' },
  { id: 'pending-transactions-tile-table-header', name: 'Transaction Type' },
  { id: 'pending-transactions-tile-table-header', name: '' }
]

const PendingTransactionsTile = props => {
  // Constants
  const history = useHistory()
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filter: {},
    filterMenu,
    limit: 5,
    order: true,
    orderBy: 'date_created',
    target_range: 'date_created',
    title: 'Pending Transactions',
    status: 'All',
    user_owned: true
  })

  // Initialize / Watch
  React.useEffect(() => {
    Promise.all([
      CollectionSharedServices.getMany({
        category: tile.category,
        limit: tile.limit,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        status: tile.status,
        target_range: tile.target_range,
        user_owned: true
      })
    ]).then(res => {
      const [CDFService] = res
      const { count, results } = CDFService.data.data
      setTile(i => ({ ...i, count, data: results }))
    })
  }, [tile.filterMenu.default])

  // Methods
  const handleOnView = ({ status, transaction_type, transaction_number }) => {
    if (!transaction_type || !transaction_number) return false
    switch (transaction_type) {
      case 'Advance Collection Distribution':
        const acdPath = {
          create: `/collection/collection-distribution/client/${transaction_number}?isAdvance=true`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}?isAdvance=true`
        }
        // history.push(status === 'New' ? acdPath.create : acdPath.view)
        history.push(acdPath.view)
        break
      case 'Collection Distribution':
        const path = {
          create: `/collection/collection-distribution/client/${transaction_number}`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}`
        }
        // history.push(status === 'New' ? path.create : path.view)
        history.push(path.view)
        break
      case 'Suspension':
        history.push(`/collection/suspension/endorse/client/${transaction_number}`)
        break
      case 'Penalty':
        history.push(`/collection/penalty/client/${transaction_number}`)
        break
      default:
        return false
        break
    }
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='pending-transactions-tile'
        count={{ color: 3, value: tile.count, id: 'pending-transactions-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        viewList={
          <Typography
            id='pending-transactions-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => history.push('/collection/lists/pending-transactions')}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`pending-transactions-tile-table-row-${index}`}
                id={`pending-transactions-tile-table-row-${index}`}
              >
                <TableCell id={`pending-transactions-tile-table-cell-account-name-${index}`}>
                  {item?.contract?.client?.account_name || ''}
                </TableCell>
                <TableCell id={`pending-transactions-tile-table-cell-transaction-type-${index}`}>
                  {item?.transaction_type || ''}
                </TableCell>
                <TableCell id={`pending-transactions-tile-table-cell-action-${index}`}>
                  <Typography
                    variant='caption'
                    className='text-link underline'
                    id={`pending-transactions-tile-view-${index}`}
                    onClick={() =>
                      handleOnView({
                        status: item?.status || null,
                        transaction_type: item?.transaction_type || null,
                        transaction_number: item?.cdf_number || item?.penalty_number || item?.spn_number || null
                      })
                    }
                  >
                    View
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

PendingTransactionsTile.displayName = 'DashboardTileComponent'

export default PendingTransactionsTile
