import React from 'react'
import { Link } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import SearchTextComponent from './../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../Components/CustomTable'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import RemindersLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import pick from 'lodash/pick'
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

// Store
import { useSelector } from 'react-redux'

// Styles
import classes from './ReminderLetters.module.scss'

// Utilities
import moment from 'moment'

export default function ReminderLetters() {
  const [data, setData] = React.useState({
    header: HEADERS,
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: 'date.created',
    page: 1,
    sort: '',
    search: '',
    // temporary added range date
    range: {
      start: moment().subtract(1, 'months').startOf('day').toISOString(),
      end: moment().endOf('day').toISOString()
    },
    rangeTarget: 'soa_due_date',
    filter: null
  })

  const getData = (params = {}) => {
    LoadingService.loading(true)

    RemindersLetterServices.index({
      ...pick(data, ['limit', 'page', 'range', 'rangeTarget', 'filter']),
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      ...params
    })
      .then(res => {
        const { count, limit, page, results } = res.data.data
        setData(prevState => ({
          ...prevState,
          count,
          body: results,
          limit,
          page
        }))
        LoadingService.loading(false)
      })
      .catch(err => {
        console.log(`Error fetching suspension list: ${err}`)
        LoadingService.loading(false)
      })
  }

  const handleDisplaySettingsApplyClicked = range => {
    getData(({ range }))
  }

  const handleDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleCloseClicked = () => {
    console.log('closed clicked')
  }

  const handleSearchChanged = search => {
    getData({ search })
  }

  React.useEffect(() => {
    getData()
  }, [data.orderBy, data.order, data.range, data.rangeTarget])

  const state = useSelector(state => state)

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems([`reminderLettersList`])
  }, [])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>
                List of Reminder Letters to Send
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder='Search List of Reminder Letters to Send'
                displaySettings
                displaySettingsTitle={`Submitted Date Range*`}
                handleOnDisplaySettingsClicked={handleDisplaySettingsClicked}
                handleOnDisplaySettingsApplyClicked={handleDisplaySettingsApplyClicked}
                value={data.search}
                onChange={handleSearchChanged}
              />
            </Grid>
          </Grid>
        </Grid>

        <Grid container>
          <Grid item xs>
            <Table
              hasLink
              data={data}
              onDataListChange={getData}
              setData={setData}
              showTextField
              body={reminders =>
                reminders.map((reminders, index) => (
                  <TableRow key={`reminder-letter-table-row-${index}`}>
                    <TableCell id={`reminder-letter-table-cell-letter-ref-no-${index}`}>
                      {reminders.letter_number}
                    </TableCell>
                    <TableCell id={`reminder-letter-table-cell-account-name-${index}`}>
                      {reminders.client.account_name}
                    </TableCell>
                    <TableCell id={`reminder-letter-table-cell-account-name-${index}`}>
                      {DateFormatterByAbbreviation(reminders.soa_due_date)}
                    </TableCell>
                    <TableCell id={`reminder-letter-table-cell-account-name${index}`}>
                      {CurrencyFormatter(reminders.soa_sub_total)}
                    </TableCell>
                    <TableCell id={`reminder-letter-table-cell-action-${index}`}>
                      <Link to={`/collection/send-reminder-letter/${reminders.letter_number}`}>
                        <Typography
                          id={`reminder-letter-view-${index}`}
                          variant='caption'
                          className='text-link underline'
                          style={{ color: '#48b77c' }}
                        >
                          View
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </div>
  )
}

const HEADERS = [
  {
    id: 'as-letter-ref-no',
    numeric: false,
    disablePadding: false,
    name: 'Letter Ref No.',
    sortField: 'letter_ref_number',
    match: 'any'
  },
  {
    id: 'as-account-number',
    numeric: false,
    disablePadding: false,
    name: 'Account Name',
    sortField: 'account_name',
    match: 'any'
  },
  {
    id: 'as-soa-due-date',
    numeric: false,
    disablePadding: false,
    name: 'SOA Due Date',
    sortField: 'soa_due_date',
    match: 'date'
  },
  {
    id: 'as-soa-amount',
    numeric: false,
    disablePadding: false,
    name: 'SOA Amount',
    sortField: 'soa_sub_total',
    match: 'numeric'
  }
]
