import React from 'react'
import _ from 'lodash'

import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import TablePagination from '@material-ui/core/TablePagination'
import FirstPageIcon from '@material-ui/icons/FirstPage'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'
import LastPageIcon from '@material-ui/icons/LastPage'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSort } from '@fortawesome/free-solid-svg-icons'
import Box from '@material-ui/core/Box'
import Checkbox from '@material-ui/core/Checkbox'
import noop from 'lodash/noop'

import CustomSearchTextComponent from '../CustomSearchTextComponent/CustomSearchTextComponent'
import classes from './CustomEnhancedTable.module.scss'

export default function CustomEnhancedTable (props) {
  const { reset, cancelReset, count, changeView, search, tableHeaders, tableHeight, children, pagination, size, sort, fetchData, params, textBoxHeight, hasLink, hasCheckbox, noSearchIcon = false, numberOfListItems,  numberOfItemsSelected, checkBoxColor = "default", handleGroupSelect = noop } = props

  const checkboxPartiallySelected = (numberOfItemsSelected < numberOfListItems && numberOfItemsSelected !== 0 )
  const checkboxAllSelected = (numberOfItemsSelected === numberOfListItems && numberOfListItems !== 0)

  const [newPage, setPage] = React.useState(pagination.page - 1)
  const [rowsPerPage, setRowsPerPage] = React.useState(pagination.rowsPerPage)
  const [selectItemPartially, setSelectItemPartially] = React.useState(false)
  const [selectAllItems, setSelectAllItems] = React.useState(false)

  const handleChangePage = (event, newPage) => {
    setPage(newPage - 1)
    changeView({ page: newPage, limit: rowsPerPage })
  }

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value))
    changeView({ page: 1, limit: parseInt(event.target.value) })
  }

  function TablePaginationActions (props) {
    const { onChangePage } = props

    const handleFirstPageButtonClick = event => {
      onChangePage(event, 1)
    }

    const handleBackButtonClick = event => {
      onChangePage(event, pagination.page - 1)
    }

    const handleNextButtonClick = event => {
      onChangePage(event, pagination.page + 1)
    }

    const handleLastPageButtonClick = event => {
      onChangePage(event, Math.max(0, Math.ceil(pagination.count / rowsPerPage)))
    }

    React.useEffect(() => {
      setPage(pagination.page - 1)
      setRowsPerPage(pagination.rowsPerPage)
    }, [pagination])

    return (
      <Box component='div' m={1} display='flex'>
        <IconButton onClick={handleFirstPageButtonClick} disabled={newPage === 0} aria-label='first page'>
          <FirstPageIcon />
        </IconButton>
        <IconButton onClick={handleBackButtonClick} disabled={newPage === 0} aria-label='previous page'>
          <KeyboardArrowLeft />
        </IconButton>
        <IconButton
          onClick={handleNextButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='next page'
        >
          <KeyboardArrowRight />
        </IconButton>
        <IconButton
          onClick={handleLastPageButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='last page'
        >
          <LastPageIcon />
        </IconButton>
      </Box>
    )
  }

  const onChangeSearch = _.debounce((e) => {
    if (cancelReset !== undefined) {
      cancelReset()
    }
    const value = {
      [e.target.name]: e.target.value
    }
    fetchData({
      filters: { ...params.filters, ...value },
      page: 1,
    })
  }, 800)

  const onSort = (name) => {
    const order = !params.order
    let nameTmp = name
    if (name.includes('|')) {
      const [field, type] = name.split('|')
      nameTmp = field
    }
    fetchData({
      ...params,
      sort: `${nameTmp}|${order ? 'asc' : 'desc'}`,
      order: order,
      orderBy: nameTmp,
      page: 1
    })
  }

  const handleGroupSelection = () => {

    // Deselect All
    if (selectAllItems) {  
      setSelectAllItems(false)
      handleGroupSelect(false)
    }

    // Delect All for Partial Selection
    else if (selectItemPartially){
      setSelectAllItems(false)
      setSelectItemPartially(false)
      handleGroupSelect(false)
    }

    // Select All
    else if (!selectAllItems && !selectItemPartially) {
      setSelectAllItems(true)
      handleGroupSelect(true)
    }

  }

  React.useEffect(()=>{

    if (checkboxAllSelected) setSelectAllItems(true) 
    if (!checkboxPartiallySelected) setSelectItemPartially(false) 

    if (!checkboxAllSelected) setSelectAllItems(false) 
    if (checkboxPartiallySelected) setSelectItemPartially(true) 

  },[numberOfItemsSelected])

  return (
    <div className={classes.root}>
      <Paper style={{ height: tableHeight }} className={classes.paper}>
        <div className={classes['table-wrapper']}>
          <Table
            stickyHeader
            aria-label='sticky table'
            className={classes.table}
            aria-labelledby='tableTitle'
            size={size}
          >
            <TableHead className={classes['table-head']}>
              <TableRow>
                {hasCheckbox && (
                  <TableCell
                    id='cet-checkbox-column'
                    key='cet-checkbox-column'
                  >
                    <Checkbox color={checkBoxColor} indeterminate={selectItemPartially} checked={selectAllItems} onChange={handleGroupSelection} disableRipple className={classes['column-header-checkbox']} />
                  </TableCell>)}
                {tableHeaders.map(header => (
                  <TableCell className={classes['th']}
                    id={header.id}
                    key={header.id}
                    align={header.center ? 'center' : header.numeric ? 'right' : 'left'}
                    padding={header.disablePadding ? 'none' : 'default'}
                    sortDirection={false}
                    width={header.width}
                  >
                    {header.label}
                    {header.label !== '' && header.sort ? (
                      <FontAwesomeIcon className={classes['sort-icon']} onClick={e => onSort(header.accessor)} icon={faSort} />
                    ) : (
                      ''
                    )}
                    {/* header.search is to know what are the fields needed needed for sorting on the header there are tables that are not needed to have a search */}
                    {header.label !== '' && header.search ? <CustomSearchTextComponent reset={reset} name={header.accessor} width={header.width} height={textBoxHeight} onChange={onChangeSearch} onSort={onSort} noIcon={noSearchIcon} /> : ''}
                  </TableCell>
                ))}
                {hasLink && (<TableCell
                  id='cet-link-column'
                  key='cet-link-column'
                 />)}
              </TableRow>
            </TableHead>
            {children}
          </Table>
        </div>
        {pagination && (
          <TablePagination
            className={classes.pagination}
            rowsPerPageOptions={[5, 10, 20]}
            count={pagination.count}
            rowsPerPage={rowsPerPage}
            page={newPage}
            ActionsComponent={TablePaginationActions}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        )}
      </Paper>
    </div>
  )
}
