import React from 'react'
import { useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import noop from 'lodash/noop'
import moment from 'moment'

// Components
import Alert from 'SharedComponents/Alert/Alert'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import PageFooter from './../../Components/PageFooter/PageFooter'
import CollectionHeader from './components/CollectionHeader'
import AnalyzeDetailsStep from './components/AnalyzeDetails/AnalyzeDetails'
import SuspensionBreakdownStep from './components/SuspensionBreakdown/SuspensionBreakdown'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetSuspensionFields } from './../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'

// Styles
import classes from './SuspensionEndorsement.module.scss'

export default function SuspensionEndorsement (props) {
  const history = useHistory()
  const dispatch = useDispatch()
  const form = useSelector(state => state.Suspension)

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = 'client' } = props.match.params
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Analyze Details', 'Suspension Breakdown', 'Submit'],
    stepNumber: 1
  })
  const [pageTitle, setPageTitle] = React.useState('Endorse for Suspension')
  const [willSubmit, setWillSubmit] = React.useState(false)
  const [willCancel, setWillCancel] = React.useState(false)
  const [confirmed, setConfirmed] = React.useState(false)
  
  const [pageData, setPageData] = React.useState({
    client: {},
    spn_number: null,
    spn_soa: [],
    effectivity: null,
    reason: '',
    remarks: '',
    availableSoa: [],
    spn_fees: {},
    selectedFees: [],
    member: null
  })

  const getData = () => {
    LoadingService.loading(true)
    Promise.all([CollectionSuspensionServices.show(data_id, {}, data_type)])
      .then(response => {
        LoadingService.loading(false)
        
        const [suspensionEndorsementResponse] = response
        const { client = {}, spn_fees = {}, status } = suspensionEndorsementResponse.data.data
        
        if (status !== 'new') {
          dispatch(
            SnackbarService.set({ 
              open: true, 
              message: 'Invalid suspension status',
              severity: 'error'
            })
          )

          history.push('/')
        }

        setPageData(data => ({ 
          ...data, 
          ...suspensionEndorsementResponse.data.data,
          selectedFees: spn_fees?.spn_summary || {}
        }))

        BreadcrumbsService.setSelectedItems([
          'collectionDistributionDashboard',
          'listOfAccountsWithAgingSummary',
          `clientAgingSummary:${client.account_name?.toUpperCase() || ''}`,
          'suspensionEndorsement'
        ])
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error('Error fetching Suspension: ', error)
      })
  }

  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    switch (stepper.stepNumber) {
      case 1:
        return <AnalyzeDetailsStep {...componentProp} />
      case 2:
        return <SuspensionBreakdownStep {...componentProp} />
      case 3:
        return <SuspensionBreakdownStep {...componentProp} />
      default:
        return ''
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(prevState => ({ 
      ...prevState, 
      stepNumber, 
      onStepChanged: stepNumber 
    }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  const handleOnAddSOAClicked = () => {
    CollectionSuspensionServices.getAvailableSoa(pageData.spn_number)
      .then(res => {
        const { results } = res.data.data
        setPageData(prevState => ({ ...prevState, availableSoa: results }))
      })
      .catch(err => console.error(`Error Fetching Available SOA(s): ${err}`))
  }

  const handleOnAddNewSOA = soaNumber => {
    if (!soaNumber) {
      return
    }

    LoadingService.loading(true)
    
    CollectionSuspensionServices.update(pageData.spn_number, {
      soas: [soaNumber]
    })
    .then(res => {
      LoadingService.loading(false)
      getData()
    })
    .catch(err => {
      console.error(`Error Updating Suspension: ${err}`)
      LoadingService.loading(false)
    })
  }

  const handleOnSelectSoaTab = (soaNumber = null) => {
    LoadingService.loading(true)

    CollectionSuspensionServices.getFees(pageData.spn_number, {
      soa_number: soaNumber
    })
    .then(res => {
      LoadingService.loading(false)
      const selectedFees = res.data.data.length > 0 ? res.data.data.pop() : {}
      setPageData(prevState => ({ ...prevState, selectedFees: selectedFees.spn_summary || {} }))
    })
    .catch(err => {
      console.error(`Error fetching SOA Fees: ${err}`)
      LoadingService.loading(false)
    })
  }

  const handleOnFetchMembers = (soaNumber, query) => {
    if (!soaNumber) {
      return
    }

    LoadingService.loading(true)

    CollectionSuspensionServices.getMembers(pageData.spn_number, {
      soa_number: soaNumber,
      ...query
    })
    .then(res => {
      LoadingService.loading(false)
      const { count, results, limit, page } = res.data.data
      setPageData(prevState => ({ 
        ...prevState, 
        member: { count, results, limit, page } 
      }))
    })
    .catch(err => {
      console.error(err)
      LoadingService.loading(false)
    })
  }

  const handleCancelSuspensionEndorsementConfirmed = () => {
    LoadingService.loading(true)

    CollectionSuspensionServices.update(pageData.spn_number, {
      ...form,
      status: 'cancelled'
    })
    .then(res => {
      setWillCancel(false)
      LoadingService.loading(false)
      history.push(`/collection/agingsummary/${data_type}/${pageData?.client?.account_number}`)
    })
    .catch(err => {
      setWillCancel(false)
      console.log(`Error updating suspension endorsement: ${err}`)
      LoadingService.loading(false)
    })
  }

  const handleSubmitSuspensionEndorsementConfirmed = () => {
      LoadingService.loading(true)

      CollectionSuspensionServices.update(pageData.spn_number, {
        ...form,
        status: 'submitted'
      })
      .then(res => {
        setWillSubmit(false)
        setConfirmed(true)
        LoadingService.loading(false)
      })
      .catch(err => {
        setWillSubmit(false)
        console.log(`Error updating suspension endorsement: ${err}`)
        LoadingService.loading(false)
      })
  }

  const handleConfirmSuspensionEndorsementConfirmed = () => {
    setConfirmed(false)
    history.push('/')
  } 

  React.useEffect(() => {
    getData()

    dispatch(
      SetSuspensionFields({
        effectivity: pageData.effectivity || moment().toISOString(),
        reason: pageData.reason,
        remarks: pageData.remarks
      })
    )
  }, [pageData.effectivity, pageData.reason, pageData.remarks])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header} direction='row' justify='space-between'>
            <Grid item>
              <Typography id='suspension-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} />
            </Grid>
            <Grid item xs={12}>
              {showComponent({
                handleOnSelectSoaTab,
                handleOnAddNewSOA,
                handleOnAddSOAClicked,
                handleOnFetchMembers,
                handleOnRemoveMember: noop,
                handlePageTitleChange,
                soa: pageData.spn_soa,
                availableSoa: pageData.availableSoa,
                member: pageData.member,
                totalFees: pageData.spn_fees?.spn_summary || {},
                selectedFees: pageData.selectedFees
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            isStepper
            showSuspensionListBtn
            stepNumber={stepper.stepNumber}
            stepCount={stepper.stepDetails.length}
            handleStepChange={handleStepChange}

            submitSuspensionEndorsement={willSubmit}
            handleSubmitSuspensionEndorsementConfirmed={handleSubmitSuspensionEndorsementConfirmed}
            handleSubmitSuspensionEndorsementCancelled={() => setWillSubmit(false)}
            handleSubmitSuspensionEndorsement={() => setWillSubmit(true)}

            cancelSuspensionEndorsement={willCancel}
            handleCancelSuspensionEndorsement={() => setWillCancel(true)}
            handleCancelSuspensionEndorsementConfirmed={handleCancelSuspensionEndorsementConfirmed}
            handleCancelSuspensionEndorsementCancelled={() => setWillCancel(false)}

            confirmSuspensionEndorsement={confirmed}
            handleConfirmSuspensionEndorsementConfirmed={handleConfirmSuspensionEndorsementConfirmed}
          />
        </Grid>
      </Grid>
      <Alert />
    </div>
  )
}
