import React from 'react'
import Paper from '@material-ui/core/Paper'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import * as moment from 'moment'

import PropTypes from 'prop-types'
import { makeStyles, withStyles } from '@material-ui/core/styles'
import clsx from 'clsx'
import StepConnector from '@material-ui/core/StepConnector'

// CSS
import classes from './HeaderDetails.module.scss'
import { Typography } from '@material-ui/core'

export default function HeaderDetails (props) {
  const { soa, activeStep, steps, type = '', entity } = props

  const QontoConnector = withStyles({
    alternativeLabel: {
      top: 10,
      left: 'calc(-50% + 5px)',
      right: 'calc(50% + 5px)'
    },
    active: {
      '& $line': {
        borderColor: '#3ab77d'
      }
    },
    completed: {
      '& $line': {
        borderColor: '#3ab77d'
      }
    },
    line: {
      borderColor: '#eaeaf0',
      borderTopWidth: 3,
      borderRadius: 1
    }
  })(StepConnector)

  const useQontoStepIconStyles = makeStyles({
    root: {
      color: '#eaeaf0',
      display: 'flex',
      height: 22,
      alignItems: 'center'
      // fontSize: 10
    },
    active: {
      color: '#3ab77d'
    },
    circle: {
      width: 10,
      height: 10,
      borderRadius: '50%',
      backgroundColor: 'currentColor'
    },
    completed: {
      color: '#3ab77d',
      zIndex: 1
      // fontSize: 18
    },
    label: {
      backgroundColor: 'powderBlue'
    }
  })

  function QontoStepIcon (props) {
    const classes = useQontoStepIconStyles()
    const { active, completed } = props

    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active
        })}
      >
        {completed ? (
          <div className={`${classes.completed} && ${classes.circle} && ${classes.active}`} />
        ) : (
          <div className={classes.circle} />
        )}
      </div>
    )
  }

  QontoStepIcon.propTypes = {
    active: PropTypes.bool,
    completed: PropTypes.bool
  }

  return (
    <React.Fragment>
      <div>
        <Paper className={classes.root}>
          {soa && (
            <React.Fragment>
              {type === 'analyst' ? (
                <div className={classes.container}>
                  {entity === 'client' ? (
                    <div className={classes.details}>
                      <Typography variant='h5' id='soa-name'>
                        {soa.account_name} - Statement of Account
                      </Typography>
                      <Typography id='soa-account-number'>
                        ACCOUNT NUMBER {soa.journal.client.account_number}
                      </Typography>
                      {soa.journal && soa.journal.contract ? (
                        <Typography id='soa-effectivity-expiration'>
                          {moment(soa.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                          {moment(soa.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                        </Typography>
                      ) : (
                        ''
                      )}
                      {soa.journal && soa.journal.contract ? (
                        <Typography id='soa-payment-terms'>
                          {soa.journal.contract.payment_term && soa.journal.contract.payment_term.toUpperCase()} PAYMENT
                          TERM
                        </Typography>
                      ) : (
                        ''
                      )}
                    </div>
                  ) : (
                    entity === 'member' && (
                      <div className={classes.details}>
                        <Typography variant='h5' id='soa-name'>
                          {soa.first_name + ' ' + soa.last_name} - Statement of Account
                        </Typography>
                        <Typography id='soa-person-id'>PERSON ID {soa.person_id}</Typography>
                        {soa.journal && soa.journal.contract ? (
                          <Typography id='soa-effectivity-expiration'>
                            {moment(soa.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                            {moment(soa.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                          </Typography>
                        ) : (
                          ''
                        )}
                      </div>
                    )
                  )}
                  <Stepper
                    activeStep={activeStep}
                    alternativeLabel
                    connector={<QontoConnector />}
                    className={classes.stepper}
                  >
                    {steps.map((label, index) => (
                      <Step key={label}>
                        <StepLabel
                          id={`step-label-${index}`}
                          className={classes.label}
                          StepIconComponent={QontoStepIcon}
                        >
                          {label}
                        </StepLabel>
                      </Step>
                    ))}
                  </Stepper>
                </div>
              ) : (
                type === 'validated' && (
                  <React.Fragment>
                    {entity === 'client' ? (
                      <div className={classes.container}>
                        <div className={classes.details}>
                          <Typography variant='h5' id='soa-name'>
                            {soa.account_name} - Statement of Account
                          </Typography>
                          <Typography id='soa-account-number'>
                            ACCOUNT NUMBER {soa.journal.client.account_number}
                          </Typography>
                          {soa.journal && soa.journal.contract ? (
                            <Typography id='soa-effectivity-expiration'>
                              {moment(soa.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                              {moment(soa.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                            </Typography>
                          ) : (
                            ''
                          )}
                          {soa.journal && soa.journal.contract ? (
                            <Typography id='soa-payment-terms'>
                              {soa.journal.contract.payment_term && soa.journal.contract.payment_term.toUpperCase()}{' '}
                              PAYMENT TERM
                            </Typography>
                          ) : (
                            ''
                          )}
                        </div>
                      </div>
                    ) : (
                      entity === 'member' && (
                        <div className={classes.container}>
                          <div className={classes.details}>
                            <Typography variant='h5' id='soa-name'>
                              {soa.account_name} - Statement of Account
                            </Typography>
                            <Typography id='soa-person-id'>PERSON ID {soa.person_id}</Typography>
                            {soa.journal && soa.journal.contract ? (
                              <Typography id='soa-effectivity-expiration'>
                                {moment(soa.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                                {moment(soa.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                              </Typography>
                            ) : (
                              ''
                            )}
                          </div>
                        </div>
                      )
                    )}
                    <div className={classes.pre_soa_details}>
                      <Typography variant='h5'>PRE- SOA NO.</Typography>
                      <Typography color='textSecondary'>DATE APPROVED</Typography>
                    </div>
                    {soa.journal && soa.journal.soa ? (
                      <div className={classes.values}>
                        <Typography variant='h5'>{soa.journal.soa.number}</Typography>
                        <Typography color='textSecondary'>
                          {moment(soa.journal.date.modified).format('MMM DD,YYYY')}
                        </Typography>
                      </div>
                    ) : (
                      ''
                    )}
                    {soa.journal && soa.journal.soa ? (
                      <div className={classes.status}>
                        <Typography variant='h5' color='error'>
                          {soa.journal.soa.status}
                        </Typography>
                      </div>
                    ) : (
                      ''
                    )}
                  </React.Fragment>
                )
              )}
            </React.Fragment>
          )}
        </Paper>
      </div>
    </React.Fragment>
  )
}
