import React from 'react'
import Typography from '@material-ui/core/Typography'

import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'

// Custom Components
import EnhancedTable from 'StandardComponents/EnhancedTable/EnhancedTable'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import MemberTransactionJournalServices from 'Services/Api/MemberTransactionJournalServices'

// CSS
import classes from './MemberTransactionJournalPage.module.scss'

const headCells = [
  { id: 'transaction_date', numeric: false, center: true, disablePadding: true, label: 'Transaction Date' },
  { id: 'member_id', numeric: false, center: true, disablePadding: true, label: 'Member ID' },
  { id: 'account_name', numeric: false, center: true, disablePadding: true, label: 'Account Name' },
  { id: 'account_no', numeric: false, center: true, disablePadding: true, label: 'Account No.' },
  { id: 'contract_period', numeric: false, center: true, disablePadding: true, label: 'Contract Period' },
  { id: 'payment_term', numeric: false, center: true, disablePadding: true, label: 'Payment Term' },
  { id: 'member_type', numeric: false, center: true, disablePadding: true, label: 'Member Type' },
  { id: 'plan_type', numeric: false, center: true, disablePadding: true, label: 'Plan Type' },
  { id: 'effectivity_date', numeric: false, center: true, disablePadding: true, label: 'Effectivity Date' },
  { id: 'cancelled_date', numeric: false, center: true, disablePadding: true, label: 'Cancelled Date' },
  { id: 'ticket_no', numeric: false, center: true, disablePadding: true, label: 'Ticket No.' },
  { id: 'transaction_no', numeric: false, center: true, disablePadding: true, label: 'Transaction No.' },
  { id: 'transaction_status', numeric: false, center: true, disablePadding: true, label: 'Transaction Status' },
  { id: 'billing_code', numeric: false, center: true, disablePadding: true, label: 'Billing Code' },
  { id: 'billing_type', numeric: false, center: true, disablePadding: true, label: 'Billing Type' },
  { id: 'amount', numeric: true, center: false, disablePadding: true, label: 'Amount' },
  { id: 'soa_no', numeric: false, center: true, disablePadding: true, label: 'SOA No.' },
  { id: 'reference_no', numeric: false, center: true, disablePadding: true, label: 'Reference No.' },
  { id: 'loa_no', numeric: false, center: true, disablePadding: true, label: 'LOA No.' },
  { id: 'claim_no', numeric: false, center: true, disablePadding: true, label: 'Claim No.' },
  { id: 'atp_no', numeric: false, center: true, disablePadding: true, label: 'ATP No.' }
]

export default function MemberTransactionJournalPage (props) {
  const personId = props.match.params.personId

  const [journalEntry, setJournalEntry] = React.useState(null)

  React.useEffect(() => {
    Promise.all([MemberTransactionJournalServices.getMemberTransactionJournal({ personId: personId })]).then(values => {
      const journal = values.pop().data.data[0]
      setJournalEntry(journal)
      PageTitleService(journal.first_name + ' ' + journal.last_name + ' - Transaction Journal')
    })
  }, [])
  return (
    <React.Fragment>
      {journalEntry && (
        <React.Fragment>
          {console.log('journal ', journalEntry)}
          <div className={classes.member_info}>
            <div className={classes.info_text_left}>
              <Typography>{'PERSON ID: ' + journalEntry._id}</Typography>
            </div>
            <div className={classes.info_text_right}>
              <Typography>account effectivity - account expiry</Typography>
            </div>
          </div>
          <EnhancedTable headCells={headCells}>
            <TableBody>
              {journalEntry.entries &&
                journalEntry.entries.map((item, index) => {
                  return (
                    <TableRow>
                      <TableCell id={`transaction-date-${index}`}>{item.transaction && item.transaction.date}</TableCell>
                      <TableCell id={`member-id-${index}`}>{item.id}</TableCell>
                      <TableCell id={`account-name-${index}`}>{item.account_name}</TableCell>
                      <TableCell id={`account-no-${index}`}>{item.account_no}</TableCell>
                      <TableCell id={`contract-period-${index}`}>{item.contract && item.contract.effectivity + '-' + item.contract.expiration }</TableCell>
                      <TableCell id={`payment-term-${index}`}>test</TableCell>
                      <TableCell id={`member-type-${index}`}>{item.type}</TableCell>
                      <TableCell id={`plan-type-${index}`}>{item.plan_type_name}</TableCell>
                      <TableCell id={`effectivity-date-${index}`}>test</TableCell>
                      <TableCell id={`cancelled-date-${index}`}>test</TableCell>
                      <TableCell id={`ticket-no-${index}`}>{item.ticket_no}</TableCell>
                      <TableCell id={`transaction-no-${index}`}>{item.transaction_no}</TableCell>
                      <TableCell id={`transaction-status-${index}`}>{item.transaction_status}</TableCell>
                      <TableCell id={`billing-code-${index}`}>test</TableCell>
                      <TableCell id={`billing-type-${index}`}>test</TableCell>
                      <TableCell id={`amount-${index}`}>123</TableCell>
                      <TableCell id={`soa-number-${index}`}>{item.soa_number}</TableCell>
                      <TableCell id={`reference-no-${index}`}>test</TableCell>
                      <TableCell id={`loa-no-${index}`}>test</TableCell>
                      <TableCell id={`claim-no-${index}`}>test</TableCell>
                      <TableCell id={`atp-no-${index}`}>test</TableCell>
                    </TableRow>
                  )
                })}
            </TableBody>
          </EnhancedTable>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}
