import React from 'react';
import PropTypes from 'prop-types';
import { useSelected, useFocused } from 'slate-react'

// Components
import Typography from '@material-ui/core/Typography';

// Constants
import { ELEMENT_TYPES } from '../constants'

// Styles
import { makeStyles } from '@material-ui/core/styles'
import clsx from 'clsx'

const useStyles = makeStyles(() => ({
  image: {
    display: 'block',
    maxWidth: '100%',
    maxHeight: '20em',
    objectFit: 'cover',
    objectPosition: 'center'
  },

  imageActive: {
    boxShadow: '0 0 0 3px #B4D5FF'
  }
}))

export default function Element({ attributes, children, element }) {
  const classes = useStyles()
  const selected = useSelected()
  const focused = useFocused()

  switch (element.type) {
    case ELEMENT_TYPES.BULLETED_LIST:
      return <ul {...attributes}>{children}</ul>;

    case ELEMENT_TYPES.NUMBERED_LIST:
      return <ol {...attributes}>{children}</ol>;

    case ELEMENT_TYPES.LIST_ITEM:
      return <li {...attributes}>{children}</li>;

    case ELEMENT_TYPES.BLOCK_QUOTE:
      return <blockquote {...attributes}>{children}</blockquote>;

    case ELEMENT_TYPES.HEADING_ONE:
      return <Typography variant="h1" component="h2" {...attributes}>{children}</Typography>;

    case ELEMENT_TYPES.HEADING_TWO:
      return <Typography variant="h2" {...attributes}>{children}</Typography>;

    case ELEMENT_TYPES.LINK:
      return (
        <a {...attributes} href={element.url}>
          {children}
        </a>
      )

    case ELEMENT_TYPES.IMAGE:
      return (
        <div {...attributes}>
          <div contentEditable={false}>
            <img
              src={element.url}
              alt=""
              className={clsx(classes[ELEMENT_TYPES.IMAGE], { [classes.imageActive]: selected && focused })}
              style={element.dimensions}
            />
          </div>
          {children}
        </div>
      )

    default:
      return <Typography {...attributes}>{children}</Typography>;
  }
}

Element.propTypes = {
  element: PropTypes.shape({
    type: PropTypes.oneOf(Object.values(ELEMENT_TYPES)),
  }),
  attributes: PropTypes.object,
  children: PropTypes.object,
}