import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Components
import TextField from 'StandardComponents/TextField/CustomTextField'
import VoidValidator from './VoidValidator'

// Styles
import classes from './Void.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction, setOpenDialog } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
    setOpenDialog(false)
  }

  const submitVoidFunction = () => {
    setOpenSuccessDialog(false)
    setOpenDialog(false)
    submitFunction()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            {page === 'soa'
              ? 'Void Statement of Account'
              : page === 'credit'
              ? 'Void Credit Memo'
              : page === 'debit'
              ? 'Void Debit Memo'
              : page === 'invoice'
              ? 'Void Invoice Transaction'
              : ''}
          </Typography>
        </div>
        {page === 'soa' ? (
          <DialogContent>
            {page === 'soa'
              ? 'Statement of Account (SOA) was successfully voided by the Supervisor and Manager.'
              : page === 'credit'
              ? 'Credit Memo was successfully voided by the Supervisor and Manager.'
              : page === 'debit'
              ? 'Debit Memo  was successfully voided by the Supervisor and Manager.'
              : page === 'invoice'
              ? 'Invoice was successfully voided by the Supervisor and Manager.'
              : ''}
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${page}-ok-button`}
                className={classes['button-positive']}
                onClick={submitVoidFunction}
              >
                OK
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'credit' ? (
          <DialogContent>
            Credit Memo was successfully voided by the Supervisor and Manager.
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${page}-ok-button`}
                className={classes['button-positive']}
                onClick={submitVoidFunction}
              >
                OK
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'debit' ? (
          <DialogContent>
            Debit Memo was successfully voided by the Supervisor and Manager.
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${page}-ok-button`}
                className={classes['button-positive']}
                onClick={submitVoidFunction}
              >
                OK
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'invoice' ? (
          <DialogContent>
            Invoice Transaction was successfully voided by the Supervisor and Manager.
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${page}-ok-button`}
                className={classes['button-positive']}
                onClick={submitVoidFunction}
              >
                OK
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
      </DialogTitle>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction, setActiveStep } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)
  const [errors, setErrors] = React.useState({
    disapprove: []
  })
  const [value, setValue] = React.useState({ remarks: '' })
  const [allowedFields, setAllowedFields] = React.useState({})
  const _allowedFields = {
    remarks: ''
  }

  setTimeout(() => {
    // setValue(_allowedFields)
    setAllowedFields(_allowedFields)
  }, 10)

  const handleOnchange = value => {
    setValue({
      remarks: value
    })
  }

  const handleOnClose = () => {
    if (page === 'credit' || page === 'debit' || page === 'invoice') {
      setOpenDialog(false)
    } else {
      setActiveStep(0)
      setOpenDialog(false)
    }
  }

  const doValidatedisapprove = value => {
    let { error } = VoidValidator({
      remarks: value.remarks,
      debit: value.remarks,
      credit: value.remarks,
      invoice: value.remarks
    })

    if (error) {
      error = { ...error }
      console.group('Base Error')
      console.log(error)
      console.groupEnd()
      setErrors(state => ({ disapprove: state.disapprove, ...error }))
      return !!error
    }
  }

  const doValidateAll = data => {
    const disapprove = doValidatedisapprove(data)

    setErrors(state => ({ ...state, disapprove }))
    return !!disapprove
  }

  const onShowSuccessDialog = () => {
    const data = {}
    for (let f in allowedFields) data[f] = value[f]
    const hasError = doValidateAll(data)
    if (hasError) return
    setOpenSuccessDialog(true)
    if (openSuccessDialog === true) setOpenDialog(false)
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '400px', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton aria-label='close' id='confirm-close-button' className={classes.button} onClick={handleOnClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='confirm-title' className={classes.title}>
              {page === 'soa'
                ? 'Void Statement of Account'
                : page === 'credit'
                ? 'Void Credit Memo'
                : page === 'debit'
                ? 'Void Debit Memo'
                : page === 'invoice'
                ? 'Void Invoice Transaction'
                : ''}
            </Typography>
          </div>
        </DialogTitle>
        {page === 'soa' ? (
          <DialogContent id='soa-content'>
            Are you sure you want to void the Statement of Account?
            <br />
            <br />
            This SOA and attachments will be included in the Bill Run Schedule for Release.
            <br />
            <br />
            <TextField
              id='remarks-approve-id'
              defaultValue={value.remarks}
              onChange={event => handleOnchange(event.target.value)}
              label='Enter your remarks here'
              required
              error={errors.remarks}
            />
            <br />
            <br />
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                No
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Yes
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'credit' ? (
          <DialogContent id='soa-content'>
            Are you sure you want to void the Credit Memo?
            <br />
            <br />
            <br />
            <br />
            <TextField
              id='remarks-approve-id'
              defaultValue={value.remarks}
              onChange={event => handleOnchange(event.target.value)}
              label='Reason for Voiding Credit Memo'
              required
              error={errors.credit}
            />
            <br />
            <br />
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                No
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Yes
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'debit' ? (
          <DialogContent id='soa-content'>
            Are you sure you want to void the Debit Memo?
            <br />
            <br />
            <br />
            <br />
            <TextField
              id='remarks-approve-id'
              defaultValue={value.remarks}
              onChange={event => handleOnchange(event.target.value)}
              label='Enter your remarks here'
              required
              error={errors.debit}
            />
            <br />
            <br />
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                No
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Yes
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
        {page === 'invoice' ? (
          <DialogContent id='soa-content'>
            Are you sure you want to void the Invoice Approval?
            <br />
            <br />
            <br />
            <br />
            <TextField
              id='remarks-approve-id'
              defaultValue={value.remarks}
              onChange={event => handleOnchange(event.target.value)}
              label='Enter your remarks here'
              required
              error={errors.invoice}
            />
            <br />
            <br />
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                No
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Yes
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
      </Dialog>
      {openSuccessDialog === true ? (
        <SuccessDialog
          page={page}
          openSuccessDialog={openSuccessDialog}
          setOpenSuccessDialog={setOpenSuccessDialog}
          submitFunction={submitFunction}
          setOpenDialog={setOpenDialog}
        />
      ) : (
        ''
      )}
    </React.Fragment>
  )
}
