import React from 'react'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'

// Components
import SearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from 'Pages/Protected/BillingAndCollection/Components/CustomTable'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { useHistory } from 'react-router-dom'

// Styles
import classes from './style.module.scss'

// Utilities
import moment from 'moment'

// Config
import { ValidateListHeaders } from './../config/config'

// Library
const numeral = require('numeral')

export default function CollectionDistributionToValidateListPage (props) {
  const history = useHistory()
  const [pageState, setPageState] = React.useState({
    body: [],
    status: 'For Approval',
    count: 0,
    filter: {},
    header: ValidateListHeaders,
    limit: 10,
    order: true,
    orderBy: 'date_submitted',
    page: 1,
    sort: ''
  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', `listOfClientCDToValidate:${'CLIENT'}`])
    handleOnFetchData()
  }, [])

  // Methods
  const handleOnFetchData = params => {
    LoadingService.loading(true)

    const _params = {
      status: pageState.status,
      limit: pageState.limit,
      page: pageState.page,
      sort: `${pageState.orderBy}|${pageState.order ? 'asc' : 'desc'}`,
      filter: pageState.filter,
      ...params
    }

    Promise.all([CollectionCDFServices.getMany(_params)])
      .then(res => {
        const [CDFService] = res
        const { count, limit, page, results } = CDFService.data.data
        setPageState(i => ({ ...i, count, body: results, limit, page }))
        LoadingService.loading(false)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(`Error fetching Data: ${err}`)
      })
  }

  const onDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleOnDisplaySettingsApplied = params => {
    if (!params) return false

    handleOnFetchData({
      dateRangeTarget: 'date_submitted',
      dateRange: { ...params }
    })
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography id='collection-distribution-page-header' variant='h5'>
                List of Client Collection Distribution to Validate
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder='Search List of Client Collection Distribution to Validate'
                displaySettings
                handleOnDisplaySettingsClicked={onDisplaySettingsClicked}
                handleOnDisplaySettingsApplyClicked={handleOnDisplaySettingsApplied}
                onChange={e => handleOnFetchData({ search: e })}
                exportFiles
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs>
              <Table
                hasLink
                data={pageState}
                onDataListChange={e => handleOnFetchData(e)}
                setData={setPageState}
                showTextField
                body={_data =>
                  _data.map(({ cdf_number, contract, date, form, transaction_type, summary }, index) => (
                    <TableRow key={`validate-list-item-${index}`}>
                      <TableCell id={`validate-item-cdf-${index}`}>{cdf_number}</TableCell>
                      <TableCell id={`validate-item-account-name-${index}`}>
                        {contract?.client?.account_name || ''}
                      </TableCell>
                      <TableCell id={`validate-item-transaction-type-${index}`}>{transaction_type}</TableCell>
                      <TableCell id={`validate-item-submitted-date-${index}`}>
                        {date?.submitted ? moment(date.submitted).format('ll') : ''}
                      </TableCell>
                      <TableCell id={`validate-item-collection-date-${index}`}>
                        {form?.collection_date ? moment(form.collection_date).format('ll') : ''}
                      </TableCell>
                      <TableCell id={`validate-item-total-amount-due-${index}`}>
                        PHP {numeral(summary?.sub_total || 0).format('0,0.00')}
                      </TableCell>
                      <TableCell id={`validate-item-account-view-${index}`}>
                        <Typography
                          id={`validate-item-action-${index}`}
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => transaction_type.includes('Advance') ? history.push(`/collection/distribution/validate/client/${cdf_number}?isAdvance=true`) : history.push(`/collection/distribution/validate/client/${cdf_number}`)}
                        >
                          Validate
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))
                }
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  )
}
