import instance from './instance'
const axiosClientSOA = instance('/b/cls')

export default class ClientJournalToSetupExports {
  static getClientJournalToSetupExports (params = [], { tile, type }) {
    return axiosClientSOA({
      url: `/billing/client-journal-setup/${type}/${tile}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
    })
  }
}
