import React from 'react'

import { Link, useHistory } from 'react-router-dom'
import { Typography,
  TextField,
  NativeSelect,
  Popper,
  Fade,
  Radio,
  RadioGroup,
  Backdrop,
  CircularProgress,
  FormControlLabel } from '@material-ui/core'
import Submit from '../Modal/Modal'
import Button from '@material-ui/core/Button'
import VisibilityIcon from '@material-ui/icons/Visibility'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import ReplyIcon from '@material-ui/icons/Reply'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit } from '@fortawesome/free-solid-svg-icons'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import * as moment from 'moment'

import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'

import { CurrencyFormatter, Currency } from 'Utilities/TextFormatter'

import FormDrawer from '../../../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../../../Components/EnhancedSearchComponent/EnhancedSearchComponent'
import classes from '../../CreateTransactionPage.module.scss'

import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
// TODO change to billing config codes once created
import BillingConfigCodesService from 'Services/Api/BillingAnalystServices'
import BillingClientInvoicesServices from 'Services/Api/BillingClientInvoicesServices'

import AutoComplete from 'SharedComponents/BicoAutoComplete/AutoComplete'

import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import CustomEnhancedTable from '../../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// transaction types and tax types
import data from '../Data'

import Sidebar from '../Helpers/Sidebar'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Transaction Type*'
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Account ID'
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Name'
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description*'
  },
  {
    id: 'pending-invoice-tax-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Tax Type*'
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Enter Taxable Amount*'
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: ''
  }
]

export default function SearchClientContract (props) {
  const history = useHistory()
  const { clientContract, setStep, handlBackToSelectClientContract, handlBackToSelectTransactionType, clientContractId, suggestions = [] } = props

  const billingCodeRegex = /([^\s]+)\s(.*)/

  const clientInfo = clientContract.data.client
  const billingInfo = clientContract.data.billing
  const contractInfo = clientContract.data.contract
  const [disabledSave, setDisabledSave] = React.useState(false)

  // Modal
  const [openSaveDialog, setOpenSaveDialog] = React.useState(false)
  const [openSubmitDialog, setOpenSubmitDialog] = React.useState(false)
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)
  const [submitStatus, setSubmitStatus] = React.useState(false)

  const pagination = {
    count: 0,
    rowsPerPage: 0,
    page: 0
  }
  const location =
  contractInfo.soa_send_to_address.unit + ' ' +
  contractInfo.soa_send_to_address.floor + 'F ' +
  contractInfo.soa_send_to_address.bldg_no + ' ' +
  contractInfo.soa_send_to_address.bldg_name + ' Bldg. ' +
  contractInfo.soa_send_to_address.street + ', ' +
  ' Brgy. ' + contractInfo.soa_send_to_address.brgy + ', ' +
  contractInfo.soa_send_to_address.region + ' ' +
  contractInfo.soa_send_to_address.zip_code

  var today = new Date()
  var dd = String(today.getDate()).padStart(2, '0')
  var mm = String(today.getMonth() + 1).padStart(2, '0') // January is 0!
  var yyyy = today.getFullYear()

  today = mm + '/' + dd + '/' + yyyy

  const listOfTransactionType = data.TransactionType.Invoice
  const listOfTaxType = data.TaxType

  const [billingConfigCodes, setBillingConfigCodes] = React.useState([])

  const [invoiceNumber, setInvoiceNumber] = React.useState('INV000000#')
  const [totalTaxableAmount, setTotalTaxableAmount] = React.useState(0)
  const [totalTax, setTotalTax] = React.useState(0)
  const [totalAmount, setTotalAmount] = React.useState(0)

  const [memberDetails, setMemberDetails] = React.useState({})
  const [editMemberDetails, setEditMemberDetails] = React.useState({})

  // TextField values
  const [transactionType, setTransactionType] = React.useState('')
  const [memberAccountId, setMemberAccountId] = React.useState('')
  const [memberName, setMemberName] = React.useState('')
  const [description, setDescription] = React.useState('')
  const [taxType, setTaxType] = React.useState('')
  const [taxableAmount, setTaxableAmount] = React.useState('')
  // edit Textfield values
  const [editTransactionType, setEditTransactionType] = React.useState('')
  const [editMemberAccountId, setEditMemberAccountId] = React.useState('')
  const [editMemberName, setEditMemberName] = React.useState('')
  const [editDescription, setEditDescription] = React.useState('')
  const [editTaxType, setEditTaxType] = React.useState('')
  const [editTaxableAmount, setEditTaxableAmount] = React.useState('')
  // TextField Error messages
  const [transactionTypeErrorMessage, setTransactionTypeErrorMessage] = React.useState('')
  const [descriptionErrorMessage, setDescriptionErrorMessage] = React.useState('')
  const [taxTypeErrorMessage, setTaxTypeErrorMessage] = React.useState('')
  const [taxableAmountErrorMessage, setTaxableAmountErrorMessage] = React.useState('')

  // Modal Messages
  const [title, setTitle] = React.useState('')
  const [confirmationText, setConfirmationText] = React.useState('')
  const [succesfulTitle, setSuccesfulTitle] = React.useState('')
  const [successfulText, setSuccessfulText] = React.useState('')
  const [buttonText, setButtonText] = React.useState('')

  const [backdrop, setBackdrop] = React.useState(false)

  const [fields, setFields] = React.useState([])

  // add field
  const handleAddField = () => {
    let check = true
    if (!transactionType) {
      setTransactionTypeErrorMessage('Please choose the transaction type')
      check = false
    }
    if (!description) {
      setDescriptionErrorMessage('Please enter Description')
      check = false
    }
    if (!taxType) {
      setTaxTypeErrorMessage('Please choose tax type')
      check = false
    }
    if (!taxableAmount || taxableAmount.includes('-')) {
      setTaxableAmountErrorMessage('Please enter the amount')
      check = false
    }

    if (check) {
      let taxPercentage = 0.12

      // TODO change to dynamic
      if (taxType === 'Withholding' || taxType === 'VAT Exempt' || taxType === 'VAT Zero Rated') {
        taxPercentage = 0
      }
      let taxAmount = taxableAmount * taxPercentage
      const newEntry = {
        memberDetails: memberDetails,
        billingCodeType: transactionType,
        memberAccountId: memberAccountId,
        memberName: memberName,
        description: description,
        taxType: taxType,
        taxAmount: taxAmount,
        taxableAmount: taxableAmount,
        canEdit: false
      }
      let tempFields = [...fields]
      tempFields.reverse()
      tempFields.push(newEntry)
      tempFields.reverse()
      // setFields(fields.concat(newEntry))
      setFields(tempFields)
      // tempFields.push(newEntry)
      let subTotal = 0
      let totalTaxAmount = 0
      for (const field of tempFields) {
        subTotal += parseFloat(field.taxableAmount)
        totalTaxAmount += parseFloat(field.taxAmount)
      }
      setTotalTaxableAmount(Currency(subTotal))
      setTotalTax(Currency(totalTaxAmount))
      setTotalAmount(Currency(subTotal + totalTaxAmount))
      // clear fields
      setTransactionType('')
      setMemberAccountId('')
      setMemberName('')
      setDescription('')
      setTaxType('')
      setTaxableAmount('')
      setTransactionTypeErrorMessage('')
      setDescriptionErrorMessage('')
      setTaxTypeErrorMessage('')
      setTaxableAmountErrorMessage('')
    }
  }
  // delete field
  const handleRemoveField = (index) => {
    setFields(entries => entries.filter((fields, i) => i !== index))
    let tempFields = fields
    tempFields.splice(index, 1)
    let subTotal = 0
    let totalTaxAmount = 0
    for (const field of tempFields) {
      subTotal += parseFloat(field.taxableAmount)
      totalTaxAmount += parseFloat(field.taxAmount)
    }
    setTotalTaxableAmount(Currency(subTotal))
    setTotalTax(Currency(totalTaxAmount))
    setTotalAmount(Currency(subTotal + totalTaxAmount))
  }
  const handleEditField = (index, status) => {
    let newArr = [...fields]
    newArr[index].canEdit = status
    if (status) {
      let codetype = newArr[index].billingCodeType
      setEditTransactionType(codetype)
      setEditMemberAccountId(newArr[index].memberAccountId)
      setEditMemberName(newArr[index].memberName)
      setEditDescription(newArr[index].description)
      setEditTaxType(newArr[index].taxType)
      setEditTaxableAmount(newArr[index].taxableAmount)
      setEditMemberDetails(newArr[index].memberDetails)
      setFields(newArr)
    } else {
      setEditTransactionType('')
      setEditMemberAccountId('')
      setEditMemberName('')
      setEditDescription('')
      setEditTaxType('')
      setEditTaxableAmount('')
      setEditMemberDetails('')
      setFields(newArr)
    }
  }
  // update field
  const handleUpdateField = (index) => {
    if (!editTransactionType || !editDescription || !editTaxableAmount || editTaxableAmount.toString().includes('-')) {
      setTransactionTypeErrorMessage('You have left an entry in the input fields. Please Perform appropriate action.')
    } else {
      let taxPercentage = 0.12
      // TODO change to dynamic
      if (editTaxType === 'Withholding' || editTaxType === 'VAT Exempt' || editTaxType === 'VAT Zero Rated') {
        taxPercentage = 0
      }
      let taxAmount = editTaxableAmount * taxPercentage
      setTransactionTypeErrorMessage('')
      let newArr = [...fields]
      newArr[index].canEdit = false
      newArr[index].memberDetails = editMemberDetails
      newArr[index].billingCodeType = editTransactionType
      newArr[index].memberAccountId = editMemberAccountId
      newArr[index].memberName = editMemberName
      newArr[index].description = editDescription
      newArr[index].taxType = editTaxType
      newArr[index].taxAmount = taxAmount
      newArr[index].taxableAmount = editTaxableAmount
      let subTotal = 0
      let totalTaxAmount = 0
      for (const field of newArr) {
        subTotal += parseFloat(field.taxableAmount)
        totalTaxAmount += parseFloat(field.taxAmount)
      }
      setTotalTaxableAmount(Currency(subTotal))
      setTotalTax(Currency(totalTaxAmount))
      setTotalAmount(Currency(subTotal + totalTaxAmount))
      setFields(newArr)
    }
  }

  // Submit Button, create invoice and submit for approval
  const handleSubmitted = async (transactionNumber) => {
    if (fields.length > 0) {
      await BillingClientInvoicesServices.assignAnalyst({ transaction_number: transactionNumber })
      await BillingClientInvoicesServices.routeInvoice({ transaction_number: transactionNumber, action: 'submit' })
      setBackdrop(false)
      setOpenSuccessDialog(true)
    } else {
      setSubmitStatus(false)
      setSuccesfulTitle('No Transaction Entries')
      setSuccessfulText('Make sure to add transaction entries before saving or submitting.')
      setOpenSuccessDialog(true)
    }
  }

  // Save Button, create pending invoice
  const handleSaveInvoice = async (button) => {
    // if there are entries
    if (fields.length > 0) {
      setBackdrop(true)
      let subTotal = 0
      let totalTaxAmount = 0
      let entityType = 'Client'
      let transcationType = 'Invoice'
      let transactionStatus = 'New'
      let submittedBy = 'User'
      let details = []
      let detail = {}

      const analystUserName = localStorage.getItem('username')
      for (const field of fields) {
        totalTaxAmount += parseFloat(field.taxAmount)
        let codetype = field.billingCodeType
        const match = billingCodeRegex.exec(codetype)
        const code = match[1]
        const name = match[2]
        subTotal += parseFloat(field.taxableAmount)
        detail = {
          memberDetails: field.memberDetails,
          memberAccountId: field.memberAccountId,
          billingCode: code,
          billingType: name,
          description: field.description,
          taxType: field.taxType,
          taxAmount: field.taxAmount,
          value: field.taxableAmount
        }
        details.push(detail)
      }
      let data = {
      // header
        clientContractId: clientContractId,
        subTotal: subTotal,
        taxType: clientContract.data.client.tax_type,
        taxAmount: totalTaxAmount,
        entityType: entityType,
        transcationType: transcationType,
        transactionNumber: invoiceNumber,
        transactionStatus: transactionStatus,
        submittedBy: analystUserName || submittedBy,
        // details
        details: details
      }
      const invoice = await BillingClientInvoicesServices.createInvoice({data: data})
      if (button === 'save') {
        setBackdrop(false)
        setOpenSuccessDialog(true)
      } else {
        handleSubmitted(invoice.data.data)
      }
    } else { // when there no entries present
      setSubmitStatus(false)
      setSuccesfulTitle('No Transaction Entries')
      setSuccessfulText('Make sure to add transaction entries before saving or submitting.')
      setOpenSuccessDialog(true)
    }
  }

  // For Modify Bill Run inclusion Popper
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'transitions-popper' : undefined
  const [value, setValue] = React.useState('Include in the upcoming bill run')
  const [errors, setErrors] = React.useState({})
  const handleChange = (event) => {
    setValue(event.target.value)
  }

  const handleOnChangeAutoSuggest = (args) => {
    const { value, isSuggestionClick, setter, field, isEdit } = args
    if (isSuggestionClick) {
      if (isEdit) {
        setEditMemberAccountId(value.value.memberId)
        setEditMemberName(value.value.name)
        setEditMemberDetails(value.member_details)
      } else {
        setMemberAccountId(value.value.memberId)
        setMemberName(value.value.name)
        setMemberDetails(value.member_details)
      }
      setErrors({})
    } else {
      let valid = false
      suggestions.map(res => {
        if (res.value[field] === value) {
          valid = true
          setErrors({})
        }
      })
      console.log(valid)
      if (!valid) {
        if (isEdit) {
          setErrors({...errors, [`edit${field}`]: 'Please enter valid Member Account'})
        } else {
          setErrors({...errors, [field]: 'Please enter valid Member Account'})
        }
      }
      setter(value)
    }
  }

  React.useEffect(() => {
    Promise.all([BillingConfigCodesService.getAllTransactionConfigCodes('Invoice')]).then(async values => {
      console.log(values[0].data.data)
      setBillingConfigCodes(values[0].data.data)
    })
  }, [])

  return (
    <div>
      <HeaderDetails>
        <Typography className={classes['header-details-create-transaction']} variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        id='form-drawer'
        drawerContent={
          <Sidebar clientInfo={clientInfo} contractInfo={contractInfo} billingInfo={billingInfo} location={location} handlBackToSelectClientContract={handlBackToSelectClientContract} />
        }
      >
        <div className={classes['modify-bill-run-inclusion']}>
          <div className={classes['modify-bill-run-inclusion-content']}>
            <Link id='view-billing-link' aria-describedby={id} onClick={handleClick} className={classes['view-link']}>
              <FontAwesomeIcon id='view-billing-icon' className={classes['view-icon']} icon={faEdit} />
              Modify Bill Run Inclusion
            </Link>
            <Popper style={{'z-index': '3000'}} id={id} open={open} anchorEl={anchorEl} transition>
              {({ TransitionProps }) => (
                <Fade {...TransitionProps} timeout={350}>
                  <div
                    style={{
                      padding: '10px',
                      'background-color': 'white',
                      'box-shadow': '5px 5px rgb(0,0,0,10%)'

                    }}
                  >
                    <div>
                      <span>Select Bill Run inlcusion for this transaction<span style={{'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer'}} onClick={e => handleClick(e)}>X</span></span>
                    </div>
                    <div>
                      <RadioGroup aria-label='gender' name='gender1' value={value} onChange={handleChange}>
                        <FormControlLabel value='Include in the upcoming bill run' control={<Radio color='primary' />} label='Include in the upcoming bill run' />
                        <FormControlLabel value='Postpone to the next bill run' control={<Radio color='primary' />} label='Postpone to the next bill run' />
                      </RadioGroup>
                    </div>
                    <div>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        onClick={e => handleClick(e)}
                        style={{color: '#3ab77d',
                          'background-color': 'white',
                          width: '100px',
                          'margin-left': '100px'
                        }}
                      >
                        Cancel
                      </Button>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        style={{color: 'white',
                          'background-color': '#3ab77d',
                          width: '100px',
                          'margin-left': '10px'
                        }}
                      >
                        Apply
                      </Button>
                    </div>
                  </div>
                </Fade>
              )}
            </Popper>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax Amount', 'Total Amount']}
          summaryValues={[
            totalTaxableAmount, totalTax, totalAmount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TICKET NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {invoiceNumber}
                <RemoveCircleIcon onClick={() => handlBackToSelectTransactionType()} id='view-billing-icon' color='secondary' className={classes['remove-icon']} />
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(today).format('MMM DD, YYYY')}

              </Typography>
              <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              />
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }} />
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable pagination={false} className={classes['create-invoice-table']} tableHeaders={tableHeaders} tableHeight={'325px'}>
          <TableBody>
            {/* Table Row for fields */}
            <TableRow hover tabIndex={-1}>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <NativeSelect
                  className={classes['select-transaction-type-select-field']}
                  error={false}
                  fullWidth={false}
                  onChange={e => { setTransactionType(e.target.value) }}
                  value={transactionType}

                >
                  <option aria-label='None' value='' />
                  {/* {listOfTransactionType.map((detail, detailIndex) => ( */}
                  {billingConfigCodes.map((detail, detailIndex) => (
                    <option value={detail.description}>{detail.description}</option>
                  ))}
                </NativeSelect>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center' style={{marginTop: '13px'}}>
                <div style={{marginTop: '16px'}}>
                  <AutoComplete
                    style={{width: '180px'}}
                    id='medical-partner-id'
                    required
                    className={classes['select-transaction-type-select-field']}
                    value={memberAccountId}
                    suggestions={suggestions}
                    field={'memberId'}
                    onChange={(value, isSuggestionClick) =>
                      handleOnChangeAutoSuggest({
                        value,
                        isSuggestionClick,
                        setter: setMemberAccountId,
                        field: 'memberId'
                      })
                    }
                    error={errors.memberId}
                  />
                </div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div style={{marginTop: '16px'}}>
                  <AutoComplete
                    id='medical-partner-id'
                    required
                    className={classes['select-transaction-type-select-field']}
                    value={memberName}
                    suggestions={suggestions}
                    field={'name'}
                    onChange={(value, isSuggestionClick) =>
                      handleOnChangeAutoSuggest({
                        value,
                        isSuggestionClick,
                        setter: setMemberName,
                        field: 'name'
                      })
                    }
                    error={errors.name}
                  />
                </div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  fullWidth={false}
                  onChange={e => { setDescription(e.target.value) }}
                  value={description}
                />
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <NativeSelect
                  className={classes['select-tax-type-select-field']}
                  error={false}
                  fullWidth={false}
                  onChange={e => { setTaxType(e.target.value) }}
                  value={taxType}
                >
                  <option aria-label='None' value='' />
                  {listOfTaxType.map((detail, detailIndex) => (
                    <option value={detail.name}>{detail.type}</option>
                  ))}
                </NativeSelect>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  style={{width: '100px'}}
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  type='number'
                  fullWidth={false}
                  InputProps={{ inputProps: { min: 0 } }}
                  onChange={e => { setTaxableAmount(e.target.value) }}
                  value={taxableAmount}
                />
              </TableCell>
              <TableCell style={{padding: '0px', width: '50px'}}
                className={classes['action-buttons-table-cell']} align='center'
              >
                <Link style={{color: 'green'}} onClick={() => { handleAddField() }}>+ Add</Link>
              </TableCell>
            </TableRow>
            {/* Table row For error message */}
            <TableRow style={{padding: '0px'}} tabIndex={-1}>
              <TableCell colSpan={3} className={classes['create-transaction-table-cell']} align='left'>
                <div style={{'font-size': '13px', color: 'red', padding: '0px', 'white-space': 'nowrap'}}>{transactionTypeErrorMessage}</div>
              </TableCell>
              {/* <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div>{''}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div>{''}</div>
              </TableCell> */}
              <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div style={{'font-size': '13px', color: 'red'}}>{descriptionErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div style={{'font-size': '13px', color: 'red'}}>{taxTypeErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div style={{'font-size': '13px', color: 'red'}}>{taxableAmountErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='right'>
                <div>{' '}</div>
              </TableCell>
            </TableRow>
            {fields.map((detail, detailIndex) => (
              <TableRow hover key={detailIndex}>
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <NativeSelect
                      className={classes['select-transaction-type-select-field']}
                      error={false}
                      fullWidth={false}
                      onChange={e => { setEditTransactionType(e.target.value) }}
                      value={editTransactionType}
                    >
                      <option aria-label='None' value='' />
                      {/* {listOfTransactionType.map((detail, detailIndex) => ( */}
                      {billingConfigCodes.map((detail, detailIndex) => (
                        <option value={detail.description}>{detail.description}</option>
                      ))}
                    </NativeSelect>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <div style={{marginTop: '16px'}}>
                      <AutoComplete
                        id='pending-invoice-member-id'
                        required
                        className={classes['select-transaction-type-select-field']}
                        value={editMemberAccountId}
                        suggestions={suggestions}
                        field={'memberId'}
                        onChange={(value, isSuggestionClick) =>
                          handleOnChangeAutoSuggest({
                            value,
                            isSuggestionClick,
                            setter: setEditMemberAccountId,
                            field: 'memberId',
                            isEdit: true
                          })
                        }
                        error={errors.editmemberId}
                      />
                    </div>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <div style={{marginTop: '16px'}}>
                      <AutoComplete
                        id='edit-pending-invoice-member-name'
                        required
                        className={classes['select-transaction-type-select-field']}
                        value={editMemberName}
                        field={'name'}
                        suggestions={suggestions}
                        onChange={(value, isSuggestionClick) =>
                          handleOnChangeAutoSuggest({
                            value,
                            isSuggestionClick,
                            setter: setEditMemberName,
                            field: 'name',
                            isEdit: true
                          })
                        }
                        error={errors.editname}
                      />
                    </div>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setEditDescription(e.target.value)}
                      value={editDescription}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <NativeSelect
                      className={classes['select-tax-type-select-field']}
                      error={false}
                      fullWidth={false}
                      onChange={e => { setEditTaxType(e.target.value) }}
                      value={editTaxType}
                    >
                      <option aria-label='None' value='' />
                      {listOfTaxType.map((detail, detailIndex) => (
                        <option value={detail.name}>{detail.type}</option>
                      ))}
                    </NativeSelect>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {detail.taxType}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setEditTaxableAmount(e.target.value)}
                      value={editTaxableAmount}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                    style={{padding: '0px', width: '50px'}}
                  >
                    <Typography style={{color: 'green', 'font-size': '11px', 'margin-bottom': '5px', cursor: 'pointer'}}
                      onClick={e => handleUpdateField(detailIndex)}>
                     Update
                    </Typography>
                    <Typography style={{color: 'red', 'font-size': '11px', cursor: 'pointer'}}
                      onClick={e => handleEditField(detailIndex, false)}>
                      Cancel
                    </Typography>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                    style={{padding: '0px', width: '50px'}}
                  >
                    <FontAwesomeIcon style={{color: 'green'}} icon={faEdit} onClick={() => { handleEditField(detailIndex, true) }} />
                    <RemoveCircleIcon
                      style={{color: 'red', padding: '8px 0 0 0'}}
                      id='view-billing-icon'
                      onClick={() => { handleRemoveField(detailIndex) }} />
                  </TableCell>
                }

              </TableRow>
            ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <Button
            id='close-button'
            variant='contained'
            color='primary'
            className={classes['button-negative']}
            onClick={e => history.push('/')}
          >
              Cancel
          </Button>
          <div className={classes['item-right-side']}>
            <Button
              id='save-button'
              variant='contained'
              color='primary'
              className={classes['button-positive']}
              disabled={disabledSave}
              onClick={e => {
                setOpenSaveDialog(true)
                setTitle('Confirm Saving')
                setConfirmationText('Are you sure you want to save the Invoice?')
                setSuccesfulTitle('Invoice Saved Successfully')
                setSuccessfulText('Invoice was successfully saved, you can edit and submit it by going to the Pending Invoice Tile.')
                setButtonText('Save')
                setSubmitStatus(true)
              }}
            >
                Save For Now
            </Button>
            <Button
              id='submitted-link'
              variant='contained'
              className={classes['button-positive']}
              onClick={e => {
                setOpenSubmitDialog(true)
                setTitle('Confirm Submission')
                setConfirmationText('Are you sure you want to submit the Invoice for approval?')
                setButtonText('Submit')
                setSuccesfulTitle('Invoice has been submmitted for approval')
                setSuccessfulText('Invoice was successfully submitted, you can edit and submit it by going to the Pending Invoice Tile.')
                setSubmitStatus(true)
              }}
            >
                Save and Submit
            </Button>
          </div>
        </div>
      </FormDrawer>
      {openSaveDialog === true ? (
        <Submit
          title={title}
          confirmationText={confirmationText}
          succesfulTitle={succesfulTitle}
          successfulText={successfulText}
          buttonText={buttonText}
          page={'createInvoice'}
          openDialog={openSaveDialog}
          setOpenDialog={setOpenSaveDialog}
          submitFunction={() => handleSaveInvoice('save')}
          openSuccessDialog={openSuccessDialog}
          setOpenSuccessDialog={setOpenSuccessDialog}
          submitStatus={submitStatus}
        />
      ) : (
        ''
      )}
      {openSubmitDialog === true ? (
        <Submit
          title={title}
          confirmationText={confirmationText}
          succesfulTitle={succesfulTitle}
          successfulText={successfulText}
          buttonText={buttonText}
          page={'createInvoice'}
          openDialog={openSubmitDialog}
          setOpenDialog={setOpenSubmitDialog}
          submitFunction={() => handleSaveInvoice('submit')}
          openSuccessDialog={openSuccessDialog}
          setOpenSuccessDialog={setOpenSuccessDialog}
          submitStatus={submitStatus}
        />
      ) : (
        ''
      )}
      {backdrop === true ? (
        <Backdrop className={classes.backdrop} open>
          <CircularProgress color='primary' />
        </Backdrop>
      )
        : (
          ''
        )}

    </div>
  )
}
