import instance from './instance'
const axios = instance('/b/cls')
const axios_root = instance('/')
const axiosSOA = instance('/b/s')
// const axiosMemberSOA = instance('/b/s')
const axiosCTL = instance('/b/ctl')
const axiosClient = instance('/client')
const axiosAnalyst = instance('/b/as')

export default class BillingSupervisorServices {

  static getAnalysts () {
    return axiosAnalyst({
      url: `/analysts/lists`,
      method: 'GET'
    })
  }

  static getAnalysts2 () {
    return axiosClient({
      url: `/api/users/list?filter={"where":{"group.name"%20:"BILLING_ANALYST"}}`,
      method: 'GET'
    })
  }

  static getClientLedgersToSetup (params) {
    return axios({
      url: '/billing/client-ledger-setup',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getClientLedgersToSetupTile (params) {
    return axios({
      url: '/billing/client-ledger-setup-tile',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static assignBillingAnalyst ({ analystId, data }) {
    return axios({
      url: `/billing/client-ledger-setup/assign/${analystId}/client`,
      method: 'PUT',
      data
    })
  }

  static getClientLedgerToSetup ({ _id }, params) {
    return axios({
      url: `/billing/client-ledger-setup/${_id}`,
      method: 'GET',
      params
    })
  }

  static getUsersByRole (role) {
    return axios_root({
      url: `/users/${role}`,
      method: 'GET'
    })
  }

  static getUsersByRoleV1 (params) {
    return axios_root({
      url: `/users/v1/${params.role}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getUserAssignedClients (params) {
    return axios_root({
      url: `/v1/user/${params.user_id}/client-contracts`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getUserInfo (user_id) {
    return axios_root({
      url: `/v1/user/${user_id}`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getClientTransactionLedgerEntries ({ userID, role }) {
    return axiosCTL({
      url: `/billing/client/transaction/entries/`,
      params: { userID: userID, role: role },
      method: 'GET'
    })
  }

  static getClientTransactionLedgerEntryContents (clientId) {
    return axiosCTL({
      url: `/billing/client/transaction/entries/view`,
      params: { corporate_id: clientId },
      method: 'GET'
    })
  }

  static getValidatedSOAs (params) {
    return axiosSOA({
      url: 'billing/client-soa/list-validated/pre-generate-soa',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getValidatedSOAsTile (params) {
    return axiosSOA({
      url: 'billing/client-soa/list-validated-tile/pre-generate-soa',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getSubmittedClientSOAsTile (params) {
    return axiosSOA({
      url: '/billing/client-soa/list-submitted-for-approval/pre-generate-soa-tile',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getSubmittedClientSOAs () {
    return axiosSOA({
      url: '/billing/client-soa/list-submitted-for-approval/pre-generate-soa',
      method: 'GET'
    })
  }

  static assignClientSOA ({ soaNumber }) {
    return axiosSOA({
      url: `/billing/client-soa/assign/${soaNumber}/pre-generate-soa`,
      method: 'PUT'
    })
  }

  // static assignMemberSOA ({ soaNumber }) {
  //   return axiosMemberSOA({
  //     url: `/billing/member/assign/${soaNumber}/pre-generate-soa`,
  //     method: 'PUT'
  //   })
  // }

  static routeSOA ({ soaNumber, action }) {
    return axiosSOA({
      url: `/billing/client-soa/route-ticket?action=${action}&soaNumber=${soaNumber}`,
      method: 'PUT'
    })
  }

  // static routeMemberSOA ({ soaNumber, action }) {
  //   return axiosMemberSOA({
  //     url: `/billing/member/route-ticket?action=${action}&soaNumber=${soaNumber}`,
  //     method: 'PUT'
  //   })
  // }
}
