import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import BillingClientInvoicesServices from 'Services/Api/BillingMemberInvoicesServices'
import handleViewport from 'react-in-viewport'
import ViewAll from '../../ViewAll2/ViewAll'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'

// Style
import classes from './ClientInvoicesToValidateTile.module.scss'

// Utilities
import moment from 'moment'
import BillingDebitMemoServices from "../../../../../../Services/Api/BillingDebitMemoServices";
import {config} from "../ViewAllConfig";
import InvoiceServices from "../../../../../../Services/Api/BillingAndCollection/InvoiceServices";

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const ClientInvoicesToValidateTile = props => {
  // Constants
  const { service = () => {} } = props

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'desc' : 'asc'}`,
      filters: list.filters,
      entityType: 'Client',
      status: 'forApproval',
      ...params
    }
    setParams(_params)

    InvoiceServices.getInvoices(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    const _params = {
      isTile: true,
      entityType: 'Client',
      status: 'forApproval',
      ...params
    }
    InvoiceServices.getInvoices(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Client Invoices to Validate Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 6, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Client Invoices To Validate'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) } }
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body && tileList.body.map((item, index) => (
              <TableRow>
                <TableCell style={{ paddingLeft: '6px' }}>{TruncateString(item.billing_client_contract[0].client.account_name, 50)}</TableCell>
                <TableCell>{item.billing_client_contract[0].billing.cycle.code}</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link to={`billing/client/invoice/validate/${item.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-invoice-modal'}
        config={config}
        tile={'clientInvoicesToValidate'}
        type='Client Invoices To Validate'
        title='List of Client Invoices To Validate'
        placeholder='Search List of Client Invoices To Validate'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['transaction.number', 'billing_client_contract.client.account_name', 'transaction.date|isDate', 'summary.sub_total|isNumber']}
      />
    </React.Fragment>
  )
}

ClientInvoicesToValidateTile.displayName = 'DashboardTileComponent'

export default ClientInvoicesToValidateTile
