import instance from '../instance'
const axios = instance('/c/crl')

export default class ReminderLetterServices {
  static async index(params) {
    return axios({
      url: '/collection/reminders-letter',
      method: 'GET',
      params
    })
  }

  static async store(data) {
    return axios({
      url: '/collection/reminders-letter',
      method: 'POST',
      data
    })
  }

  static async showPreview(letterNumber) {
    return axios({
      url: `/collection/reminders-letter/${letterNumber}/preview`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static async send(letterNumber, data) {
    return axios({
      url: `/collection/reminders-letter/${letterNumber}/send`,
      method: 'POST',
      data
    })
  }
}