import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Box from '@material-ui/core/Box';
import Select from '@material-ui/core/Select'

// CSS Classes
import classes from './DashboardTileComponent.module.scss'

// Security
import RBAC from 'Security/RBAC'

/**
 * Create dashboard tile component
 *
 * @param children child components
 * @param count count value to be displayed; keys: color, value
 * @param header tile header label; keys: color, value
 * @param title title string
 * @param viewList view list
 * @param onFilterChange change filter handler
 * @param isFilterShown flag for showing filter
 * @param filterMenu menu items to show on filter { date: [], default: 'default value' }
 */
const DashboardTileComponent = ({
  children,
  count,
  countSize = 'h4',
  header,
  title,
  subTitle,
  viewList,
  actionHeader = null,
  onFilterChange,
  isFilterShown = true,
  id,
  headerSize = 'h5',
  filterMenu = {data: [], default: ''}
}) => {
  const [filter, setFilter] = React.useState(filterMenu.default || '')
  const [subtitle, setSubtitle] = React.useState('')
  const handleFilterChange = event => {
    setFilter(event.target.value)
    if (typeof onFilterChange === 'function') onFilterChange(event.target.value)
  }

  // React.useEffect(() => {
  //   try {
  //     const sub
  //   } catch (error) {
      
  //   }
  // }, [])

  return (
    <Paper className={classes.root} id={id}>
      <div className={classes.wrapper}>
        <div className={classes.header}>
          {header && (
            <div className={classes.count}>
              <Typography variant={headerSize} className={`color-${header.color}`}>
                {header.value}
              </Typography>
            </div>
          )}

          <div className={classes['first-row']}>
            <div className={classes.count}>
              <Box style={{paddingLeft: '5px'}} fontWeight={550} fontSize='h4.fontSize' className={`color-${count.color}`} id={count.id}>
                {count.value || 0}
              </Box>
            </div>
            {isFilterShown && (
              <div className={classes.filter}>
                <span>
                  <FormControl>
                    <Select
                      className={classes['select']}
                      value={filter}
                      onChange={handleFilterChange}
                      id={`${id}-filter`}
                    >
                      {isFilterShown &&
                        filterMenu.data.length > 0 &&
                        filterMenu.data.map((menuItem, index) => (
                          <MenuItem value={menuItem.value} id={`${id}-menu-item-${index}`}>
                            <Typography className={classes.input}>{menuItem.text}</Typography>
                          </MenuItem>
                        ))}
                    </Select>
                  </FormControl>
                </span>
              </div>
            )}
          </div>

          <div className={classes['second-row']}>
            <div className={classes.title} style={{marginBottom: '15px'}}>
              <Box fontWeight={600} style={{paddingLeft: '5px'}} variant='subtitle2'>{title}</Box>
              {RBAC.hasPolicy({and: ['BC-017', 'BC-021']}) &&
                <Box fontWeight={400} style={{paddingLeft: '5px'}} variant='span'>{subTitle}</Box>
              }           
            </div>
            <div className={classes.action}>{actionHeader || ''}</div>
          </div>
        </div>

        <div className={classes.list}>
          {React.Children.map(children, child => {
            return child
          })}
        </div>

        <div className={classes.action}>{viewList || ''}</div>
      </div>
    </Paper>
  )
}

DashboardTileComponent.displayName = 'DashboardTileComponent'

export default DashboardTileComponent
