import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Disapprove from '../Disapprove/Disapprove'

// Components
import TextField from 'StandardComponents/TextField/CustomTextField'
import RemoveSOAEntriesValidator from './RemoveSOAEntriesValidator'

// Styles
import classes from './RemoveSOAEntries.module.scss'

export default function CustomizedDialogs (props) {
  const defaultFunction = function () {}
  const { openDialog, setOpenDialog, forDeletion, handleOnRemoveLineItem = defaultFunction } = props
  const [errors, setErrors] = React.useState({})
  const [value, setValue] = React.useState({reason: ''})

  const handleOnClose = () => {
    setOpenDialog(false)
  }

  const handleOnchange = value => {
    setValue({
      reason: value
    })
  }

  const validate = () => {
    const { error } = RemoveSOAEntriesValidator(value)
    setErrors({ ...error })
    return !!error
  }

  const onSuccessRemoval = () => {
    if (!validate()) {
      setOpenDialog(false) 
      handleOnRemoveLineItem(forDeletion)
    }
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '400px', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton aria-label='close' id='confirm-close-button' className={classes.button} onClick={handleOnClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='confirm-title' className={classes.title}>
              Remove Selected Entries from the SOA
            </Typography>
          </div>
        </DialogTitle>
        <DialogContent>
          <br />
          <br />
          <TextField
            id='remarks-approve-id'
            defaultValue={value.reason}
            onChange={event => handleOnchange(event.target.value)}
            label='Please enter your reason for removing the selected entries:'
            error={errors.reason}
            placeholder='Enter your reason for removing selected entries here'
          />
          <br />
          <br />
          <div className={classes.button}>
            <Button
              variant='outlined'
              id='soa-cancel-button'
              className={classes['button-negative']}
              onClick={handleOnClose}
            >
              No
            </Button>
            <Button
              variant='contained'
              id='soa-submit-button'
              className={classes['button-positive']}
              onClick={onSuccessRemoval}
            >
              Yes
            </Button>
          </div>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  )
}
