import Grid from '@material-ui/core/Grid'
import React from 'react'
import {
  Checkbox,
  Typography,
  IconButton,
  Button,
  MenuItem,
  Menu,
  ListItemText,
  TableRow,
  TableCell
} from '@material-ui/core'

// Icons
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import AddIcon from '@material-ui/icons/Add'

// Components
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import RadioGroup from './../../../../Components/FormComponents/RadioGroup/RadioGroup'
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable } from '../../../../Components/CustomTable/index'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

import { withStyles, makeStyles } from '@material-ui/core/styles'

// Config
const config = require('../../config/config.json')

// Mock
const mock = require('../../mock-data/soa-data.json')
const dataTable = require('../../mock-data/member-table-data.json')

const headCells = require('../../mock-data/member-table-head.js')

const useStyles = makeStyles(theme => ({
  title: {
    fontSize: '16px',
    fontWeight: 'bold'
  },
  details: {
    fontSize: '14px'
  }
}))

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5'
  }
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center'
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles(theme => ({
  root: {
    '&:focus': {
      backgroundColor: theme.palette.primary.main,
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        color: theme.palette.common.white
      }
    }
  }
}))(MenuItem)

export default function MemberAnalyzeDetailsComponent (props) {
  const { handlePageTitleChange = () => {} } = props
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)

  const [_state, _setState] = React.useState({
    body: dataTable,
    count: 20,
    filter: {},
    header: headCells.headCells,
    limit: 1,
    order: true,
    orderBy: 'account_no',
    page: 1,
    sort: ''
  })
  const [activeSlider, setActiveSlider] = React.useState(0)
  const [anchorEl, setAnchorEl] = React.useState(null)

  const muiClasses = useStyles()

  React.useState(() => {
    handlePageTitleChange('Create Collection Distribution')
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'JULIUS NARAG'}`,
      'create'
    ])
  }, [])

  const onClickBackSlider = steps => {
    setActiveSlider(steps)
  }

  const onClickNextSlider = steps => {
    setActiveSlider(steps)
  }

  const onClickBtnSOA = e => {
    setAnchorEl(e.currentTarget)
  }

  const onCloseBtnSOA = e => {
    setAnchorEl(null)
  }

  console.log('activeSlider', activeSlider)

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'cdf-no'} item lg={3}>
            <DetailTile id={'cdf-no'} label={'cdf no.'} text={_data.cdf_number} />
          </Grid>
          <Grid key={'member-name'} item lg={3}>
            <DetailTile label={'member name'} text={_data.member_name} />
          </Grid>
          <Grid key={'member-person-id'} item lg={3}>
            <DetailTile label={'person id'} text={_data.person_id} />
          </Grid>
          <Grid key={'member-id'} item lg={3}>
            <DetailTile label={'member id'} text={_data.member_id} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'member-account-name'} item lg={3}>
            <DetailTile label={'account name'} text={_data.account_name} />
          </Grid>
          <Grid key={'member-account-no'} item lg={3}>
            <DetailTile label={'account no.'} text={_data.account_number} />
          </Grid>
          <Grid key={'member-account-tin'} item lg={3}>
            <DetailTile label={'account tin'} text={_data.account_tin} />
          </Grid>
          <Grid key={'member-broker'} item lg={3}>
            <DetailTile label={'broker'} text={_data.broker} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'member-transaction-type-radio'} item lg={3}>
            <RadioGroup
              id='member-transaction-type'
              {...config.transaction_type}
              checked={_data.transaction_type}
              required
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'transaction_type', value: e.target.value }))
              }}
            />
          </Grid>
          <Grid key={'member-status'} item lg={3}>
            <DetailTile label={'status'} text={_data.status} />
          </Grid>
          <Grid key={'member-payment-type'} item lg={3}>
            <DetailTile label={'payment-type'} text={_data.payment_type} />
          </Grid>
          <Grid key={'member-location'} item lg={3}>
            <DetailTile label={'location'} text={_data.location} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container spacing={2}>
          <Grid item xs>
            <TextField
              id='member-contact-person'
              label='Contact Person'
              value={_data.contact_person}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_person'
                  })
                )
              }}
            />
          </Grid>
          <Grid item xs>
            <TextField
              id='member-contact-email'
              label='Contact Email'
              value={_data.contact_email}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_email'
                  })
                )
              }}
            />
          </Grid>
          <Grid item xs>
            <TextField
              id='member-contact-number'
              label='Contact No.'
              value={_data.contact_number}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_number'
                  })
                )
              }}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h5'>Collection Reference</Typography>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={12} md={10} lg={10}>
            <Grid container>
              <FormSlider
                activeSlider={activeSlider}
                details={mock}
                // removeData={}
              />
            </Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={2} lg={2}>
            <Grid container>
              <Grid style={{ minHeight: '50px' }}>
                {(activeSlider && activeSlider) > 0 ? (
                  <IconButton onClick={() => onClickBackSlider(activeSlider - 1)}>
                    <KeyboardArrowLeftIcon />
                  </IconButton>
                ) : (
                  ''
                )}
              </Grid>
              <Grid style={{ minHeight: '50px' }}>
                <IconButton onClick={() => onClickNextSlider(activeSlider + 1)}>
                  <KeyboardArrowRightIcon />
                </IconButton>
              </Grid>
            </Grid>
            <Grid container style={{ justifyContent: 'flex-end' }}>
              <Button style={{ background: '#3ab77d', color: '#fff' }} onClick={onClickBtnSOA}>
                <AddIcon style={{ fontSize: '12px' }} />
                <Typography style={{ paddingLeft: '5px' }}>ADD SOA</Typography>
              </Button>
              <StyledMenu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={onCloseBtnSOA}
                // PaperProps={{
                //   style: {
                //     maxHeight: ITEM_HEIGHT * 4.5,
                //     width: '150px'
                //   }
                // }}
              >
                {dataTable &&
                  dataTable.map((data, key) => (
                    <StyledMenuItem key={key}>
                      <ListItemText>
                        <Grid>
                          <Typography className={muiClasses.title}>{data.member_id}</Typography>
                        </Grid>
                        <Grid>
                          <Typography className={muiClasses.details}>{data.first_name}</Typography>
                        </Grid>
                        <Grid>
                          <Typography className={muiClasses.details}>{data.last_name}</Typography>
                        </Grid>
                      </ListItemText>
                    </StyledMenuItem>
                  ))}
              </StyledMenu>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1.5rem' }}>
        <CustomTable
          // hasLink
          showCheckBox
          data={_state}
          setData={_setState}
          showTextField
          body={_data =>
            _data.map(({ premium_fee, processing_fee, dental_fee, maternity_fee }, index) => (
              <TableRow key={`member-detail-list-item-${index}`}>
                <TableCell id={`account-no-${index}`}>
                  <Checkbox
                    onChange={() => {
                      console.log(`${index}`)
                    }}
                    // style={{ color: '#212a71' }}
                    color='inherit'
                    inputProps={{ 'aria-label': 'primary checkbox' }}
                  />
                </TableCell>
                <TableCell id={`member-detail-id-${index}`}>{index + 1}</TableCell>
                <TableCell id={`member-detail-premium-fee-${index}`}>{premium_fee}</TableCell>
                <TableCell id={`member-detail-processing-fee-${index}`}>{processing_fee}</TableCell>
                <TableCell id={`member-detail-dental-fee-${index}`}>{dental_fee}</TableCell>
                <TableCell id={`member-detail-maternity-fee-${index}`}>{maternity_fee}</TableCell>
                <TableCell id={`member-detail-ref-date-${index}`}>{maternity_fee}</TableCell>
              </TableRow>
            ))
          }
        />
      </Grid>
    </Grid>
  )
}
