import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import noop from 'lodash/noop'

import CustomDialog from 'SharedComponents/CustomDialog/CustomDialog'

SentDialog.propTypes = {
  open: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
}

export default function SentDialog({ open = false, onCancel = noop, onSubmit = noop, ...props }) {
  return (
    <CustomDialog
      open={open}
      title="Reminder Letter Successfully Sent"
      content={
        <div style={{ marginBottom: 20 }}>
          <Typography>
            The reminder letter was successfully to the Client's email and to the Online Portal
          </Typography>
        </div>
      }
      cancelBtn={false}
      onOk={onSubmit}
      {...props}
    />
  )
}
