import React from 'react'
import { Grid, Typography, IconButton } from '@material-ui/core'
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded'
import clsx from 'clsx'

import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'

import moment from 'moment'

import { makeStyles } from '@material-ui/core/styles'
import { useSelector, useDispatch } from 'react-redux'

// Style
import classes from './FormSlider.module.scss'

const numeral = require('numeral')

const FormSlider = props => {
  const _data = useSelector(state => state.CollectionForm)
  const defaultFunction = function () {}
  const {
    data = {
      cdf_soa: [],
      cdf_summary: {}
    },
    OnChanges = defaultFunction,
    OnRemove = defaultFunction,
    showForCollection = false,
    showSummary = false,
    isAdvance = false,
    disabled = false
  } = props

  const [slider, setSlider] = React.useState({
    count: 0,
    cut: 3,
    data: '',
    member: '',
    index: 0,
    selected: showSummary ? -1 : 0
  })

  React.useEffect(() => {
    setSlider(i => ({ ...i, count: data.cdf_soa?.length || 0 }), OnChanges(slider))
  }, [data.cdf_soa, slider.index, slider.selected])

  React.useEffect(() => {
    const soa_number = data.cdf_soa?.length ? data.cdf_soa[0].soa_number : null
    OnChanges({ ...slider, member: soa_number })
  }, [data])

  return (
    <Grid className={classes.root} container direction='row'>
      {showSummary && (
        <Grid item xs={2}>
          <Grid className={classes['summary-container']} container direction='row'>
            <Grid
              id='slider-summary-tile'
              className={`${classes['item-container']} ${classes['summary-wrapper']} ${
                slider.selected === -1 ? classes.active : ''
              }`}
              item
              onClick={e => {
                setSlider(p => ({ ...p, data: null, selected: -1 }))
              }}
            >
              <Grid container direction='column'>
                <Grid item xs>
                  <Grid container direction='row'>
                    <Typography
                      className={`${classes.title} ${slider.selected === -1 ? classes.active : ''}`}
                      variant='h6'
                    >
                      SUMMARY
                    </Typography>
                  </Grid>
                </Grid>
                {!isAdvance && (
                  <React.Fragment>
                    <Grid item xs>
                      <Typography className={`${classes.details} ${classes.highlight}`}>Total SOA Amount</Typography>
                    </Grid>
                    <Grid item xs>
                      <Typography className={`${classes.details} ${classes.highlight}`}>
                        PHP <span id='slider-summary-soa-amount'>{numeral(data.cdf_summary?.soa_summary?.sub_total + data.cdf_summary?.soa_summary?.tax_amount || 0).format('0,0.00')}</span>
                      </Typography>
                    </Grid>
                  </React.Fragment>
                )}
                <Grid item xs>
                  <Typography className={`${classes.details} ${classes.highlight}`}>Total For Collection Amount</Typography>
                </Grid>
                <Grid item xs>
                  <Typography className={`${classes.details} ${classes.highlight}`}>
                    PHP <span id='slider-summary-for-collection-amount'>{numeral(_data.total_fees ? _data.total_fees.grand_total : 0).format('0,0.00')}</span>
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      )}

      {slider.index !== 0 && (
        <Grid item>
          <Grid className={classes['action-container']} container direction='column' justify='flex-start'>
            <Grid item>
              <IconButton
                id='slider-action-previous'
                onClick={() => {
                  setSlider(i => ({ ...i, index: i.index - i.cut, selected: 0 }))
                }}
              >
                <ArrowBackIosIcon fontSize='large' />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
      )}

      <Grid item xs>
        {/* SOA Container */}
        <Grid container direction='row' spacing='5'>
          {/* SOA Item */}
          {data.cdf_soa &&
            data.cdf_soa.slice(slider.index, slider.index + slider.cut).map((soa, i) => (
              <Grid
                id={`slider-${soa.soa_number}-tile`}
                className={`${classes['item-container']} ${i === slider.selected ? classes.active : ''}`}
                item
                xs={4}
                onClick={e => {
                  setSlider(p => ({ ...p, data: soa.soa_number, member: soa.soa_number, selected: i }))
                }}
              >
                <Grid container direction='column'>
                  <Grid item xs>
                    <Grid container direction='row'>
                      <Grid item>
                        <Typography
                          id={`slider-${soa.soa_number}`}
                          className={`${classes.title} ${i === slider.selected ? classes.active : ''}`}
                          variant='h6'
                        >
                          {soa.soa_number}
                        </Typography>
                      </Grid>
                      {!disabled && (
                        <Grid className={classes['action-remove-container']} item>
                          <IconButton className={classes['action-remove']}>
                            <RemoveCircleRoundedIcon
                              id={`slider-action-remove-${soa.soa_number}`}
                              onClick={e => {
                                e.stopPropagation()
                                OnRemove(soa.soa_number)
                              }}
                            />
                          </IconButton>
                        </Grid>
                      )}
                    </Grid>
                  </Grid>
                  {showForCollection && (
                    <Grid item xs>
                      <Typography className={`${classes.details} ${classes.highlight} ${classes.bold}`}>
                        For Collection: PHP <span id={`slider-${soa.soa_number}-for-collection`}>{numeral(soa.grand_total || 0).format('0,0.00')}</span>
                      </Typography>
                    </Grid>
                  )}
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Balance: PHP <span id={`slider-${soa.soa_number}-balance`}>{numeral(
                        (soa.soa_summary?.sub_total + soa.soa_summary?.sub_total_tax || 0) - (soa.grand_total || 0)
                      ).format('0,0.00')}</span>
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      SOA Amount: PHP <span id={`slider-${soa.soa_number}-amount`}>{numeral(soa.soa_summary?.sub_total + soa.soa_summary?.sub_total_tax || 0).format('0,0.00')}</span>
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Period Covered: <span id={`slider-${soa.soa_number}-period`}>{moment(soa.soa_period?.start).format('ll')} - {moment(soa.soa_period?.end).format('ll')}</span>
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Due: <span id={`slider-${soa.soa_number}-due`}>{moment(soa.soa_due || null).format('ll')}</span>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            ))}
        </Grid>
      </Grid>
      {slider.index + slider.cut < slider.count && (
        <Grid item>
          <Grid className={classes['action-container']} container direction='column' justify='flex-start'>
            <IconButton
              id='slider-action-next'
              onClick={() => {
                setSlider(i => ({ ...i, index: i.index + i.cut, selected: 0 }))
              }}
            >
              <ArrowForwardIosIcon fontSize='large' />
            </IconButton>
          </Grid>
        </Grid>
      )}
    </Grid>
  )
}

export default FormSlider
