const EndorsedAccountPenaltyReducer = (state = {}, {type, payload}) => {
  switch (type) {
    case 'SET_ENDORSED_FIELDS':
      return payload

    case 'UPDATE_ENDORSED_FIELD':
      return (() => {
        const { value, field } = payload
        return { ...state, [field]: value }
      })()

    default:
      return state
  }
}

export default EndorsedAccountPenaltyReducer
