import Grid from '@material-ui/core/Grid'
import React, { useRef } from 'react'
import { Checkbox, Typography, Link, TableRow, TableCell } from '@material-ui/core'
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable } from '../../../../Components/CustomTable/index'
import SoaMenu from './../../../../Components/SoaMenu'

// Services
import moment from 'moment'

// Styles
import classes from './AnalyzeDetailsComponent.module.scss'

// Config
const headCells = require('./mock-data/table-head.js')

export default function AnalyzeDetailsComponent (props) {
  const defaultFunction = function () {}
  const {
    handleOnAddNewSOA = defaultFunction,
    handleOnAddSOAClicked = defaultFunction,
    handleOnRemoveMember = defaultFunction,
    handleOnFetchMembers = defaultFunction,
    data = {},
    availableSoa = [],
    member = [],
    isAdvance,
    taxInfo,
    disabled = false
  } = props

  const refContainer = useRef()

  const [_state, _setState] = React.useState({
    body: [],
    count: 0,
    filter: {},
    header: headCells.headCells,
    limit: 10,
    order: true,
    orderBy: 'account_no',
    page: 1,
    soa_number: '',
    sort: ''
  })

  const [selectedMembers, setSelectedMembers] = React.useState({})

  React.useEffect(() => {
    const { results, count, limit, page, soa_number } = member

    _setState(i => ({
      ...i,
      body: results || [],
      count,
      limit,
      page,
      soa_number
    }))
  }, [member])

  const handleOnRemoveSoa = params => {
    // TODO
    console.log(params)
  }

  const handleOnSelectAll = params => {
    const selected = {}
    _state.body.map(({ _id }, i) => {
      selected[_id] = params
    })
    setSelectedMembers(i => ({ ...i, ...selected }))
  }

  const handleRemoveMember = () => {
    const _data = {}
    _data.soa_number = _state.soa_number
    _data.member = []

    Object.keys(selectedMembers).map(key => {
      if (selectedMembers[key]) _data.member.push(key)
    })

    if (_data.member.length === 0) return false

    handleOnRemoveMember(_data)
  }

  React.useEffect(() => {
    refContainer.current.parentElement.parentElement.parentElement.parentElement.parentElement.parentElement.scrollTo(0,0)
  }, [])

  return (
    <Grid className={classes.root} container spacing={2} ref={refContainer}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h6' className={classes.bold}>Collection Reference</Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <Grid item xs>
            <FormSlider
              disabled={!!disabled}
              data={data}
              OnChanges={e => {
                handleOnFetchMembers(e.member)
              }}
              OnRemove={e => {
                handleOnRemoveSoa(e)
              }}
            />
          </Grid>
          {!disabled && (
            <Grid item>
              <Grid container direction='column' justify='flex-end' style={{ height: '100%' }}>
                <Grid item>
                  <SoaMenu
                    details={availableSoa || []}
                    onAddClicked={e => {
                      handleOnAddSOAClicked(e)
                    }}
                    onSelect={e => {
                      handleOnAddNewSOA(e)
                    }}
                  />
                </Grid>
              </Grid>
            </Grid>
          )}
        </Grid>
      </Grid>
      <Grid item xs={12} style={{ marginTop: '20px' }}>
        {!disabled && (
          <Link
            component='button'
            variant='body2'
            onClick={() => {
              handleRemoveMember()
            }}
          >
            <Grid container direction='row'>
              <Grid item>
                <Grid container direction='column' justify='center' style={{ height: '100%' }}>
                  <RemoveCircleRoundedIcon className={classes['remove-icon']} style={{ fontSize: '15px', marginRight: '5px' }} />
                </Grid>
              </Grid>
              <Grid item>
                <Typography className={classes['remove-text']}>Remove</Typography>
              </Grid>
            </Grid>
          </Link>
        )}
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <CustomTable
          handleSelectAll={e => {
            handleOnSelectAll(e.target.checked)
          }}
          showCheckBox={!disabled}
          data={_state}
          setData={_setState}
          showTextField
          body={_data =>
            _data.map(
              (
                { _id, account_no, last_name, first_name, middle_initial, renewal_date, end_date, plan_type_name },
                index
              ) => (
                <TableRow key={`detail-list-item-${index}`}>
                  {!disabled && (
                    <TableCell id={`account-no-${index}`}>
                      <Checkbox
                        checked={selectedMembers[_id] || false}
                        onClick={e => {
                          setSelectedMembers(i => ({ ...i, [_id]: e.target.checked }))
                        }}
                        color='inherit'
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                      />
                  </TableCell>
                  )}
                  <TableCell id={`detail-id-${index}`}>{index + 1}</TableCell>
                  <TableCell style={{ whiteSpace: 'nowrap' }} id={`detail-member-id-${index}`}>
                    {account_no}
                  </TableCell>
                  <TableCell id={`detail-last-name-${index}`}>{last_name}</TableCell>
                  <TableCell id={`detail-first-name-${index}`}>{first_name}</TableCell>
                  <TableCell id={`detail-middle-initial-${index}`}>{middle_initial}</TableCell>
                  <TableCell id={`detail-ref-date-${index}`}>{moment(renewal_date).format('ll')}</TableCell>
                  <TableCell id={`detail-end-date-${index}`}>{moment(end_date).format('ll')}</TableCell>
                  <TableCell id={`detail-contract-type-${index}`}>{plan_type_name}</TableCell>
                </TableRow>
              )
            )
          }
        />
      </Grid>
    </Grid>
  )
}
