import React from 'react'
import { Typography } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'

import CreateOutlinedIcon from '@material-ui/icons/CreateOutlined'
import FlagIcon from '@material-ui/icons/Flag'
import NotInterestedOutlinedIcon from '@material-ui/icons/NotInterestedOutlined'
import LocalPrintshopIcon from '@material-ui/icons/LocalPrintshop'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'

import RBAC from 'Security/RBAC'

// Styles
import classes from './ControlList.module.scss'

/**
 * ControlList
 * @param createCollectionDistribution true/false trigger for control items display
 * @param endorseForPenalty true/false trigger for control items display
 * @param suggestForSuspension true/false trigger for control items display
 * @param handleOnCreateCollectionDistributionClicked function handle for onClick
 * @param handleOnSuggestForSuspensionClicked function handle for onClick
 * @param handleOnEndorseForPenaltyClicked function handle for onClick
 */
export default function ControlList (props) {
  const defaultFunction = function () {}
  const {
    createCollectionDistribution = false,
    endorseForPenalty = false,
    suggestForSuspension = false,
    downloadGenerateReports = false,
    printGenerateReports = false,
    handleOnGenerateReportsDownloadClicked = defaultFunction,
    handleOnGenerateReportsPrintClicked = defaultFunction,
    handleOnCreateCollectionDistributionClicked = defaultFunction,
    handleOnSuggestForSuspensionClicked = defaultFunction,
    handleOnEndorseForPenaltyClicked = defaultFunction,
    soaNumber = '',
    type = ''
  } = props

  return (
    <React.Fragment>
      <div className={classes.root}>
        <Grid container direction='row' justify='flex-start'>
          {RBAC.hasPolicy('BC-083') && (
            <React.Fragment>
              {createCollectionDistribution && (
                <Grid item className={classes.action} onClick={handleOnCreateCollectionDistributionClicked}>
                  <CreateOutlinedIcon className={classes.icon} />
                  <Typography className={classes.label}>Create Collection Distribution</Typography>
                </Grid>
              )}
            </React.Fragment>
          )}
          {suggestForSuspension && (
            <Grid item className={classes.action} onClick={handleOnSuggestForSuspensionClicked}>
              <FlagIcon className={classes.icon} />
              <Typography className={classes.label}>Suggest for Suspension</Typography>
            </Grid>
          )}
          {endorseForPenalty && (
            <Grid item className={classes.action} onClick={handleOnEndorseForPenaltyClicked}>
              <NotInterestedOutlinedIcon className={classes.icon} />
              <Typography className={classes.label}>Endorse for Penalty</Typography>
            </Grid>
          )}

          {downloadGenerateReports && (
            <Grid item className={classes.action} onClick={handleOnGenerateReportsDownloadClicked}>
              <ArrowDownwardIcon
                fontSize={'small'}
                style={{ color: '#fff', borderRadius: '25px', background: '#48b77c' }}
                className={classes.icon}
              />
              <Typography className={classes.label}>Download Report</Typography>
            </Grid>
          )}

          {printGenerateReports && (
            <Grid
              item
              className={classes.action}
              onClick={() => handleOnGenerateReportsPrintClicked({ type: type, soaNumber: soaNumber })}
            >
              <LocalPrintshopIcon className={classes.icon} />
              <Typography className={classes.label}>Print Report</Typography>
            </Grid>
          )}
        </Grid>
      </div>
    </React.Fragment>
  )
}
