import React from 'react'
import { useHistory } from 'react-router-dom'
import Chart from 'react-apexcharts'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Services
import AgingSummaryServices from '../../../../../Services/Api/AgingSummaryServices'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this week', text: 'This Week' },
    { value: 'this month', text: 'This Month' },
  ],
  default: 'today',
}

const params = {
  category: '',
  count: 0,
  search: '',
  limit: 10,
  body: [],
  page: 1,
  filters: null,
  data: [],
  orderBy: '_id',
  order: false,
  sort: '',
  range: '',
}

const AgingSummaryTile = (props) => {
  const history = useHistory()
  const [chart, setChart] = React.useState({
    chart: {
      toolbar: {
        show: false,
      },
      type: 'bar',
      height: 400,
    },
    legend: {
      position: 'top',
      markers: {
        fillColors: ['#3929D3', '#34D0CF', '#3EDE1F', '#7778D8', '#C55CDF', '#D2DF05', '#F20E2A'],
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '95%',
      },
    },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: false,
    },
    xaxis: {
      categories: [' '],
    },
    yaxis: {
      labels: {
        formatter: (val) => {
          if (Math.abs(val) > 999 && Math.abs(val) < 999999)
            return Math.sign(val) * (Math.abs(val) / 1000).toFixed(1) + 'K'
          else if (Math.abs(val) > 999999) return Math.sign(val) * (Math.abs(val) / 1000000).toFixed(1) + 'M'
          else return Math.sign(val) * Math.abs(val).toFixed(1)
        },
      },
    },
    fill: {
      colors: ['#1E2071', '#21908C', '#269515', '#4D4AB2', '#7337A4', '#AAB006', '#C20744'],
      type: 'gradient',
      opacity: 1,
      gradient: {
        shade: 'dark',
        type: 'vertical',
        shadeIntensity: 0.2,
        opacityFrom: 0.8,
        opacityTo: 1,
        stops: [0, 100],
      },
    },
    tooltip: {
      y: {
        formatter: (val) => {
          if (Math.abs(val) > 999 && Math.abs(val) < 999999)
            return Math.sign(val) * (Math.abs(val) / 1000).toFixed(1) + 'K'
          else if (Math.abs(val) > 999999) return Math.sign(val) * (Math.abs(val) / 1000000).toFixed(1) + 'M'
          else return Math.sign(val) * Math.abs(val).toFixed(1)
        },
      },
    },
    series: [],
  })

  const changeTotalAmountDueName = (data) => {
    return data.map((item) => {
      return {
        name: item.name,
        data: [item.total_amount_due],
      }
    })
  }

  const fetchData = (params, tileParams = 'today') => {
    // TODO: Change when end-point is created
    AgingSummaryServices.getSummaryBarData({ type: 'account', params, tileParams }).then((results) => {
      const { ageSummary } = results.data.data

      const chartData = ageSummary.length ? ageSummary : null

      setChart((i) => ({ ...i, series: changeTotalAmountDueName(chartData) }))
    })
  }

  const onFilterChange = (value) => {
    fetchData(params, value)
  }

  React.useEffect(() => {
    fetchData(params)
  }, [])

  // Methods
  /**
   * Handle View All Link Behavior
   * @param route Link to where the page will redirect
   */
  const handleOnViewAll = () => {
    history.push('/collection/agingsummary/accounts')
  }

  return (
    <React.Fragment>
      <DashboardTile
        id="aging-summary-tile"
        count={{ color: 20, value: 'Client Aging Summary', id: 'aging-summary-tile-count' }}
        sizes={{ sm: 12 }}
        title=""
        isFilterShown={false} //Temporary workaround : Hide aging summary tile filter
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id="aging-summary-tile-view-list"
            variant="caption"
            className="bold text-link"
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Chart options={{ ...chart }} series={chart.series} type="bar" width={'100%'} height={250} />
      </DashboardTile>
    </React.Fragment>
  )
}

AgingSummaryTile.displayName = 'DashboardTileComponent'

export default AgingSummaryTile
