import React from 'react'
import Grid from '@material-ui/core/Grid'
import { useDispatch, useSelector } from 'react-redux'
import upperFirst from 'lodash/upperFirst'
import DatePicker from '../../../../Components/FormComponents/DatePicker/DatePicker'
import DetailTile from '../../../../Components/FormComponents/DetailTile/DetailTile'
import TextField from '../../../../Components/FormComponents/TextField/CustomTextField'
import { UpdateSuspensionField } from './../../../../Store/actions'

export default function CollectionHeader(props) {
  const dispatch = useDispatch()
  const form = useSelector(state => state.Suspension)
  
  return (
    <Grid container>
      <Grid item xs={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'spn-no'} item>
            <DetailTile id={'spn-no'} label={'spn no.'} text={props.spn_number} />
          </Grid>
          <Grid key={'account-name'} item>
            <DetailTile label={'account name'} text={props.client?.account_name} />
          </Grid>
          <Grid key={'account-no'} item>
            <DetailTile label={'account no.'} text={props.client?.account_number} />
          </Grid>
          <Grid key={'account-tin'} item>
            <DetailTile label={'account tin'} text={props.client?.tin} />
          </Grid>
          <Grid key={'broker'} item>
            <DetailTile label={'broker'} text={props.client?.broker} />
          </Grid>
          <Grid key={'status'} item>
            <DetailTile label={'status'} text={upperFirst(props.status)} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key='effectivity-key' item>
            <DatePicker
              id='effectivity'
              label='Effectivity of Suspension'
              value={form.effectivity}
              onChange={event => {
                dispatch(
                  UpdateSuspensionField({ field: 'effectivity', value: event.toISOString() })
                )
              }}
              orientation={'column'}
              required
            />
          </Grid>
          <Grid key='reason-key' item xs>
            <Grid item xs>
              <TextField
                id='reason'
                label='Reason for Suspension'
                value={form.reason}
                onChange={e => {
                  dispatch(
                    UpdateSuspensionField({
                      value: e.target.value,
                      field: 'reason'
                    })
                  )
                }}
                required
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <TextField
          containerStyle={{ width: 'unset', flex: 1 }}
          multiline
          rows={3}
          id='remarks'
          label='Remarks'
          value={form.remarks || ''}
          onChange={event => {
            dispatch(UpdateSuspensionField({ field: 'remarks', value: event.target.value }))
          }}
          required
        />
      </Grid>
    </Grid>
  )
}
