import React from 'react'

// Store
import { useDispatch } from 'react-redux'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import LetterTemplateServices from 'Services/Api/CollectionMaintenance/LetterTemplateServices'

// Hooks
import { useHistory } from 'react-router-dom'
import useForm from 'Hooks/useForm'

// Components
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import PageFooter, { ActionButton } from 'SharedComponents/PageFooter'
import Select from '../../../Components/FormComponents/Select/Select'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import Editor from './Editor'
import { DEFAULT_VALUE } from './Editor/constants'
import PublishDialog from './PublishDialog'

// Utilities
import get from 'lodash/get'

// Styles
import classes from './CreateLetterTemplate.module.scss'

export default function CreateLetterTemplate() {
  const history = useHistory()
  const dispatch = useDispatch()

  const form = useForm({
    letter_type: '',
    entity_type: '',
    name: '',
    description: '',
    template: DEFAULT_VALUE
  })

  const [publishing, setPublishing] = React.useState(false)
  const [uploadedImage, setUploadedImage] = React.useState(null)

  function handleSave() {
    storeTemplate()
  }

  function handlePublish() {
    setPublishing(true)
  }

  function handlePublishCancelled() {
    setPublishing(false)
  }

  function handlePublishConfirmed() {
    storeTemplate({ _status: 'published' })
  }

  async function storeTemplateImage(image) {
    try {
      const { data } = await LetterTemplateServices.storeImages({ images: [image] })
      const { images } = data.data

      if (images.length === 0) {
        throw new Error('No image uploaded')
      }

      setUploadedImage(images[0])
    } catch (error) {
      console.log(`Error storing template image attachment: ${error}`)
    }
  }

  async function storeTemplate(params = {}) {
    try {
      LoadingService.loading(true)

      const response = await LetterTemplateServices.store({
        ...form.values,
        template: JSON.parse(form.values.template),
        ...params
      })

      LoadingService.loading(false)

      dispatch(
        SnackbarService.set({
          open: true,
          title: 'Success!',
          message: response.data.data,
          severity: 'success'
        })
      )

      history.push('/collection/maintenance')
    } catch (error) {
      const { data } = error.response

      form.setErrors(data.message)

      if (params._status === 'published') {
        setPublishing(false)
      }

      LoadingService.loading(false)

      dispatch(
        SnackbarService.set({
          open: true,
          message: 'Something went wrong.',
          severity: 'error'
        })
      )
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance', 'createLetterTemplate'])
  }, [])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction="column" justify="space-between">
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant="h5">
                Create Letter Template
              </Typography>
            </Grid>
          </Grid>

          <Grid style={{ marginTop: 50 }} container>
            <Grid item xs={8}>
              <Grid container justify="space-between">
                <Grid item xs={5}>
                  <Select
                    id="letter-type"
                    label="Letter Type *"
                    name="letter_type"
                    value={form.values.letter_type}
                    onChange={form.onChange}
                    defaultItemText="Select Letter Type"
                    data={[
                      { value: 'reminder', text: 'Reminder' },
                      { value: 'suspension', text: 'Suspension' },
                    ]}
                    error={get(form.errors, 'letter_type')}
                  />
                </Grid>

                <Grid item xs={5}>
                  <Select
                    id="entity-type"
                    label="Category *"
                    name="entity_type"
                    value={form.values.entity_type}
                    onChange={form.onChange}
                    defaultItemText="Select Category"
                    data={[
                      { value: 'client', text: 'Client' },
                      { value: 'member', text: 'Member' },
                    ]}
                    error={get(form.errors, 'entity_type')}
                  />
                </Grid>
              </Grid>

              <Grid style={{ marginTop: 30 }} container justify="space-between">
                <Grid item xs={5}>
                  <TextField
                    id="template-name"
                    label="Template Name"
                    name="name"
                    placeholder="Enter letter template name"
                    value={form.values.name}
                    onChange={form.onChange}
                    required
                    error={get(form.errors, 'name')}
                  />
                </Grid>
              </Grid>

              <Grid container justify="space-between">
                <Grid item xs={12}>
                  <TextField
                    multiline
                    rows={3}
                    id="description"
                    label="Description"
                    name="description"
                    placeholder="Enter letter template description"
                    value={form.values.description}
                    onChange={form.onChange}
                  />
                </Grid>
              </Grid>

              <Grid container justify='space-between'>
                <Grid item xs={12}>
                  <Editor
                    label="Letter Template"
                    defaultValue={form.values.template}
                    onChange={value => form.updateValue('template', value)}
                    uploadedImage={uploadedImage}
                    onImageInserted={storeTemplateImage}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            actions={[
              <ActionButton onClick={handleSave}>Save</ActionButton>,
              <ActionButton onClick={handlePublish}>Publish</ActionButton>
            ]}
          />
        </Grid>
      </Grid>

      <PublishDialog
        open={publishing}
        onCancel={handlePublishCancelled}
        onSubmit={handlePublishConfirmed}
      />
    </div>
  )
}
