import instance from '../instance'
const axios = instance('/c/csl')

export default class ScheduleLetterServices {
  static async index(params) {
    return axios({
      url: '/collection/schedule-letter',
      method: 'GET',
      params
    })
  }

  static async store(data) {
    return axios({
      url: '/collection/schedule-letter',
      method: 'POST',
      data
    })
  }

  static async update(data) {
    return axios({
      url: `/collection/schedule-letter/${data.id}`,
      method: 'PUT',
      data
    })
  }

  static async delete(id) {
    return axios({
      url: `/collection/schedule-letter/${id}`,
      method: 'DELETE'
    })
  }
}