import instance from './instance'
const axios = instance('/c/cp')

export default class CollectionPenaltyServices {
  static async index(params) {
    return axios({
      url: `/collection/penalties/client`,
      method: 'GET',
      params
    })
  }

  static async store(data) {
    return axios({
      url: `/collection/penalties/client`,
      method: 'POST',
      data
    })
  }

  static async show(pNumber, params = {}) {
    return axios({
      url: `/collection/penalties/client/${pNumber}`,
      method: 'GET',
      params
    })
  }

  static update(pNumber, data) {
    return axios({
      url: `/collection/penalties/client/${pNumber}`,
      method: 'PUT',
      data
    })
  }

  static async getAvailableSoa(pNumber, params = {}) {
    return axios({
      url: `/collection/penalties/client/${pNumber}/available-soa`,
      method: 'GET',
      params
    })
  }

  static async getFees(pNumber, params = {}) {
    return axios({
      url: `/collection/penalties/client/${pNumber}/fees`,
      method: 'GET',
      params
    })
  }
}