const CollectionForm = (state = {}, { type, payload }) => {
  switch (type) {
    case 'SET_FIELDS':
      return payload

    case 'UPDATE_FIELD':
      return (() => {
        const { value, field } = payload
        return { ...state, [field]: value }
      })()

    case 'UPDATE_FIELD_DATA':
      return (() => {
        const { value, field, target } = payload
        return { ...state, [target]: { ...state[target], [field]: value } }
      })()

    default:
      return state
  }
}

export default CollectionForm
