import React from 'react'
import Button from '@material-ui/core/Button'
import { Grid, Typography } from '@material-ui/core'
import CloseIcon from '@material-ui/icons/Close'
import IconButton from '@material-ui/core/IconButton'
import isArray from 'lodash/isArray'
import isString from 'lodash/isString'

import TextField from '../../FormComponents/TextField/CustomTextField'

// Style
import classes from './Alert.module.scss'

function Paragraph ({ body = [] }) {
  return (
    <React.Fragment>
      {body.map(paragraph => (
        <Typography style={{ marginBottom: '1rem' }}>
          {paragraph}
        </Typography>
      ))}
    </React.Fragment>
  )
}

function ConfirmAlert (props) {
  const { onConfirm = () => {}, onCancel = () => {}, title, body = [] } = props

  return (
    <Grid className={classes.alert} container direction='column'>
      <Grid container direction='col' alignItems='center' justify='space-between'>
        <Grid className={classes.title} item xs={10}>
          <Typography variant='h6' className={classes.text} style={{ fontSize: '1.1rem' }}>
            {title}
          </Typography>
        </Grid>
        <Grid item>
          <IconButton onClick={onCancel}>
            <CloseIcon />
          </IconButton>
        </Grid>
      </Grid>

      <Grid className={classes.content} item xs={12}>
        <Grid className={classes.details} container direction='column'>
          <Grid className={classes['details-wrapper']} item xs={12}>
            {isString(body) ? (
              <Typography className={classes.text}>{body}</Typography>
            ) : isArray(body) ? (
              <Paragraph body={body} />
            ) : body}
          </Grid>
        </Grid>
      </Grid>

      <Grid className={classes.control} item xs={12}>
        <Grid className={classes['control-wrapper']} container direction='row' justify='space-around'>
          <Grid className={classes['control-container']} item xs={5}>
            <Button
              variant='outlined'
              size='medium'
              color='success'
              className={classes['button-outline-success']}
              onClick={() => {
                onCancel(false)
              }}
            >
              No
            </Button>
          </Grid>
          <Grid className={classes['control-container']} item xs={5}>
            <Button
              variant='contained'
              size='medium'
              color='success'
              className={classes['button-fill-success']}
              onClick={() => {
                onConfirm(true)
              }}
            >
              Yes
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

function OkAlert (props) {
  const { onOk = () => {}, title, text } = props

  return (
    <Grid className={classes.alert} container direction='column'>
      <Grid container direction='col' alignItems='center' justify='space-between'>
        <Grid className={classes.title} item xs={10}>
          <Typography variant='h6' className={classes.text} style={{ fontSize: '1.1rem' }}>
            {title}
          </Typography>
        </Grid>
        <Grid item>
          <IconButton onClick={onOk}>
            <CloseIcon />
          </IconButton>
        </Grid>
      </Grid>

      <Grid className={classes.content} item xs={12}>
        <Grid className={classes.details} container direction='column'>
          <Grid className={classes['details-wrapper']} item xs={12}>
            <Typography className={classes.text}>{text}</Typography>
          </Grid>
        </Grid>
      </Grid>

      <Grid className={classes.control} item xs={12}>
        <Grid className={classes['control-wrapper']} container direction='row' justify='flex-end'>
          <Grid className={classes['control-container']} item xs={5}>
            <Button
              variant='contained'
              size='medium'
              color='success'
              className={classes['button-fill-success']}
              onClick={() => {
                onOk(true)
              }}
            >
              Ok
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

