export function CollectionDistributionDialog (props) {
  const {
    firstParagraph = '',
    secondParagraph = '',
    thirdParagraph = '',
    showRemarksField = false,
    placeholder = '',
    labelRemarksField = '',
    required
  } = props
  return (
    <Grid>
      <Grid>
        <Typography gutterBottom>{firstParagraph}</Typography>
        <Typography style={{ padding: secondParagraph === '' ? '.25rem 0rem' : '1rem 0rem' }} gutterBottom>
          {secondParagraph}
        </Typography>
        <Typography style={{ padding: thirdParagraph === '' ? '0rem' : '0rem 0rem 0.75rem' }} gutterBottom>
          {thirdParagraph} {required ? '*' : ''}
        </Typography>
      </Grid>
      <Grid>{showRemarksField && <TextField label={labelRemarksField} placeholder={placeholder} noMargin />}</Grid>
    </Grid>
  )
}

