import React from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment'

// MATERIAL UI
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'

// SHARED COMPONENTS
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// UTILITIES
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function ListView(props) {
  const {
    config,
    contents,
    count,
    list,
    fetchData,
    params,
    setShowModal,
    fetchItemDetails,
    header,
    reportType
  } = props

  // LOCAL CONSTANTS
  const tableHeaders = config.header

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  // LOCAL METHODS
  const handleViewLogDetail = (params) => {
    setShowModal(true)
    fetchItemDetails(params)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }


  return (
    <CustomEnhancedTable
      count={count}
      changeView={changeView}
      tableHeaders={tableHeaders}
      pagination={pagination}
      textBoxHeight={40}
      search
      sort
      fetchData={fetchData}
      params={params}
      hasLink
    >
      <TableBody>
        {contents && contents.map((row, contentsIndex) => (
          <StyledTableRow key={row._id}>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {row.account_number}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center' style={{ whiteSpace: 'no-wrap' }}>
                {row.account_name}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {row.number_of_open_soa}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {row.age}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {row.collection_analyst}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='right' style={{ marginRight: 20}}>
                {CurrencyFormatter(row.total_amount_due)}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                <Link to={`/collection/agingsummary/client/${row.account_number}`} style={{ marginRight: 20, color: "#48b77c" }}>View</Link>
              </Typography>
            </StyledTableCell>
          </StyledTableRow>
        ))}
      </TableBody>
    </CustomEnhancedTable>
  )
}
