import React, { useRef } from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import queryString from 'query-string';

// SUPERVISOR
import FirstStepComponent from './components/AnalyzeDetailsComponent/AnalyzeDetailsComponent'
import SecondStepComponent from './components/CollectionBreakDownComponent/CollectionBreakDownComponent'
import ThirdStepComponent from './components/CollectionDetailsComponent/CollectionDetailsComponent'

// MEMBER
import MemberFirstStep from './_members/MemberAnalyzeDetailsComponent/MemberAnalyzeDetailsComponent'
import MemberSecondStep from './_members/MemberBreakdownComponent/MemberBreakdownComponent'
import MemberThirdStep from './_members/MemberMeansOfPaymentComponent/MemberMeansOfPaymentComponent'

// Components
import CollectionHeader from './../../Components/CollectionDistribution/CollectionHeader/CollectionHeader'
import PageFooter from './../../Components/PageFooter/PageFooter'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import { SubmitDialog, SuccessDialog } from './components/ActionDialog'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetFields, UpdateField } from './../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Styles
import classes from './CollectionDistributionPage.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Mock-data
const mock = require('./mock-data/collection-distribution.json')

export default function CollectionDistributionPage (props) {
  // send data in redux store
  const dispatch = useDispatch()
  const _formData = useSelector(state => state.CollectionForm)

  // Used to reference element
  const refContainer = useRef()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '' } = props.match.params
  const { isAdvance } = queryString.parse(props.location.search)
  const [advance, setAdvance] = React.useState(isAdvance === 'true')
  const [taxInfo, setTaxInfo] = React.useState({})
  const history = useHistory()

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: advance ? ['Analyze Details', 'Collection Details', 'Submit'] : ['Analyze Details', 'Collection Breakdown', 'Collection Details', 'Submit'],
    stepNumber: 1
  })

  const [actionDialog, setActionDialog] = React.useState({ clientSubmit: false, clientSuccess: false })
  const [pageData, setPageData] = React.useState({
    availableSoa: [],
    form: [],
    selectedFees: {}
  })
  const [pageTitle, setPageTitle] = React.useState('Create Collection Distribution')
  // const [autoSave, setAutoSave] = React.useState()

  // TODO AUTOSAVE
  // React.useEffect(() => {
  //   clearTimeout(autoSave)
  //   setAutoSave(
  //     setTimeout(() => {
  //       // TODO: Handle Auto Save
  //     }, 60000)
  //   )
  // }, [_formData])

  React.useEffect(() => {
    // Change view to start from top of page
    refContainer.current.parentElement.scrollTo(0, 0)
    PageTitleService(null)
    handleGetData()
  }, [])

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    if ((data_type && data_type) === 'client') {
      if (advance) {
        switch (stepper.stepNumber) {
          case 1:
            return <SecondStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          case 2:
            return <ThirdStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          case 3:
              return <ThirdStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          default:
            return ''
        }
      } else {
        switch (stepper.stepNumber) {
          case 1:
            return <FirstStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          case 2:
            return <SecondStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          case 3:
            return <ThirdStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          case 4:
            return <ThirdStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
          default:
            return ''
        }
      }
    } else if ((data_type && data_type) === 'member') {
      switch (stepper.stepNumber) {
        case 1:
          return <MemberFirstStep {...componentProp} />
        case 2:
          return <MemberSecondStep {...componentProp} />
        case 3:
          return <MemberThirdStep {...componentProp} />
        case 4:
          return <h4>Step 4</h4>
        default:
          return ''
      }
    }
  }

  const handlePageStatus = _params => {
    switch (_params.status) {
      case 'Disapproved':
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'pendingTransactionsList',
          'disapprovedCD'
        ])
        setPageTitle('Disapproved Collection Distribution')
        break
      default:
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'listOfAccountsWithAgingSummary',
          `clientAgingSummary:${_params.client.account_name || ''}`,
          advance ? 'createAdvance' : 'create'
        ])

        if (advance) {
          setPageTitle('Create Advance Collection Distribution')
        }
        break
    }
  }

  const handleGetData = () => {
    Promise.all([CollectionCDFServices.getCDF({ data_id, isAdvance: advance })])
      .then(res => {
        const [cdfServiceRes] = res
        const {
          client = {},
          cdf_fees = {},
          form = {},
          ref = {},
          status = null,
          tax_type,
          summary = {}
        } = cdfServiceRes.data.data
        setPageData(i => ({
          ...i,
          ...cdfServiceRes.data.data,
          reference: ref
        }))
        dispatch(SetFields({
          ...form,
          total_fees: { ...summary },
        }))

        CollectionCDFServices.getTaxValue(tax_type).then( res => {
          const { data } = res.data
          setTaxInfo(data[0])
        })

        handlePageStatus({ client,  status })
      })
      .catch(err => console.error(`Error Fetching CDF: ${err}`))
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handleOnAddSOAClicked = params => {
    CollectionCDFServices.getAvailableSoa({
      cdf_number: pageData.cdf_number
    })
      .then(res => {
        const { count, results } = res.data.data
        setPageData(i => ({ ...i, availableSoa: results }))
      })
      .catch(err => console.error(`Error Fetching Available SOA(s): ${err}`))
  }

  const handleCDFSubmitForApproval = () => {
    handleOnSave()
    LoadingService.loading(true)
    CollectionCDFServices.submit({ data: { cdf_number: data_id } })
      .then(res => {
        setActionDialog(i => ({ ...i, clientSubmit: false, clientSuccess: true }))
        LoadingService.loading(false)
      })
      .catch(e => {
        console.error(`Error submitting cdf: ${e}`)
        LoadingService.loading(false)
      })
  }

  const handleOnAddNewSOA = params => {
    LoadingService.loading(true)
    if (!params) {
      LoadingService.loading(false)
      return false
    }

    CollectionCDFServices.addSoa({
      data: {
        cdfNumber: pageData.cdf_number,
        cdfSoa: [params]
      }
    })
      .then(res => {
        LoadingService.loading(false)
        handleGetData()
      })
      .catch(err => {
        console.error(`Error Updating CDF: ${err}`)
        LoadingService.loading(false)
      })
  }

  const handleOnRemoveMember = params => {
    LoadingService.loading(true)
    if (!params) {
      LoadingService.loading(false)
      return false
    }
    CollectionCDFServices.removeMember({
      data: { member: params.member, soaNumber: params.soa_number },
      params: pageData.cdf_number
    })
      .then(res => {
        LoadingService.loading(false)
        handleGetData()
      })
      .catch(err => {
        console.error(err)
        LoadingService.loading(false)
      })
  }

  const handleOnSave = () => {
    const { formData, ...others } = _formData
    LoadingService.loading(true)
    CollectionCDFServices.updateCDF({ data: { ...others }, cdfNumber: pageData.cdf_number })
      .then(res => {
        LoadingService.loading(false)
        handleGetData()
      })
      .catch(err => {
        console.error(`Error Updating CDF: ${err}`)
        LoadingService.loading(false)
      })
  }

  const handleOnSelectSoaTab = params => {
    let _data = null
    if (!params.data) {
      _data = pageData?.cdf_fees?.cdf_summary || {}
      dispatch(
        UpdateField({
          value: {
            ..._data,
          },
          field: 'formData'
        })
      )
      return false
    }
    _data = pageData?.cdf_fees?.cdf_soa || []
    dispatch(
      UpdateField({
        value: {
          ..._data[params.selected] || {},
        },
        field: 'formData'
      })
    )
  }

  const handleOnFetchMembers = params => {
    LoadingService.loading(true)
    if (!params || params.length <= 0) {
      LoadingService.loading(false)
      return false
    }
    CollectionCDFServices.getCDFMember({
      cdfNumber: pageData.cdf_number,
      soaNumber: params
    })
      .then(res => {
        LoadingService.loading(false)
        const { count, results, limit, page } = res.data.data
        setPageData(i => ({ ...i, member: { count, results, limit, page, soa_number: params } }))
      })
      .catch(err => {
        console.error(err)
        LoadingService.loading(false)
      })
  }

  const handleOnCancelClicked = () => {
    history.push(`/collection/agingsummary/client/${pageData.client.account_number}`)
  }

  const pageProps = {
    isStepper: true,
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    showCollectionDistributionBtn: true,
    stepNumberCDToShowListBtn: stepper.stepDetails.length - 1
  }

  return (
    <div className={classes.root} ref={refContainer}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5' className={classes.bold}>
                { pageTitle }
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({
                handleOnAddNewSOA,
                handleOnAddSOAClicked,
                handleOnFetchMembers,
                handleOnRemoveMember,
                handleOnSelectSoaTab,
                handleOnSave,
                member: pageData.member || [],
                availableSoa: pageData.availableSoa,
                data: pageData.cdf_fees
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            {...pageProps}
            handleOnSubmitClicked={() => {
              handleStepChange(stepper.stepNumber + 1)
              setActionDialog(i => ({ ...i, clientSubmit: true }))
            }}
            handleOnCancelClicked={() => handleOnCancelClicked()}
            handleOnSaveClicked={() => handleOnSave()}
          />
        </Grid>
      </Grid>
      <SubmitDialog
        open={actionDialog.clientSubmit}
        handleOnAssign={() => handleCDFSubmitForApproval()}
        handleOnClose={e => {
          handleStepChange(stepper.stepNumber - 1)
          setActionDialog(i => ({ ...i, clientSubmit: e }))
        }}
        isAdvance={advance}
      />
      <SuccessDialog
        open={actionDialog.clientSuccess}
        handleOnClose={e => setActionDialog(i => ({ ...i, clientSuccess: e }))}
        handleOnOk={() => {
          history.push('/')
        }}
      />
    </div>
  )
}
