import React from 'react'
import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import Typography from '@material-ui/core/Typography'
import { TableContainer, Table, TableHead, TableBody, TableRow, TableCell } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import classes from './SOAModal.module.scss'

const StyledTableCell = withStyles(theme => ({
  root: {
    backgroundColor: `${theme.palette.common.white}!important`,
    border: 'none',
    paddingTop: `${0}!important`,
    paddingBottom: `${0}!important`
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    backgroundColor: `${theme.palette.common.white}!important`
  }
}))(TableRow)

const details = [{
  label: 'Account No:',
  value: '2019 7570 0001910'
},
{
  label: 'Period Covered',
  value: 'Oct 29, 2019 - Oct 28, 2020'
},
{
  label: 'Amount Due',
  value: 'PHP 7,982.00'
}]

const headers = [
  'Category',
  'Enrolees',
  'Membership Fee (per enrollee)',
  'Network Access Fee (per enrollee)',
  'Tax Type',
  'Tax Amount',
  'Total Amount'
]

const totalAmountDue = (rows) => {
  return rows.map(row => row.total_amount).reduce((sum, totalAmount) => sum + totalAmount, 0)
}

const SOAModal = ({ open, handleClose, soa }) => {

  const { data, details, soaNumber } = soa

  const total = totalAmountDue(data)

  return (
    <Dialog
      fullWidth
      maxWidth='100%'
      open={open}
      onClose={handleClose}
      aria-labelledby='alert-dialog-title'
      aria-describedby='alert-dialog-description'
    >
      <DialogTitle id='alert-dialog-title'>{`${soaNumber}`}</DialogTitle>
      <DialogContent>
        <DialogContentText id='alert-dialog-description'>
          <Grid container>
            <Grid item xs={6}>
              <TableContainer>
                <Table size='small'>
                  {details.map(({label, value}) => (
                    <StyledTableRow>
                      <StyledTableCell component='th' variant='head'>{label}</StyledTableCell>
                      <StyledTableCell>{value}</StyledTableCell>
                    </StyledTableRow>
                  ))}
                </Table>
              </TableContainer>
            </Grid>
            <Grid item xs='12' />
            <TableContainer>
              <Table className={classes['table-bordered']}>
                <TableHead>
                  {headers.map(header => (
                    <TableCell key={header}>{header}</TableCell>
                  ))}
                </TableHead>
                <TableBody>
                  {data.length && data.map(({
                    category,
                    enrollees,
                    membership_fee: membershipFee,
                    network_access_fee: networkAccessFee,
                    tax_type: taxType,
                    tax_amount: taxAmount,
                    total_amount: totalAmount
                  }) => (
                    <TableRow>
                      <TableCell>{category}</TableCell>
                      <TableCell>{enrollees}</TableCell>
                      <TableCell>{CurrencyFormatter(membershipFee)}</TableCell>
                      <TableCell>{networkAccessFee}</TableCell>
                      <TableCell>{taxType}</TableCell>
                      <TableCell>{CurrencyFormatter(taxAmount)}</TableCell>
                      <TableCell>{CurrencyFormatter(totalAmount)}</TableCell>
                    </TableRow>
                  ))}
                  <TableRow >
                    <TableCell colSpan={5} />
                    <TableCell colSpan={1} className={classes['total-amount']}>Total Amount Due</TableCell>
                    <TableCell >{CurrencyFormatter(total)}</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        </DialogContentText>
      </DialogContent>
    </Dialog>
  )
}

export default SOAModal
