import React from 'react'
import clsx from 'clsx'
import { Grid, Typography } from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'

import TextFieldCurrency from '../TextFieldCurrency/TextFieldCurrency'

const useStyles = makeStyles(theme => ({
  padding: {
    padding: '1rem 3rem'
  },
  widthSubTotal: {
    width: '50%'
  },
  align: {
    textAlign: 'right'
  }
}))

const SIZE_PADDING = 4

const SubTotalTile = props => {
  const { data = {}, subTotalHeader = '', subTotalLabel = '', subTotalTaxLabel = '', showSubTotalHeader } = props
  const muiClasses = useStyles()

  return (
    <React.Fragment>
      {showSubTotalHeader && (
        <Grid>
          <Typography variant='h5'>{subTotalHeader}</Typography>
        </Grid>
      )}
      <Grid container spacing={SIZE_PADDING} className={muiClasses.padding}>
        <Grid item className={muiClasses.widthSubTotal}>
          <TextFieldCurrency label={subTotalLabel} wrapperSize={'75%'} disabled value={data.sub_total || 0} />
        </Grid>
        <Grid item className={muiClasses.widthSubTotal}>
          <TextFieldCurrency label={subTotalTaxLabel} wrapperSize={'75%'} disabled value={data.sub_total_tax || 0} />
        </Grid>
      </Grid>
    </React.Fragment>
  )
}

export default SubTotalTile
