import instance from './instance'
const axios = instance('/c/cs')

export default class CollectionSuspensionServices {
  static async index(params) {
    return axios({
      url: `/collection/suspensions`,
      method: 'GET',
      params
    })
  }

  static async store(data) {
    return axios({
      url: `/collection/suspensions`,
      method: 'POST',
      data
    })
  }

  static async show(spnNumber, params = {}) {
    return axios({
      url: `/collection/suspensions/${spnNumber}`,
      method: 'GET',
      params
    })
  }

  static update(spnNumber, data) {
    return axios({
      url: `/collection/suspensions/${spnNumber}`,
      method: 'PUT',
      data
    })
  }

  static async getAvailableSoa(spnNumber, params = {}) {
    return axios({
      url: `/collection/suspensions/${spnNumber}/available-soa`,
      method: 'GET',
      params
    })
  }

  static async getFees(spnNumber, params = {}) {
    return axios({
      url: `/collection/suspensions/${spnNumber}/fees`,
      method: 'GET',
      params
    })
  }

  static async getMembers(spnNumber, params = {}) {
    return axios({
      url: `/collection/suspensions/${spnNumber}/members`,
      method: 'GET',
      params
    })
  }
}