import React from 'react'
import { useHistory } from 'react-router-dom'

// Components
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import CancelIcon from '@material-ui/icons/Cancel'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit } from '@fortawesome/free-solid-svg-icons'

import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'
import PageFooter from 'SharedComponents/PageFooter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TimePicker from 'SharedComponents/TimePicker'
import Select from '../../../Components/FormComponents/Select/Select'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// Hooks
import useForm from 'Hooks/useForm'

// Store
import { useDispatch } from 'react-redux'

// Services
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import ScheduleLetterServices from 'Services/Api/CollectionMaintenance/ScheduleLetterServices'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

// Utilities
import moment from 'moment'
import debounce from 'lodash/debounce'
import get from 'lodash/get'

// Styles
import { withStyles } from '@material-ui/core/styles'
import classes from './ScheduleLetters.module.scss'

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    width: '100%',
    paddingTop: 8,
    paddingBottom: 8,
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.grey,
    color: theme.palette.common.white
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover
    }
  }
}))(TableRow)

export default function ScheduleLetters() {
  const dispatch = useDispatch()
  const history = useHistory()

  const initialValue = {
    header: HEADERS,
    accountData: [],
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: '_id',
    filters: null,
    page: 1,
    sort: '',
    search: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [isFetching, setIsFetching] = React.useState(false)
  const [params, setParams] = React.useState({})

  const [clientContractList, setClientContractList] = React.useState([])
  const [accountNumberList, setAccountNumberList] = React.useState([])
  const [accountNameList, setAccountNameList] = React.useState([])

  const form = useForm({
    account: '',
    sending_time: null,
    days_prior: ''
  })

  async function createScheduleLetter() {
    try {
      await ScheduleLetterServices.store({
        ...form.values,
        sending_time: moment(form.values.sending_time).format('HH:mm')
      })

      form.reset()
      fetchData(params)
    } catch (error) {
      const { data } = error.response

      form.setErrors(data.message)

      dispatch(
        SnackbarService.set({
          open: true,
          message: 'Something went wrong.',
          severity: 'error'
        })
      )
    }
  }

  async function fetchData(params) {
    setIsFetching(true)

    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }

    setParams(_params)

    const response = await ScheduleLetterServices.index(_params)

    const { results } = response.data.data

    const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
    const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
    setList(i => ({ ...i, body: results, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    setIsFetching(false)
  }

  function editSchedule(row) {
    row.edit = true
    row.oldValue = {
      days_prior: row.days_prior,
      account_number: row.contract[0].client.account_number,
      account_name: row.contract[0].client.account_name
    }

    setList(prevList => ({ ...prevList, body: list.body }))
  }

  function cancelEditSchedule(row) {
    row.days_prior = row.oldValue.days_prior
    row.contract[0].client.account_number = row.oldValue.account_number
    row.contract[0].client.account_name = row.oldValue.account_name
    row.edit = false
    setList(prevList => ({ ...prevList, body: list.body }))
  }

  function saveEditSchedule(row) {
    const params = {
      id: row._id,
      accountNumber: row.contract[0].client.account_number,
      daysPriorDueDate: row.days_prior
    }

    ScheduleLetterServices.update(params).then(() => {
      row.edit = false
      setList(prevState => ({ ...prevState, body: list.body }))
    })
  }

  function removeSchedule(row) {
    ScheduleLetterServices.delete(row._id).then(() => {
      fetchData()
    })
  }

  function daysPriorOnChange(event, row) {
    event.persist()
    row.days_prior = event.target.value
    setList(prevState => ({ ...prevState, body: list.body }))
  }

  function accountOnChange(event, row) {
    event.persist()
    const accountName = clientContractList.find(item => item.accountNumber === event.target.value).accountName
    row.contract[0].client.account_name = accountName
    row.contract[0].client.account_number = event.target.value
    setList(prevState => ({ ...prevState, body: list.body }))
  }

  const mainSearchChange = debounce(e => {
    const params = {
      search: e.target.value,
      accessors
    }
    fetchData(params)
  }, 300)

  React.useEffect(() => {
    fetchData()

    const fetchContracts = async () => {
      const response = await BillingAnalystServices.getAllClientContract()
      const { data } = response.data

      setClientContractList(data.map(item => ({
        value: item.client.account_number,
        text: item.client.account_number + " - " + item.client.account_name,
        accountNumber: item.client.account_number,
        accountName: item.client.account_name
      })))

      setAccountNumberList(data.map(item => ({
        value: item.contract.id,
        text: item.client.account_number,
      })))

      setAccountNameList(data.map(item => ({
        value: item.contract.id,
        text: item.client.account_name,
      })))
    }

    fetchContracts()
  }, [])

  return (
    <>
      <Grid item xs={10} style={{ marginTop: '1rem' }}>
        <Grid container spacing={2}>
          <Grid item xs={3}>
            <Select
              id="account-number"
              label="Account No"
              name="account"
              value={form.values.account}
              onChange={form.onChange}
              data={accountNumberList}
              defaultItemText="Select Account Number"
            />
          </Grid>

          <Grid item xs={3}>
            <Select
              id="account-name"
              label="Account Name *"
              name="account"
              value={form.values.account}
              onChange={form.onChange}
              data={accountNameList}
              defaultItemText="Select Account Name"
              error={get(form.errors, 'account')}
            />
          </Grid>

          <Grid item xs={2}>
            <TimePicker
              id="sending-time"
              label="Select sending time"
              placeholder="08:00 AM"
              mask="__:__ _M"
              value={form.values.sending_time}
              onChange={date => form.updateValue('sending_time', date)}
              required
              error={get(form.errors, 'sending_time')}
            />
          </Grid>

          <Grid item xs={2}>
            <TextField
              id="days-prior"
              label="Days Prior"
              name="days_prior"
              placeholder="Enter no. of days"
              value={form.values.days_prior}
              onChange={form.onChange}
              required
              error={get(form.errors, 'days_prior')}
            />
          </Grid>

          <Grid item xs={2}>
            <br />
            <StyledButton onClick={createScheduleLetter} style={{ marginTop: 5 }}>
              Add
            </StyledButton>
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={10}>
        <Grid container className={classes.search}>
          <Grid item xs>
            <SearchTextComponent
              placeholder='Search from the list of letter schedules'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={10}>
        <Grid container>
          <Grid item xs>
            <CustomEnhancedTable
              tableHeaders={HEADERS}
              search
              sort
              fetchData={fetchData}
              changeView={fetchData}
              params={params}
              pagination={false}
            >
              <TableBody>
                {isFetching && (
                  <StyledTableRow>
                    <StyledTableCell component='th' colspan={10}>
                      <CircularLoading style={{ marginLeft: '-200px' }} />
                    </StyledTableCell>
                  </StyledTableRow>
                )}
                {!isFetching && list && list.body.map((row, contentsIndex) => (
                  <StyledTableRow key={contentsIndex}>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                          <Select
                            defaultItemText="Select Account Number"
                            disableDefaultItemText={true}
                            name="accountNumber"
                            value={row.contract[0].client.account_number}
                            onChange={(event) => accountOnChange(event, row)}
                            data={accountNumberList}
                            variant="standard"
                          />
                          : row.contract[0].client.account_number
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                          <Select
                            defaultItemText="Select Account Name"
                            disableDefaultItemText={true}
                            name="accountNumber"
                            value={row.contract[0].client.account_number}
                            onChange={(event) => accountOnChange(event, row)}
                            data={accountNameList}
                            variant="standard"
                          />
                          : row.contract[0].client.account_name
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                          <TextField
                            fullWidth
                            id="schedule"
                            type="number"
                            name="daysPriorDueDate"
                            value={row.days_prior}
                            onChange={(event) => daysPriorOnChange(event, row)}
                            InputLabelProps={{
                              shrink: true,
                            }}
                            InputProps={{ inputProps: { min: 1 } }}
                            variant="standard"
                          />
                          : row.days_prior
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='right' style={{ cursor: "pointer" }}>
                        {!row.edit ?
                          <React.Fragment>
                            <FontAwesomeIcon style={{ color: '#40b981', fontSize: '20px', marginBottom: '5px' }} icon={faEdit} onClick={() => { editSchedule(row) }} /> &nbsp;&nbsp;
                          {/* <EditTwoTone style={{color: "#40b981"}} onClick={() => editSchedule(row)} /> &nbsp;&nbsp; */}
                            <RemoveCircleIcon style={{ color: "#fd6663" }} onClick={() => removeSchedule(row)} />
                          </React.Fragment>
                          :
                          <React.Fragment>
                            <CheckCircleIcon style={{ color: "#40b981" }} onClick={() => saveEditSchedule(row)} /> &nbsp;&nbsp;
                          <CancelIcon style={{ color: "#fd6663" }} onClick={() => cancelEditSchedule(row)} />
                          </React.Fragment>
                        }
                      </Typography>
                    </StyledTableCell>
                  </StyledTableRow>
                ))}
              </TableBody>

            </CustomEnhancedTable>
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <PageFooter closeControl onClose={() => history.push('/')} />
      </Grid>
    </>
  )
}

const HEADERS = [
  {
    id: 'as-account-noe',
    numeric: false,
    disablePadding: false,
    label: 'Account No',
    search: true,
    sort: true,
    accessor: 'contract.client.account_number'
  },
  {
    id: 'as-account-name',
    numeric: false,
    disablePadding: false,
    label: 'Account Name',
    search: true,
    sort: true,
    accessor: 'contract.client.account_name'
  },
  {
    id: 'as-schedule',
    numeric: false,
    disablePadding: false,
    label: 'Schedule (no. of days prior due date)',
    search: true,
    sort: true,
    accessor: 'days_prior|isNumber'
  },
  {
    id: 'actions',
    numeric: false,
    disablePadding: false,
    label: ''
  }
]

const accessors = [
  'contract.client.account_number',
  'contract.client.account_name',
  'days_prior|isNumber'
]
