import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import CheckIcon from '@material-ui/icons/Check'
import Link from '@material-ui/core/Link'
import Radio from '@material-ui/core/Radio'
import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'
import queryString from 'query-string';

// Components
import { CustomTable as Table } from './../../../Components/CustomTable'
import PageFooter from './../../../Components/PageFooter/PageFooter'
import { AccountAssignDialog, AssignSuccessDialog, UserInfoDialog } from './components/ActionDialog'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionClientLedgerServices from 'Services/Api/CollectionClientLedgerServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import moment from 'moment'
import { useHistory } from 'react-router-dom'

// Styles
import classes from './ClientSetupViewList.module.scss'

// Config
import { ClientSetupViewListConfig } from '../config/config'

export default function ClientSetupViewList (props) {
  const { _id } = props.match.params
  const history = useHistory()

  const [list, setList] = React.useState({
    body: [],
    count: 0,
    filter: {},
    header: ClientSetupViewListConfig,
    limit: 10,
    order: true,
    orderBy: 'last_name',
    page: 1,
    role: 'COLLECTION_ANALYST',
    sort: ''
  })

  const [selectedUser, setSelectedUser] = React.useState('')

  const [showUserInfo, setShowUserInfo] = React.useState(false)
  const [assignedClients, setAssignedClients] = React.useState({ data: [], count: 0, collectionAnalyst: '' })
  const [clientDetails, setClientDetails] = React.useState({})
  const [pageAction, setPageAction] = React.useState({ assign: false, success: false })
  const [pageState, setPageState] = React.useState('setup')
  const { reAssign } = queryString.parse(props.location.search)

  React.useState(() => {
    LoadingService.loading(true)
    BreadcrumbsService.setSelectedItemsWithoutHome([
      'collectionDistributionDashboard',
      'listOfAccountToSetup',
      'setupAssignCollectionAnalyst'
    ])
    Promise.all([
      CollectionClientLedgerServices.getUsers({
        role: list.role,
        limit: list.limit,
        sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
        filter: list.filter
      }),
      CollectionClientLedgerServices.getClient({ _id })
    ])
      .then(res => {
        const [collectionUsers, clientDetails] = res
        const { count, results } = collectionUsers.data.data
        const { results: clientDetailsResults = {} } = clientDetails.data.data

        setList(i => ({ ...i, body: results, count }))
        setClientDetails(i => ({ ...i, ...clientDetailsResults }))
        LoadingService.loading(false)
      })
      .catch(e => {
        console.error(`Error fetching data: ${e}`)
        LoadingService.loading(false)
      })
  }, [])

  const handleOnAssignAccount = () => {
    LoadingService.loading(true)
    CollectionClientLedgerServices.assignCollectionAnalyst({
      data: {
        userId: selectedUser,
        clientDocumentId: _id,
        reAssign
      }
    })
      .then(res => {
        setPageAction(i => ({ ...i, assign: false, success: true }))
        LoadingService.loading(false)
      })
      .catch(e => console.error(`Error assigning analyst: ${e}`))
    LoadingService.loading(false)
  }

  const handleOnDialogClose = _params => {
    setShowUserInfo(_params)
  }

  const handleOnListChange = params => {
    LoadingService.loading(true)
    const _params = {
      role: list.role,
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filter: list.filter,
      ...params
    }

    CollectionClientLedgerServices.getUsers(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({ ...i, body: results, count, limit, page }))
      LoadingService.loading(false)
    })
  }

  const handleSelectUser = _params => {
    setSelectedUser(_params)
  }

  const handleOnViewMore = ({ collectionAnalyst, userId }) => {
    LoadingService.loading(true)
    CollectionClientLedgerServices.getUsersAccounts({ userId }).then(res => {
      const { count, results } = res.data.data
      setAssignedClients(i => ({ ...i, collectionAnalyst, userId, count, data: results }))
      LoadingService.loading(false)
    })
  }

  const handleInfoSearch = _params => {
    LoadingService.loading(true)
    CollectionClientLedgerServices.getUsersAccounts({ ..._params }).then(res => {
      const { count, results } = res.data.data
      setAssignedClients(i => ({ ...i, count, data: results }))
      LoadingService.loading(false)
    })
  }

  return (
    <React.Fragment>
      <Grid container className={classes.root} spacing={2}>
        <Grid item xs={12}>
          <Typography variant='h5'>Setup - Assign Collection Analyst </Typography>
        </Grid>
        <Grid item xs={12}>
          <Grid container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>{clientDetails.account_name}</Typography>
            </Grid>
            {/* Collection Instruction Field */}
            <Grid item xs={8}>
              <Grid container direction='row'>
                <Grid item xs={12}>
                  <Typography className={classes['header-text']}>Basic Information</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Grid container direction='row'>
                    <Grid item xs={6}>
                      <Grid container direction='row'>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            ACCOUNT NO.
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>
                            {clientDetails.account_number || ''}
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            ACCOUNT STATUS
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>
                            {clientDetails.account_status || ''}
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            PAYMENT TERM
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>
                            {clientDetails.payment_term || ''}
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            TAX TYPE
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>{clientDetails.tax_type || ''}</Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            ACCOUNT TIN
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>{clientDetails.account_tin || ''}</Typography>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item xs={6}>
                      <Grid container direction='row'>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            BROKER
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>{clientDetails.broker || ''}</Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={classes.text} color='textSecondary'>
                            Location
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Typography className={`${classes.text} value`}>
                            {`${clientDetails.location?.bldg_name || ''} ${clientDetails.location?.floor ||
                              ''} ${clientDetails.location?.unit || ''} ${clientDetails.location?.bldg_no ||
                              ''}, ${clientDetails.location?.street || ''}, ${clientDetails.location?.brgy ||
                              ''}, ${clientDetails.location?.city || ''}, ${clientDetails.location?.province ||
                              ''}, ${clientDetails.location?.region || ''}, ${clientDetails.location?.zip_code || ''}`}
                          </Typography>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={4}>
              <Grid container direction='row'>
                <Grid item xs={12}>
                  <Typography className={classes['header-text']}>HMO Information</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Grid container direction='row'>
                    <Grid item xs={6}>
                      <Typography className={classes.text} color='textSecondary'>
                        CONTRACT
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={`${classes.text} value`}>{clientDetails.contract_name}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={classes.text} color='textSecondary'>
                        CONTRACT TYPE
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={`${classes.text} value`}>{clientDetails.contract_type}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={classes.text} color='textSecondary'>
                        EFFECTIVITY DATE
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={`${classes.text} value`}>
                        {moment(clientDetails.effectivity_date).format('ll')}
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={classes.text} color='textSecondary'>
                        EXPIRATION DATE
                      </Typography>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography className={`${classes.text} value`}>
                        {moment(clientDetails.expiration_date).format('ll')}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container direction='row'>
            <Grid item style={{ marginRight: '10px' }}>
              <Button
                className={`${classes['page-action']} ${pageState === 'setup' ? classes.active : ''}`}
                onClick={() => {
                  setPageState('setup')
                }}
              >
                COLLECTION ANALYST
              </Button>
            </Grid>
            <Grid item>
              <Button
                className={`${classes['page-action']} ${pageState === 'information' ? classes.active : ''}`}
                onClick={() => {
                  setPageState('information')
                }}
                disabled={!clientDetails.collection_instructions}
              >
                COLLECTION INSTRUCTIONS
              </Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          {pageState === 'setup' && (
            <Grid container direction='row'>
              <Grid item xs>
                <Table
                  data={list}
                  setData={setList}
                  hasLink
                  showTextField
                  onDataListChange={e => handleOnListChange(e)}
                  body={_data =>
                    _data.map((item, index) => (
                      <TableRow key={`client-setup-list-item-${index}`}>
                        <TableCell id={`client-setup-item-select-${index}`}>
                          <Radio
                            id={`client-setup-item-select-radio-${index}`}
                            classes={{ root: classes.radio }}
                            checked={selectedUser === item._id}
                            onChange={e => handleSelectUser(e.target.value)}
                            value={item._id}
                          />
                        </TableCell>
                        <TableCell id={`client-setup-item-analyst-name-${index}`}>
                          <Typography className={`${classes.text} value`}>
                            {item.first_name} {item.last_name}
                          </Typography>
                        </TableCell>
                        <TableCell id={`client-setup-item-status-${index}`}>
                          <Typography className={`${classes.text} value`}>{item.main_role?.status}</Typography>
                        </TableCell>
                        <TableCell id={`client-setup-item-no-assigned-${index}`}>
                          <Typography className={`${classes.text} value`}>{item.clientsAssigned || 0}</Typography>
                        </TableCell>
                        <TableCell id={`client-setup-item-clients-assigned-${index}`}>
                          {item.clients && (
                            <Typography className={`${classes.text} value ${classes['no-overflow']}`}>
                              {item.clients.join(', ')}
                            </Typography>
                          )}
                        </TableCell>

                        <TableCell id={`client-setup-item-action-view-more`}>
                          <Link
                            className={`${classes.action} ${classes['view-more']}`}
                            component='button'
                            variant='body2'
                            onClick={() => {
                              setShowUserInfo(true)
                              handleOnViewMore({
                                collectionAnalyst: `${item.first_name} ${item.last_name}`,
                                userId: item._id
                              })
                            }}
                          >
                            View More
                          </Link>
                        </TableCell>
                      </TableRow>
                    ))
                  }
                />
              </Grid>
            </Grid>
          )}
          {pageState === 'information' && (
            <Grid className={classes['collection-instructions-container']} container direction='column'>
              <Grid item xs={12}>
                <Grid container direction='row'>
                  <Grid item xs={6}>
                    <Grid className={classes.container} container direction='column'>
                      <Grid item>
                        <Typography>PREFERRED MEANS OF PAYMENT</Typography>
                      </Grid>
                      {clientDetails.collection_instructions?.means_of_payment && (
                        <React.Fragment>
                          <Grid item>
                            {clientDetails.collection_instructions.means_of_payment.option &&
                            clientDetails.collection_instructions.means_of_payment.option.length > 0 ? (
                              <Typography>
                                <CheckIcon className={classes.icon} fontSize='small' />
                                {clientDetails.collection_instructions.means_of_payment.option}
                              </Typography>
                            ) : (
                              <Typography>No Preferred Means of Payment</Typography>
                            )}
                          </Grid>

                          {clientDetails.collection_instructions.means_of_payment.field_values &&
                            clientDetails.collection_instructions.means_of_payment.field_values.map(item => (
                              <React.Fragment>
                                <Grid item>
                                  <Typography className={`${classes.indent} ${classes.uppercase}`}>
                                    {item.name}
                                  </Typography>
                                </Grid>
                                <Grid item>
                                  <Typography className={classes.indent}>
                                    <CheckIcon className={classes.icon} fontSize='small' />
                                    {item.values.map(val => val)}
                                  </Typography>
                                </Grid>
                              </React.Fragment>
                            ))}
                        </React.Fragment>
                      )}
                      {!clientDetails.collection_instructions?.means_of_payment && (
                        <Grid item>
                          <Typography>N/A</Typography>
                        </Grid>
                      )}
                    </Grid>
                  </Grid>
                  <Grid item xs={6}>
                    <Grid className={classes.container} container direction='column'>
                      <Grid item>
                        <Typography>DETAILS FOR PAYMENT PICK-UP</Typography>
                      </Grid>
                      {clientDetails.collection_instructions?.details_for_payment && (
                        <Grid item>
                          <Typography className={classes.indent}>
                            {clientDetails.collection_instructions?.details_for_payment}
                          </Typography>
                        </Grid>
                      )}
                      {!clientDetails.collection_instructions?.details_for_payment && (
                        <Grid item>
                          <Typography className={classes.indent}>N/A</Typography>
                        </Grid>
                      )}
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={6}>
                <Grid className={classes.container} container direction='column'>
                  <Grid item>
                    <Typography>CONTACT PERSON FOR FOLLOW UPS </Typography>
                  </Grid>
                  {clientDetails.collection_instructions?.contact_person && (
                    <Grid item>
                      <Typography>
                        <CheckIcon className={classes.icon} fontSize='small' />
                        {clientDetails.collection_instructions?.contact_person}
                      </Typography>
                    </Grid>
                  )}
                  {!clientDetails.collection_instructions?.contact_person && (
                    <Grid item>
                      <Typography className={classes.indent}>N/A</Typography>
                    </Grid>
                  )}
                </Grid>
              </Grid>
            </Grid>
          )}
        </Grid>
        <Grid item xs={12}>
          {pageState === 'setup' && (
            <PageFooter
              isAssign
              handleOnCancelClicked={() => {
                history.goBack()
              }}
              handleOnSubmitClicked={() => {
                if (selectedUser.trim().length === 0) return false
                setPageAction(i => ({ ...i, assign: true }))
              }}
            />
          )}
        </Grid>
      </Grid>
      <UserInfoDialog
        data={assignedClients}
        open={showUserInfo}
        handleOnClose={e => handleOnDialogClose(e)}
        onDataListChange={e => handleInfoSearch(e)}
      />
      <AccountAssignDialog
        open={pageAction.assign}
        handleOnAssign={() => handleOnAssignAccount()}
        handleOnClose={e => {
          setPageAction(i => ({ ...i, assign: e }))
        }}
      />
      <AssignSuccessDialog
        open={pageAction.success}
        handleOnClose={e => setPageAction(i => ({ ...i, success: e }))}
        handleOnOk={() => {
          history.push('/')
        }}
      />
    </React.Fragment>
  )
}
