import React from 'react'
import { Typography } from '@material-ui/core'

// Style
import classes from './DetailTile.module.scss'

/**
 * Detail Tile
 * @param label String data displayed as label
 * @param orientation column/row orientation of Component
 * @param text String data displayed as text
 */
const DetailTile = props => {
  const { id, label, orientation = 'column', text } = props
  return (
    <React.Fragment>
      <div id={`${id}-detail-tile`} className={classes.root}>
        <div className={classes[orientation]}>
          <Typography variant='subtitle1' className={classes.subtitle}>{label}</Typography>
          <div>
            <Typography className={classes.description}>{text}</Typography>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default DetailTile
