import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
// TODO: Implement if Viewlist otherwise, remove
import ViewList from './../../ViewList2/ViewList'

// Style
import classes from './MemberInvoicesToValidateTile.module.scss'

// Utilities
import moment from 'moment'

import ViewAll from '../../ViewAll2/ViewAll'
import BillingClientInvoicesServices from "../../../../../../Services/Api/BillingMemberInvoicesServices";
import InvoiceServices from "../../../../../../Services/Api/BillingAndCollection/InvoiceServices";
// import data from 'Pages/Protected/BillingAndCollection/CollectionPages/ActivityLog/mock/data'

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const MemberInvoicesToValidateTile = props => {
  // Constants
  const { services = () => {} } = props

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    range: [],
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      entityType: 'Member',
      status: 'forApproval',
      ...params
    }
    setParams(_params)

    InvoiceServices.getInvoices(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    const _params = {
      isTile: true,
      entityType: 'Member',
      status: 'forApproval',
      ...params
    }
    InvoiceServices.getInvoices(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Client Invoices to Validate Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Member Invoices To Validate'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Member Name</TableCell>
              <TableCell>Account Name</TableCell>
              <TableCell>Billing Type</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body &&
              tileList.body.map((item, index) => (
                // Sample
                <TableRow>
                  <TableCell>
                    {item.invoice_details.member.first_name + ' ' + item.invoice_details.member.last_name}
                  </TableCell>
                  <TableCell>{item.billing_client_contract[0] ? TruncateString(item.billing_client_contract[0].client.account_name, 30) : null}</TableCell>
                  <TableCell>{item.invoice_details.computed_fees.billing_type}</TableCell>
                  <TableCell>
                    <Link to='/billing/member/invoice/validate/sample'>
                      <Typography variant='caption' className='text-link underline'>
                        Validate
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'member-invoice-modal'}
        config={config}
        tile={'invoicesSubmittedForApproval'}
        type='Approved Invoices'
        title='List of Member Invoices To Validate'
        placeholder='Search List of Member Invoices To Validate'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['transaction.number', 'billing_client_contract.client.account_name']}
      />
    </React.Fragment>
  )
}

MemberInvoicesToValidateTile.displayName = 'DashboardTileComponent'

export default MemberInvoicesToValidateTile
