import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import grey from '@material-ui/core/colors/grey';
import clsx from 'clsx';

const useStyles = makeStyles({
  reversedActive: { color: 'white' },
  reversedInactive: { color: grey[600] },
  nonReversedActive: { color: grey[900] },
  nonReversedInactive: { color: grey[500] },
});

const Button = React.forwardRef(({ title = '', active, reversed, className, ...props }, ref) => {
  const classes = useStyles();

  return (
    <Tooltip title={title} placement="top">
      <IconButton
        {...props}
        ref={ref}
        className={clsx('px-1 cursor-pointer', className, {
          [classes.reversedActive]: reversed && active,
          [classes.reversedInactive]: reversed && !active,
          [classes.nonReversedActive]: !reversed && active,
          [classes.nonReversedInactive]: !reversed && !active,
        })}
      />
    </Tooltip>
  )
});

Button.propTypes = {
  title: PropTypes.string,
  reversed: PropTypes.bool,
  active: PropTypes.bool,
  className: PropTypes.string,
};

Button.displayName = 'Button';

export default Button;