import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'
import BillingSOAServices from 'Services/Api/BillingSOAServices'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll3/ViewAll'

// Style
import classes from './MemberPreSOAsToValidateTile.module.scss'

// Utilities
import moment from 'moment'
import SOAService from "../../../../../../Services/Api/BillingAndCollection/SOAService";

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const MemberPreSOAsToValidateTile = props => {
  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: 'name',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})
  const current_user_id = localStorage.getItem('user_id')

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      entityType: 'Client',
      status: 'forApproval',
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['currentUserId'] = current_user_id
      _params['analystUser'] = localStorage.getItem('username')
    }
    setParams(_params)

    SOAService.getSoaList(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = params => {
    const _params = {
      entityType: 'Member',
      isTile: true,
      status: 'forApproval',
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['currentUserId'] = current_user_id
      _params['analystUser'] = localStorage.getItem('username')
    }
    SOAService.getSoaList(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = value => {
    fetchTileData({ range: rangeDate[value] })
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Member PreSOAs to Analyze Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Member Pre-SOAs to Validate'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => {
              setShow(true)
              fetchData({})
            }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Member Name</TableCell>
              <TableCell>Account Name</TableCell>
              <TableCell>Due Date</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body &&
              tileList.body.map((item, index) => (
                <TableRow>
                  <TableCell style={{ padding: '0' }}>{item.first_name + ' ' + item.last_name}</TableCell>
                  <TableCell style={{ padding: '0' }}>{TruncateString(item.account_name, 30)}</TableCell>
                  <TableCell>{DateFormatterByAbbreviation(item.due_date)}</TableCell>
                  <TableCell align={'right'} style={{ paddingRight: '0' }}>
                    <Link to={`/billing/soa/member/${item.soa_number}/${'supervisor'}`}>
                      <Typography variant='caption' className='text-link underline'>
                        Analyze
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-transaction-ledgers-to-setup-modal'}
        config={config}
        tile={'MemberPreSoaToValidate'}
        type='Member Pre-SOAs To Validate'
        title='List of Member Pre-SOAs To Validate'
        placeholder='Search List of Member Pre-SOAs To Validate'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['account_number', 'account_name', 'first_name', 'last_name', 'soa_number', 'billing_cycle']}
      />
    </React.Fragment>
  )
}

MemberPreSOAsToValidateTile.displayName = 'DashboardTileComponent'

export default MemberPreSOAsToValidateTile
