import React from 'react'

// Components
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import PageFooter, { ActionButton } from 'SharedComponents/PageFooter'
import PreviewStep from './PreviewStep'
import SendStep from './SendStep'

// Services
import ReminderLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'

// Utilities
import noop from 'lodash/noop'

export default function Page({ title, letterNumber, onSend = noop, onCancel = noop }) {
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Preview', 'Send'],
    stepNumber: 1
  })

  function handleStepChange(stepNumber) {
    setStepper(prevState => ({
      ...prevState,
      stepNumber,
      onStepChanged: stepNumber
    }))
  }

  return (
    <div>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item>
              <Typography id='suspension-page-header' variant='h5'>
                {title}
              </Typography>
            </Grid>

            <Grid item xs={6}>
              <FormStepper {...stepper} />
            </Grid>

            <Grid container>
              <Grid item xs={12}>
                {stepper.stepNumber === 1 && <PreviewStep pdfProps={{
                  fileName: `${letterNumber}.pdf`,
                  initialPageNumber: 1,
                  initialScale: 1.5,
                  service: ReminderLetterServices.showPreview(letterNumber),
                  enabled: true
                }} />}

                {stepper.stepNumber === 2 && <SendStep />}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <PageFooter
            actions={[<ActionButton onClick={onSend}>Send</ActionButton>]}
            multiPage
            currentStep={stepper.stepNumber}
            onStepChange={handleStepChange}
            onCancel={onCancel}
          />
        </Grid>
      </Grid>
    </div>
  )
}
