import React from 'react'

// Slate
import { useSlate } from 'slate-react'

// Hooks
import useForm from 'Hooks/useForm'

// Components
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import LinkIcon from '@material-ui/icons/Link'
import TextField from '../../../../../Components/FormComponents/TextField/CustomTextField'

// Styles
import { makeStyles } from '@material-ui/core/styles'
import greyColor from '@material-ui/core/colors/grey'

// Utilities
import get from 'lodash/get'
import isUrl from 'is-url'
import { insertLink, isLinkActive } from '../linkify'

const useStyles = makeStyles(() => ({
  root: {
    display: 'inline-block'
  },

  container: {
    display: 'flex',
    flexWrap: 'wrap',
    width: 400
  },
}))

export default function InsertLinkDialog() {
  const editor = useSlate()
  const classes = useStyles()
  const form = useForm({ url: '' })

  const [open, setOpen] = React.useState(false)

  function handleInsert() {
    const { url } = form.values

    if (isUrl(url)) {
      insertLink(editor, url)
      setOpen(false)
    } else {
      form.setErrors({ url: 'Please insert a valid URL' })
    }
  }

  function handleClose() {
    setOpen(false)
  }

  function handleClickOpen() {
    setOpen(true)
  }

  return (
    <div className={classes.root}>
      <Tooltip title="Insert a link" placement="top">
        <IconButton onClick={handleClickOpen}>
          <LinkIcon style={{ color: isLinkActive(editor) ? greyColor[900] : greyColor[500] }} />
        </IconButton>
      </Tooltip>

      <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>Insert a Link</DialogTitle>
        <DialogContent>
          <div className={classes.container}>
            <TextField
              id="url"
              label="URL"
              name="url"
              placeholder="Enter the URL"
              value={form.values.url}
              onChange={form.onChange}
              error={get(form.errors, 'url')}
            />
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleInsert} color="primary">
            Insert
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
