import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
// TODO: Implement if Viewlist otherwise, remove
import ViewList from './../../ViewList2/ViewList'

// Style
import classes from './TicketsWaitingForResponseTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    {value: 'me', text: 'Assigned to Me'}
  ],
  default: 'me'
}

const columnConfig = [
  { id: 'ticket-list-tile-table-header', name: '' },
  { id: 'ticket-list-tile-table-header', name: 'ID' },
  { id: 'ticket-list-tile-table-header', name: 'Due Date' },
  { id: 'ticket-list-tile-table-header', name: 'Account Name' }
]

// Mock data
const mock = require('./../mock-data/collection-ticketList.json')

const TicketsWaitingForResponseTile = props => {
  // Constants
  const { service = () => {} } = props
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Tickets Waiting for Response'
  })
  // TODO: Implement if Viewlist otherwise, remove
  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'Tickets Waiting for Response'
  })

  // Initialize / Watch
  React.useEffect(() => {
    const data = mock.slice(0, 5)
    setTile(i => ({...i, count: data.length, data}))
  }, [])

  // Methods
  // TODO: Implement if Viewlist otherwise, remove
  // TODO: Call Initial service here for viewlist data
  /**
   * API Call for tile data
   * @param service passed down to component as props for api-service
   * @param get service used to fetch data
   */
  const handleOnShowViewList = () => {
    setViewList(
      i => ({...i, show: true}),
      service()
    )
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{...tile.filterMenu}}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow key={`ticket-list-tile-table-row-${index}`} id={`ticket-list-tile-table-row-${index}`} className={`${classes[item.status]} ${classes['table-row']}`}>
                <TableCell id={`ticket-list-tile-table-cell-badge-${index}`} className={classes['table-cell']}>
                  <span className={classes.badge}>{index}</span>
                </TableCell>
                <TableCell id={`ticket-list-tile-table-cell-id-${index}`} className={classes['table-cell']}>{item.id || '00000'}</TableCell>
                <TableCell id={`ticket-list-tile-table-cell-due-date-${index}`} className={classes['table-cell']}>{moment(item.due_date).format('ll') || ''}</TableCell>
                <TableCell id={`ticket-list-tile-table-cell-account-name-${index}`} className={classes['table-cell']}>{item.account_name || ''}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

TicketsWaitingForResponseTile.displayName = 'DashboardTileComponent'

export default TicketsWaitingForResponseTile
