import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    {value: 'this-month', text: 'This Month'},
    {value: 'this-week', text: 'This Week'},
    {value: 'today', text: 'Today'}
  ],
  default: 'this-month'
}

const columnConfig = [
  { id: 'suspended-accounts-members-tile-table-header', name: 'Account Name' },
  { id: 'suspended-accounts-members-tile-table-header', name: 'Category' },
  { id: 'suspended-accounts-members-tile-table-header', name: 'Date Submitted' },
  { id: 'suspended-accounts-members-tile-table-header', name: '' }
]

// Mock data
const mock = require('./mock-data/collection-distribution.json')

const SuspendedAccountsMembersTile = props => {
  // Constants
  const { service = () => {} } = props
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Suspended Accounts or Members'
  })

  // Initialize / Watch
  React.useEffect(() => {
    const data = mock.slice(0, 4)
    setTile(i => ({...i, count: data.length, data}))
  }, [])

  // Method
  /**
   * Handle Transfer to page
   * @param location route/path to view all member aging summary page
   */
  const handleOnViewAll = () => {}
  return (
    <React.Fragment>
      <DashboardTile
        id='suspended-accounts-members-tile'
        count={{ color: 3, value: tile.count, id: 'suspended-accounts-members-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{...tile.filterMenu}}
        viewList={
          <Typography
            id='suspended-accounts-members-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow key={`suspended-accounts-members-tile-table-row-${index}`} id={`suspended-accounts-members-tile-table-row-${index}`}>
                <TableCell id={`suspended-accounts-members-tile-table-cell-account-name-${index}`}>{item.account_name || ''}</TableCell>
                <TableCell id={`suspended-accounts-members-tile-table-cell-category-${index}`}>{item.category || ''}</TableCell>
                <TableCell id={`suspended-accounts-members-tile-table-cell-date-approved-${index}`}>{moment(item.date_approved).format('ll') || ''}</TableCell>
                <TableCell id={`suspended-accounts-members-tile-table-cell-action-${index}`}>
                  {/* TODO: Change link when end-point and data is available */}
                  <Link to={`/`}>
                    <Typography variant='caption' className='text-link underline' id={`suspended-accounts-members-tile-view-${index}`}>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

SuspendedAccountsMembersTile.displayName = 'DashboardTileComponent'

export default SuspendedAccountsMembersTile
