import React from 'react'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { Link } from 'react-router-dom'
import TableHead from '@material-ui/core/TableHead'

import { Popover, Grid } from '@material-ui/core'

// Components
import Table from 'SharedComponents/Table/Table'

// Styles
import classes from './ViewMore.module.scss'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSort} from "@fortawesome/free-solid-svg-icons";
import CustomSearchTextComponent from "../CustomSearchTextComponent/CustomSearchTextComponent";
import _ from "lodash";

// const headCells = [
//   { id: 'accounts', numeric: false, center: false, disablePadding: true, label: 'Assigned Accounts' },
//   { id: 'terms', numeric: false, center: true, disablePadding: true, label: 'Payment Terms' },
//   { id: 'cycle', numeric: false, center: false, disablePadding: true, label: 'Billing Cycle' }
// ]
const headCells = [{ name: 'Assigned Accounts' }, { name: 'Payment Terms' }, { name: 'Billing Cycle' }]

export default function CustomizedDialogs (props) {
  const { viewMore, setViewMore, onViewMoreChange, fetchData, params, changeViewForViewMore } = props
  const tableHeaders = [
        {
          id: 'assigned-accounts',
          numeric: false,
          disablePadding: false,
          label: 'Assigned Accounts',
          width: 161,
          search: true,
          sort: true,
          accessor: 'client.account_name'
        },
        {
          id: 'payment-terms',
          numeric: false,
          disablePadding: false,
          label: 'Payment Terms',
          width: 150,
          search: true,
          sort: true,
          accessor: 'contract.payment_term'
        },
        {
          id: 'billing-cycle',
          numeric: false,
          disablePadding: false,
          label: 'Billing Cycle',
          width: 300,
          search: true,
          sort: true,
          accessor: 'billing.cycle.code'
        },
      ]

  const onChangeSearch = _.debounce((e) => {
    const value = {
      [e.target.name]: e.target.value
    }
    fetchData({
      ...params,
      filters: { ...params.filters, ...value },
    })
  }, 300)

  const onSort = (name) => {
    const order = !params.order
    fetchData({
      ...params,
      sort: `${name}|${order ? 'asc' : 'desc'}`,
      order: order,
      orderBy: name,
      page: 1,
      show: true,
    })
  }

  return (
    <Popover
      open={viewMore.show}
      anchorEl={viewMore.show}
      onClose={() => setViewMore(false)}
      anchorOrigin={{
        vertical: 'center',
        horizontal: 'right'
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right'
      }}
    >
      <div style={{ padding: '1rem' }}>
        <Grid container className={classes.header}>
          <Grid container justify='flex-end'>
            <IconButton aria-label='close' className={classes.button} onClick={() => setViewMore(false)}>
              <CloseIcon />
            </IconButton>
          </Grid>
          <Grid>
            <Typography variant='h6' className={classes.title} style={{ marginBottom: '10px' }}>
              {viewMore.title}
            </Typography>
            <Typography variant='subtitle1' className={classes.subtitle} style={{ marginBottom: '10px' }}>
              {viewMore.subtitle}
            </Typography>
          </Grid>
        </Grid>
        <TableHead>
          <TableRow >
            {tableHeaders.map(header => (
              <TableCell className={classes['th']}
                id={header.id}
                key={header.id}
                align={header.center ? 'center' : header.numeric ? 'right' : 'left'}
                padding={header.disablePadding ? 'none' : 'default'}
                sortDirection={false}
                width={header.width}
              > 
                {header.label}
                {header.label !== '' && header.sort ? (
                  <FontAwesomeIcon className={classes['sort-icon']} style={{ float: 'right', cursor: 'pointer' }} onClick={e => onSort(header.accessor)} icon={faSort} />
                ) : (
                  ''
                )}
                {header.label !== '' && header.search ? <CustomSearchTextComponent name={header.accessor} width={header.width} onChange={onChangeSearch} noIcon={true}/> : ''}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <Table
          TableContainerProps={{ style: { height: '220px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          body={
            viewMore.data === undefined
              ? ''
              : viewMore.data.map((row, index) => (
                
                <TableRow key={index}>
                  <TableCell width={161}>{row.client.account_name}</TableCell>
                  <TableCell width={150}>{row.contract.payment_term}</TableCell>
                  <TableCell width={300}>
                    {row.billing.cycle.code ? row.billing.cycle.code + ' - ' + row.billing.cycle.name : ''}
                  </TableCell>
                </TableRow>
               
              ))
          }
          count={viewMore.count}
          page={viewMore.page}
          limit={viewMore.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={changeViewForViewMore}
        />
      </div>
    </Popover>
  )
}
