export default function ValidatedListTransactionsPage (props) {
  const [validatedTransactions, setValidatedTransactions] = React.useState({
    body: mock,
    count: 20,
    filters: {},
    header: ValidatedListTransactionHeaders,
    limit: 1,
    order: true,
    orderBy: 'account_no',
    page: 1,
    sort: ''
  })

  const initialValue = {
    show: false,
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: '_id',
    order: true,
    sort: '',
  }


  const [isFetching, setIsFetchingState] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const fetchData = (params) => {
    setIsFetchingState(true)

    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      userOwned: true,
      category: 'SM',
      entityType: "Client",
      status: list.category,
      target_range: 'date_approved',
      user_owned: true,
      ...params
    }

    setParams(_params)
    
    CollectionSharedServices.getManyWithPagination(_params).then(res => {
      const { count, results, limit, page  } = res.data.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'listOfValidatedTransactions'])
    fetchData()
  }, [])

  return (
    <ViewAllPage
      config={config}
      tile='collectionValidatedTransactions'
      type='Validated Transactions'
      title='List of Validated Transactions'
      placeholder='Search List of Validated Transactions'
      contents={list.body}
      count={list.count}
      list={list}
      fetchData={fetchData}
      params={params}
      isFetching={isFetching}
      accessors={['category', 'transaction_type', 'cdf_number', 'contract.client.account_name', 'status', 'summary.grand_total|isNumber', 'date.approved|isDate','date.disapproved|isDate','date.void|isDate']}
    />
  )
}
