import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cl')
const axios_root = instance('/')

export default class CollectionClientLedgerServices {
  static assignCollectionAnalyst ({ data }) {
    return axios({
      url: `/client-ledger`,
      method: 'PATCH',
      data
    })
  }

  static getAssignedAccounts () {
    return axios({
      url: `/client-ledger/collection/accounts`,
      method: 'GET'
    })
  }

  static getClient (params) {
    return axios({
      url: `/client-ledger/collection/client`,
      method: 'GET',
      params
    })
  }

  static getClientDetails ({ accountNumber }) {
    return axios({
      url: `/client-ledger/account/${accountNumber}`,
      method: 'GET'
    })
  }

  static getClientLedgersToSetup (params) {
    return axios({
      url: '/client-ledger',
      method: 'GET',
      params
    })
  }

  static getUsers (params) {
    return axios({
      url: '/client-ledger/collection/users',
      method: 'GET',
      params
    })
  }

  static getUsersAccounts (params) {
    return axios({
      url: '/client-ledger/collection/users/assigned',
      method: 'GET',
      params
    })
  }

  static getUsersByRole (role) {
    return axios_root({
      url: `/users/${role}`,
      method: 'GET'
    })
  }
}
