import React from 'react'
import moment from 'moment'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Collapse from '@material-ui/core/Collapse'

import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import IconButton from '@material-ui/core/IconButton'
import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function CollapsibleRow (props) {
  const {
    index,
    id,
    data,
    objectKey,
    tableColumnOrder,
    subColumnOrder,
    offset,
    collapseAllState,
    collapseFunction,
    actionText,
    actionTo,
    param,
    subActionTo,
    subParam,
    subActionText,
    monetary_columns,
    emphasis_columns,
    multipleActions
  } = props

  const [collapse, setCollapse] = React.useState(true)
  const [iconImage, setIconImage] = React.useState(null)
  const add_icon = 'plus-square'
  const remove_icon = 'minus-square'

  React.useEffect(() => {
    handleTableCollapseAll()
  }, [collapseAllState])

  const handleTableCollapse = () => {
    setCollapse(!collapse)
    if (collapse) {
      setIconImage(add_icon)
    } else {
      setIconImage(remove_icon)
    }
  }

  const handleTableCollapseAll = () => {
    setCollapse(collapseAllState)

    if (collapseAllState) {
      setIconImage(remove_icon)
    } else {
      setIconImage(add_icon)
    }

    collapseFunction(collapse)
  }

  // const handleTableCollapse = () => {
  //   if (collapseAllState) {
  //     if (collapse) {
  //       setCollapse(collapseAllState)
  //       setIconImage(remove_icon)
  //     } else {
  //       setIconImage(add_icon)
  //       // setCollapse(!collapse)
  //     }
  //   } else {
  //     if (collapse) {
  //       setCollapse(collapseAllState)
  //       setIconImage(add_icon)
  //     } else {
  //       setCollapse(!collapse)
  //       setIconImage(remove_icon)
  //     }
  //   }
  //   collapseFunction(collapse)
  //   console.log(' set: to', collapse)
  // }

  const offsetCells = []
  for (var i = 0; i < offset; i++) {
    offsetCells.push(<TableCell colspan='1' />)
  }

  return (
    <React.Fragment>
      <TableRow key={index}>
        <TableCell id={`button-${id}`} className={classes.rowCollapseButton}>
          <IconButton onClick={handleTableCollapse} className={classes.rowCollapseIcon}>
            <FontAwesomeIcon icon={iconImage} />
          </IconButton>
        </TableCell>
        {tableColumnOrder.map((rowItem, rowIndex) => (
          <TableCell
            id={rowIndex}
            className={monetary_columns.includes(rowItem) ? classes.contentCellMonetary : classes.contentCell}
            // align={monetary_columns.includes(rowItem) ? 'right' : 'inherit'}
            style={
              emphasis_columns.includes(rowItem) === true ? { 'font-weight': 'bold' } : { 'font-weight': 'normal' }
            }
          >
            {monetary_columns.includes(rowItem) === true
              ? 'Php ' + CurrencyFormatter(data[rowItem])
              : moment(data[rowItem], moment.ISO_8601).isValid()
              ? DateFormatterByAbbreviation(data[rowItem])
              : data[rowItem]}
          </TableCell>
        ))}
        {actionTo != null && (
          <TableCell className={classes.contentCell}>
            <Link
              id={`link-table-${index}`}
              to={param == null ? actionTo : actionTo.concat('/', data[param])}
              className={classes.link}
            >
              {actionText}
            </Link>
          </TableCell>
        )}
        {multipleActions ? (
          <TableCell className={classes.contentCell}>
            {multipleActions.map((action, iteration) => (
              <Link
                id
                onClick={() => action.handleClick(data)}
                className={classes.link}
                style={{ textDecoration: 'underline', marginRight: '10px' }}
              >
                {action.label}
              </Link>
            ))}
          </TableCell>
        ) : null}
      </TableRow>
      {/* <Collapse in={collapse.show} className={classes.collapse}> */}
      {collapse &&
        data[objectKey].map((subItem, subIndex) => (
          <TableRow key={index + '-' + subIndex}>
            {subColumnOrder.map((colItem, colindex) =>
              subColumnOrder === ' ' ? (
                <TableCell className={classes.rowCollapseButton}> test</TableCell>
              ) : (
                <TableCell
                  className={monetary_columns.includes(colItem) ? classes.contentCellMonetary : classes.contentCell}
                  // align={monetary_columns.includes(colItem) ? 'right' : 'inherit'}
                >
                  {monetary_columns.includes(colItem) === true
                    ? 'Php ' + CurrencyFormatter(subItem[colItem])
                    : moment(subItem[colItem], moment.ISO_8601).isValid()
                    ? DateFormatterByAbbreviation(subItem[colItem])
                    : subItem[colItem]}
                </TableCell>
              )
            )}
            {actionTo != null && (
              <TableCell className={classes.contentCell}>
                <Link
                  color='primary'
                  id={`link-table-sub-${index}`}
                  to={subParam == null ? subActionTo : subActionTo.concat('/', subItem[subParam])}
                  className={classes.link}
                >
                  {subActionText}
                </Link>
              </TableCell>
            )}
            {multipleActions ? (
              <TableCell className={classes.contentCell}>
                {multipleActions.map((action, iteration) => (
                  <Link
                    id
                    onClick={() => action.handleClick(subItem)}
                    className={classes.link}
                    style={{ textDecoration: 'underline', marginRight: '10px' }}
                  >
                    {action.label}
                  </Link>
                ))}
              </TableCell>
            ) : null}
          </TableRow>
        ))}
      {/* </Collapse> */}
    </React.Fragment>
  )
}
