import React from 'react'
import { useHistory } from 'react-router-dom'

// Components
import Page from './components/Page'
import SendDialog from './components/SendDialog'
import SentDialog from './components/SentDialog'

// Hooks
import useToggle from 'Hooks/useToggle'

// Store
import { useDispatch } from 'react-redux'

// Services
import ReminderLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'

export default function SendReminderLetter(props) {
  const history = useHistory()
  const dispatch = useDispatch()
  const sendToggle = useToggle()
  const sentToggle = useToggle()
  const letterNumber = props.match.params.letter_number

  async function send() {
    try {
      LoadingService.loading(true)

      const response = await ReminderLetterServices.send(letterNumber)

      LoadingService.loading(false)

      dispatch(
        SnackbarService.set({
          open: true,
          title: 'Success!',
          message: response.data.data,
          severity: 'success'
        })
      )

      sendToggle.close()
      sentToggle.open()
    } catch (error) {
      console.log(`Error sending reminder letter: ${error}`)
      LoadingService.loading(true)
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['reminderLettersList', 'sendReminderLetter'])
  }, [])

  return (
    <React.Fragment>
      <Page
        title="Reminder Letter to Send"
        letterNumber={letterNumber}
        onSend={() => sendToggle.open()}
        onCancel={() => history.push('/')}
      />

      <SendDialog
        open={sendToggle.show}
        onSubmit={send}
        onCancel={() => sendToggle.close()}
      />

      <SentDialog
        open={sentToggle.show}
        submitLabel="OK"
        onSubmit={() => history.push('/')}
      />
    </React.Fragment>
  )
}
