import React from 'react'
import _ from 'lodash'
// import { makeStyles } from '@material-ui/core/styles'
import TableContainer from '@material-ui/core/TableContainer'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Box from '@material-ui/core/Box'
import FirstPageIcon from '@material-ui/icons/FirstPage'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'
import LastPageIcon from '@material-ui/icons/LastPage'
import IconButton from '@material-ui/core/IconButton'
import { Exposure } from '@material-ui/icons'
import { ArrowForwardIos } from '@material-ui/icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSort } from '@fortawesome/free-solid-svg-icons'
import classes from './TreeViewTable.module.scss'
import CustomSearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/CustomSearchTextComponent/CustomSearchTextComponent'

export default function TreeViewTable (props) {
  const {
    headCells,
    children,
    size = 'small',
    handleRequestSort,
    handleCollapseEvent,
    collapseAllState,
    order,
    orderBy,
    pagination = {},
    sorter,
    filter,
    isViewTree,
    actionText,
    actionTo,
    params,
    fetchData,
    count
  } = props
  const [newPage, setPage] = React.useState(pagination.page - 1)
  const [rowsPerPage, setRowsPerPage] = React.useState(pagination.rowsPerPage)
  const handleChangePage = (event, newPage) => {
    setPage(newPage - 1)
    fetchData({ page: newPage, limit: rowsPerPage })
  }
  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value))
    fetchData({ page: 1, limit: parseInt(event.target.value) })
  }
  function TablePaginationActions (props) {
    const { onChangePage } = props

    const handleFirstPageButtonClick = event => {
      onChangePage(event, 1)
    }

    const handleBackButtonClick = event => {
      onChangePage(event, pagination.page - 1)
    }

    const handleNextButtonClick = event => {
      onChangePage(event, pagination.page + 1)
    }

    const handleLastPageButtonClick = event => {
      onChangePage(event, Math.max(0, Math.ceil(pagination.count / rowsPerPage)))
    }

    return (
      <Box component='div' m={1} display='flex'>
        <IconButton onClick={handleFirstPageButtonClick} disabled={newPage === 0} aria-label='first page'>
          <FirstPageIcon />
        </IconButton>
        <IconButton onClick={handleBackButtonClick} disabled={newPage === 0} aria-label='previous page'>
          <KeyboardArrowLeft />
        </IconButton>
        <IconButton
          onClick={handleNextButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='next page'
        >
          <KeyboardArrowRight />
        </IconButton>
        <IconButton
          onClick={handleLastPageButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='last page'
        >
          <LastPageIcon />
        </IconButton>
      </Box>
    )
  }

  const onSort = name => {
    const order = !params.order
    let nameTmp = name
    if (name.includes('|')) {
      const [field, type] = name.split('|')
      nameTmp = field
    }
    fetchData({
      ...params,
      sort: `${nameTmp}|${order ? 'asc' : 'desc'}`,
      order: order,
      orderBy: nameTmp
    })
  }

  const onChangeSearch = _.debounce(e => {
    const value = {
      [e.target.name]: e.target.value
    }
    fetchData({
      filters: { ...params.filters, ...value },
      page: 1
    })
  }, 800)

  React.useEffect(() => {
    setPage(pagination.page - 1)
    setRowsPerPage(pagination.rowsPerPage)
  }, [pagination])

  return (
    <React.Fragment>
      <TableContainer component={Paper}>
        <Table size={size} aria-label='a dense table'>
          <TableHead className={classes.headerbar}>
            <TableRow>
              {isViewTree ? (
                <TableCell className={classes.headerCollapseIcon}>
                  <IconButton onClick={handleCollapseEvent}>
                    <Exposure />
                  </IconButton>
                </TableCell>
              ) : (
                ''
              )}
              {headCells.map(headCell => (
                <TableCell
                  key={headCell.id}
                  sortDirection={orderBy === headCell.id ? order : false}
                  width={headCell.width}
                  className={
                    headCell.center
                      ? classes.headerCell
                      : headCell.numeric
                      ? classes.headerCellMonetary
                      : classes.headerCell
                  }
                  style={headCell.boldface ? { 'font-weight': 'bold' } : { 'font-weight': 'normal' }}
                  align={headCell.center ? 'center' : headCell.numeric ? 'right' : 'left'}
                  // padding={headCell.disablePadding ? 'none' : 'default'}
                >
                  {headCell.id !== 'action' && headCell.show ? (
                    <React.Fragment>
                      <Box style={{ width: `${headCell.width}` }}>
                        {headCell.label}
                        {sorter && (
                          <FontAwesomeIcon
                            className={classes['sort-icon']}
                            onClick={e => onSort(headCell.accessor)}
                            icon={faSort}
                          />
                        )}
                        {orderBy === headCell.id ? (
                          <span>{order === 'desc' ? 'sorted descending' : 'sorted ascending'}</span>
                        ) : null}
                      </Box>
                    </React.Fragment>
                  ) : collapseAllState & (headCell.label === '>') ? (
                    <IconButton onClick={handleCollapseEvent}>
                      <ArrowForwardIos />
                    </IconButton>
                  ) : (
                    <></>
                  )}

                  {filter && headCell.accessor ? (
                    <CustomSearchTextComponent onChange={onChangeSearch} name={headCell.accessor} noIcon={true} />
                  ) : null}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          {children}
        </Table>
        {pagination.page && (
          <TablePagination
            className={classes.pagination}
            rowsPerPageOptions={[5, 10, 20]}
            count={pagination.count}
            rowsPerPage={rowsPerPage}
            page={newPage}
            ActionsComponent={TablePaginationActions}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        )}
        {/* pagination && (<TablePagination rowsPerPageOptions={[5, 10, 25]} component='div' {...pagination} />) */}
      </TableContainer>
    </React.Fragment>
  )
}
