import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { ErrorPageService } from 'SharedComponents/ErrorPage/ErrorPage'
import CommonServices from 'Services/Api/CommonServices'
import ConfigController2 from 'Controllers/ConfigController2'
// Routes by role
import BillingModuleRoutes from './Routes'

// RBAC
import RBAC from 'Security/RBAC'

export default function BillingModule () {
  const [isRbacLoaded, setIsRbacLoaded] = React.useState(false)
  
  ConfigController2.getUserManagementData().then(res => {
    const access = res.access_token
    CommonServices.auth(access).then(res => {
      if (res.data === null) {
        ErrorPageService.unauthorized()
      }
    })
  })
  
  const loadRbac = async () => {
    await RBAC.load()
    setIsRbacLoaded(true)
  }
  React.useEffect(() => {
    document.title = 'HIMS - Billing'
    loadRbac()

    return () => RBAC.unload()
  }, [])

  return <React.Fragment>
    {isRbacLoaded && <Switch>
      <Route path='/' component={BillingModuleRoutes} />
    </Switch> }
  </React.Fragment>
}
