import React from 'react'
import PropTypes from 'prop-types'

// Components
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Tooltip from '@material-ui/core/Tooltip'
import IconButton from '@material-ui/core/IconButton'
import AddIcon from '@material-ui/icons/Add'
import Select from '../../../../../Components/FormComponents/Select/Select'

// Hooks
import useForm from 'Hooks/useForm'

// Utilities
import isString from 'lodash/isString'
import noop from 'lodash/noop'

// Styles
import { makeStyles } from '@material-ui/core/styles'
import grey from '@material-ui/core/colors/grey'
import { ELEMENT_TYPES } from '../constants'

const useStyles = makeStyles(() => ({
  root: {
    display: 'inline-block'
  },

  container: {
    display: 'flex',
    flexWrap: 'wrap',
    width: 400
  },
}))

const FORM_VALUES = {
  type: null,
  collection: null,
  field: null
}

export default function InsertFieldDialog({ onInsert = noop }) {
  const classes = useStyles()
  const form = useForm(FORM_VALUES)
  const [open, setOpen] = React.useState(false)

  function handleInsert() {
    if (form.values.field !== null) {
      const { field } = form.values
      const { type, value } = parse(field)

      onInsert({ type, value })
      setOpen(false)
    }
  }

  function handleClickOpen() {
    setOpen(true)
  }

  function handleClose() {
    setOpen(false)
  }

  function renderSelect(data) {
    return (
      <Select
        id="field"
        label="Field"
        name="field"
        value={form.field}
        onChange={form.onChange}
        defaultItemText="Select Field"
        data={data}
      />
    )
  }

  React.useEffect(() => {
    if (!open) {
      form.setValues(FORM_VALUES)
    }
  }, [open])

  return (
    <div className={classes.root}>
      <Tooltip title="Insert dynamic field" placement="top">
        <IconButton onClick={handleClickOpen}>
          <AddIcon style={{ color: grey[500] }} />
        </IconButton>
      </Tooltip>

      <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>Insert a dynamic field</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <Select
              id="type"
              label="Field Type"
              name="type"
              value={form.type}
              onChange={form.onChange}
              defaultItemText="Select Field Type"
              data={[
                { value: FIELD_TYPES.DATABASE, text: 'Database' },
                { value: FIELD_TYPES.CUSTOM, text: 'Custom' },
              ]}
            />

            {form.values.type === FIELD_TYPES.DATABASE ? (
              <div style={{ marginTop: 30, width: '100%' }}>
                <Select
                  id="collection"
                  label="Collection"
                  name="collection"
                  value={form.collection}
                  onChange={form.onChange}
                  defaultItemText="Select Collection"
                  data={mapFields(form.values.type)}
                />

                {form.values.collection && (
                  <div style={{ marginTop: 30, width: '100%' }}>
                    {renderSelect(mapDatabaseFields(form.values.collection))}
                  </div>
                )}
              </div>
            ) : form.values.type ? (
              <div style={{ marginTop: 30, width: '100%' }}>
                {renderSelect(mapFields(form.values.type))}
              </div>
            ) : null}
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleInsert} color="primary">
            Insert
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

InsertFieldDialog.propTypes = {
  onInsert: PropTypes.func
}

function parse(field) {
  if (!isString(field)) {
    return ELEMENT_TYPES.PARAGRAPH
  }

  const [, name, type = ELEMENT_TYPES.PARAGRAPH] = field.split(':')

  return {
    type,
    value: `#{${field}}`,
    ...(type === ELEMENT_TYPES.IMAGE && {
      value: customField(name)
    })
  }
}

function customField(name) {
  switch (name) {
    case 'logo':
      return 'https://intellicare.net.ph/wp-content/uploads/Intellicare_green-play.png'
    default:
      return name
  }
}

function mapDatabaseFields(collection) {
  const format = fields => fields.map(field => ({
    text: field,
    value: `database:${collection}.${field}`
  }))

  switch (collection) {
    case COLLECTION_TYPES.CLIENT:
      return format([
        'account_number',
        'account_name',
        'account_status',
        'type',
        'tax_type',
        'tin',
        'head_company_name',
        'termination_date',
        'broker'
      ])

    default:
      return []
  }
}

function mapFields(type) {
  switch (type) {
    case FIELD_TYPES.DATABASE:
      return DATABASE_FIELDS

    case FIELD_TYPES.CUSTOM:
      return CUSTOM_FIELDS

    default:
      return []
  }
}

const FIELD_TYPES = {
  DATABASE: 'database',
  CUSTOM: 'custom',
}

const COLLECTION_TYPES = {
  CLIENT: 'client'
}

const DATABASE_FIELDS = [
  {
    text: 'Client',
    value: COLLECTION_TYPES.CLIENT
  },
]

const CUSTOM_FIELDS = [
  {
    text: 'Logo',
    value: `custom:logo:${ELEMENT_TYPES.IMAGE}`
  },

  {
    text: 'Current Date',
    value: 'custom:currentDate'
  },
]