const initialValue = {
  means_of_payment: '',
  preferred_bank: '',
  check_number: '',
  check_date: new Date().toISOString(),
  check_type: '',
  amount: 0
}
const MeansOfPayment = (state = initialValue, { type, payload }) => {
  switch (type) {
    case 'UPDATE_FIELD':
      return (() => {
        const { value, field } = payload
        return { ...state, [field]: value }
      })()

    default:
      return state
  }
}

export default MeansOfPayment
