import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import NumberFormat from 'react-number-format'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

import classes from './TextFieldNumeric.module.scss'
export default function TextFieldNumeric(props) {
  const {
    alignItems = '',
    wrapperSize = '',
    label,
    required,
    readOnly,
    error,
    onChange,
    withSeparator,
    unit = '',
    decimalPlace = 0,
    ...other
  } = props

  const [focus, setFocus] = React.useState()

  const handleOnChange = (e) => {
    if (typeof onChange === 'function') onChange({ target: { value: e.floatValue } })
  }

  return (
    <div className={classes.root} style={{ alignItems: `${alignItems}` }}>
      <Typography variant="subtitle2" className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={{ width: `${wrapperSize}` }}>
        {/* {readOnly ? ( */}
        {/* //  Read Only Value */}
        {/* <Typography id={props.id} variant="subtitle2" className={`${classes.label} ${classes['read-only-value']}`}> */}
        {/* {CurrencyFormatter(other.value)} */}
        {/* </Typography> */}
        {/* ) : ( */}
        <React.Fragment>
          <Paper
            className={`${classes['text-field']} ${
              error ? classes['has-error'] : focus && classes.focus
            } box-shadow-field`}
          >
            <NumberFormat
              id={props.id}
              className={`${classes['input']} ${readOnly ? classes['read-only'] : ''}`}
              required
              customInput={InputBase}
              // isNumericString
              displayType={'input'}
              thousandSeparator={withSeparator}
              decimalScale={decimalPlace}
              fixedDecimalScale
              allowLeadingZeros={false}
              value={other.value}
              autoComplete="off"
              onClick={(e) => {
                readOnly && e.target.blur()
              }}
              onFocus={(e) => {
                readOnly ? e.target.blur() : setFocus(true)
              }}
              onBlur={() => setFocus(false)}
              // {...other}
            />
            {unit && (
              <div className={classes.currency}>
                <Typography
                  variant="subtitle2"
                  className={`${classes['label']} ${readOnly ? classes['read-only'] : ''}`}
                >
                  {unit}
                </Typography>
              </div>
            )}
          </Paper>
          {error && <span className={classes.error}>{error}</span>}
        </React.Fragment>
        {/* )} */}
      </div>
    </div>
  )
}
