import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import _ from 'lodash'
import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import { withStyles, makeStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import { Popper, TextField, Fade, Button } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'
import CircularLoading from '../../../../../SharedComponents/CircularLoading/CircularLoading'
import { GrabByString } from 'Utilities/GrabObject'
import classes from './ViewAll.module.scss'

// Services
import ExportsServices from 'Services/Api/ExportsServices'

export default function CustomizedDialogs (props) {
  const {
    config,
    tile,
    type,
    title,
    contents,
    route,
    linkCaption,
    viewAll,
    setViewAll,
    passProperty,
    subProperty,
    subSubProperty,
    changeViewList,
    count,
    show,
    list,
    fetchData,
    params,
    accessors,
    setShow,
    isFetching,
    placeholder,
    statuses,
    modal,
    preSoa,
    status,
    preSoaAnalyst
  } = props

  const history = useHistory()

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const tableHeaders = config[tile].headers
  const tableColumnSequence = config[tile].sequence
  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])
  // const handleOnClose = () => {
  //   setViewAll(false)
  //   history.push('/')
  // }

  const handleOnClose = () => {
    setShow(false)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value,
      accessors: accessors,
      page: 1
    }
    changeView(params)
  }, 300)
  // For Display Settings
  var today = moment().format('YYYY-MM-DD')
  const [fromDate, setFromDate] = React.useState(today)
  const [toDate, setToDate] = React.useState(today)
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = event => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined
  const handleOnApply = event => {
    let fdate = new Date(fromDate)
    let tdate = new Date(toDate)
    const params = {
      range: {
        start: fdate.toISOString(),
        end: tdate.toISOString()
      }
    }

    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError('Please enter a valid effectivity start date')
      setToDateError('Please enter a valid effectivity end date')
    } else if (fdate.valueOf() > tdate.valueOf()) {
      setToDateError('Please enter a valid effectivity end date that is not later than the current date')
    } else {
      setFromDateError('')
      setToDateError('')
      if (fetchData) {
        fetchData(params)
      }
    }
  }

  const typeTile = type => {
    let tile
    if (type === 'Client Pre-SOAs To Analyze' || type === 'Pending Pre-SOAs' || type === 'Client Pre-SOAs To Validate' || type === 'Approved Pre-SOAs' || type === 'Pre-SOAs Submitted for Approval' || type === 'Validated Pre-SOAs') {
      tile = 'CLIENT_SOA'
    }
    if (type === 'Member Pre-SOAs To Analyze' || type === 'Member Invoices To Validate') {
      tile = 'MEMBER_SOA'
    }
    return tile
  }

  const downloadServices = async  args => {
    const { type, contents } = args
    console.log(contents)
    const list = []
    
    if (type === 'Member Pre-SOAs To Analyze' || type === 'Member Pre-SOAs to Validate') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_name,
          field_2: i.first_name + ' ' + i.last_name,
          field_3: i.assigned_analyst,
          field_4: DateFormatterByAbbreviation(
            new Date(moment(i.date.created).subtract(parseInt(i.date.created.substring(11, 13)), 'h'))
          )
        })
      }
    }
    if (type === 'Validated Pre-SOAs') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_number,
          field_2: i.account_name,
          field_3: i.soa_number,
          field_4: i.billing_cycle_code + ' ' + i.billing_cycle,
          field_5: DateFormatterByAbbreviation(
            new Date(moment(i.date.created).subtract(parseInt(i.date.created.substring(11, 13)), 'h'))
          )
        })
      }
    } if (type === 'Client Pre-SOAs To Analyze' || type === 'Pending Pre-SOAs' || type === 'Client Pre-SOAs To Validate' || type === 'Approved Pre-SOAs' || type === 'Pre-SOAs Submitted for Approval') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_number,
          field_2: i.account_name,
          field_3: i.billing_cycle_code + ' ' + i.billing_cycle,
          field_4: DateFormatterByAbbreviation(
            new Date(moment(i.due_date).subtract(parseInt(i.due_date.substring(11, 13)), 'h'))
          )
        })
      }
    }
    console.log(list)
    ExportsServices.getClientSOAExports(list, {tile: type, type: typeTile(type)}).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `${type}.xlsx`)
      document.body.appendChild(link)
      link.click()
    })
  }
  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          <div id='modal-search-field' className={classes['search-field']}>
            <SearchTextComponent
              placeholder={placeholder}
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </div>
          <Link
            id='display-settings-link'
            className={classes['display-settings']}
            onClick={handleClick}
            color='primary'
            size='medium'
          >
            <Grid container id='display-settings-icon'>
              <ViewListRoundedIcon className={classes['display-settings-icon']} />
              <Typography className='text-link underline'>Display Settings</Typography>
            </Grid>
          </Link>
          <Popper id={id} open={open} anchorEl={anchorEl} transition style={{ 'z-index': '1301' }}>
            {({ TransitionProps }) => (
              <Fade {...TransitionProps} timeout={350}>
                <div
                  style={{
                    padding: '10px',
                    'background-color': 'white',
                    'box-shadow': '5px 5px rgb(0,0,0,10%)'
                  }}
                >
                  <Grid container spacing={3}>
                    <Grid item xs={10}>
                      Effectivity Date Range
                    </Grid>
                    <Grid
                      item
                      xs={2}
                      style={{ 'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer' }}
                      onClick={e => handleClick(e)}
                    >
                      X
                    </Grid>
                  </Grid>
                  <Grid container spacing={3}>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='From'
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={fromDate}
                        onChange={e => {
                          setFromDate(e.target.value)
                        }}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='To'
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={toDate}
                        onChange={e => {
                          setToDate(e.target.value)
                        }}
                      />
                    </Grid>
                  </Grid>
                  <Grid container spacing={3} style={{ 'padding-bottom': '50px' }}>
                    <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                      {fromDateError}
                    </Grid>
                    <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                      {toDateError}
                    </Grid>
                  </Grid>
                  <div>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      onClick={e => handleClick(e)}
                      style={{ color: '#3ab77d', 'background-color': 'white', width: '100px', 'margin-left': '140px' }}
                    >
                      Cancel
                    </Button>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      style={{ color: 'white', 'background-color': '#3ab77d', width: '100px', 'margin-left': '20px' }}
                      onClick={handleOnApply}
                    >
                      Apply
                    </Button>
                  </div>
                </div>
              </Fade>
            )}
          </Popper>
          <Link
            id='exports-link'
            className={classes['exports']}
            onClick={() => downloadServices({ type: type, contents: contents })}
            color='primary'
            size='medium'
          >
            <Grid container id='exports-icon'>
              <ViewListRoundedIcon className={classes['exports-icon']} />
              <Typography className='text-link underline'>Export</Typography>
            </Grid>
          </Link>
        </div>
        {/* For Pre Soa to Analyze Modal */}
        { statuses ? 
        <div className={classes.count}>
          <Grid container spacing={2} direction='row'>
            <Grid item xs={12}>
              <Grid container justify='center' spacing={10}>
                <Grid item className={classes.grids}>
                {type === 'Client Pre-SOAs To Analyze' ? (
                  <React.Fragment>
                    <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                      {statuses.toAnalyze}
                    </Typography>
                    <Typography variant='h6'>TO ANALYZE</Typography>
                  </React.Fragment>
                ) : (
                  <React.Fragment>
                    <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                      {statuses.forApproval}
                    </Typography>
                    <Typography variant='h6'>TO VALIDATE</Typography>
                  </React.Fragment>
                )}
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.approved}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    APPROVED
                  </Typography>
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.disapproved}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    DISAPPROVED
                  </Typography>
                  <Typography className={classes.smtext} align='center'>
                    (FOR REVISION)
                  </Typography>
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.void}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    VOID
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div> : null}
        
        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {isFetching && (
              <StyledTableRow>
                <StyledTableCell component='th' colspan={10}>
                  <CircularLoading />
                </StyledTableCell>
              </StyledTableRow>
            )}
            {!isFetching &&
              contents &&
              contents.map((row, contentsIndex) => (
                <StyledTableRow key={contentsIndex}>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Validated Pre-SOAs' ||
                      type === 'Approved Pre-SOAs' || type === 'Pending Pre-SOAs'
                        ? row.account_number
                        : ''}
                      {type === 'Member Pre-SOAs To Analyze' ? row.account_name : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Validated Pre-SOAs' ||
                      type === 'Approved Pre-SOAs'  || type === 'Pending Pre-SOAs'
                        ? row.account_name
                        : null}
                      {/* TODO: member name for Pending Pre-SOAs is undefined */}
                      {type === 'Member Pre-SOAs To Analyze' || type === 'Pending Pre-SOAs'
                        ? `${row.first_name + ' ' + row.last_name}`
                        : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th' align='left'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Approved Pre-SOAs' || type === 'Pending Pre-SOAs'
                        ? row.billing_cycle_code + ' ' + row.billing_cycle
                        : ''}
                      {type === 'Member Pre-SOAs To Analyze' ? row.assigned_analyst : ''}
                      {type === 'Validated Pre-SOAs' ? row.soa_number : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate'
                        ? DateFormatterByAbbreviation(row.due_date)
                        : null}
                      {type === 'Validated Pre-SOAs' ? row.billing_cycle_code_and_description : ''}
                      {type === 'Member Pre-SOAs To Analyze' ? DateFormatterByAbbreviation(row.date.created) : null}
                      {/* TODO: this should be date.submitted, API is not setting the date submitted */}
                      {type === 'Pending Pre-SOAs' ? DateFormatterByAbbreviation(row.date.created) : null}
                      {/* TODO: this should be date.approved, API is not setting the date approved */}
                      {type === 'Approved Pre-SOAs' ? DateFormatterByAbbreviation(row.date.created) : null}
                    </Typography>
                  </StyledTableCell>

                  {type === 'Member Pre-SOAs To Analyze' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {/* {row.case_number} */}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Pending Pre-SOAs' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {CurrencyFormatter(row.summary.sub_total + row.summary.tax_amount)}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Validated Pre-SOAs' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {/* TODO: this should be date.approved, API is not setting the date approved */}
                        {DateFormatterByAbbreviation(row.date.created)}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  <StyledTableCell component='th' align='right'>
                    {type === 'Client Pre-SOAs To Analyze' && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {(type === 'Pre-SOAs Submitted for Approval' || type === 'Pending Pre-SOAs') && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                    {type === 'Client Pre-SOAs To Validate' && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'supervisor'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Validate
                        </Typography>
                      </Link>
                    )}

                    {type === 'Member Pre-SOAs To Analyze' && (
                      <Link to={`/billing/soa/member/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {type === 'Member Pre-SOAs To Validate' && (
                      <Link to={`/billing/soa/member/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {type === 'Validated Pre-SOAs' && (
                      <Link to={`/billing/ledgers-validated/${row.soa_number}/${'validated'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                  </StyledTableCell>
                </StyledTableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
