import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Services
import CollectionPenaltyServices from 'Services/Api/CollectionPenaltyServices'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const columnConfig = [
  { id: 'endorsed-accounts-tile-table-header', name: 'Account Name' },
  { id: 'endorsed-accounts-tile-table-header', name: 'Date Endorsed' },
  { id: 'endorsed-accounts-tile-table-header', name: '' }
]

// Mock data
const mock = require('../_mock-data/collection-distribution.json')

const EndorsedAccountsTile = props => {
  // Constants
  const { service = () => {} } = props

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }
  const ViewportBlock = handleViewport(Block)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    category: 'SM',
    count: 0,
    data: [],
    filter: {},
    limit: 5,
    order: true,
    orderBy: 'account_name',
    sort: '',
    title: 'Endorsed Accounts to Charge Penalty',
  })

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    const _params = {
      limit: 5,
      ...params
    }
    CollectionPenaltyServices.index(_params).then(({ data: res }) => {
      const { results, count } = res.data
      console.log(results)
      setTile(i => ({ ...i, count, data: results }))
    })
  }

  const onFilterChange = value => {
    fetchData({ range: rangeDate[value] })
  }
  // Method
  /**
   * Handle Transfer to page
   * @param location route/path to view all member aging summary page
   */
  const handleOnViewAll = () => {}
  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Approved Invoices Tile is not on viewport')}
      />
      <DashboardTile
        id='endorsed-accounts-tile'
        count={{ color: 3, value: tile.count, id: 'endorsed-accounts-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='endorsed-accounts-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`endorsed-accounts-tile-table-row-${index}`}
                id={`endorsed-accounts-tile-table-row-${index}`}
              >
                <TableCell id={`endorsed-accounts-tile-table-cell-account-name-${index}`}>
                  {item.billing_client_contract ? item.billing_client_contract[0].client.account_name : ''}
                </TableCell>
                <TableCell id={`endorsed-accounts-tile-table-cell-date-approved-${index}`}>
                  {item.date ? moment(item.date.created).format('ll') : ''}
                </TableCell>
                <TableCell id={`endorsed-accounts-tile-table-cell-action-${index}`}>
                  <Link to={`/collection/penalty/validate/client/${item.penalty_number}`}>
                    <Typography
                      variant='caption'
                      className='text-link underline'
                      id={`endorsed-accounts-tile-view-${index}`}
                    >
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

EndorsedAccountsTile.displayName = 'DashboardTileComponent'

export default EndorsedAccountsTile
