import instance from '../instance'

const axios = instance('/b/s/')
export default class SOAService {
  static getSoaList (params) {
    return axios({
      url: `/billing/${params.entityType}/pre-generate-soa/${params.status}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getSOAsStatuses (params) {
    return axios({
      url: `/billing/pre-generate-soa/current-status`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
