import React from 'react'

import { Link } from 'react-router-dom'
import { Typography, TextField, Backdrop, CircularProgress } from '@material-ui/core'

import Button from '@material-ui/core/Button'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../Components/EnhancedSearchComponent/EnhancedSearchComponent'

import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

// Components
import SelectClientContract from './Components/SelectClientContract'
import SelectTransactonType from './Components/SelectTransactonType'
import CreateInvoice from './Components/Client/CreateInvoice'
import CreateCreditMemo from './Components/Client/CreateCreditMemo'
import CreateDebitMemo from './Components/Client/CreateDebitMemo'

import classes from './CreateTransactionPage.module.scss'

export default function CreateTransactionPage (props = {}) {
  PageTitleService()
  BreadcrumbsService.setSelectedItemsWithoutHome(['createTransaction'])

  const [clientContract, setClientContract] = React.useState({
    data: []
  })
  const [clientId, setClientId] = React.useState('')
  const [clientContractId, setClientContractId] = React.useState('')
  const [accountNumber, setAccountNumber] = React.useState('')
  const [selectedEntity, setSelectedEntity] = React.useState('Member')
  const [errorMessage, setErrorMessage] = React.useState('')
  const [step, setStep] = React.useState(1)
  const [backdrop, setBackdrop] = React.useState(false)

  // autosuggest
  const [suggestions, setSuggestions] = React.useState([])
  const [memberNames, setMemberNames] = React.useState([])
  const [members, setMembers] = React.useState([])
  const [selectedRef, setSelectedRef] = React.useState([])

  React.useEffect(() => {
    fetchMembers()
  }, [clientContract])

  const fetchMembers = async () => {
    const { data } = await BillingAnalystServices.getSelectedClientMembers(clientContract.data._id)
    if (data.data) {
      const memberList = []
      if (data.data) {
        data.data.map(res => {
          memberList.push({
            id: res._id,
            value: {
              memberId: res.member.id,
              name: `${res.member.first_name} ${res.member.last_name}`,
              clientName: res.client_name
            },
            member_details: {
              id: res.member.id,
              person_id: res.member.person_id,
              account_no: res.member.account_no,
              type: res.member.type,
              status: res.member.status,
              first_name: res.member.first_name,
              middle_initial: res.member.middle_initial,
              middle_name: res.member.middle_name,
              last_name: res.member.last_name,
              plan_type_code: res.member.plan_type_code,
              plan_type_name: res.member.plan_type_name,
              effectivity_date: res.member.effectivity_date,
              renewal_date: res.member.renewal_date,
              end_date: res.member.end_date,
              cancellation_date: res.member.cancellation_date
            }
          })
        })
      }
      setSuggestions(memberList)
    }
  }
  // function to search for client
  const handleEntitySearch = (e, click) => {
    let account_number = ''
    if (click) {
      setBackdrop(true)
      setAccountNumber(e)
      setErrorMessage('')
      account_number = e
    } else {
      setAccountNumber(e.target.value)
      setErrorMessage('')
      account_number = e.target.value
    }
    Promise.all([BillingAnalystServices.getSelectedClient(account_number)]).then(async values => {
      console.log(values[0].data.data)
      if (values[0].data.data) {
        setClientContract(prevState => ({
          ...prevState,
          data: values[0].data.data
        }))
        setClientId(values[0].data.data.client.id)
        setClientContractId(values[0].data.data._id)
        if (click)handleShowEntity(true)
      } else {
        setClientContract({
          data: []
        })
        setClientId('')
        setClientContractId('')
      }
    })
  }
  const handleShowEntity = (status) => {
    switch (selectedEntity) {
      case 'Client':
        console.log(clientContract.data)
        if (clientContract.data.length !== 0 || status === true) {
          setBackdrop(false)
          setStep(2)
          setErrorMessage('')
        } else {
          setErrorMessage('Account Number not found')
        }
        break
      case 'Member':
        // console.log('Member Entity is selected')
        console.log(clientContract.data)
        if (clientContract.data.length !== 0) {
          setStep(2)
          setErrorMessage('')
        } else {
          setErrorMessage('Account Number not found')
        }
    }
  }
  const handlBackToSelectClientContract = () => {
    setStep(1)
  }
  const handlBackToSelectTransactionType = () => {
    setStep(2)
  }
  if (step === 1) {
    return (
      <div>
        <SelectClientContract
          clientId={clientId}
          accountNumber={accountNumber}
          handleEntitySearch={handleEntitySearch}
          handleShowEntity={handleShowEntity}
          selectedEntity={selectedEntity}
          setSelectedEntity={setSelectedEntity}
          errorMessage={errorMessage}
          setErrorMessage={setErrorMessage}
        />
        {backdrop === true ? (
          <Backdrop className={classes.backdrop} open>
            <CircularProgress color='primary' />
          </Backdrop>
        )
          : (
            ''
          )}
      </div>
    )
  } else if (step === 2) {
    return (
      <div>
        <SelectTransactonType
          clientContract={clientContract}
          setStep={setStep}
          handlBackToSelectClientContract={handlBackToSelectClientContract}
          setSelectedRef={setSelectedRef}
        />
        {backdrop === true ? (
          <Backdrop className={classes.backdrop} open>
            <CircularProgress color='primary' />
          </Backdrop>
        )
          : (
            ''
          )}
      </div>
    )
  } else if (step === 'invoice' && selectedEntity === 'Client') {
    return (
      <div>
        <CreateInvoice
          clientContract={clientContract}
          setStep={setStep}
          handlBackToSelectClientContract={handlBackToSelectClientContract}
          handlBackToSelectTransactionType={handlBackToSelectTransactionType}
          clientContractId={clientContractId}
          suggestions={suggestions}
        />
        {backdrop === true ? (
          <Backdrop className={classes.backdrop} open>
            <CircularProgress color='primary' />
          </Backdrop>
        )
          : (
            ''
          )}
      </div>
    )
  } else if (step === 'credit' && selectedEntity === 'Client') {
    return (
      <div>
        <CreateCreditMemo
          clientContract={clientContract}
          setStep={setStep}
          handlBackToSelectClientContract={handlBackToSelectClientContract}
          handlBackToSelectTransactionType={handlBackToSelectTransactionType}
          clientContractId={clientContractId}
          suggestions={suggestions}
          selectedRef={selectedRef}
        />
        {backdrop === true ? (
          <Backdrop className={classes.backdrop} open>
            <CircularProgress color='primary' />
          </Backdrop>
        )
          : (
            ''
          )}
      </div>
    )
  } else if (step === 'debit' && selectedEntity === 'Client') {
    return (
      <div>
        <CreateDebitMemo
          clientContract={clientContract}
          setStep={setStep}
          handlBackToSelectClientContract={handlBackToSelectClientContract}
          handlBackToSelectTransactionType={handlBackToSelectTransactionType}
          clientContractId={clientContractId}
          suggestions={suggestions}
          selectedRef={selectedRef}
        />
        {backdrop === true ? (
          <Backdrop className={classes.backdrop} open>
            <CircularProgress color='primary' />
          </Backdrop>
        )
          : (
            ''
          )}
      </div>
    )
  }
}
