import instance from './instance'
const axiosClientSOA = instance('/b/ci')

export default class InvoiceExportsServices {
  static getClientInvoiceExports (params = [], { tile, type }) {
    return axiosClientSOA({
      url: `/billing/client/invoice-export/${type}/${tile}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
    })
  }
}
