import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import _ from 'lodash'
import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import { withStyles, makeStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import { Popper, TextField, Fade, Button } from '@material-ui/core'

import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'

import { GrabByString } from 'Utilities/GrabObject'
// Services
import CreditMemoExportsServices from 'Services/Api/CreditMemoExportsServices'
import DebitMemoExportsServices from 'Services/Api/DebitMemoExportsServices'

import classes from './ViewAll.module.scss'
import CircularLoading from '../../../../../SharedComponents/CircularLoading/CircularLoading'

export default function CustomizedDialogs (props) {
  const {
    config,
    tile,
    type,
    title,
    contents,
    route,
    linkCaption,
    viewAll,
    setViewAll,
    passProperty,
    subProperty,
    subSubProperty,
    changeViewList,
    count,
    placeholder,
    fetchData,
    params,
    accessors,
    show,
    list,
    setShow,
    isFetching,
    statuses,
    modal,
    preSoa,
    status,
    preSoaAnalyst,
  } = props

  const history = useHistory()

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const tableHeaders = config[tile].headers
  const tableColumnSequence = config[tile].sequence
  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  // const handleOnClose = () => {
  //   setViewAll(false)
  //   history.push('/')
  // }

  const handleOnClose = () => {
    setShow(false)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  const mainSearchChange = _.debounce((e) => {
    const params = {
      'search': e.target.value,
      'accessors': accessors,
      page: 1,
    }
    changeView(params)
  }, 300)

  // For Display Settings
  var today = moment().format('YYYY-MM-DD')
  const [fromDate, setFromDate] = React.useState(today)
  const [toDate, setToDate] = React.useState(today)
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined
  const handleOnApply = (event) => {
    let fdate = new Date(fromDate)
    let tdate = new Date(toDate)
    const params = {
      'range': {
        'start': fdate.toISOString(),
        'end': tdate.toISOString()
      }
    }
    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError('Please enter a valid effectivity start date')
      setToDateError('Please enter a valid effectivity end date')
    } else if (fdate.valueOf() > tdate.valueOf()) {
      setToDateError('Please enter a valid effectivity end date that is not later than the current date')
    } else {
      setFromDateError('')
      setToDateError('')
      if (fetchData) {
        fetchData(params)
      }
    }
  }

  const typeTile = type => {
    let tile
    if (type === 'Validated Transactions' || type === 'Approved Debit Memos' || type === 'Pending Debit Memos' || type === 'Client Debit Memos To Validate' || type === 'Debit Memos Submitted') {
      tile = 'DEBIT_MEMO'
    }
    if (type === 'Approved Credit Memos' || type === 'Pending Credit Memos' || type === 'Client Credit Memos To Validate' || type === 'Credit Memos Submitted') {
      tile = 'CREDIT_MEMO'
    }
    return tile
  }

  const downloadServices = async args => {
    const { type, contents } = args
    const list = []
    for (const i of contents) {
      console.log(i)
      for (const c of i.billing_client_contract) {
        if (type === 'Approved Credit Memos' || type === 'Approved Debit Memos') {
          await list.push({
            field_1: DateFormatterByAbbreviation(
              new Date(moment(i.transaction.date).subtract(parseInt(i.transaction.date.substring(11, 13)), 'h'))
            ),
            field_2: i.transaction.number,
            field_3: c.client.account_name,
            field_4: i.approved_by ? i.approved_by : 'undefined',
            field_5: DateFormatterByAbbreviation(
              new Date(moment(i.date.modified).subtract(parseInt(i.date.modified.substring(11, 13)), 'h'))
            )
          })
        }
        if (type === 'Debit Memos Submitted' || type === 'Credit Memos Submitted' || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate') {
          await list.push({
            field_1: DateFormatterByAbbreviation(
              new Date(moment(i.transaction.date).subtract(parseInt(i.transaction.date.substring(11, 13)), 'h'))
            ),
            field_2: i.transaction.number,
            field_3: c.client.account_name,
            field_4: i.submitted_by ? i.submitted_by : 'undefined',
            field_5: DateFormatterByAbbreviation(
              new Date(moment(i.date.modified).subtract(parseInt(i.date.modified.substring(11, 13)), 'h'))
            ),
            field_6: i.summary.sub_total
          })
        }
        if (type === 'Pending Debit Memos' || type === 'Pending Credit Memos') {
          await list.push({
            field_1: DateFormatterByAbbreviation(
              new Date(moment(i.transaction.date).subtract(parseInt(i.transaction.date.substring(11, 13)), 'h'))
            ),
            field_2: i.transaction.number,
            field_3: c.client.account_name,
            field_4: DateFormatterByAbbreviation(
              new Date(moment(i.date.modified).subtract(parseInt(i.date.modified.substring(11, 13)), 'h'))
            ),
            field_5: i.summary.sub_total
          })
        }
        if (type === 'Validated Transactions') {
          await list.push({
            field_1: DateFormatterByAbbreviation(
              new Date(moment(i.transaction.date).subtract(parseInt(i.transaction.date.substring(11, 13)), 'h'))
            ),
            field_2: i.transaction.number,
            field_3: c.client.account_name,
            field_4: DateFormatterByAbbreviation(
              new Date(moment(i.date.modified).subtract(parseInt(i.date.modified.substring(11, 13)), 'h'))
            ),
            field_5: i.summary.sub_total
          })
        }
      }
    }
    switch (typeTile(type)) {
      case 'CREDIT_MEMO':
        CreditMemoExportsServices.getClientCreditMemoExports(list, {tile: type, type: typeTile(type)}).then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `${type}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
        break
      case 'DEBIT_MEMO':
        DebitMemoExportsServices.getClientDebitMemoExports(list, {tile: type, type: typeTile(type)}).then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `${type}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
    }

  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title} style={{fontWeight: 'bold'}}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          <div id='modal-search-field' className={classes['search-field']}>
            <SearchTextComponent placeholder={placeholder}
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </div>
          <Link id='display-settings-link' className={classes['display-settings']} onClick={handleClick} color='primary' size='medium'>
            <Grid container id='display-settings-icon'>
              <ViewListRoundedIcon className={classes['display-settings-icon']} />
              <Typography className='text-link underline'>Display Settings</Typography>
            </Grid>
          </Link>
          <Popper id={id} open={open} anchorEl={anchorEl} transition
            style={{'z-index': '1301'}}>
            {({ TransitionProps }) => (
              <Fade {...TransitionProps} timeout={350}>
                <div
                  style={{
                    padding: '10px',
                    'background-color': 'white',
                    'box-shadow': '5px 5px rgb(0,0,0,10%)'
                  }}
                >
                  <Grid container spacing={3}>
                    <Grid item xs={10}>Effectivity Date Range</Grid><Grid item xs={2} style={{'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer'}} onClick={e => handleClick(e)}>X</Grid>
                  </Grid>
                  <Grid container spacing={3}>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='From'
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={fromDate}
                        onChange={e => {
                          setFromDate(e.target.value)
                        }}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='To'
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={toDate}
                        onChange={e => {
                          setToDate(e.target.value)
                        }}
                      />
                    </Grid>
                  </Grid>
                  <Grid container spacing={3}
                    style={{'padding-bottom': '50px'}}>
                    <Grid item xs={6}
                      style={{color: 'red', 'font-size': '10px'}}
                    >
                      {fromDateError}
                    </Grid>
                    <Grid item xs={6}
                      style={{color: 'red', 'font-size': '10px'}}
                    >
                      {toDateError}
                    </Grid>
                  </Grid>
                  <div>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      onClick={e => handleClick(e)}
                      style={{color: '#3ab77d',
                        'background-color': 'white',
                        width: '100px',
                        'margin-left': '140px'
                      }}
                    >
                        Cancel
                    </Button>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      style={{color: 'white',
                        'background-color': '#3ab77d',
                        width: '100px',
                        'margin-left': '20px'
                      }}
                      onClick={handleOnApply}
                    >
                        Apply
                    </Button>
                  </div>
                </div>
              </Fade>
            )}
          </Popper>
          <Link
            id='exports-link'
            className={classes['exports']}
            onClick={() => downloadServices({ type: type, contents: contents })}
            color='primary'
            size='medium'
          >
            <Grid container id='exports-icon'>
              <ViewListRoundedIcon className={classes['exports-icon']} />
              <Typography className='text-link underline'>Export</Typography>
            </Grid>
          </Link>
        </div>

        { statuses ? 
        <div className={classes.count}>
          <Grid container spacing={2} direction='row'>
            <Grid item xs={12}>
              <Grid container justify='center' spacing={10}>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.forApproval}
                  </Typography>
                  <Typography variant='h6'>TO VALIDATE</Typography>
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.approved}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    APPROVED
                  </Typography>
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.disapproved}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    DISAPPROVED
                  </Typography>
                  <Typography className={classes.smtext} align='center'>
                    (FOR REVISION)
                  </Typography>
                </Grid>
                <Grid item className={classes.grids}>
                  <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: "bold"}}>
                    {statuses.void}
                  </Typography>
                  <Typography variant='h6' align='center'>
                    VOID
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div> : null}

        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
          noSearchIcon={true}
        >
          <TableBody>
            {isFetching && <StyledTableRow>
              <StyledTableCell component='th' colspan={10}>
                <CircularLoading />
              </StyledTableCell>
            </StyledTableRow>}
            {!isFetching && contents && contents.map((row, contentsIndex) => (
              <StyledTableRow>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>

                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client.account_number : '' }
                    { type === 'Approved Debit Memos' || type === 'Pending Debit Memos' || type === 'Debit Memos Submitted' ||
                    type === 'Approved Credit Memos' || type === 'Pending Credit Memos' || type === 'Credit Memos Submitted' ||
                    type === 'Validated Transactions' || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate'
                      ? DateFormatterByAbbreviation(row.transaction.date) : null }
                  </Typography>
                </StyledTableCell>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>
                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client ? row.client.account_name : null : '' }
                    { type === 'Approved Debit Memos' || type === 'Pending Debit Memos' || type === 'Debit Memos Submitted' ||
                    type === 'Approved Credit Memos' || type === 'Pending Credit Memos' || type === 'Credit Memos Submitted' ||
                    type === 'Validated Transactions' || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate'
                      ? row.transaction.number : null}
                  </Typography>
                </StyledTableCell>
                { type === 'Client Credit Memos To Validate'
                  ? <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      { type === 'Client Credit Memos To Validate' ? row.transaction ? row.transaction.type : null : '' }
                    </Typography>
                  </StyledTableCell>
                  : null
                }
                <StyledTableCell component='th' align='left'>
                  <Typography variant='caption' align='left'>
                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.contract ? row.contract.type : null : '' }
                    {
                      type === 'Validated Transactions' || type === 'Approved Credit Memos' || type === 'Pending Credit Memos' ||
                      type === 'Credit Memos Submitted' || type === 'Client Credit Memos To Validate' ||
                      type === 'Approved Debit Memos' || type === 'Pending Debit Memos' ||
                      type === 'Debit Memos Submitted' || type === 'Client Debit Memos To Validate'
                        ? row.billing_client_contract[0] ? row.billing_client_contract[0].client.account_name : null : null }
                  </Typography>
                </StyledTableCell>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>

                    { type === 'Assign Analyst' ? row.contract ? DateFormatterByAbbreviation(row.contract.effectivity) : null : null }

                    { type === 'Setup Billing Cycle' ? row.contract ? DateFormatterByAbbreviation(row.contract.effectivity) : null : null }

                    { type === 'Approved Debit Memos' || type === 'Approved Credit Memos' ? row.approved_by : null}
                    {/* Date here should be date submitted, put date.modified as temp workaround */}
                    { type === 'Pending Debit Memos' || type === 'Pending Credit Memos' ? DateFormatterByAbbreviation(row.date.modified) : null }
                    {/* Date here should be date submitted, put date.modified as temp workaround */}
                    { type === 'Debit Memos Submitted' || type === 'Credit Memos Submitted' || type === 'Validated Transactions' ? 
                      DateFormatterByAbbreviation(row.date.modified) : null }
                    { type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate' ? row.submitted_by : null}

                  </Typography>
                </StyledTableCell>

                { type === 'Approved Debit Memos' || type === 'Approved Credit Memos' || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate'
                  ? <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      { type === 'Approved Debit Memos' || type === 'Approved Credit Memos'
                      || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate'
                        ? DateFormatterByAbbreviation(row.date.modified) : '' }
                    </Typography>
                  </StyledTableCell>
                  : null
                }

                { type === 'Pending Debit Memos' || type === 'Approved Debit Memos' || type === 'Debit Memos Submitted' ||
                  type === 'Pending Credit Memos' || type === 'Approved Credit Memos' || type === 'Credit Memos Submitted' ||
                  type === 'Validated Transactions' || type === 'Client Credit Memos To Validate' || type === 'Client Debit Memos To Validate'
                  ? <StyledTableCell component='th' align='right'>
                    <Typography variant='caption'>
                      { CurrencyFormatter(row.summary.sub_total) }
                    </Typography>
                  </StyledTableCell>
                  : null
                }

                <StyledTableCell component='th' align='right'>

                  { type === 'Assign Analyst' && <Link to={`/clients/assign/${row._id}/${row.client.account_number}`}>
                    <Typography variant='caption' className='text-link underline'>Setup</Typography></Link> }

                  { type === 'Setup Billing Cycle' && <Link to={`/clients/analyze/${row._id}/${row.client.account_number}`}>
                    <Typography variant='caption' className='text-link underline'>Setup</Typography></Link> }

                  { type === 'Pending Debit Memos' && <Link to={`/billing/debit/pending/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Debit Memos Submitted' && <Link to={`/billing/debit/forapproval/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Approved Debit Memos' && <Link to={`/billing/debit/approved/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Pending Credit Memos' && <Link to={`/billing/credit/pending/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>Edit</Typography></Link> }

                  { type === 'Credit Memos Submitted' && <Link to={`/billing/credit/forapproval/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Approved Credit Memos' && <Link to={`/billing/credit/approved/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Validated Transactions' && <Link to={`/billing/validated/transactions/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Client Credit Memos To Validate' && <Link to={`billing/client/credit/validate/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>Validate</Typography></Link> }

                  { type === 'Client Debit Memos To Validate' && <Link to={`billing/client/debit/validate/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>Validate</Typography></Link> }

                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
