import React from 'react'

import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import IconButton from '@material-ui/core/IconButton'
import SearchIcon from '@material-ui/icons/Search'

import classes from './CustomSearchTextComponent.module.scss'

export default function CustomSearchTextComponent (props) {
  const { reset, id, onChange, placeholder = '', width, name = '', noIcon } = props

  const infoRef = React.useRef(null)
  const myRef = React.useRef(null)
  React.useEffect(() => {
    myRef.current = infoRef.current.children[0]
  }, [])

  if (reset) {
    myRef.current.value = ''
  }

  return (
    <Paper style={noIcon ? {} : { width: width }} className={classes.root}>
      {!noIcon && 
      <IconButton className={classes.iconButton} aria-label='search'>
        <SearchIcon />
      </IconButton>}
      <InputBase
        ref={infoRef}
        id={id}
        className={classes.input}
        placeholder={placeholder}
        inputProps={{ 'aria-label': 'search google maps' }}
        onChange={e => {
          e.persist()
          onChange(e)
        }}
        autoComplete='no'
        name={name}
      />
    </Paper>
  )
}
