import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Disapprove from '../Disapprove/Disapprove'

// Components
import TextField from 'StandardComponents/TextField/CustomTextField'
import ApproveValidator from './ApproveValidator'

// Styles
import classes from './Approve.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
  }

  const handleOnSuccessDialog = () => {
    submitFunction()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            {page === 'soa'
              ? 'Statement of Account (SOA) Successfully Approved'
              : page === 'credit'
                ? 'Approve Credit Memo'
                : page === 'debit'
                  ? 'Approve Debit Memo'
                  : page === 'invoice'
                    ? 'Approve Invoice Transaction'
                    : ''}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        {page === 'soa'
          ? 'Statement of Account (SOA) was successfully approved and will be included in the scheduled bill run for release.'
          : page === 'credit'
            ? 'Credit Memo was successfully approved by the Supervisor and Manager.'
            : page === 'debit'
              ? 'Debit Memo was successfully approved by the Supervisor and Manager.'
              : page === 'invoice'
                ? 'Invoice Transaction was successfully approved by the Supervisor and Manager.'
                : ''}
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={handleOnSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction, disapproveFunction, setActiveStep } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)
  const [errors, setErrors] = React.useState({
    disapprove: []
  })
  const [value, setValue] = React.useState({ remarks: '' })
  const [allowedFields, setAllowedFields] = React.useState({})
  const _allowedFields = {
    remarks: ''
  }
  const [openDisapproveDialog, setOpenDisapproveDialog] = React.useState(false)

  setTimeout(() => {
    // setValue(_allowedFields)
    setAllowedFields(_allowedFields)
  }, 10)

  const handleDisapprove = () => {
    setOpenDisapproveDialog(true)
  }

  const handleOnchange = value => {
    setValue({
      remarks: value
    })
  }

  const handleOnClose = () => {
    if (page === 'credit' || page === 'debit' || page === 'invoice') {
      setOpenDialog(false)
    } else {
      setActiveStep(0)
      setOpenDialog(false)
    }
  }

  const doValidatedisapprove = value => {
    let { error } = ApproveValidator({
      remarks: value.remarks,
      debit: value.remarks,
      credit: value.remarks,
      invoice: value.remarks
    })

    if (error) {
      error = { ...error }
      console.group('Base Error')
      console.log(error)
      console.groupEnd()
      setErrors(state => ({ disapprove: state.disapprove, ...error }))
      return !!error
    }
  }

  const doValidateAll = data => {
    const disapprove = doValidatedisapprove(data)

    setErrors(state => ({ ...state, disapprove }))
    return !!disapprove
  }

  const onShowSuccessDialog = () => {
    const data = {}
    for (let f in allowedFields) data[f] = value[f]
    const hasError = doValidateAll(data)
    if (hasError) return
    if (openDialog === true) {
      setOpenSuccessDialog(true)
      if (openSuccessDialog === true) setOpenDialog(false)
    }
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '400px', display: 'flex' } }}
      >
        {page === 'soa' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes.button}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes.title}>
                  Confirm Validate Statement
                </Typography>
              </div>
            </DialogTitle>
            <DialogContent id='soa-content'>
              Are you sure you want to Aaprove the Statement of Account?
              <br />
              <br />
              This SOA and attachment will be included in the Bill Run Schedule for Release.
              <br />
              <br />
              <TextField
                id='remarks-approve-id'
                defaultValue={value.remarks}
                onChange={event => handleOnchange(event.target.value)}
                label='Reason for Approving SOA'
                required
                error={errors.remarks}
              />
              <br />
              <br />
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleDisapprove}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'credit' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes.button}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes.title}>
                Approve Credit Memo
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='credit-content'>
            Are you sure you want to approve the Credit Memo entry?
              <br />
              <br />
              <TextField
                id='remarks-approve-id'
                defaultValue={value.remarks}
                onChange={event => handleOnchange(event.target.value)}
                label='Reason for Approving Credit Memo'
                required
                error={errors.credit}
              />
              <br />
              <br />
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleDisapprove}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'debit' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes.button}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes.title}>
                  Confirm Debit Memo
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='debit-content'>
              Are you sure you want to Approve the Debit Memo?
              <br />
              <br />
              note will be Approve by both the Billing Supervisor and Billing Manager.
              <br />
              <br />
              <TextField
                id='remarks-approve-id'
                defaultValue={value.remarks}
                onChange={event => handleOnchange(event.target.value)}
                label='Reason for Approving Debit Memo'
                required
                error={errors.debit}
              />
              <br />
              <br />
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleDisapprove}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'invoice' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes.button}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes.title}>
                  Confirm Invoice Approval
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='invoice-content'>
              Are you sure you want to Approve the Invoice Approval?
              <br />
              <br />
              note will be Approve by both the Billing Supervisor and Billing Manager.
              <br />
              <br />
              <TextField
                id='remarks-approve-id'
                defaultValue={value.remarks}
                onChange={event => handleOnchange(event.target.value)}
                label='Reason for Approving Invoice Approval'
                required
                error={errors.invoice}
              />
              <br />
              <br />
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleDisapprove}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
      </Dialog>
      <SuccessDialog
        page={page}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        submitFunction={submitFunction}
      />
      {openDisapproveDialog === true ? (
        <Disapprove
          page={page}
          openDisapproveDialog={openDisapproveDialog}
          setOpenDisapproveDialog={setOpenDisapproveDialog}
          setActiveStep={setActiveStep}
          submitFunction={disapproveFunction}
        />
      ) : (
        ''
      )}
    </React.Fragment>
  )
}
