import React from 'react'
import { useHistory } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'

import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

// Components
import HeaderDetails from 'StandardComponents/HeaderDetailsNew/HeaderDetails'
import FormDrawer from 'StandardComponents/FormDrawer/FormDrawer'
import FormDrawerSupervisor from '../../Components/SoaSupervisorFormDrawer/FormDrawer'
import BIR from 'StandardComponents/BIR/BIR'
import Corporate from 'StandardComponents/Corporate/Corporate'
import Attachment from 'StandardComponents/Attachment/Attachment'
import SOAAnalyze from 'StandardComponents/SOAAnalyze/SOAAnalyze'
import TabPanel from '../../Components/TabPanelComponent/TabPanelComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingSOAServices from 'Services/Api/BillingSOAServices'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

// CSS
import classes from './MemberSOAPage.module.scss'

function a11yProps (index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB'
  },
  tabs: {
    fontSize: 12
  },
  indicator: {
    backgroundColor: '#3AB77D'
  },
  // button: {
  selected: {
    backgroundColor: 'yellow'
  }
  // }
}))

export default function MemberSOAPage (props) {
  const { soaNumber, type } = props.match.params
  const [userType, setUserType] = React.useState(null)
  const [soa, setSOA] = React.useState({})
  const [soaPage, setSOAPage] = React.useState(false)
  const tabClasses = useStyles()
  const [openDialog, setOpenDialog] = React.useState(false)
  const [activeStep, setActiveStep] = React.useState(0)
  const [value, setValue] = React.useState(0)
  const history = useHistory()

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }

  PageTitleService()

  React.useEffect(() => {
    Promise.all([BillingSOAServices.getMemberPreSOAToAnalyze({ soaNumber })]).then(values => {
      setUserType(type)
      setSOA(state => ({ ...state, details: values[0].data.data[0] }))
      setSOAPage(true)
    })
  }, [activeStep])

  if (soa && soa.details) {
    console.log(soa.details)
    BreadcrumbsService.setSelectedItems([
      'soa',
      `soaId:${soa.details.first_name.toUpperCase() + ' ' + soa.details.last_name.toUpperCase()} - STATEMENT OF ACCOUNT`
    ])
  }

  const voidFunction = async soaNumber => {
    await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
    BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'void' }).then(res => {
      console.group('Void SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const getSteps = () => {
    return ['Analyze Statement', 'Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Submit']
  }

  const getStepContent = props => {
    const { stepIndex, activeStep } = props
    switch (stepIndex) {
      case 0:
        return <SOAAnalyze values={{ soa: soa.details, type: type, entity: 'member' }} />
      case 1:
        return <BIR soaNumber={soaNumber} soaType={'member'} />
      case 2:
        return <Corporate soaNumber={soaNumber} soaType={'member'} />
      case 3:
        return activeStep ? (
          <Attachment submit={{ activeStep: activeStep, type: type, soaType: 'member' }} />
        ) : (
          <Attachment soaNumber={soaNumber} />
        )
    }
  }

  const steps = getSteps()

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1)
  }

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1)
    if (activeStep === 0) history.push('/')
  }

  const handleReset = () => {
    setActiveStep(0)
  }
  // for saving
  const save = soaNumber => {
    // BillingAnalystServices.saveClientSOA({ soaNumber: soaNumber }).then(res => {
    //   console.group('Save SOA')
    //   console.log(res)
    //   console.groupEnd()
    // })
    history.push('/')
  }

  const group = localStorage.getItem('role')

  return (
    <div className={classes.root}>
      <React.Fragment>
        <HeaderDetails soa={soa.details} activeStep={activeStep} steps={steps} type={userType} entity={'member'} />
        {group === 'SOA Analyst' && (
          <FormDrawer
            voided={() => voidFunction(soaNumber)}
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            save={() => save(soaNumber)}
            nextStep={handleNext}
            stepBack={handleBack}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={soa.details} value={value} />}
          />
        )}

        {group === 'SOA Approver' && (
          <FormDrawerSupervisor
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            openDialog={openDialog}
            setOpenDialog={setOpenDialog}
            nextStep={handleNext}
            stepBack={handleBack}
            voided={() => voidFunction(soaNumber)}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={soa.details} value={value} />}
          />
        )}
      </React.Fragment>
    </div>
  )
}
