import React from 'react'
import { Link } from 'react-router-dom'

import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import VisibilityIcon from '@material-ui/icons/Visibility'
import ReplyIcon from '@material-ui/icons/Reply'

import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'

import * as moment from 'moment'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'

import BillingClientInvoicesServices from 'Services/Api/BillingClientInvoicesServices'

import classes from './InvoiceApprovedPage.module.scss'

const tableHeaders = [
  {
    id: 'approved-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Billing Code & Type *',
    center: false,
    search: true,
    sort: true,
    accessor: 'computed_fees.billing_code'
  },
  {
    id: 'approved-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Member ID',
    search: true,
    sort: true,
    accessor: 'member.id'
  },
  {
    id: 'approved-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Member Name',
    search: true,
    sort: true,
    accessor: 'member.first_name'
  },
  {
    id: 'approved-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Description *',
    search: true,
    sort: true,
    accessor: 'computed_fees.description'
  },
  {
    id: 'approved-invoice-tax-type',
    numeric: false,
    disablePadding: false,
    label: 'Taxable Amount *',
    search: true,
    sort: true,
    accessor: 'computed_fees.value|isNumber'
  },
  {
    id: 'approved-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Tax Amount'
  }
]

export default function InvoicePage (props) {
  PageTitleService()
  const { transaction_number } = props.match.params
  const [totalTaxAmount, setTotalTaxAmount] = React.useState(0)
  const [approvedInvoice, setApprovedInvoice] = React.useState({
    data: {}
  })

  const [invoiceDetail, setInvoiceDetail] = React.useState({})

  React.useEffect(() => {
    Promise.all([BillingClientInvoicesServices.getTobeRevisedInvoice({ transaction_number })]).then(value => {
      const [invoice] = value
      setApprovedInvoice(prevState => ({
        ...prevState,
        data: invoice.data.data
      }))
    })

    BreadcrumbsService.setSelectedItems(['approveInvoice', `approvedInvoiceTransNumber:${transaction_number}`])
  }, [])

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  // Initialize
  React.useEffect(() => {
    fetchData({})
  }, [])

  const fetchData = (params) => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    BillingClientInvoicesServices.getInvoiceDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }

  const transactionInfo = approvedInvoice.data
  const clientInfo = approvedInvoice.data.client
  const billingInfo = approvedInvoice.data.billing
  const contractInfo = approvedInvoice.data.contract
  const summaryInfo = approvedInvoice.data.summary
  const dateInfo = approvedInvoice.data.date
  const invoiceInfo = invoiceDetail.data

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }

  return (
    <div className={classes.root}>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5'>
          {clientInfo && clientInfo.account_name} - Invoice
        </Typography>
        <Typography id='header-details-account-number' color='textSecondary'>
          ACCOUNT NUMBER {clientInfo && clientInfo.account_number}
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'580px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>{clientInfo && clientInfo.account_status}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>{clientInfo && clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                {billingInfo && billingInfo.cycle.code + ' ' + billingInfo.cycle.name}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>{clientInfo && clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                {moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                {moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>
            <ReplyIcon id='export-icon' className={classes['export-icon']} />
            <Link id='export-link' to={`/`} className={classes['export-link']}>
              Export
            </Link>
            <VisibilityIcon id='view-billing-icon' className={classes['view-icon']} />
            <Link id='view-billing-link' to={`/`} className={classes['view-link']}>
              View Billing Cycle
            </Link>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              {transactionInfo.soa_number && (
                <Typography id='content-details-soa-number-field' color='textSecondary'>
                  SOA NO
                </Typography>
              )}
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(transactionInfo.transaction && transactionInfo.transaction.date).format('MMM DD, YYYY')}
              </Typography>
              <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {transactionInfo.transaction && transactionInfo.transaction.period.start ? moment(transactionInfo.transaction.period.start).format('MMM DD, YYYY') : ' '}{' '}
                - {transactionInfo.transaction && transactionInfo.transaction.period.end ? moment(transactionInfo.transaction.period.end).format('MMM DD, YYYY') : ' '}
              </Typography>
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
              {transactionInfo.soa_number && (
                <Typography id='content-details-soa-number-value' style={{ width: '100px' }}>
                  {transactionInfo.soa_number}
                </Typography>
              )}
            </div>
            <div className={classes.status}>
              <Typography id='content-details-status' style={{ fontSize: '14px', marginTop: '8px', fontWeight: '500' }}>
                APPROVED
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='left'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='left'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='left'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='left'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxAmount)}
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='close-link' to={`/`} style={{ textDecoration: 'none' }}>
              <Button id='close-button' variant='contained' className={classes['close-button']}>
                Close
              </Button>
            </Link>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
