import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Services
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this-week', text: 'This Week' },
    { value: 'this-month', text: 'This Month' }
  ],
  default: 'this-month'
}

const columnConfig = [
  { id: 'collection-distribution-for-approval-tile-table-header', name: 'Account Name' },
  { id: 'collection-distribution-for-approval-tile-table-header', name: 'Category' },
  { id: 'collection-distribution-for-approval-tile-table-header', name: 'Date Submitted' },
  { id: 'collection-distribution-for-approval-tile-table-header', name: '' }
]

const CDForApprovalTile = props => {
  // Constants
  const { service = () => {} } = props
  const history = useHistory()
  const [tile, setTile] = React.useState({
    status: 'For Approval',
    count: 0,
    data: [],
    filter: {},
    filterMenu,
    limit: 10,
    order: true,
    orderBy: 'account_name',
    page: 1,
    sort: '',
    title: 'Collection Distribution Submitted for Approval',
    userOwned: true
  })

  // Initialize / Watch
  React.useEffect(() => {
    Promise.all([
      CollectionCDFServices.getMany({
        status: tile.status,
        limit: 5,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        rangeTarget: 'date_submitted',
        userOwned: true
      })
    ]).then(res => {
      const [CDFService] = res
      const { count, results } = CDFService.data.data
      setTile(i => ({ ...i, count, data: results }))
    })
  }, [tile.filterMenu.default])

  const handleOnViewAll = () => {}
  return (
    <React.Fragment>
      <DashboardTile
        id='collection-distribution-for-approval-tile'
        count={{ color: 3, value: tile.count, id: 'collection-distribution-for-approval-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        viewList={
          <Typography
            id='collection-distribution-for-approval-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => history.push('/collection/lists/collection-distribution/client/submitted-analyst')}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`collection-distribution-for-approval-tile-table-row-${index}`}
                id={`collection-distribution-for-approval-tile-table-row-${index}`}
              >
                <TableCell id={`collection-distribution-for-approval-tile-table-cell-account-name-${index}`}>
                  {item.contract?.client?.account_name || ''}
                </TableCell>
                <TableCell id={`collection-distribution-for-approval-tile-table-cell-category-${index}`}>
                  {item.category || ''}
                </TableCell>
                <TableCell id={`collection-distribution-for-approval-tile-table-cell-date-submitted-${index}`}>
                  {item.date?.submitted ? moment(item.date.submitted).format('ll') : ''}
                </TableCell>
                <TableCell id={`collection-distribution-for-approval-tile-table-cell-action-${index}`}>
                  {/* TODO: Change link when end-point and data is available */}
                  <Link to={item.transaction_type.includes('Advance') ? `/collection/distribution/view/submitted/client/${item.cdf_number}?isAdvance=true` : `/collection/distribution/view/submitted/client/${item.cdf_number}`}>
                    <Typography
                      variant='caption'
                      className='text-link underline'
                      id={`collection-distribution-for-approval-tile-view-${index}`}
                    >
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

CDForApprovalTile.displayName = 'DashboardTileComponent'

export default CDForApprovalTile
