import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import noop from 'lodash/noop'

import CustomDialog from 'SharedComponents/CustomDialog/CustomDialog'

PublishDialog.propTypes = {
  open: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
}

export default function PublishDialog({ open = false, onCancel = noop, onSubmit = noop, ...props }) {
  return (
    <CustomDialog
      open={open}
      title="Publish Letter"
      content={
        <div>
          <Typography>This will publish the letter you've created.</Typography>

          <Typography style={{ marginTop: 20 }}>
            Please note that there should only be one published letter for
            each type. This will disable the current published letter type.
            </Typography>

          <Typography style={{ marginTop: 20 }}>
            Are you sure you want to continue?
            </Typography>
        </div>
      }
      onClose={onCancel}
      onOk={onSubmit}
      {...props}
    />
  )
}
