import React from 'react'
import { Grid, Typography, Tabs, Tab } from '@material-ui/core'

import LetterTemplatePage from './LetterTemplate/LetterTemplate'
import ScheduleLettersPage from './ScheduleLetters/ScheduleLetters'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import classes from './CollectionMaintenance.module.scss'

export default function CollectionMaintenance() {
  const [selectedTab, setSelectedTab] = React.useState(2)

  const handleChange = (event, newValue) => {
    setSelectedTab(newValue)
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance'])
  })

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>
                Collection Maintenance
              </Typography>
            </Grid>
          </Grid>
        </Grid>

        <Tabs
          indicatorColor='xyz'
          value={selectedTab}
          onChange={handleChange}
        >
          <Tab label='Letter Template' className={selectedTab === 0 ?  `${classes['tab-selected']}` : ''} />
          <Tab label='Report Template' className={selectedTab === 1 ?  `${classes['tab-selected']}` : ''} />
          <Tab label='Schedule Letters' className={selectedTab === 2 ?  `${classes['tab-selected']}` : ''} />
        </Tabs>

        {selectedTab === 0 && <LetterTemplatePage />}
        {selectedTab === 1 && <LetterTemplatePage />}
        {selectedTab === 2 && <ScheduleLettersPage />}
      </Grid>
    </div>
  )
}
