import instance from '../instance'

const axios = instance('/b/m/')

export default class BillingMaintenanceService {
  static getAllBillingCodes (params) {
    return axios({
      url: `/billing/maintenance/codes`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getAllBillingCodesWithHierarchy (params) {
    return axios({
      url: `/billing/maintenance/codes/view/hierarchy`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static updateBillingCode ({codeId, data}) {
    return axios({
      url: `/billing/maintenance/codes/${codeId}`,
      method: 'PUT',
      data
    })
  }
}
