import React from 'react'
import { withStyles, makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import MuiDialogContent from '@material-ui/core/DialogContent'
import MuiDialogActions from '@material-ui/core/DialogActions'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import clsx from 'clsx'

const styles = theme => ({
  root: {
    margin: 0,
    padding: '25px 25px 0px'
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: '12px',
    color: theme.palette.grey[500]
  },
  pdRight: {
    paddingRight: '1rem'
  }
})

const useStyles = makeStyles(theme => ({
  leftGrid: {
    width: '50%',
    paddingRight: '1.5rem'
  },
  rightGrid: {
    width: '50%',
    paddingLeft: '1.5rem'
  },
  btnOutlined: {
    padding: '.75rem 1rem',
    color: '#3ab77d',
    border: '1px solid #3ab77d',
    width: '100%'
  },
  btnContained: {
    padding: '.75rem 1rem',
    color: '#fff',
    background: '#3ab77d',
    width: '100%'
  },
  alignEnd: {
    textAlign: 'end'
  }
}))

const DialogTitle = withStyles(styles)(props => {
  const { children, classes, onClose, ...other } = props
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant='h6' className={classes.pdRight}>
        {children}
      </Typography>
      {onClose ? (
        <IconButton aria-label='close' className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  )
})

const DialogContent = withStyles(theme => ({
  root: {
    padding: '25px 25px 5px'
  }
}))(MuiDialogContent)

const DialogActions = withStyles(theme => ({
  root: {
    margin: 0,
    padding: '10px 25px 25px'
  }
}))(MuiDialogActions)

export default function CustomizedDialogs (props) {
  const defaultFunction = () => {}
  const {
    open = false,
    onClose = defaultFunction,
    onOk = defaultFunction,
    onCancel = defaultFunction,
    title = '',
    content,
    submitBtn = true,
    cancelBtn = true,
    cancelLabel = 'NO',
    submitLabel = 'YES'
  } = props

  const classes = useStyles()
  return (
    <div>
      <Dialog
        PaperProps={{
          style: {
            borderRadius: '0px',
            width: '450px',
            boxShadow: 'none'
          }
        }}
        onClose={onClose}
        open={open}
        aria-labelledby='customized-dialog-parent'
      >
        <DialogTitle id='customized-dialog-title' onClose={onClose}>
          {title}
        </DialogTitle>
        <DialogContent>{content}</DialogContent>
        <DialogActions>
          <Grid container>
            <Grid item className={classes.leftGrid}>
              {cancelBtn && (
                <Button
                  className={classes.btnOutlined}
                  autoFocus
                  onClick={onClose}
                  disableElevation
                  variant='outlined'
                  size='large'
                  color='success'
                >
                  {cancelLabel}
                </Button>
              )}
            </Grid>
            <Grid item className={clsx(classes.rightGrid, classes.alignEnd)}>
              {submitBtn && (
                <Button
                  className={classes.btnContained}
                  autoFocus
                  onClick={onOk}
                  disableElevation
                  variant='contained'
                  size='large'
                  color='success'
                >
                  {submitLabel}
                </Button>
              )}
            </Grid>
          </Grid>
        </DialogActions>
      </Dialog>
    </div>
  )
}
