import React, { useRef } from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import queryString from 'query-string';

// Components
import CollectionHeader from './../../../Components/CollectionDistribution/CollectionHeader/CollectionHeader'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import FirstStepComponent from './../components/AnalyzeDetailsComponent/AnalyzeDetailsComponent'
import PageFooter from './../../../Components/PageFooter/PageFooter'
import SecondStepComponent from './../components/CollectionBreakDownComponent/CollectionBreakDownComponent'
import { SubmitDialog, SuccessDialog } from './../components/ActionDialog'
import ThirdStepComponent from './../components/CollectionDetailsComponent/CollectionDetailsComponent'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetFields, UpdateField } from './../../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { useHistory } from 'react-router-dom'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Styles
import classes from './style.module.scss'

const steps = require('./../config/stepper.json')

export default function ValidateCollectionDistributionPage (props) {
  const { data_id = null, data_type = '', data_status = '' } = props.match.params
  let { isAdvance, stepNumber } = queryString.parse(props.location.search)
  const dispatch = useDispatch()
  const refContainer = useRef()
  const history = useHistory()
  const _formData = useSelector(state => state.CollectionForm)

  const [taxInfo, setTaxInfo] = React.useState({})
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: steps[`${isAdvance ? 'advance-' : ''}${data_status}`] || [],
    stepNumber: 1,
    stepCount: steps[`${isAdvance ? 'advance-' : ''}count-${data_status}`] || 0
  })
  const [pageData, setPageData] = React.useState({})
  const [pageTitle, setPageTitle] = React.useState('')
  const [actionDialog, setActionDialog] = React.useState({ clientSubmit: false, clientSuccess: false })

  React.useEffect(() => {
    refContainer.current.parentElement.scrollTo(0, 0)
    PageTitleService(null)
    handleGetData()
  }, [])

  React.useEffect(() => {
    handlePageStatus()
  }, [pageData])

  React.useEffect(() => {
    const size = Object.keys(_formData.adjustments || {}).length
    if (stepNumber && size > 1) {
      handleStepChange(parseInt(stepNumber))
      stepNumber = null
    }
  }, [_formData.adjustments])

  const handlePageStatus = () => {
    switch (data_status) {
      case 'approved':
        setPageTitle(`Approved ${isAdvance ? 'Advance ' : '' }Collection Distribution`)
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'listOfCDApproved',
          'approvedCD'
        ])
        break
      case 'submitted':
        setPageTitle(`${isAdvance ? 'Advance ' : '' }Collection Distribution Submitted for Approval`)
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'listOfCDSubmittedApproval',
          'collectionDistributionSubmittedApproval'
        ])
        break
      case 'disapproved':
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'pendingTransactionsList',
          'disapprovedCD'
        ])
        setPageTitle(`Disapproved ${isAdvance ? 'Advance ' : '' }Collection Distribution`)
        break
      case 'new':
        setPageTitle(`Create ${isAdvance ? 'Advance ' : '' }Collection Distribution`)
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'listOfAccountsWithAgingSummary',
          `clientAgingSummary:${pageData.client?.account_name || ''}`,
          isAdvance ? 'createAdvance' : 'create'
        ])
        break
      case 'void':
        setPageTitle(`Void ${isAdvance ? 'Advance ' : '' }Collection Distribution`)
        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          'voidCD'
        ])
        break
    }
  }

  const showComponent = componentProp => {
    if ((data_type && data_type) === 'client') {
      if (data_status === 'new' || data_status === 'disapproved') {
        if (!isAdvance) {
          switch (stepper.stepNumber) {
            case 1:
              return <FirstStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
            case 2:
              return <SecondStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
            case 3:
              return <ThirdStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
            default:
              return <ThirdStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
          }
        } else {
          switch (stepper.stepNumber) {
            case 1:
              return <SecondStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
            case 2:
              return <ThirdStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
            default:
              return <ThirdStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
          }
        }
        
      } else {
        switch (stepper.stepNumber) {
          case 1:
            return <SecondStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
          case 2:
            return <ThirdStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
          default:
            return ''
        }
      }
    }
  }

  const handleGetData = () => {
    Promise.all([CollectionCDFServices.getCDF({ data_id, isAdvance })])
      .then(res => {
        const [cdfServiceRes] = res
        const { cdf_fees = {}, form = {}, ref = {}, tax_type, summary = {} } = cdfServiceRes.data.data

        setPageData(i => ({
          ...i,
          ...cdfServiceRes.data.data,
          reference: ref
        }))
        dispatch(SetFields({ ...form, total_fees: { ...summary } }))

        CollectionCDFServices.getTaxValue(tax_type).then( res => {
          const { data } = res.data
          setTaxInfo(data[0])
        })
      })
      .catch(err => console.error(`Error Fetching Data: ${err}`))

    handlePageStatus()
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handleOnFetchMembers = params => {
    LoadingService.loading(true)
    if (!params || params.length <= 0) {
      LoadingService.loading(false)
      return false
    }
    CollectionCDFServices.getCDFMember({
      cdfNumber: pageData.cdf_number,
      soaNumber: params
    })
      .then(res => {
        LoadingService.loading(false)
        const { count, results, limit, page } = res.data.data
        setPageData(i => ({ ...i, member: { count, results, limit, page } }))
      })
      .catch(err => {
        console.error(err)
        LoadingService.loading(false)
      })
  }

  const handleOnSelectSoaTab = params => {
    let _data = null
    if (!params.data) {
      _data = pageData?.cdf_fees?.cdf_summary || {}
      dispatch(
        UpdateField({
          value: {
            ..._data,
          },
          field: 'formData'
        })
      )
      return false
    }
    _data = pageData?.cdf_fees?.cdf_soa || []
    dispatch(
      UpdateField({
        value: {
          ..._data[params.selected] || {},
        },
        field: 'formData'
      })
    )
  }

  const handleCDFSubmitForApproval = () => {
    LoadingService.loading(true)
    CollectionCDFServices.submit({ data: { cdf_number: data_id } })
      .then(res => {
        setActionDialog(i => ({ ...i, clientSubmit: false, clientSuccess: true }))
        LoadingService.loading(false)
      })
      .catch(e => {
        console.error(`Error submitting cdf: ${e}`)
        LoadingService.loading(false)
      })
  }

  return (
    <div className={classes.root} ref={refContainer}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5' className={classes.bold}>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} disabled />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({
                disabled: true,
                handleOnSelectSoaTab,
                handleOnFetchMembers,
                member: pageData.member || [],
                data: pageData.cdf_fees
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            isStepper
            handleStepChange={e => handleStepChange(e)}
            stepNumber={stepper.stepNumber}
            stepCount={stepper.stepCount}
            isView
            isEdit={!!(pageData.status === 'New' || pageData.status === 'Disapproved')}
            isPrint={!!(pageData.status === 'Approved')}
            isVoid={!!(pageData.status !== 'Void')}
            handleOnCloseClicked={() => {
              history.push('/')
            }}
            handleOnCancelClicked={() => history.push(`/`)}
            handOnEditClicked={() => {
              const _isAdvance = pageData.transaction_type.includes('Advance') ? 'true' : 'false'
              history.push(`/collection/collection-distribution/client/${pageData.cdf_number}?isAdvance=${_isAdvance}`)
            }}
            handleOnPrintClicked={() => history.push(`/collection/pdf-view/${pageData.cdf_number}`)}
            handleOnSubmitClicked={() => {
              handleStepChange(stepper.stepNumber + 1)
              setActionDialog(i => ({ ...i, clientSubmit: true }))
            }}
          />
        </Grid>
      </Grid>
      <SubmitDialog
        open={actionDialog.clientSubmit}
        handleOnAssign={() => handleCDFSubmitForApproval()}
        handleOnClose={e => {
          handleStepChange(stepper.stepNumber - 1)
          setActionDialog(i => ({ ...i, clientSubmit: e }))
        }}
        isAdvance={isAdvance}
      />
      <SuccessDialog
        open={actionDialog.clientSuccess}
        handleOnClose={e => setActionDialog(i => ({ ...i, clientSuccess: e }))}
        handleOnOk={() => {
          history.push('/')
        }}
      />
    </div>
  )
}
