import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll2/ViewAll'

// Style
import classes from './ClientTPAsToBeReplenishedTile.module.scss'

import { TruncateString } from 'Utilities/TextFormatter'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' },
    { value: 'showAll', text: 'Show All' }
  ],
  default: 'showAll'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const ClientTPAsToBeReplenishedTile = props => {
  // Constants
  const { services = () => {} } = props
  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUserId'] = localStorage.getItem('user_id')
    }
    setParams(_params)

    services.getTpaAccounts(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({
        ...i,
        body: results,
        count,
        limit,
        page,
        sort: _params.sort,
        orderBy: _params.orderBy ? _params.orderBy : list.orderBy
      }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = params => {
    const _params = params
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUserId'] = localStorage.getItem('user_id')
    }
    services.getTpaAccountsTile(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = value => {
    fetchTileData({ range: rangeDate[value] })
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Clients TPAs to be Replenished Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 6, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Clients TPAs to be Replenished'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => {
              setShow(true)
              fetchData({})
            }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Budget</TableCell>
              <TableCell>Date Last Billed</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body &&
              tileList.body.map((item, index) => (
                <TableRow>
                  <TableCell style={{ padding: '0' }}>{TruncateString(item.client.account_name, 40)}</TableCell>
                  <TableCell>
                    {item.contract.tpa ? item.contract.tpa.rsd_usage && item.contract.tpa.rsd ? parseInt((item.contract.tpa.rsd_usage / item.contract.tpa.rsd) * 100) + '%' : 0 + '%' : 0 + '%'}
                  </TableCell>
                  <TableCell>{item.invoice ? moment(item.invoice.date.created).format('MMM DD, YYYY') : ''}</TableCell>
                  <TableCell align={'right'} style={{ paddingRight: '0' }}>
                    <Link to={`/billing/journal/transactions/${item.client.account_number}`}>
                      <Typography variant='caption' className='text-link underline'>
                        View Journal
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'debit-memos-submitted-for-approval-modal'}
        config={config}
        tile={'tpaMonitoring'}
        type='TPA Replenishment'
        title='List of TPA Accounts to be Replenished'
        placeholder='Search List of TPA Accounts'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['client.account_number', 'client.account_name', 'contract.type']}
      />
    </React.Fragment>
  )
}

ClientTPAsToBeReplenishedTile.displayName = 'DashboardTileComponent'

export default ClientTPAsToBeReplenishedTile
