const ClientInformation = ({ classes, clientInfo, hmoInfo }) => {
  return (
    <React.Fragment className={classes.client_details}>
      {clientInfo && (
        <React.Fragment>
          <Typography variant='h6' className={classes.title}>
            {clientInfo.client.account_name}
          </Typography>

          <Typography className={classes.title} style={{ fontWeight: 'bold', marginTop: '10px' }}>
            Basic Information
          </Typography>

          <CustomPropertyText
            className={classes.customText}
            readOnly='true'
            label='ACCOUNT NO.'
            value={clientInfo.client.account_number}
          />

          <CustomPropertyText readOnly='true' label='ACCOUNT STATUS' value={clientInfo.client.account_status} />

          <CustomPropertyText
            className={classes.customTextComponent}
            readOnly='true'
            label='PAYMENT TERM'
            value={clientInfo.contract.payment_term}
          />

          <CustomPropertyText readOnly='true' label='TAX TYPE' value={clientInfo.client.tax_type} />

          <CustomPropertyText readOnly='true' label='ACCOUNT TIN' value={clientInfo.client.tin} />
          {clientInfo.client.sales_channel ? (
            <CustomPropertyText
              readOnly='true'
              label='SALES CHANNEL'
              value={clientInfo.client.sales_channel.type ? clientInfo.client.sales_channel.type : '-'}
            />
          ) : (
            <CustomPropertyText
              readOnly='true'
              label='SALES CHANNEL'
              value='-'
            />
          )}
          {clientInfo.client.sales_channel &&
            (clientInfo.client.sales_channel.type === SALES_CHANNEL.AGENT ? (
              <React.Fragment>
                <CustomPropertyText
                  readOnly='true'
                  label='AGENT NAME'
                  value={clientInfo.client.sales_channel.name ? clientInfo.client.sales_channel.name : '-'}
                />
              </React.Fragment>
            ) : clientInfo.client.sales_channel.type === SALES_CHANNEL.BDO ? (
              <React.Fragment>
                <CustomPropertyText
                  readOnly='true'
                  label='BDO NAME'
                  value={clientInfo.client.sales_channel.name ? clientInfo.client.sales_channel.name : '-'}
                />
              </React.Fragment>
            ) : (
              clientInfo.client.sales_channel.type === SALES_CHANNEL.BROKER && (
                <React.Fragment>
                  <CustomPropertyText
                    readOnly='true'
                    label='NAME OF COMPANY:'
                    value={clientInfo.client.sales_channel.name ? clientInfo.client.sales_channel.name : '-'}
                  />
                  <CustomPropertyText
                    readOnly='true'
                    label='BROKER NAME'
                    value={
                      clientInfo.client.sales_channel.contact_person
                        ? clientInfo.client.sales_channel.contact_person
                        : '-'
                    }
                  />
                  <CustomPropertyText
                    readOnly='true'
                    label='POSITION'
                    value={clientInfo.client.sales_channel.position ? clientInfo.client.sales_channel.position : '-'}
                  />
                  <CustomPropertyText
                    readOnly='true'
                    label='CONTACT NO.'
                    value={
                      clientInfo.client.sales_channel.contact_no ? clientInfo.client.sales_channel.contact_no : '-'
                    }
                  />
                  <CustomPropertyText
                    readOnly='true'
                    label='EMAIL'
                    value={
                      clientInfo.client.sales_channel.email_address
                        ? clientInfo.client.sales_channel.email_address
                        : '-'
                    }
                  />
                </React.Fragment>
              )
            ))}

          <Typography className={classes.title} style={{ fontWeight: 'bold', marginTop: '15px' }}>
            HMO Information
          </Typography>

          <CustomPropertyText readOnly='true' label='CONTRACT' value={clientInfo.contract.name} />

          <CustomPropertyText readOnly='true' label='CONTRACT TYPE' value={clientInfo.contract.type} />
          <CustomPropertyText
            readOnly='true'
            label='EFFECTIVITY DATE'
            value={DateFormatterByAbbreviation(
              new Date(
                moment(clientInfo.contract.effectivity).subtract(
                  parseInt(clientInfo.contract.effectivity.substring(11, 13)),
                  'h'
                )
              )
            )}
          />
          <CustomPropertyText
            readOnly='true'
            label='EXPIRATION DATE'
            value={DateFormatterByAbbreviation(
              new Date(
                moment(clientInfo.contract.expiration).subtract(
                  parseInt(clientInfo.contract.expiration.substring(11, 13)),
                  'h'
                )
              )
            )}
          />
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

const ActionTabs = props => {
  const {
    headCells,
    users,
    clientInfo,
    onShowViewMore,
    viewMore,
    setViewMore,
    pagination,
    params,
    fetchData,
    changeView,
    userInfo,
    classes
  } = props
  const [reset, setReset] = React.useState(false)

  const cancelAssigning = params => {
    setReset(true)
  }

  const cancelReset = params => {
    setReset(false)
  }

  return (
    <React.Fragment className={classes.client_details}>
      <Typography variant='h6' className={classes.title} style={{ marginTop: '8px' }}>
        Setup Billing Cycle
      </Typography>
      <CustomTabs defaultIndex={1}>
        <CustomTab id='billing-analyst-tab' label='BILLING ANALYST'>
          <BillingAnalystTab
            headCells={headCells}
            users={users}
            onShowViewMore={onShowViewMore}
            viewMore={viewMore}
            setViewMore={setViewMore}
            clientInfo={clientInfo}
            pagination={pagination}
            params={params}
            fetchData={fetchData}
            changeView={changeView}
            cancelAssigning={cancelAssigning}
            cancelReset={cancelReset}
            reset={reset}
          />
        </CustomTab>
        <CustomTab id='billing-cycle-tab' label='BILLING CYCLE'>
          <BillingCycleTab clientInfo={clientInfo} />
        </CustomTab>
        <CustomTab id='billing-instructions-tab' label='BILLING INSTRUCTIONS'>
          <BillingInstructionsTab contract={clientInfo.contract} />
        </CustomTab>
        <CustomTab id='billing-ternms-and-conditions-tab' label='BILLING TERMS & CONDITIONS'>
          <BillingTermsAndConditionsTab contract={clientInfo.contract} />
        </CustomTab>
      </CustomTabs>
    </React.Fragment>
  )
}

export default function AnalyzeClientPage (props) {
  const [clientContract, setClientContract] = React.useState({ data: 0 })

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [viewMore, setViewMore] = React.useState(initialValue)

  const handleOnShowViewMore = analyst => () => {
    const title = analyst.first_name + ' ' + analyst.last_name
    const count = analyst.assignedCount
    const subtitle = `Billing Analyst for ${count} Accounts`
    const limit = 5
    const page = 1

    setViewMore({ ...initialValue, show: true })

    // TODO API CALL

    const results = analyst.clients

    setViewMore(state => ({ ...state, data: results, title, subtitle, count, limit, page }))
  }

  const [list, setList] = React.useState(initialValue)
  const [userInfo, setUserInfo] = React.useState({})
  const [params, setParams] = React.useState({})
  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 0,
    page: 0
  })

  React.useEffect(() => {
    const { _id } = props.match.params
    PageTitleService('Setup Billing Cycle')
    // BreadcrumbsService.setSelectedItems(['dashboard', 'clientSetup', 'clientBillingCycle'])

    /* Client Info */
    Promise.all([BillingSupervisorServices.getClientLedgerToSetup({ _id })]).then(values => {
      const client_contract = values[0].data.data

      setClientContract(prevState => ({
        ...prevState,
        data: client_contract
      }))
    })

    fetchData({})
    fetchDataUser({})
  }, [])

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      user_id: localStorage.getItem('user_id'),
      ...params
    }
    setParams(_params)

    BillingSupervisorServices.getUserAssignedClients(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({
        ...i,
        body: results,
        count,
        limit,
        page,
        sort: _params.sort,
        orderBy: orderByValue,
        order: orderValue
      }))
    })
  }

  const fetchDataUser = () => {
    BillingSupervisorServices.getUserInfo(localStorage.getItem('user_id')).then(({ data: res }) => {
      setUserInfo(res.data)
    })
  }

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const changeView = params => {
    fetchData(params)
  }

  return (
    <React.Fragment>
      <FormDrawer
        permanent
        drawerWidth='500'
        drawerContent={<ClientInformation clientInfo={clientContract.data} classes={classes} />}
      >
        <FormGroup className={classes.form_right}>
          <ActionTabs
            classes={classes}
            headCells={header_items}
            users={list}
            clientInfo={clientContract.data}
            onShowViewMore={handleOnShowViewMore}
            viewMore={viewMore}
            setViewMore={setViewMore}
            pagination={pagination}
            params={params}
            fetchData={fetchData}
            changeView={changeView}
            userInfo={userInfo}
          />
          <div className={classes.form_divider} />
        </FormGroup>
      </FormDrawer>
    </React.Fragment>
  )
}
