import React from 'react'
import { useHistory } from 'react-router-dom'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

// Styles
import classes from './ConfirmDialog.module.scss'
import { Typography, DialogContent, Button } from '@material-ui/core'

export default function CustomizedDialogs (props) {
  const { assign, setAssign, onCancel, onOK, negative_label, positive_label, preGenConflict } = props
  const history = useHistory()

  const handleOnCloseSuccessDialog = () => {
    setAssign(false)
    // history.push('/')
  }

  return (
    <Dialog
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      open={assign.show}
      className={classes.root}
      PaperProps={{ style: { display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes['success-close-button']}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id='assign-title' className={classes.title}>
            {assign.title}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        <Typography>{assign.text}</Typography>
        <br />
        {preGenConflict ? <Typography>{assign.text2}</Typography> : null}

        <div className={classes.button}>
          {/* {negative_label && preGenConflict ? (
            <Button variant='outlined' className={classes['button-negative']} onClick={onCancel}>
              {negative_label}
            </Button>
          ) : (
            ''
          )} */}
          {positive_label ? (
            <Button variant='contained' className={classes['button-positive']} onClick={onOK}>
              {positive_label}
            </Button>
          ) : (
            ''
          )}
        </div>
      </DialogContent>
    </Dialog>
  )
}
