import React from 'react'
import { Typography, Grid, Checkbox, FormControl, FormGroup, FormControlLabel } from '@material-ui/core'

import { withStyles, makeStyles } from '@material-ui/core/styles'
import classes from './Checkbox.module.scss'

// Icon
import CropSquareIcon from '@material-ui/icons/CropSquare'
import StopIcon from '@material-ui/icons/Stop'

/**
 * Custom Radio Group
 * @param checked String value for default value of radio-group
 * @param direction String direction of label and row
 * @param handleOnChange callback() on select change
 * @param id String used for rendering ID for element
 * @param label String used as label for element group
 */

const CustomCheckbox = props => {
  const defaultFunction = function () {}
  const {
    checked = '',
    required = false,
    label = '',
    direction = 'row',
    justify = 'flex-start',
    handleOnChange = defaultFunction,
    name = '',
    id = '',
    disableRipple = false,
    disabled = false,
    inputProps = {},
    size = ''
  } = props

  return (
    <Grid container className={classes.root} direction={direction} justfiy={justify}>
      <Grid item>
        <Grid container spacing={2}>
          <FormGroup>
            <Grid id={`${id}-checkbox-item`} item xs={12} className={classes['radio-wrapper']}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={checked}
                    disabled={!!disabled}
                    onChange={handleOnChange}
                    name={`${id}-name`}
                    // style={{ color: '#212a71' }}
                    color='primary'
                    // icon={<CropSquareIcon />}
                    // checkedIcon={<StopIcon className={classes.icon} />}
                    iconStyle={{ fill: 'white' }}
                  />
                }
              />
            </Grid>
          </FormGroup>
        </Grid>
      </Grid>
      <Grid item className={classes['label-wrapper']}>
        <Typography className={`${classes.label} ${disabled ? classes.disabled : ''}`}>
          {label} {required ? '*' : ''}
        </Typography>
      </Grid>
    </Grid>
  )
}

export default CustomCheckbox
