const Suspension = (state = {}, { type, payload }) => {
  switch (type) {
    case 'SET_SUSPENSION_FIELDS':
      return payload

    case 'UPDATE_SUSPENSION_FIELD':
      return (() => {
        const { value, field } = payload
        return { ...state, [field]: value }
      })()

    default:
      return state
  }
}

export default Suspension
