import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'

import classes from './AutoComplete.module.scss'
export default function AutoComplete (props) {
  const { label = '', required, readOnly, suggestions = [], onChange, error, field = '', ...other } = props
  const { value = '' } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()
  const [_suggestions, setSuggestions] = React.useState([])

  React.useEffect( () => {
    setSuggestions(suggestions)
  }, [suggestions])

  React.useEffect(() => {
    setError()
    setError(error)

    if (value === '') {
      setSuggestions(suggestions)
    }
  }, [error, value])

  const handleOnChange = event => {
    setError()

    if (typeof onChange === 'function') {
      let newFilter
      if (_suggestions.length && field) {
        newFilter = _suggestions.filter( data => data.value[field].toLowerCase().includes(event.target.value.toLowerCase()))
        setSuggestions(newFilter)
      }
      onChange(event.target.value)
    }
  }

  const handleItemClick = index => {
    setFocus(false)
    setError()
    if (typeof onChange === 'function') onChange(_suggestions[index], true, index)
  }

  const handleBlur = e => {
    if (!e.currentTarget.contains(e.relatedTarget)) setFocus(false)
  }

  const display = (value) => {
    return (
      <>
        {value} <br/>
      </>
    )
  }

  const displaySuggestion = (suggestions) => {
    const keys = Object.keys(suggestions.value)
    return (
      <>
        {
          keys.map( res => {
            return display(suggestions.value[res])
          })
        }
      </>
    )
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}

      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['autocomplete-field']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
              onBlur={handleBlur}
            >
              <InputBase
                className={classes.input}
                onChange={handleOnChange}
                {...other}
                autoComplete='off'
                onFocus={() => setFocus(true)}
              />
              {focus && (
                <Paper id={`${props.id}-suggestion`} className={classes.suggestions} square style={{maxHeight: '400px', overflowY: 'auto'}}>
                  {_suggestions && _suggestions.map((suggestion, index) => (
                    <MenuItem
                      id={`${props.id}-suggestion-${index}`}
                      key={`${props.id}-suggestion-${index}`}
                      onClick={() => {
                        setFocus(true)
                        handleItemClick(index)
                      }}
                      style={{borderBottom: '1px solid #858585'}}
                    >
                      {displaySuggestion(suggestion)}
                    </MenuItem>
                  ))}
                </Paper>
              )}
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
