import React from 'react'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Checkbox, Link, Typography } from '@material-ui/core'
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable } from './../../../../Components/CustomTable'
import AddSOASelect from './../../../../Components/SoaMenu'

// Styles
import classes from './EndorseForSuspension.module.scss'

// Utilities
import moment from 'moment'
import noop from 'lodash/noop'
import pick from 'lodash/pick'

EndorseForSuspension.propTypes = {
  soa: PropTypes.array,
  availableSOA: PropTypes.array,
  member: PropTypes.object,
  handleOnAddNewSOA: PropTypes.func,
  handleOnAddSOAClicked: PropTypes.func,
  handleOnFetchMembers: PropTypes.func,
  handleOnRemoveMember: PropTypes.func,
  handlePageTitleChange: PropTypes.func,
}

export default function EndorseForSuspension (props) {
  const { 
    soa = [],
    availableSoa = [],
    member = null,
    handleOnAddNewSOA = noop,
    handleOnAddSOAClicked = noop, 
    handleOnFetchMembers = noop,
    handleOnRemoveMember = noop,
    handlePageTitleChange = noop
  } = props

  const [members, setMembers] = React.useState({
    header: HEADER,
    body: [],
    count: 0,
    filter: {},
    limit: 10,
    page: 1,
    sort: '',
    order: true,
    orderBy: 'hc',
  })

  const [activeSoa, setActiveSoa] = React.useState(null)
  const [selectedMembers, setSelectedMembers] = React.useState({})

  const fetchMembers = (query = {}) => {
    handleOnFetchMembers(activeSoa, {
      ...pick(members, ['limit', 'page']),
      ...query
    })
  }

  const handleOnRemoveSoa = params => {
    // TODO
    console.log(params)
  }

  const handleOnSelectAll = params => {
    const selected = {}
    members.body.map((key, i) => {
      selected[i] = params
    })
    setSelectedMembers(prevState => ({ ...prevState, ...selected }))
  }

  const handleRemoveMember = () => {
    const _data = {}
    _data.soa_number = members.soa_number
    _data.member = []

    Object.keys(member).forEach(key => {
      if (member[key]) _data.member.push(members.body[key]._id)
    })

    handleOnRemoveMember(_data)
  }

  React.useEffect(() => {
    fetchMembers()
  }, [activeSoa])

  React.useEffect(() => {
    if (! member) {
      return
    }

    const { results, count, limit, page } = member

    setMembers(prevState => ({
      ...prevState,
      body: results || [],
      count,
      limit,
      page
    }))
  }, [member])

  React.useState(() => {
    handlePageTitleChange('Endorse For Suspension')
  }, [])

  return (
    <Grid container className={classes.root} spacing={2}>
      <Grid item xs={12}>
        <Typography variant='h5'>Suspension Reference</Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <FormSlider 
            details={soa} 
            OnChanges={query => {
              setActiveSoa(query.member)
            }}
            OnRemove={e => {
              handleOnRemoveSoa(e)
            }} 
          />
        </Grid>
      </Grid>
      <Grid item className={classes['add-soa']}>
        <AddSOASelect 
          details={availableSoa || []} 
          onAddClicked={handleOnAddSOAClicked}
          onSelect={handleOnAddNewSOA}
        />
      </Grid>
      <Grid item xs={12} style={{ marginTop: '20px' }}>
        <Link
          component='button'
          variant='body2'
          onClick={handleRemoveMember}
        >
          <Grid container direction='row'>
            <Grid item>
              <Grid container direction='column' justify='center' style={{ height: '100%' }}>
                <RemoveCircleRoundedIcon style={{ fontSize: '15px', marginRight: '5px' }} />
              </Grid>
            </Grid>
            <Grid item>
              <Typography>Remove</Typography>
            </Grid>
          </Grid>
        </Link>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <Grid item xs>
            <CustomTable
              handleSelectAll={e => handleOnSelectAll(e.target.checked)}
              data={members}
              setData={setMembers}
              onDataListChange={fetchMembers}
              showTextField
              body={data =>
                data.map((item, index) => (
                  <TableRow key={`endorse-for-suspension-list-item-${index}`}>
                    <TableCell id={`account-no-${index}`}>
                      <Checkbox
                        checked={member ? member[index] : false}
                        onClick={e => {
                          setSelectedMembers(prevState => ({ ...prevState, [index]: e.target.checked }))
                        }}
                        color='inherit'
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                      />
                    </TableCell>
                    <TableCell id={`endorse-for-suspension-item-member-id-${index}`}>{index + 1}</TableCell>
                    <TableCell id={`endorse-for-suspension-item-last-name-${index}`}>{item.last_name}</TableCell>
                    <TableCell id={`endorse-for-suspension-item-first-name-${index}`}>{item.first_name}</TableCell>
                    <TableCell id={`endorse-for-suspension-item-middle-name-${index}`}>{item.middle_name}</TableCell>
                    <TableCell id={`endorse-for-suspension-item-reference-date-${index}`}>
                      {moment(new Date(item.renewal_date)).format('ll')}
                    </TableCell>
                    <TableCell id={`endorse-for-suspension-item-end-date-${index}`}>
                      {moment(new Date(item.end_date)).format('ll')}
                    </TableCell>
                    <TableCell id={`endorse-for-suspension-item-plan-type-${index}`}>{item.plan_type_name}</TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

const HEADER = [
  { id: 'hc', name: 'HC', sortField: 'hc', match: 'any' },
  { id: 'member-id', name: 'Member ID', sortField: 'member_id', match: 'any' },
  { id: 'last-name', name: 'Last Name', sortField: 'last_name', match: 'any' },
  { id: 'first-name', name: 'First Name', sortField: 'first_name', match: 'any' },
  { id: 'middle-name', name: 'MI', sortField: 'middle_name', match: 'any' },
  { id: 'reference-date', name: 'Ref Date', sortField: 'reference_date', match: 'date' },
  { id: 'end-date', name: 'End Date', sortField: 'end_date', match: 'date' },
  { id: 'plan-type', name: 'Plan Type', sortField: 'plan_type', match: 'any' }
]