import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  remarks: Joi.string()
    .label('Remarks')
    .messages({ 'string.empty': `Please enter your reason for approving the SOA.` })
    .required(),
  invoice: Joi.string()
    .label('Debit Memo')
    .messages({ 'string.empty': `Please enter your reason for approving the Invoice.` })
    .required(),
  debit: Joi.string()
    .label('Debit Memo')
    .messages({ 'string.empty': `Please enter your reason for approving the Debit Memo.` })
    .required(),
  credit: Joi.string()
    .label('Credit Memo')
    .messages({ 'string.empty': `Please enter your reason for approving the Credit Memo.` })
    .required()
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
