import instance from './instance'
const axiosClientSOAReports = instance('/b/s')

export default class BillingSOAReportService {
  static getReport ({ type, soaNumber }) {
    return axiosClientSOAReports({
      url: `/billing/client/reports?category=${type}&soaNumber=${soaNumber}`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static getAttachment ({ type, soaNumber }) {
    return axiosClientSOAReports({
      url: `/billing/client/export-attachment?category=${type}&soaNumber=${soaNumber}`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }
    })
  }

  static getMemberReport ({ type, soaNumber }) {
    return axiosClientSOAReports({
      url: `/billing/member/reports?category=${type}&soaNumber=${soaNumber}`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }
}
