import React from 'react'
import { Link } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'

// Components
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'

// Services
import CollectionClientLedgerServices from 'Services/Api/CollectionClientLedgerServices'

// Shared
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Styles
import classes from './AccountToSetup.module.scss'

// Config
import { accountHeaders } from '../config/config'
import moment from 'moment'

export default function AccountToSetup (props) {
  const [listOfAccounts, setListOfAccounts] = React.useState({
    header: accountHeaders,
    body: [],
    count: 0,
    filter: {},
    limit: 10,
    order: true,
    orderBy: 'date_created',
    page: 1,
    sort: '',
    role: 'COLLECTION_ANALYST',
    rowsPerPage: 10
  })

  React.useEffect(() => {
    setTimeout(() => {
      LoadingService.loading(false)
    }, 1000)

    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'listOfAccountToSetup'])

    Promise.all([
      CollectionClientLedgerServices.getClientLedgersToSetup({
        limit: listOfAccounts.limit,
        sort: `${listOfAccounts.orderBy}|${listOfAccounts.order ? 'asc' : 'desc'}`,
        filter: listOfAccounts.filter
      })
    ])
      .then(res => {
        const [clientsToSetup] = res
        const { results, count } = clientsToSetup.data.data
        setListOfAccounts(i => ({ ...i, count, body: results }))
      })
      .catch(e => console.error(`Error fetching data: ${e}`))
  }, [])

  // Methods
  const onDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleOnCloseClicked = () => {
    console.log('closed clicked')
  }

  const handleOnListChange = params => {
    LoadingService.loading(true)
    const _params = {
      role: listOfAccounts.role,
      limit: listOfAccounts.limit,
      page: listOfAccounts.page,
      sort: `${listOfAccounts.orderBy}|${listOfAccounts.order ? 'asc' : 'desc'}`,
      filter: listOfAccounts.filter,
      ...params
    }

    CollectionClientLedgerServices.getClientLedgersToSetup(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setListOfAccounts(i => ({ ...i, limit, body: results, count, page }))
      LoadingService.loading(false)
    })
  }

  const handleOnDisplaySettingsApplied = params => {
    if (!params) return false

    handleOnListChange({
      dateRangeTarget: 'date_effectivity',
      dateRange: { ...params },
      page: 1
    })
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography id='collection-distribution-page-header' variant='h5'>
                List of Accounts to Setup
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder='Search List of Accounts to Setup'
                displaySettings
                handleOnDisplaySettingsClicked={onDisplaySettingsClicked}
                handleOnDisplaySettingsApplyClicked={handleOnDisplaySettingsApplied}
                onChange={e => {
                  handleOnListChange({ search: e })
                }}
                exportFiles
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs>
              <Table
                hasLink
                showTextField
                data={listOfAccounts}
                setData={setListOfAccounts}
                onDataListChange={e => handleOnListChange(e)}
                body={_data =>
                  _data.map((item, index) => (
                    <TableRow key={`account-list-item-${index}`}>
                      <TableCell id={`account-item-account-number-${index}`}>{item.account_number}</TableCell>
                      <TableCell id={`account-item-account-name-${index}`}>{item.account_name}</TableCell>
                      <TableCell id={`account-item-account-contract-type-${index}`}>{item.contract_type}</TableCell>
                      <TableCell id={`account-item-account-effectivity-date-${index}`}>
                        {item.effectivity_date ? moment(item.effectivity_date).format('ll') : ''}
                      </TableCell>
                      <TableCell id={`account-item-account-view-${index}`}>
                        <Link to={`/collection/assign/${item._id}/${item.account_number}`}>
                          <Typography
                            variant='caption'
                            className='text-link underline'
                            id={`account-setup-tile-view-${index}`}
                          >
                            Setup
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))
                }
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  )
}
