import React, { useState, useEffect } from 'react'

// MATERIAL COMPONENTS
import {
  Modal, Grid, Typography, Button, TableCell,
  TableRow
} from '@material-ui/core';

// MATERIAL ICONS
import { Close } from '@material-ui/icons';

// SHARED COMPONENTS
import { CustomTable as Table } from '../../../Components/CustomTable'

// UTILITIES
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

// STYLES
import style from '../AuditLog.module.scss'

/*
 * MOCK DATA 
 * 
 * To be deleted on API integration
*/
import mockData from '../mock/data.js'

export default function DetailsModal(props) {
  const { showModal, setShowModal, itemDetails } = props

  // LOCAL CONSTANTS
  const tableData = itemDetails ? itemDetails.fieldsData : []

  const tableHeader = [
    { id: 'category', name: 'Field Name', sortField: 'field_name', match: 'any' },
    { id: 'penalty-no', name: 'Old Value', sortField: 'old_value', match: 'any' },
    { id: 'account-name', name: 'New Value', sortField: 'new_value', match: 'any' },
  ]

  // LOCAL STATES
  const [show, setShow] = useState(false)
  const [detailTable, setDetailTable] = useState({
    body: [],
    count: 0,
    filter: {},
    header: tableHeader,
    limit: 1,
    order: true,
    orderBy: 'letter_reference_no',
    page: 1,
    sort: '',
    pagination: false
  })

  const handleCloseModal = () => {
    setShow(false)
    setShowModal(false)
  }

  useEffect(() => {
    setShow(showModal)
  }, [showModal])

  useEffect(() => {
    setDetailTable(items => ({ ...items, body: tableData }))
  }, [itemDetails])

  return (
    <div>
      <Modal
        open={show}
        style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}
        onClose={handleCloseModal}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"

      >
        <div
          style={{ width: 650, height: 'fit-content', padding: '20px 30px', backgroundColor: '#fff', border: 'none', outline: 'none' }}
        >
          <div style={{ width: '100%', textAlign: 'right' }}>
            <Close onClick={handleCloseModal} style={{ cursor: 'pointer' }} />
          </div>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography variant="h6">
                Setup Billing Cycle of Seaoil
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <ModalContent detailTable={detailTable} handleSetDetailTable={setDetailTable} handleCloseModal={handleCloseModal} itemDetails={itemDetails} />
            </Grid>
          </Grid>
        </div>
      </Modal>
    </div >
  )
}

const ModalContent = ({ detailTable, itemDetails, setDetailTable, handleCloseModal }) => {
  return (
    <>
      {itemDetails ? <Grid container spacing={3}>
        <Grid item xs={12}>
          <Grid container justify="space-between">
            <Grid item xs> <DetailViewTileItem label='activity id' value={itemDetails ? itemDetails.activity_id : ''} /></Grid>
            <Grid item xs> <DetailViewTileItem label='module' value={itemDetails ? itemDetails.module : ''} /></Grid>
            <Grid item xs> <DetailViewTileItem label='user' value={itemDetails ? itemDetails.user_name : ''} /></Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container justify="space-between">
            <Grid item xs> <DetailViewTileItem label='ip address' value={itemDetails ? itemDetails.ip_address : ''} /></Grid>
            <Grid item xs> <DetailViewTileItem label='data' value={itemDetails ? DateFormatterByAbbreviation({ value: itemDetails.date_of_entry }) : ''} /></Grid>
            <Grid item xs> <DetailViewTileItem label='time' value={itemDetails ? itemDetails.date_timestamp : ''} /></Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container justify="space-evenly">
            <Grid item xs> <DetailViewTileItem label='object' value={itemDetails ? itemDetails.object_name : ''} /></Grid>
            <Grid item xs> <DetailViewTileItem label='context' value={itemDetails ? itemDetails.context : ''} /></Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Table
            data={detailTable}
            setData={setDetailTable}
            body={_data =>
              _data.map((item, index) => (
                <TableRow key={`billing-cycle-${index}`}>
                  <TableCell >{item.field_name}</TableCell>
                  <TableCell >{item.old_value}</TableCell>
                  <TableCell >{item.new_value}</TableCell>
                </TableRow>
              ))
            }
          />
        </Grid>
        <Grid item xs={12} style={{ textAlign: 'center' }}>
          <Button
            variant='contained'
            color='primary'
            className={`button-positive ${style.detailModalButton}`}
            onClick={handleCloseModal}
          >
            Ok
      </Button>
        </Grid>
      </Grid> : <>Loading...</>}
    </>
  )
}

const DetailViewTileItem = (props) => {
  return <div >
    <Typography className={style.detailItemLabel}>{props.label}</Typography>
    <div>
      <Typography className={style.detailItemValue}>{props.value}</Typography>
    </div>
  </div>
}
