import React, { useState, useEffect } from 'react'
import { Button } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { Exposure } from '@material-ui/icons'

import _ from 'lodash'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import BillingMaintenanceService from 'Services/Api/BillingAndCollection/BillingMaintenanceService'

// Hooks
import { useHistory, Link } from 'react-router-dom'
import useForm from 'Hooks/useForm'

// Components
import PageFooter from '../../../Components/PageFooter/PageFooter'
import Select from '../../../Components/FormComponents/Select/Select'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TreeViewTable from 'StandardComponents/TreeViewTable/TreeViewTable'
import CollapsibleRow from 'StandardComponents/TreeViewTable/CollapsibleRow'
import NormalRow from 'StandardComponents/TreeViewTable/NormalRow'
import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'

// import Editor from './Editor'
// import { DEFAULT_VALUE } from './Editor/constants'
import PublishDialog from './PublishDialog'

// Utilities
import get from 'lodash/get'

// MuiStyles
import { withStyles } from '@material-ui/core/styles'

// Styles
import classes from './BillingCodeSetup.module.scss'

const header_items = [
  {
    id: 'billing_code',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Billing Code',
    accessor: 'billing_code'
  },
  {
    id: 'billing_type',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Billing Type',
    accessor: 'billing_type'
  },
  {
    id: 'description',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Description',
    accessor: 'description'
  },
  {
    id: 'transaction_type',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Transaction Type',
    accessor: 'transaction_type'
  },
  {
    id: 'action',
    numeric: false,
    center: false,
    disablePadding: true,
    label: ''
  }
]

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    width: '100%',
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)


export default function BillingCodeSetup () {
  const history = useHistory()
  const table_columns = ['billing_code', 'billing_type', 'description', 'transaction_type']
  const sub_columns = ['', 'billing_code', 'billing_type', 'description', 'transaction_type']

  const form = useForm({
    is_parent: '',
    parent: '',
    billing_type: '',
    description: '',
    transaction_type: ''
  })

  const initialValue = {
    show: false,
    count: 1,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    body: [],
    orderBy: 'billing_code',
    order: true,
    sort: '',
    accessors: ['billing_code', 'billing_type', 'description', 'transaction_type']
  }

  const [collapseAll, setCollapseAll] = useState(false)
  const [headers, setHeaders] = useState(header_items)
  const [isFetching, setIsFetchingState] = useState(false)
  const [list, setList] = useState(initialValue)
  const [params, setParams] = useState({})
  const [codeEdit, setCodeEdit] = useState(null)
  const [edit, setEdit] = useState(false)
  const [parentList, setParentList] = useState(null)

  const isParentOptions = [
    {value: "Yes", text: "Yes"}, 
    {value: "No", text: "No"}
  ]
  const transactionTypeOptions = [
    {value: "Invoice", text: "Invoice"}, 
    {value: "Credit Memo", text: "Credit Memo"}, 
    {value: "Debit Memo", text: "Debit Memo"}
  ]

  const storeBillingCode = () => {
    try {
      const error = {}
      error.is_parent = !form.values.is_parent ? "Please select a value" : ""
      error.parent = !form.values.parent && form.values.is_parent === "No" ? "Please select Parent Billing Type value" : ""
      error.billing_type = !form.values.billing_type ? "Please enter Billing Type" : ""
      error.description = !form.values.description ? "Please enter description" : ""
      error.transaction_type = !form.values.transaction_type ? "Please select the Transaction Type" : ""
      form.setErrors(error)
      
      if (!error.is_parent && !error.parent && !error.billing_type && !error.description && !error.transaction_type) {

        if (edit) {
          const codeData = {
            parentId: form.values.parent, 
            billingCode: codeEdit.billing_code, 
            billingType: form.values.billing_type, 
            description: form.values.description, 
            transactionType: form.values.transaction_type, 
            category: codeEdit.category, 
            moduleSource: codeEdit.source.module, 
            sourceCode: codeEdit.source.code
          }
          BillingMaintenanceService.updateBillingCode({codeId: codeEdit._id, data: codeData}).then(() => {
            fetchData()
            setEdit(false)
            form.reset()
          })
        }
      }
      

    } catch (error) {
      console.log(`Error storing template image attachment: ${error}`)
    }
  }

  const handleTableCollapseAll = () => {
    setCollapseAll(!collapseAll)
  }

  const editBillingCode = (billingCode) => {
    setEdit(true)
    setCodeEdit(billingCode)
    form.setErrors(null)
    const formValues = {
      is_parent: billingCode.parent_id ? "No" : "Yes",
      parent: billingCode.parent_id ? billingCode.parent_id : "",
      billing_type: billingCode.billing_type,
      description: billingCode.description,
      transaction_type: billingCode.transaction_type
    }
    form.setValues(formValues)
  }

  const multipleActions = [
    {label: "Edit", handleClick: editBillingCode},
    {label: "Deactivate", handleClick: () => console.log("DEACTIVATE")}
  ]

  useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance'])
    fetchData()
  }, [])

  const fetchData = params => {
    if (params) {
      setCollapseAll(true)
    }
    setIsFetchingState(true)
    const _params = {
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    setParams(_params)
    console.log(_params)
    BillingMaintenanceService.getAllBillingCodesWithHierarchy(_params).then(({ data: res }) => {
      const { results, count } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)

      if (!parentList) {
        const list = results.map(item => {
          return {value: item._id, text: item.billing_code + ' - ' + item.billing_type}
        })
        setParentList(list)
      }
    })
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value,
    }
    fetchData(params)
  }, 500)

  return (
    <div className={classes.root}>
      <Grid container spacing={2} style={{ marginTop: '1rem', padding: '0 0 10px 10px' }}>
        <Grid item container spacing={2} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Billing Type Header *
            </Typography>
          </Grid>
          <Grid item xs={2}>
            <Select 
              defaultItemText="Select if Parent"
              name="is_parent"
              value={form.values.is_parent}
              onChange={form.onChange}
              data={isParentOptions} 
              variant="outlined"
              error={get(form.errors, 'is_parent')}
            />
          </Grid>

          { form.values.is_parent === 'No' ? 
            <Grid item xs={3}>
              <Select 
                defaultItemText="Select Parent Billing Type"
                name="parent"
                value={form.values.parent}
                onChange={form.onChange}
                data={parentList} 
                variant="outlined"
                error={get(form.errors, 'parent')}
              />
            </Grid>
          : null}
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Billing Type *
            </Typography>
          </Grid>

          <Grid item xs={3}>
            <TextField
              variant='outlined'
              size='small'
              name="billing_type"
              placeholder="Enter Billing Type"
              value={form.values.billing_type}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: "#c7c7c7"}}
              error={get(form.errors, 'billing_type')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Description *
            </Typography>
          </Grid>

          <Grid item xs={9}>
            <TextField
              variant='outlined'
              size='small'
              name="description"
              placeholder="Enter Description"
              value={form.values.description}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: "#c7c7c7"}}
              error={get(form.errors, 'description')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Transaction Type *
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Select 
              defaultItemText="Select the Transaction Type"
              name="transaction_type"
              value={form.values.transaction_type}
              onChange={form.onChange}
              data={transactionTypeOptions} 
              variant="outlined"
              error={get(form.errors, 'transaction_type')}
            />
          </Grid>
          <Grid item xs={2}>
            <StyledButton onClick={() => storeBillingCode()} >{edit ? 'Update Billing Code':'Add Billing Code'}</StyledButton>
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12} style={{margin: "10px 10px 0 0"}}>
          <Grid item xs={9}>
            <SearchTextComponent
              placeholder='Search list of Billing Codes'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
          <Grid item xs={3}>
            <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
              <IconButton id='display-settings-icon'>
                <ViewListRoundedIcon className={classes['display-settings-icon']} />
                <Typography className='text-link underline' style={{color: '#3ab77d'}}>Display Settings</Typography>
              </IconButton>
            </Link>
          </Grid>
        </Grid>
      </Grid>

      <div style={{padding: '0 0 20px 20px' }}>
        <TreeViewTable
          headCells={headers}
          handleCollapseEvent={handleTableCollapseAll}
          isViewTree
          sorter
          filter
          fetchData={fetchData}
          params={params}
          count={list.count}
          size={"md"}
        >
          <TableBody>
            {isFetching && (
              <TableRow>
                <TableCell component='th' colSpan={10}>
                  <CircularLoading />
                </TableCell>
              </TableRow>
            )}
            {!isFetching &&
              list &&
              list.body &&
              list.body.map((item, index) =>
                item.billing_code_children.length ? (
                  <CollapsibleRow
                    index={index}
                    id={`account-id-${index}`}
                    data={item}
                    objectKey={'billing_code_children'}
                    tableColumnOrder={table_columns}
                    subColumnOrder={sub_columns}
                    collapseAllState={collapseAll}
                    collapseFunction={() => 1}
                    monetary_columns={[]}
                    multipleActions={multipleActions}
                  />
                ) : (
                  <NormalRow
                    index={index}
                    table_columns={table_columns}
                    data={item}
                    id={`link-table-${index}`}
                    style={{ textDecoration: 'none' }}
                    multipleActions={multipleActions}
                  />
                )
              )}
          </TableBody>
        </TreeViewTable>
      </div>

      <PageFooter isControl handleOnCloseClicked={() => history.push('/')} />


      {/* <PublishDialog
        open={publishing}
        onCancel={handlePublishCancelled}
        onSubmit={handlePublishConfirmed}
      /> */}
    </div>
  )
}
