/* eslint-disable no-unused-vars */
import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles, withStyles } from '@material-ui/core/styles'
import FormControl from '@material-ui/core/FormControl'
import FormHelperText from '@material-ui/core/FormHelperText'
import Typography from '@material-ui/core/Typography'
import MuiSelect, { SelectProps } from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'

const useStyles = makeStyles((theme) => ({
  formControl: {
    width: '100%',
  },

  label: {
    marginBottom: 5
  }
}))

const StyledSelect = withStyles(theme => ({
  root: {
    backgroundColor: theme.palette.common.white,
    boxShadow: '0.5px 0.5px 1px 0.8px rgba(0, 0, 0, 0.4)',
    padding: 10,
    borderRadius: 4,
    '&:focus': {
      backgroundColor: 'transparent',
      borderRadius: 4,
      borderStyle: 'solid',
      borderColor: 'green'
    }
  }
}))(MuiSelect)

/**
 * @param {SelectProps} ...props 
 */
export default function Select({ defaultItemText, disableDefaultItemText = true, data = [], error = '', ...props }) {
  const classes = useStyles();

  return (
    <FormControl error={error.length > 0} className={classes.formControl}>
      {props.label && (
        <Typography variant="subtitle2" id={`${props.id}-label`} className={classes.label}>
          {props.label}
        </Typography>
      )}

      <StyledSelect labelId={`${props.id}-label`} disableUnderline displayEmpty {...props}>
        <MenuItem value="" disabled={disableDefaultItemText}>
          <Typography style={{fontWeight: "300", color: "#969799"}}>
            {defaultItemText}
          </Typography>
        </MenuItem>

        {data.map(item => (
          <MenuItem value={item.value}>
            <Typography >
              {item.text}
            </Typography>
          </MenuItem>
        ))}
      </StyledSelect>

      {error.length > 0 && (
        <FormHelperText>{error}</FormHelperText>
      )}
    </FormControl>
  )
}

Select.propTypes = {
  defaultItemText: PropTypes.string.isRequired,
  data: PropTypes.arrayOf(PropTypes.shape({
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    text: PropTypes.string.isRequired,
  })),
  error: PropTypes.string
}