import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { useHistory } from 'react-router-dom'
// Styles
import classes from './Modal.module.scss'

const SuccessDialog = props => {
  const { page, succesfulTitle, successfulText, openSuccessDialog, setOpenSuccessDialog, setOpenDialog, submitFunction, submitStatus } = props
  const history = useHistory()
  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
    if (submitStatus) {
      history.push('/')
    } else {
      setOpenDialog(false)
    }
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            {succesfulTitle}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        {successfulText}
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={handleOnCloseSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const {title, confirmationText, succesfulTitle, successfulText, buttonText, page, openDialog, openSuccessDialog, setOpenSuccessDialog, setOpenDialog, submitFunction, submitStatus} = props

  const handleOnClose = () => {
    setOpenDialog(false)
  }

  // const onShowSuccessDialog = () => {
  //   setOpenSuccessDialog(true)
  // }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '270px', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton aria-label='close' id='confirm-close-button' className={classes.button} onClick={handleOnClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='confirm-title' className={classes.title}>
              {title}
            </Typography>
          </div>
        </DialogTitle>

        <DialogContent id='content'>
          {confirmationText}
          <div className={classes.button}>
            <Button
              variant='outlined'
              id='cancel-button'
              className={classes['button-negative']}
              onClick={handleOnClose}
            >
                Cancel
            </Button>
            <Button
              variant='contained'
              id='debit-submit-button'
              className={classes['button-positive']}
              onClick={() => {
                // onShowSuccessDialog()
                submitFunction()
              }}
            >
              {buttonText}
            </Button>
          </div>
        </DialogContent>
      </Dialog>
      <SuccessDialog
        page={page}
        succesfulTitle={succesfulTitle}
        successfulText={successfulText}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        setOpenDialog={setOpenDialog}
        submitFunction={submitFunction}
        submitStatus={submitStatus}
      />
    </React.Fragment>
  )
}
