import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Config
const filterMenu = {
  data: [
    { value: 'this-month', text: 'This Month' },
    { value: 'this-week', text: 'This Week' },
    { value: 'today', text: 'Today' },
  ],
  default: 'this-month',
}

const columnConfig = [
  { id: 'member-aging-summary-tile-table-header', name: 'Member Name' },
  { id: 'member-aging-summary-tile-table-header', name: 'Outstanding Amount' },
  { id: 'member-aging-summary-tile-table-header', name: '' },
]

// Mock data
const mock = require('./mock-data/member-aging-summary.json')

const MemberAgingSummaryTile = (props) => {
  // Constants
  const { service = () => {} } = props
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Member A/R Summary'
  })

  // Initialize / Watch
  React.useEffect(() => {
    const data = mock.slice(0, 10)
    setTile((i) => ({ ...i, count: data.length, data }))
  }, [])

  // Method
  /**
   * Handle Transfer to page
   * @param location route/path to view all member aging summary page
   */
  const handleOnViewAll = () => {}
  return (
    <React.Fragment>
      <DashboardTile
        id="member-aging-summary-tile"
        count={{ color: 15, value: tile.count, id: 'member-aging-summary-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id="member-aging-summary-tile-view-list"
            variant="caption"
            className="bold text-link"
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size="small">
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`member-aging-summary-tile-table-row-${index}`}
                id={`member-aging-summary-tile-table-row-${index}`}
              >
                <TableCell id={`member-aging-summary-tile-table-cell-member-name-${index}`}>
                  {item.member_name || ''}
                </TableCell>
                <TableCell id={`member-aging-summary-tile-table-cell-outstanding-amount-${index}`}>
                  PHP {item.outstanding_amount || '0'}
                </TableCell>
                <TableCell id={`member-aging-summary-tile-table-cell-action-${index}`}>
                  {/* TODO: Change link when end-point and data is available */}
                  <Link to={`/`}>
                    <Typography
                      variant="caption"
                      className="text-link underline"
                      id={`view-member-aging-summary-${index}`}
                    >
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

MemberAgingSummaryTile.displayName = 'DashboardTileComponent'

export default MemberAgingSummaryTile
