import React from 'react'

import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import CreateIcon from '@material-ui/icons/Create';
import BlockIcon from '@material-ui/icons/Block';
import FlagIcon from '@material-ui/icons/Flag';

import style from '../AgingSummaryClientPage.module.scss'

export default function CreateOptions(props) {

  const {
    handlePregenerateCDF,
    handlePregenerateSuspensionEndorsement,
    handlePregeneratePenaltyEndorsement,
    /** ADVANCE */
    handleAdvancePregenerateCDF
  } = props

  return (
    <div>
      {!handleAdvancePregenerateCDF &&
        <>
          <Button className={style.createOptionButton}>
            <CreateIcon fontSize='small' />
            <Typography variant='caption' className={style.createOptionLabel} onClick={handlePregenerateCDF}>
              Create Collection Distribution
            </Typography>
          </Button>
          <Button className={style.createOptionButton}>
            <BlockIcon fontSize='small' />
            <Typography variant='caption' className={style.createOptionLabel} onClick={handlePregenerateSuspensionEndorsement}>
              Suggest for Suspension
            </Typography>
          </Button>
          <Button className={style.createOptionButton}>
            <FlagIcon fontSize='small' />
            <Typography variant='caption' className={style.createOptionLabel} onClick={handlePregeneratePenaltyEndorsement}> 
              Endorse for Penalty
            </Typography>
          </Button>
        </>
      }
      { handleAdvancePregenerateCDF &&
        <>
          <Button className={style.createOptionButton}>
            <CreateIcon fontSize='small' />
            <Typography variant='caption' className={style.createOptionLabel} onClick={handleAdvancePregenerateCDF}>
              Create Advanced Collection Distribution
            </Typography>
          </Button>
        </>
      }

    </div>
  )
}
