import instance from '../instance'

const axios = instance('/b/cdm')
export default class DebitMemoServices {
  static getDebitMemos (params) {
    return axios({
      url: `/billing/${params.entityType}/debit/${params.status}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
