import Grid from '@material-ui/core/Grid'
import CloseIcon from '@material-ui/icons/Close'
import React from 'react'
import { Button, Dialog, Typography } from '@material-ui/core'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './ActionDialog.module.scss'

// Config
import { UserInfoDialogConfig } from './../../../config/config'

export const AccountAssignDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnAssign = defaultFunction, handleOnClose = defaultFunction } = props

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-assign-dialog']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Assign Client to Collection Analyst</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>Are you sure you want to assign the Account to the selected Collection Analyst?</Typography>
        </Grid>
        <Grid item className={classes.info} xs={12}>
          <Typography>The Collection Analyst will be notified of this new account assigned for processing</Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={5}>
              <Button id='client-setup-cancel-button' className={classes.cancel} variant='outlined' onClick={() => handleOnClose(false)}>
                Cancel
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button id='client-setup-assign-button' className={classes.assign} variant='contained' onClick={() => handleOnAssign()}>
                Assign
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}

export const AssignSuccessDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnClose = defaultFunction, handleOnOk = defaultFunction } = props

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-assign-success']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Account Successfully Assigned</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>Account was successfully assigned to Collection Analyst</Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='flex-end'>
            <Grid item xs={5}>
              <Button id='client-setup-ok-button' className={classes.confirm} variant='contained' onClick={() => handleOnOk()}>
                OK
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}

export const UserInfoDialog = props => {
  const { data = { data: [], count: 0, collectionAnalyst: '', userId: null }, open = false, handleOnClose = () => {}, onDataListChange = () => {} } = props
  const [sort, setSort] = React.useState({ orderBy: '', order: '' })
  const [debounce, setDebounce] = React.useState(null)


  const handleOnSort = orderBy => e => {
    setSort(i => ({ ...i, orderBy, order: !sort.order }))
    onDataListChange({ userId: data.userId, sort: `${orderBy}|${sort.order ? 'asc' : 'desc'}` })
  }

  const handleColumnFiltering = args => {
    const { field, value, match } = args
    setSort(p => ({ ...p, filter: { ...p.filter, [field]: { value, match } } }))
    onDataListChange({ userId: data.userId, filter: { ...sort.filter, [field]: { value, match } }, page: 1 })
  }

  return (
    <Dialog
      className={classes['root-user-info']}
      onClose={() => handleOnClose(false)}
      open={open}
      PaperProps={{ style: { position: 'absolute', right: '0' } }}
    >
      <Grid className={classes.container} container direction='column'>
        <Grid item className={classes['close-button']}>
          <CloseIcon className={classes['close-icon']} onClick={() => handleOnClose(false)} />
        </Grid>
        <Grid item>
          <Typography variant='h6'>{data.collectionAnalyst}</Typography>
        </Grid>
        <Grid item>
          <Typography variant='subtitle'>Collection Analyst for {data.count} Accounts</Typography>
        </Grid>
        <Grid item className={classes['padded-table']}>
          <Table className={classes.table} stickyHeader size='small'>
            <TableHead>
              <TableRow>
                {UserInfoDialogConfig &&
                  UserInfoDialogConfig.map((item, index) => (
                    <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                      <Grid container direction='column'>
                        <Grid item xs={12}>
                          <TableSortLabel
                            active={sort.orderBy === item.sortField}
                            direction={sort.order ? 'asc' : 'desc'}
                            onClick={handleOnSort(item.sortField)}
                          >
                            {item.name}
                          </TableSortLabel>
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            onChange={event => {
                              const search = event.target.value
                              clearTimeout(debounce)
                              setDebounce(
                                setTimeout(() => {
                                  handleColumnFiltering({
                                    field: item.sortField,
                                    value: search,
                                    match: item.match
                                  })
                                }, 500)
                              )
                            }}
                          />
                        </Grid>
                      </Grid>
                    </TableCell>
                  ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {data.data.map((item, index) => (
                <TableRow
                  key={`user-info-dialog-table-row-${index}`}
                  id={`user-info-dialog-table-row-${index}`}
                  className={classes['table-row']}
                >
                  <TableCell
                    id={`user-info-dialog-table-cell-accounts-assigned-${index}`}
                    className={`${classes['table-cell']} ${classes['no-overflow']}`}
                  >
                    {item.account_name || ''}
                  </TableCell>
                  <TableCell
                    id={`user-info-dialog-table-cell-payment-term-${index}`}
                    className={`${classes['table-cell']} ${classes['no-overflow']}`}
                  >
                    {item.payment_term || ''}
                  </TableCell>
                  <TableCell
                    id={`user-info-dialog-table-cell-billing-cycle-${index}`}
                    className={`${classes['table-cell']} ${classes['no-overflow']}`}
                  >
                    {item.billing_cycle || ''}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Grid>
      </Grid>
    </Dialog>
  )
}
