import React, { useState } from 'react'

import clsx from 'clsx'

// Material Components
import { Grid, Typography, Paper, Checkbox, FormControlLabel, FormControl, FormGroup } from '@material-ui/core'

// Material Icons
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

// Material Styles
import { makeStyles, withStyles } from '@material-ui/core/styles'

// Components
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import DatePicker from './../../../../Components/FormComponents/DatePicker/DatePicker'
import CollectionHeader from './../../../../Components/AdvanceCollectionComponents/CollectionHeader/CollectionHeader'
import { InputTable } from './../../../../Components/CustomTable'
import DropDown from './../../../../Components/FormComponents/DropDown/DropDown'
import RadioGroup from './../../../../Components/FormComponents/RadioGroup/RadioGroup'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// JSON Data
import { COLLECTION_DATA } from './../../CollectionData'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Styles
import classes from './CollectionDetailsComponent.module.scss'

// Config
import { inputTableConfig } from './config/input-table'
const config = require('./config/config.json')

const useStyles = makeStyles(theme => ({
  gridMargin: {
    margin: '10px 0px'
  }
}))

export default function CollectionDetailsComponent (props) {
  const classes = useStyles()
  const [state, setState] = React.useState({
    bir: true,
    commission: false,
    creditable: false
  })
  const [sections] = useState(COLLECTION_DATA)

  ///  // From Collection Distribution
  const { handlePageTitleChange = () => {} } = props

  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [meansOfPayment, setMeansOfPayment] = React.useState({ header: [], data: [] })

  const handleOnAddLineItem = _params => {
    const { data } = meansOfPayment
    data.push(_params)
    setMeansOfPayment(p => ({ ...p, data }))
  }
  ///  //

  const handleChange = event => {
    setState({ ...state, [event.target.name]: event.target.checked })
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'STAR MARGARINE'}`,
      'createAdvanceCollectionDistribution'
    ])
    setMeansOfPayment(p => ({ ...p, header: inputTableConfig }))
  }, [])

  const { bir, commission, creditable } = state

  return (
    <div>
      <Grid container>
        <Grid item xs={12}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography variant='h6'>Collection Details</Typography>
            </Grid>
            <Grid item={12} sm={12} md={12} lg={12}>
              <Paper elevation={3}>
                <Grid container justify='space-between' spacing={2}>
                  <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
                    <SummaryTile id={'total-taxable'} label={'total taxable amount'} text={'PHP 222,000.00'} />
                  </Grid>
                  <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
                    <SummaryTile id={'total-tax'} label={'total tax (vat) amount'} text={'PHP 26,640.00'} />
                  </Grid>
                  <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
                    <SummaryTile id={'total-amount'} label={'total amount'} text={'PHP 248,640.00'} />
                  </Grid>
                </Grid>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </Grid>

      <Grid container style={{ marginTop: 30, marginBottom: 30, padding: '0px 20px' }} spacing={2}>
        <Grid container justify='space-between' spacing={3}>
          <Grid item xs={12} sm={12} md={3} lg={3} className={clsx(classes.itemMargin)}>
            <DatePicker id='collection-date' required label='Collection Date' orientation={'row'} />
            <FormControl component='fieldset' className={classes.formControl}>
              <FormGroup>
                <FormControlLabel
                  control={<Checkbox checked={bir} onChange={handleChange} name='bir' color='primary' />}
                  label='with BIR form 2307'
                />
                <FormControlLabel
                  control={<Checkbox checked={commission} onChange={handleChange} name='commission' color='primary' />}
                  label='Net of Commission'
                />
                <FormControlLabel
                  control={<Checkbox checked={creditable} onChange={handleChange} name='creditable' color='primary' />}
                  label='Net of Creditable w/ Tax'
                />
              </FormGroup>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={12} md={6} lg={6} className={clsx(classes.itemMargin)}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={6}
              id='remarks'
              label='Remarks'
              value='Pick-up Check from 10AM to 3PM, week days.'
            />
          </Grid>
          <Grid item xs={12} sm={12} md={3} lg={3} className={clsx(classes.itemMargin)}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={6}
              id='or'
              label='OR No.'
              value=''
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid container>
        <Grid item xs={12}>
          <InputTable data={meansOfPayment} onAddLineItem={e => handleOnAddLineItem(e)} />
        </Grid>
      </Grid>
    </div>
  )
}
