import Grid from '@material-ui/core/Grid'
import React from 'react'
import { Typography, Paper } from '@material-ui/core'

// Components
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import DatePicker from './../../../../Components/FormComponents/DatePicker/DatePicker'
import { InputTable } from './../../../../Components/CustomTable'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import Checkbox from './../../../../Components/FormComponents/Checkbox/Checkbox'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Config
import { inputTableConfig } from '../../mock-data/input-table'
const config = require('../../config/config.json')

export default function CreateCollectionDistribution (props) {
  const { handlePageTitleChange = () => {} } = props
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [meansOfPayment, setMeansOfPayment] = React.useState({ header: [], data: [] })

  React.useState(() => {
    handlePageTitleChange('Create Collection Distribution')
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'JULIUS NARAG'}`,
      'create'
    ])
    setMeansOfPayment(p => ({ ...p, header: inputTableConfig }))
  }, [])

  const handleOnAddLineItem = _params => {
    const { data } = meansOfPayment
    data.push(_params)
    setMeansOfPayment(p => ({ ...p, data }))
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'cdf-no'} item lg={3}>
            <DetailTile id={'cdf-no'} label={'cdf no.'} text={_data.cdf_number} />
          </Grid>
          <Grid key={'member-name'} item lg={3}>
            <DetailTile label={'member name'} text={_data.member_name} />
          </Grid>
          <Grid key={'member-person-id'} item lg={3}>
            <DetailTile label={'person id'} text={_data.person_id} />
          </Grid>
          <Grid key={'member-id'} item lg={3}>
            <DetailTile label={'member id'} text={_data.member_id} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'member-account-name'} item lg={3}>
            <DetailTile label={'account name'} text={_data.account_name} />
          </Grid>
          <Grid key={'member-account-no'} item lg={3}>
            <DetailTile label={'account no.'} text={_data.account_number} />
          </Grid>
          <Grid key={'member-account-tin'} item lg={3}>
            <DetailTile label={'account tin'} text={_data.account_tin} />
          </Grid>
          <Grid key={'member-broker'} item lg={3}>
            <DetailTile label={'broker'} text={_data.broker} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'member-transaction-type-radio'} item lg={3}>
            <DetailTile label={config.transaction_type.label} text={config.transaction_type.options[0].name} />
          </Grid>
          <Grid key={'member-status'} item lg={3}>
            <DetailTile label={'status'} text={_data.status} />
          </Grid>
          <Grid key={'member-payment-type'} item lg={3}>
            <DetailTile label={'payment-type'} text={_data.payment_type} />
          </Grid>
          <Grid key={'member-location'} item lg={3}>
            <DetailTile label={'location'} text={_data.location} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container spacing={2}>
          <Grid item xs>
            <TextField
              id='contact-person'
              label='Contact Person'
              value={_data.contact_person}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_person'
                  })
                )
              }}
            />
          </Grid>
          <Grid item xs>
            <TextField
              id='contact-email'
              label='Contact Email'
              value={_data.contact_email}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_email'
                  })
                )
              }}
            />
          </Grid>
          <Grid item xs>
            <TextField
              id='contact-number'
              label='Contact No.'
              value={_data.contact_number}
              required
              onChange={e => {
                dispatch(
                  UpdateField({
                    value: e.target.value,
                    field: 'contact_number'
                  })
                )
              }}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Typography variant='h5'>Collection Details</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-taxable'} label={'total taxable amount'} text={'PHP 222,000.00'} />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-tax'} label={'total tax (vat) amount'} text={'PHP 26,640.00'} />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-amount'} label={'total amount'} text={'PHP 248,640.00'} />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item lg={12} style={{ marginTop: '1rem' }}>
        <Grid container spacing={4}>
          <Grid item lg={4}>
            <DatePicker
              id='collection-date'
              required
              label='Collection Date'
              value={_data.collection_date}
              onChange={event => {
                dispatch(UpdateField({ field: 'collection_date', value: event.toISOString() }))
              }}
              orientation={'row'}
            />
            <Checkbox
              id='bir-form-2307'
              checked={_data.net_of_commission}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'bir_form_2307', value: e.target.value }))
              }}
              label={'with BIR form 2307'}
            />
            <Checkbox
              id='net-of-commission'
              checked={_data.net_of_commission}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'net_of_commission', value: e.target.value }))
              }}
              label={'Net of Commission'}
            />
            <Checkbox
              id='net-of-creditable-with-tax'
              checked={_data.net_of_commission}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'net_of_creditable_with_tax', value: e.target.value }))
              }}
              label={'Net of Creditable w/ Tax'}
            />
          </Grid>
          <Grid item lg={6}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={7}
              id='remarks'
              label='Remarks'
              value={_data.remarks || ''}
              onChange={event => {
                dispatch(UpdateField({ field: 'remarks', value: event.target.value }))
              }}
            />
          </Grid>
          <Grid item lg={2}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={7}
              id='qr-no'
              label='QR No.'
              value={_data.qr_no || ''}
              onChange={event => {
                dispatch(UpdateField({ field: 'qr', value: event.target.value }))
              }}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <InputTable data={meansOfPayment} onAddLineItem={e => handleOnAddLineItem(e)} />
        <Grid container style={{ justifyContent: 'flex-end', marginTop: '1rem', paddingRight: '5rem' }}>
          <Typography style={{ fontSize: '20px', fontWeight: 600 }}>TOTAL: {'PHP ' + _data.total_amount}</Typography>
        </Grid>
      </Grid>
    </Grid>
  )
}
