import React from 'react'
import { Link } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'

// Store
import { useSelector } from 'react-redux'

// Utilities
import pick from 'lodash/pick'
import upperFirst from 'lodash/upperFirst'
import moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './SubmittedSuspension.module.scss'
import { useSelect } from 'downshift'

export default function SubmittedSuspension() {
  const user_group = localStorage.getItem('group')
  const [data, setData] = React.useState({
    header: HEADERS,
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: 'date_submitted',
    page: 1,
    sort: '',
    search: '',
    range: {
      start: null,
      end: null
    },
    rangeTarget: 'date_submitted',
    filter: {
      status: 'submitted'
    },
  })

  const getData = (params = {}) => {
    LoadingService.loading(true)

    CollectionSuspensionServices.index({
      ...pick(data, ['limit', 'page', 'range', 'rangeTarget', 'filter']),
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      ...params
    })
    .then(res => {
      const { count, limit, page, results } = res.data.data
      setData(prevState => ({ 
        ...prevState, 
        count, 
        body: results, 
        limit, 
        page,
      }))
      LoadingService.loading(false)
    })
    .catch(err => {
      console.log(`Error fetching suspension list: ${err}`)
      LoadingService.loading(false)
    })
  }

  const handleDisplaySettingsApplyClicked = range => {
    getData(({ range }))
  }

  const handleDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleCloseClicked = () => {
    console.log('closed clicked')
  }

  const handleSearchChanged = search => {
    getData({ search })
  }

  React.useEffect(() => {
    getData()
  }, [data.orderBy, data.order, data.range, data.rangeTarget])

  const state = useSelector(state => state)

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems([`submittedSuspensionList`])
  }, [])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>
                List of Submitted Endorsements for Suspension
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder="Search List of Submitted Endorsements for Suspension"
                displaySettings
                displaySettingsTitle={`Submitted Date Range*`}
                handleOnDisplaySettingsClicked={handleDisplaySettingsClicked}
                handleOnDisplaySettingsApplyClicked={handleDisplaySettingsApplyClicked}
                value={data.search}
                onChange={handleSearchChanged}
              />
            </Grid>
          </Grid>

          <Grid container>
            <Grid item xs>
              <Table
                hasLink
                data={data}
                onDataListChange={getData}
                setData={setData}
                showTextField
                body={suspensions =>
                  suspensions.map((suspension, index) => (
                    <TableRow key={`suspension-list-item-${index}`}>
                      <TableCell id={`suspension-category-${index}`}>
                        {upperFirst(suspension.category)}
                      </TableCell>
                      <TableCell id={`suspension-ref-number-${index}`}>
                        {suspension.spn_number}
                      </TableCell>
                      <TableCell id={`suspension-account-name-${index}`}>
                        {suspension.contract?.client?.account_name}
                      </TableCell>
                      <TableCell id={`suspension-date-${index}`}>
                        {moment(suspension.date?.submitted || suspension.date.created).format('ll')}
                      </TableCell>
                      <TableCell id={`suspension-outstanding-soa-amount${index}`}>
                        {CurrencyFormatter(suspension.spn_summary?.sub_total)}
                      </TableCell>
                      <TableCell id={`suspension-view-link-${index}`}>
                        <Link 
                          to={
                            user_group === 'COLLECTION_ANALYST' 
                              ? `/collection/suspensions/${suspension.spn_number}` 
                              : `/collection/suspension/validate/${suspension.category}/${suspension.spn_number}`
                          }
                        >
                          <Typography
                            id={`suspension-item-action-${index}`}
                            variant='caption'
                            className='text-link underline'
                          >
                            {user_group === 'COLLECTION_ANALYST' ? 'View' : 'Validate'}
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))
                }
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <PageFooter isControl handleOnCloseClicked={handleCloseClicked} />
        </Grid>
      </Grid>
    </div>
  )
}

const HEADERS = [
  { id: 'category', name: 'Category', sortField: 'category', match: 'any' },
  { id: 'suspension-no', name: 'Suspension No.', sortField: 'spn_number', match: 'any' },
  { id: 'account-name', name: 'Account Name', sortField: 'account_name', match: 'any' },
  { id: 'date-submitted', name: 'Date Submitted', sortField: 'date_submitted', match: 'date' },
  { id: 'outstanding-soa-amount', name: 'Outstanding SOA Amount', sortField: 'sub_total', match: 'any' },
]
