export default function ClientTransactionJournalPage (props) {
  const clientId = props.match.params.clientId
  const [collapseAll, setCollapseAll] = React.useState(false)
  const [header_collapsed, setHeaderState] = React.useState()
  const [headers, setHeaders] = React.useState(header_items)
  const [data, setTableData] = React.useState([])
  const [clientData, setClientData] = React.useState([])
  const [sourceData, setSourceData] = React.useState([])
  const [table_columns, setColumns] = React.useState(mainColumn)
  const [sub_columns, setSubColumns] = React.useState(subColumn)

  const initialValue = {
    show: false,
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    data: [],
    orderBy: '_id',
    order: true,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list && list.count ? list.count : list.body ? list.body.length : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  React.useEffect(() => {
    console.group('Client:')
    console.log(clientId)
    Promise.all([BillAnalystServices.getSelectedClient(clientId)]).then(values => {
      const { data } = values.pop().data
      setClientData(data)
      PageTitleService(data.client.account_name.concat(' - Transaction Journal'))
    })
  }, [])

  // Get the Client transactionLedger Entry
  React.useEffect(() => {
    try {
      // const current_user_id = JSON.parse(localStorage.getItem('me'))._id
      fetchData(params)
    } catch (error) {
      console.log(error)
    }
  }, [])

  React.useEffect(() => {
    setCollapseAll(false)
    BreadcrumbsService.setSelectedItems(['journal', 'accountList', 'clientJournal'])
    setTableData(sourceData)
  }, [])

  const accountName = clientData.client != null ? clientData.client.account_name : ''
  const accountNumber = clientData.client != null ? clientData.client.account_number : ''
  const accountEffectivity = DateFormatter(clientData.contract != null ? clientData.contract.effectivity : '')
  const accountExpiry = DateFormatter(clientData.contract != null ? clientData.contract.expiration : '')
  const paymentTerm = clientData.contract != null ? clientData.contract.payment_term : ''
  const taxType = clientData.client != null ? clientData.client.tax_type : ''
  const contractType = clientData.contract != null ? clientData.contract.type : ''

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      corporate_id: clientId,
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }

    setParams(_params)
    Promise.all([BillAnalystServices.getClientTransactionLedgerEntryContents(_params)]).then(values => {
      const [transactions] = values
      const { results, count, limit, page } = transactions.data.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({
        ...i,
        body: results,
        count,
        limit,
        page,
        sort: _params.sort,
        orderBy: orderByValue,
        order: orderValue
      }))
      setIsFetchingState(false)
    })
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value,
      accessors: ['category', 'amount|isNumber'],
      page: 1
    }
    fetchData(params)
  }, 300)

  // EVENT HANDLING
  const handleTableCollapseAll = () => {
    console.log('ALL: ', collapseAll)
    setCollapseAll(!collapseAll)
    setHeaderState(!collapseAll)
    header_collapsed == false ? setHeaders(header_items) : setHeaders(header_items)
    header_collapsed == false ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
    console.log('set to: ', collapseAll)
  }

  const handleHeaders = header_collapsed => {
    header_collapsed ? setHeaders(header_items) : setHeaders(header_items)
    header_collapsed ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  }

  const handleFileExport = async () => {
    let exportList = []
    const tile = 'Client Transaction Journal'
    try {
      if (!list & !clientData.account_name) {
        console.log('Waiting for Data loading to complete...')
      } else {
        console.log('asasdafagfa', list.body)

        const exportHeaders = {
          field_1: accountName,
          field_2: accountNumber,
          field_3: accountEffectivity + '--' + accountExpiry,
          field_4: paymentTerm
        }

        list.body.map((item, index) => {
          console.log(item)
          exportList.push({
            field_5: item.category,
            field_19: item.amount
          })
          item.members.map((memberItem, memberIndex) => {
            exportList.push({
              field_5: memberIndex === 0 ? item.category : '',
              field_6: DateFormatterByAbbreviation(memberItem.transaction_date, 'DD-MMM-YYYY').toString(),
              field_7: memberItem.member_name,
              field_8: memberItem.member_id,
              field_9: memberItem.member_account_no,
              field_10: memberItem.member_type,
              field_11: memberItem.plan_type,
              field_12: DateFormatterByAbbreviation(memberItem.effectivity_date, 'DD-MMM-YYYY').toString(),
              field_13: memberItem.cancelled_date
                ? DateFormatterByAbbreviation(memberItem.cancelled_date, 'mm-DDD-yyyy').toString()
                : '',
              field_14: memberItem.ticket_no,
              field_15: memberItem.transaction_no,
              field_16: memberItem.transaction_status,
              field_17: memberItem.billing_code,
              field_18: memberItem.billing_type,
              field_19: memberItem.amount,
              field_20: memberItem.loa_no,
              field_21: memberItem.claim_no,
              field_22: memberItem.soa_no,
              field_23: memberItem.atp_no
            })
          })
        })

        ExportsServices.getClientJournalExports(
          { dataHeaders: exportHeaders, list: exportList },
          { tile: tile, type: 'JOURNAL' }
        ).then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `${tile}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
      }
    } catch (error) {
      console.log(error)
    }
  }

  console.log(headers)

  return (
    <React.Fragment>
      <div className={classes.account_info}>
        <div className={classes.info_text_left}>
          <Typography>{'ACCOUNT NUMBER   ' + accountNumber}</Typography>
          <Typography>{`ACCOUNT TAX TYPE (${taxType})`}</Typography>
          <Typography>{`ACCOUNT CONTRACT TYPE (${contractType})`}</Typography>
        </div>
        <div className={classes.info_text_right}>
          <Typography>{accountEffectivity + '--' + accountExpiry}</Typography>
          <Typography>{paymentTerm.toUpperCase() + ' PAYMENT TERM'}</Typography>
        </div>
      </div>
      <div className={classes['search-container']}>
        <div id='modal-search-field' className={classes['search-field']}>
          <SearchTextComponent
            placeholder={'Search transaction, category or member'}
            onChange={e => {
              e.persist()
              mainSearchChange(e)
            }}
          />
        </div>
        {/* <Link id='display-settings-link' className={classes['display-settings']} color='primary' size='medium'> */}
        <IconButton id='export-icon' onClick={handleFileExport}>
          <ReplyIcon className={classes['export-icon']} />
          <Typography className='text-link underline'>Export</Typography>
        </IconButton>
        {/* </Link> */}
        <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
          <IconButton id='display-settings-icon'>
            <ViewListRoundedIcon className={classes['display-settings-icon']} />
            <Typography className='text-link underline'>Display Settings</Typography>
          </IconButton>
        </Link>
      </div>
      <TreeViewTable
        headCells={headers}
        handleCollapseEvent={handleTableCollapseAll}
        isViewTree
        fetchData={fetchData}
        params={params}
        count={list && list.body ? list.body.length : 0}
        pagination={pagination}
      >
        <TableBody>
          {isFetching && (
            <StyledTableRow>
              <StyledTableCell component='th' colspan={10}>
                <CircularLoading />
              </StyledTableCell>
            </StyledTableRow>
          )}
          {!isFetching &&
            list &&
            list.body &&
            list.body.map((item, index) =>
              Array.isArray(item.members) && item.members.length > 0 ? (
                <CollapsibleRow
                  // colspan={'2'}
                  key={index}
                  id={`account-id-${index}`}
                  data={item}
                  objectKey={'members'}
                  index={index}
                  tableColumnOrder={table_columns}
                  subColumnOrder={subColumn}
                  offset={3}
                  collapseAllState={collapseAll}
                  collapseFunction={handleHeaders}
                  actionTo={null}
                  monetary_columns={monetary_columns}
                  emphasis_columns={[]}
                />
              ) : (
                <TableRow key={index}>
                  <TableCell id={`account-id-${index}`} />
                  {mainColumn.map((column, iteration) => (
                    <TableCell>{item[column]}</TableCell>
                  ))}
                </TableRow>
              )
            )}
        </TableBody>
      </TreeViewTable>
    </React.Fragment>
  )
}
