import React from 'react'

import { Link } from 'react-router-dom'
import { Typography, TextField } from '@material-ui/core'

import Button from '@material-ui/core/Button'
import * as moment from 'moment'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import FormDrawer from '../../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../../Components/EnhancedSearchComponent/EnhancedSearchComponent'
import classes from '../CreateTransactionPage.module.scss'

import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

import { withStyles } from '@material-ui/core/styles'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faAngleDown,
  faFileInvoice,
  faMoneyBillWaveAlt,
  faFileAlt,
  faFileSignature
} from '@fortawesome/free-solid-svg-icons'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

import InvoiceServices from "../../../../../../Services/Api/BillingAndCollection/InvoiceServices";
import SOAService from "../../../../../../Services/Api/BillingAndCollection/SOAService";

// temp
import EnhacedSearchReferenceNo from '../EnhacedSearchReferenceNo'

import Sidebar from './Helpers/Sidebar'
import Footer from './Helpers/Footer'

export default function SearchClientContract (props) {
  const { clientContract, setStep, handlBackToSelectClientContract, setSelectedRef } = props
  const clientInfo = clientContract.data.client
  const billingInfo = clientContract.data.billing
  const contractInfo = clientContract.data.contract

  const location =
    contractInfo.soa_send_to_address.unit +
    ' ' +
    contractInfo.soa_send_to_address.floor +
    'F ' +
    contractInfo.soa_send_to_address.bldg_no +
    ' ' +
    contractInfo.soa_send_to_address.bldg_name +
    ' Bldg. ' +
    contractInfo.soa_send_to_address.street +
    ', ' +
    ' Brgy. ' +
    contractInfo.soa_send_to_address.brgy +
    ', ' +
    contractInfo.soa_send_to_address.region +
    ' ' +
    contractInfo.soa_send_to_address.zip_code

  const [transactionType, setTransactionType] = React.useState('none')
  const [showReference, setShowReference] = React.useState(false)
  const [referenceTransaction, setReferenceTransaction] = React.useState('')

  const StyledMenu = withStyles({
    paper: {
      // border: '1px solid #d3d4d5'
    }
  })(props => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right'
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right'
      }}
      {...props}
    />
  ))

  const StyledMenuItem = withStyles(theme => ({
    root: {
      '& .MuiListItemText-primary': {
        fontSize: '13px'
      }
    }
  }))(MenuItem)

  const [dropDownAnchor, setDropDownAnchor] = React.useState(null)
  const [currentSelected, setCurrentSelected] = React.useState(1)
  const [selectedTrans, setSelectedTrans] = React.useState('')
  const [refInvoiceTrans, setRefInvoiceTrans] = React.useState([])
  const [soaTrans, setSoaTrans] = React.useState([])
  const [selectedListItem, setSelectedListItem] = React.useState([])
  
  const handleDropDownClick = event => {
    setDropDownAnchor(event.currentTarget)
  }

  const handleDropDownClose = () => {
    setDropDownAnchor(null)
  }

  React.useEffect( () => {
    setTransactionType(currentSelected)
    if (['credit', 'debit'].includes(transactionType)) {
      setSelectedRef(selectedListItem)
      setStep(transactionType)
    }
  }, [selectedListItem])

  React.useEffect( () => {
    if (showReference) {
      setReferenceTransaction('invoice')
    }
  }, [showReference])

  React.useEffect( () => {
    if (referenceTransaction === 'invoice' && refInvoiceTrans.length === 0) {
      const _params = {
        limit: 9999999, // to get all
        page: 1,
        entityType: 'Client',
        status: 'approved',
        clientContractId: clientContract.data._id
      }
      if (localStorage.getItem('isAnalyst') === 'true') {
        _params['analystUser'] = localStorage.getItem('username')
      }
      console.log('MELVINN', _params)
      LoadingService.loading(true)
      InvoiceServices.getInvoices(_params).then(({ data: res }) => {
        const { results } = res.data
        const data = []
        results.map( res => {
          data.push({
            client_name: res.billing_client_contract[0].client.account_name,
            name: res.transaction.number,
            amount: `Amount: PHP ${res.summary ? res.summary.sub_total.toFixed(2) : 0.00}`,
            id: res._id
          })
        })
        setRefInvoiceTrans(data)
        LoadingService.loading(false)

        if (data.length === 0) {
          alert('No Invoice Data')
        }
      })
    } else if (referenceTransaction === 'soa' && soaTrans.length === 0) {
      const _params = {
        limit: 9999999, // to get all
        page: 1,
        entityType: 'Client',
        status: 'approved',
        clientContractAcctNumber: clientContract.data.client.account_number
      }
      if (localStorage.getItem('isAnalyst') === 'true') {
        _params['analystUser'] = localStorage.getItem('username')
      }
      LoadingService.loading(true)
      SOAService.getSoaList(_params).then(({ data: res }) => {
        const { results } = res.data
        const data = []
        results.map( res => {
          data.push({
            client_name: res.account_name,
            name: res.soa_number,
            amount: `Total Amount Due: PHP ${res.summary ? res.summary.sub_total.toFixed(2) : 0.00}`,
            id: res._id
          })
        })
        setSoaTrans(data)
        if (data.length === 0) {
          alert('No Soa Data')
        }
        LoadingService.loading(false)
      })
    }
  }, [referenceTransaction])

  const selectedItem = item => {
    switch (item) {
      case 1:
        return (
          <React.Fragment>
            <ListItemText secondary='Select transaction type' />
          </React.Fragment>
        )
      case 2:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
            </ListItemIcon>
            <ListItemText primary='Invoice' />
          </React.Fragment>
        )
      case 3:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon
                className={classes['transaction-type-item-icon-credit-memo']}
                icon={faMoneyBillWaveAlt}
              />
            </ListItemIcon>
            <ListItemText primary='Credit Memo' />
          </React.Fragment>
        )
      case 4:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
            </ListItemIcon>
            <ListItemText primary='Debit Memo' />
          </React.Fragment>
        )
    }
  }

  if (transactionType === 'invoice') {
    setStep('invoice')
  }

  console.log('REACHED')

  return (
    <div>
      <HeaderDetails>
        <Typography className={classes['header-details-create-transaction']} variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerContent={
          <Sidebar clientInfo={clientInfo} contractInfo={contractInfo} billingInfo={billingInfo} location={location} handlBackToSelectClientContract={handlBackToSelectClientContract} />
        }
      >
        <div className={classes['main-content-wrapper']}>
          <div className={classes['transaction-type-container']}>
            <div className={classes['transaction-type-field']}>
              <Typography id='transaction-type-field' color='textSecondary'>
                Transaction Type
              </Typography>
            </div>

            <div className={classes['transaction-type-value']}>
              <Button
                id='transaction-type-button'
                className={classes['transaction-type-button']}
                aria-controls='customized-menu'
                aria-haspopup='true'
                onClick={handleDropDownClick}
                endIcon={<FontAwesomeIcon className={classes['transaction-type-button-icon']} icon={faAngleDown} />}
              >
                {selectedItem(currentSelected)}
              </Button>
              <StyledMenu
                id='transaction-type-items'
                anchorEl={dropDownAnchor}
                keepMounted
                open={Boolean(dropDownAnchor)}
                onClose={handleDropDownClose}
              >
                <StyledMenuItem
                  id='transaction-type-item-1'
                  onClick={e => {
                    setCurrentSelected(1)
                    handleDropDownClose()
                    setTransactionType('none')
                  }}
                >
                  <ListItemText primary='' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-2'
                  onClick={e => {
                    setCurrentSelected(2)
                    handleDropDownClose()
                    setShowReference(false)
                    setTransactionType('invoice')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
                  </ListItemIcon>
                  <ListItemText primary='Invoice' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-3'
                  onClick={e => {
                    setCurrentSelected(3)
                    handleDropDownClose()
                    setShowReference(true)
                    setTransactionType('credit')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon
                      className={classes['transaction-type-item-icon-credit-memo']}
                      icon={faMoneyBillWaveAlt}
                    />
                  </ListItemIcon>
                  <ListItemText primary='Credit Memo' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-4'
                  onClick={e => {
                    setCurrentSelected(4)
                    handleDropDownClose()
                    setShowReference(true)
                    setTransactionType('debit')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
                  </ListItemIcon>
                  <ListItemText primary='Debit Memo' />
                </StyledMenuItem>
              </StyledMenu>
            </div>
          </div>

          {showReference &&
            <div className={classes['reference-number-container']}>
              <div className={classes['reference-number-field']}>
                <Typography id='reference-number-field' color='textSecondary'>
                  Reference No.
                </Typography>
              </div>
              <div className={classes['reference-number-value']}>
                <EnhacedSearchReferenceNo width={'450px'} setReferenceTransaction={setReferenceTransaction} dropDownData={referenceTransaction === 'invoice' ? refInvoiceTrans : soaTrans} setSelectedListItem={setSelectedListItem} />
              </div>
            </div>
          }
          {/*
            Commented
            <Link
            id='advanced-search-link'
            className={classes['advanced-search-link']}
            // to='/'
            color='primary'
            size='medium'
          >
            Advanced Search
          </Link> */}
        </div>

        <Footer />
      </FormDrawer>
    </div>
  )
}
