import React, { useEffect, useState } from 'react'

import {
  Grid, Typography, TableCell,
  TableRow
} from '@material-ui/core'
import _ from 'lodash'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import SearchTextComponent from '../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import AgingSummaryBar from '../../Components/AgingSummaryBar/AgingSummaryBar'

import ListView from './components/ListView'

import AgingSummaryServices from '../../../../../Services/Api/AgingSummaryServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

import style from './AgingSummaryAccountsPage.module.scss'

import { ViewAllListConfig } from './config/config.js'

export default function AgingSummaryAccountsPage () {
  const initalSummaryBarValue = {
    total_collectibles: 0,
    ageSummary: [
      { name: '1 - 45 Days', total_amount_due: 0 },
      { name: '46 - 75 Days', total_amount_due: 0 },
      { name: '76 - 105 Days', total_amount_due: 0 },
      { name: '106 - 136 Days', total_amount_due: 0 },
      { name: '137 - 166 Days', total_amount_due: 0 },
      { name: '167 - 196 Days', total_amount_due: 0 },
      { name: 'Over 197 Days', total_amount_due: 0 }
    ]
  }

  const initialValue = {
    category: '',
    count: 0,
    search: '',
    limit: 10,
    body: [],
    page: 1,
    filters: null,
    data: [],
    orderBy: 'total_amount_due',
    order: false,
    sort: '',
    range: ''
  }

  const [summaryBarData, setSummaryBarData] = useState(initalSummaryBarValue)
  const [list, setList] = useState(initialValue)
  const [params, setParams] = useState({})

  const fetchData = (params) => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      range: list.range,
      ...params
    }
    setParams(_params)

    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'listOfAccountsWithAgingSummary'])

    PageTitleService(`List of Accounts with Aging Summary`)

    Promise.all([AgingSummaryServices.getAccountList(_params), AgingSummaryServices.getSummaryBarData({ type: 'account', params: _params })])
      .then(res => {
        const [getAssignedAccountsRes, summary] = res
        const { count, results, limit, page } = getAssignedAccountsRes.data.data
        const summaryData = summary.data.data

        setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: _params.orderBy ? _params.orderBy : list.orderBy, search: _params.search, filters: _params.filters, range: _params.range }))

        setSummaryBarData(summaryData)
      })
      .catch(e => console.error(`Error fetching Assigned Accounts: ${e}`))
  }

  const onSearchChange = _.debounce((value) => {
    const params = {
      search: value,
      accessors: ['account_number', 'account_name', 'age', 'number_of_open_soa', 'collection_analyst_id', 'total_amount_due'],
      page: 1
    }
    fetchData(params)
  }, 300)

  const onDisplaySettingsChange = (value) => {
    fetchData({ range: value })
  }

  useEffect(() => {
    fetchData()
  }, [])

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <SearchTextComponent
              placeholder='Search List of Accounts with Aging Summary'
              fromDateErrorMessage='Please enter a valid due start date'
              toDateErrorMessage='Please enter a valid due end date'
              displaySettingsButton = {false}
              exportFiles
              displaySettings
              onChange={onSearchChange}
              handleOnDisplaySettingsApplyClicked={onDisplaySettingsChange}
            />
          </Grid>
          <Grid item xs={12} >
            <Grid container>
              <Grid item xs={12}>
                <AgingSummaryBar summaryBarData={summaryBarData} />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={12} style={{ width: 'fit-content', overflow: 'auto', boxShadow: '1px 2px 5px #ddd' }}>
            <ListView
              id={'audit-log-view-all-list'}
              config={ViewAllListConfig}
              contents={list.body}
              count={list.count}
              list={list}
              // fetchItemDetails={fetchItemDetails}
              fetchData={fetchData}
              params={params}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
