import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this_week', text: 'This Week' },
    { value: 'this_month', text: 'This Month' }
  ],
  default: 'this_month'
}

const columnConfig = [
  { id: 'member-distribution-tile-table-header', name: 'Member Name' },
  { id: 'member-distribution-tile-table-header', name: 'Date Submitted' }
]

// Mock data
const mock = require('../_mock-data/collection-distribution.json')

const MemberDistributionTile = props => {
  // Constants
  const { service = () => {} } = props
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Member Collection Distribution To Validate'
  })

  // Initialize / Watch
  React.useEffect(() => {
    const data = mock.slice(0, 5)
    setTile(i => ({ ...i, count: data.length, data }))
  }, [])

  // Method
  /**
   * Handle Transfer to page
   * @param location route/path to view all member aging summary page
   */
  const handleOnViewAll = () => {}
  return (
    <React.Fragment>
      <DashboardTile
        id='member-distribution-tile'
        count={{ color: 3, value: tile.count, id: 'member-distribution-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id='member-distribution-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`member-distribution-tile-table-row-${index}`}
                id={`member-distribution-tile-table-row-${index}`}
              >
                <TableCell id={`member-distribution-tile-table-cell-account-name-${index}`}>
                  {item.member_name || ''}
                </TableCell>
                <TableCell id={`member-distribution-tile-table-cell-date-submitted-${index}`}>
                  {moment(item.date_submitted).format('ll') || ''}
                </TableCell>
                <TableCell id={`member-distribution-tile-table-cell-action-${index}`}>
                  {/* TODO: Change link when end-point and data is available */}
                  <Link to={`/`}>
                    <Typography
                      variant='caption'
                      className='text-link underline'
                      id={`member-distribution-tile-view-${index}`}
                    >
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

MemberDistributionTile.displayName = 'DashboardTileComponent'

export default MemberDistributionTile
