import { Link, useHistory } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Services
import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this_week', text: 'This Week' },
    { value: 'this_month', text: 'This Month' }
  ],
  default: 'this_month'
}

const columnConfig = [
  { id: 'endorsed-accounts-for-suspension-tile-table-header', name: 'Account Name', width: '50%' },
  { id: 'endorsed-accounts-for-suspension-tile-table-header', name: 'Date Endorsed', width: '40%' }
]

export default function EndorsedAccountsForSuspensionTile(props) {
  // const { service = () => {} } = props
  const history = useHistory()

  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filter: {
      status: 'submitted'
    },
    filterMenu,
    limit: 5,
    order: false,
    orderBy: 'date_submitted',
    page: 1,
    sort: '',
    title: 'Endorsed Accounts for Suspension',
    userOwned: false // TODO: This should be set to `true`
  })

  // Initialize / Watch
  React.useEffect(() => {
    Promise.all([
      CollectionSuspensionServices.index({
        limit: tile.limit,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        rangeTarget: 'date_submitted'
      })
    ]).then(res => {
      const [submittedSuspensions] = res
      const { count, results } = submittedSuspensions.data.data
      setTile(prevState => ({ ...prevState, count, data: results }))
    })
  }, [tile.filterMenu.default])

  
  function handleOnFilterChange(event) {
    setTile(prevState => ({
      ...prevState,
      filterMenu: { 
        ...prevState.filterMenu, 
        default: event 
      }
    }))
  }

  function handleOnViewAllClicked() {
    history.push('/collection/suspensions/submitted')
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='endorsed-accounts-for-suspension-tile'
        count={{ color: 3, value: tile.count, id: 'endorsed-accounts-for-suspension-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={handleOnFilterChange}
        viewList={
          <Typography
            id='endorsed-accounts-for-suspension-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnViewAllClicked}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell 
                  style={{ width: item.width || 'auto'}}
                  key={`${item.id}-${index}`} 
                  id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((suspension, index) => (
              <TableRow
                key={`endorsed-accounts-for-suspension-tile-table-row-${index}`}
                id={`suspension-tile-table-row-${index}`}
              >
                <TableCell id={`endorsed-accounts-for-suspension-tile-table-cell-account-name-${index}`}>
                  {suspension?.contract?.client?.account_name || ''}
                </TableCell>
                <TableCell id={`endorsed-accounts-for-suspension-tile-table-cell-date-submitted-${index}`}>
                  {moment(suspension?.date?.submitted).format('ll') || ''}
                </TableCell>
                <TableCell id={`endorsed-accounts-for-suspension-tile-table-cell-action-${index}`}>
                  {/* TODO: Replace with link to list of submitted suspensions page */}
                  <Link to={'/'}>
                    <Typography
                      variant='caption'
                      className='text-link underline'
                      id={`endorsed-accounts-for-suspension-tile-view-${index}`}
                    >
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

EndorsedAccountsForSuspensionTile.displayName = 'DashboardTileComponent'
