import React from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer2'

// Store
import { useDispatch, useSelector } from 'react-redux'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Styles
import classes from './reports.module.scss'

export default function CollectionDistributionPage (props) {
  const history = useHistory()
  const [file, setFile] = React.useState(null)
  const { cdf_number = null } = props.match.params

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['listOfCDApproved', 'approvedCD'])
    CollectionCDFServices.getReport({ cdfNumber: cdf_number }).then(response => {
      const blob = new Blob([response.data])
      setFile(blob)
    })
  }, [])

  const handlePrint = () => {
    CollectionCDFServices.getReport({ cdfNumber: cdf_number }).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `report_${cdf_number}.pdf`)
      document.body.appendChild(link)
      link.click()
    })
  }

  return (
    <Grid
      container
      className={classes['collection-reports-container']}
      spacing={2}
      direction='column'
      justify='space-between'
    >
      <Grid item xs={12}>
        <PdfViewer
          initialScale={1}
          file={file}
          download={() => handlePrint()}
          onPrint={() => handlePrint()}
        />
      </Grid>

      <Grid item xs={12}>
        <PageFooter isControl handleOnCloseClicked={() => history.push(`/collection/distribution/view/approved/client/${cdf_number}?stepNumber=2`)} />
      </Grid>
    </Grid>
  )
}
