import React from 'react'

import { Link } from 'react-router-dom'
import { Typography, TextField } from '@material-ui/core'

import Button from '@material-ui/core/Button'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../Components/EnhancedSearchComponent/EnhancedSearchComponent'

import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
// TODO change to billing config codes once created
import BillingConfigCodesService from 'Services/Api/BillingAnalystServices'
import BillingCreditMemoServices from 'Services/Api/BillingCreditMemoServices'

// Components
import SelectClientContract from './Components/SelectClientContract'
import SelectTransactonType from './Components/SelectTransactonType'
import PendingCreditMemo from './Components/Client/PendingCreditMemo'

import classes from './CreditMemoPendingPage.module.scss'

export default function CreateTransactionPage (props = {}) {
  PageTitleService()
  // BreadcrumbsService.setSelectedItems(['createTransaction'])

  const transaction_number = props.match.params.transactionNumber
  const transaction_status = props.match.params.transactionStatus
  const type = props.match.params.type
  const [pendingCreditMemos, setPendingCreditMemos] = React.useState({
    data: []
  })

  const [clientContract, setClientContract] = React.useState({
    data: []
  })
  const [clientId, setClientId] = React.useState('')
  const [clientContractId, setClientContractId] = React.useState('')
  const [accountNumber, setAccountNumber] = React.useState('')
  const [selectedEntity, setSelectedEntity] = React.useState('Client')
  const [errorMessage, setErrorMessage] = React.useState('')
  const [step, setStep] = React.useState('credit')
  const [clientInfo, setClientInfo] = React.useState('')
  const [billingInfo, setBillingInfo] = React.useState('')
  const [contractInfo, setContractInfo] = React.useState('')
  const [location, setLocation] = React.useState('')
  const [dateInfo, setDateInfo] = React.useState('')
  const [transactionInfo, setTransactionInfo] = React.useState('')
  const [entriesChanged, setEntriesChanged] = React.useState(false)
  const [clientContractChanged, setClientContractChanged] = React.useState(false)

  const [totalTaxableAmount, setTotalTaxableAmount] = React.useState(0)
  const [totalTax, setTotalTax] = React.useState(0)
  const [totalAmount, setTotalAmount] = React.useState(0)

  const [billingConfigCodes, setBillingConfigCodes] = React.useState([])

  const pagination = {
    count: 0,
    rowsPerPage: 0,
    page: 0
  }
  const [fields, setFields] = React.useState([])
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }
  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})
  React.useEffect(() => {
    Promise.all([BillingCreditMemoServices.getTobeRevisedCreditMemo({ transaction_number })]).then(values => {
      setPendingCreditMemos(prevState => ({
        ...prevState,
        data: values[0].data.data
      }))
      console.log(values[0].data.data)
      setClientContractId(values[0].data.data.ref.client_contract)
      setClientInfo(values[0].data.data.client)
      setBillingInfo(values[0].data.data.billing)
      setContractInfo(values[0].data.data.contract)
      setLocation(values[0].data.data.client.location)
      setDateInfo(values[0].data.data.date.date)
      setTransactionInfo(values[0].data.data)
      setTotalTaxableAmount(values[0].data.data.summary.sub_total)
      setTotalTax(values[0].data.data.summary.tax_amount)
      setTotalAmount(values[0].data.data.summary.sub_total + values[0].data.data.summary.tax_amount)
    })
    fetchData()

    Promise.all([BillingConfigCodesService.getAllTransactionConfigCodes('Credit Memo')]).then(async values => {
      console.log(values[0].data.data)
      setBillingConfigCodes(values[0].data.data)
    })
  }, [])
  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    BillingCreditMemoServices.getCreditMemoDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      console.log(res)
      setFields(res.data.results)
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }

  const changeView = params => {
    fetchData(params)
  }

  const handleEntitySearch = (e, click) => {
    let account_number = ''
    if (click) {
      setAccountNumber(e)
      setErrorMessage('')
      account_number = e
    } else {
      setAccountNumber(e.target.value)
      setErrorMessage('')
      account_number = e.target.value
    }

    Promise.all([BillingAnalystServices.getSelectedClient(account_number)]).then(values => {
      console.log(values[0].data.data)
      if (values[0].data.data) {
        setClientContract(prevState => ({
          ...prevState,
          data: values[0].data.data
        }))
        setClientId(values[0].data.data.client.id)
        setClientContractId(values[0].data.data._id)
        setClientContractChanged(true)
      } else {
        setClientContract({
          data: []
        })
        setClientId('')
        setClientContractId('')
      }
    })
  }
  const handleShowEntity = () => {
    switch (selectedEntity) {
      case 'Client':
        console.log(clientContract.data)
        if (clientContract.data.length !== 0) {
          let contractInfo = clientContract.data.contract
          let location = contractInfo.soa_send_to_address.unit + ' ' +
          contractInfo.soa_send_to_address.floor + 'F ' +
          contractInfo.soa_send_to_address.bldg_no + ' ' +
          contractInfo.soa_send_to_address.bldg_name + ' Bldg. ' +
          contractInfo.soa_send_to_address.street + ', ' +
          ' Brgy. ' + contractInfo.soa_send_to_address.brgy + ', ' +
          contractInfo.soa_send_to_address.city + ', ' +
          contractInfo.soa_send_to_address.province + ', ' +
          contractInfo.soa_send_to_address.region + ' ' +
          contractInfo.soa_send_to_address.zip_code
          setStep('credit')
          setErrorMessage('')
          setClientContractId(clientContract.data._id)
          setClientInfo(clientContract.data.client)
          setBillingInfo(clientContract.data.billing)
          setContractInfo(clientContract.data.contract)
          setLocation(location)
          setDateInfo(clientContract.data.date.date)
        } else {
          setErrorMessage('Account Number not found')
        }
        break
      case 'Member':
        // console.log('Member Entity is selected')
        console.log(clientContract.data)
        if (clientContract.data.length !== 0) {
          setStep(2)
          setErrorMessage('')
        } else {
          setErrorMessage('Account Number not found')
        }
    }
  }

  // autosuggest
  const [suggestions, setSuggestions] = React.useState([])

  React.useEffect(() => {
    if (clientContractId) {
      fetchMembers()
    }
  }, [clientContractId])

  const fetchMembers = async () => {
    const { data } = await BillingAnalystServices.getSelectedClientMembers(clientContractId)
    if (data.data) {
      const memberList = []
      data.data.map(res => {
        memberList.push({
          id: res._id,
          value: {
            memberId: res.member.id,
            name: `${res.member.first_name} ${res.member.last_name}`,
            clientName: res.client_name
          },
          member_details: {
            id: res.member.id,
            person_id: res.member.person_id,
            account_no: res.member.account_no,
            type: res.member.type,
            status: res.member.status,
            first_name: res.member.first_name,
            middle_initial: res.member.middle_initial,
            middle_name: res.member.middle_name,
            last_name: res.member.last_name,
            plan_type_code: res.member.plan_type_code,
            plan_type_name: res.member.plan_type_name,
            effectivity_date: res.member.effectivity_date,
            renewal_date: res.member.renewal_date,
            end_date: res.member.end_date,
            cancellation_date: res.member.cancellation_date
          }
        })
      })
      setSuggestions(memberList)
    }
  }
  const handlBackToSelectClientContract = () => {
    setStep(1)
  }
  const handlBackToSelectTransactionType = () => {
    setStep(2)
  }
  if (step === 1) {
    return (
      <SelectClientContract
        clientId={clientId}
        accountNumber={accountNumber}
        handleEntitySearch={handleEntitySearch}
        handleShowEntity={handleShowEntity}
        selectedEntity={selectedEntity}
        setSelectedEntity={setSelectedEntity}
        errorMessage={errorMessage}
        setErrorMessage={setErrorMessage}
      />
    )
  } else if (step === 2) {
    return (
      <SelectTransactonType
        clientContract={clientContract}
        setStep={setStep}
        handlBackToSelectClientContract={handlBackToSelectClientContract}
      />
    )
  } else if (step === 'credit' && selectedEntity === 'Client') {
    return (
      <PendingCreditMemo
        list={list}
        params={params}
        fetchData={fetchData}
        changeView={changeView}
        clientContract={clientContract}
        setStep={setStep}
        handlBackToSelectClientContract={handlBackToSelectClientContract}
        handlBackToSelectTransactionType={handlBackToSelectTransactionType}
        clientContractId={clientContractId}
        clientInfo={clientInfo}
        billingInfo={billingInfo}
        contractInfo={contractInfo}
        location={location}
        fields={fields}
        setFields={setFields}
        transaction_number={transaction_number}
        dateInfo={dateInfo}
        transactionInfo={transactionInfo}
        totalTaxableAmount={totalTaxableAmount}
        setTotalTaxableAmount={setTotalTaxableAmount}
        totalTax={totalTax}
        setTotalTax={setTotalTax}
        totalAmount={totalAmount}
        setTotalAmount={setTotalAmount}
        entriesChanged={entriesChanged}
        setEntriesChanged={setEntriesChanged}
        clientContractChanged={clientContractChanged}
        suggestions={suggestions}
        billingConfigCodes={billingConfigCodes}
      />
    )
  }
}
