/* eslint-disable no-unused-vars */
import * as React from 'react'
import FormControl from '@material-ui/core/FormControl'
import FormHelperText from '@material-ui/core/FormHelperText'
import Typography from '@material-ui/core/Typography'
import { MuiPickersUtilsProvider, TimePicker, TimePickerProps } from '@material-ui/pickers'
import { makeStyles, withStyles } from '@material-ui/core/styles'
import MomentUtils from '@date-io/moment'

const useStyles = makeStyles(() => ({
  formControl: {
    width: '100%',
  },

  label: {
    marginBottom: 5
  }
}))

const StyledTimePicker = withStyles(theme => ({
  root: {
    backgroundColor: theme.palette.common.white,
    boxShadow: '0.5px 0.5px 1px 0.8px rgba(0, 0, 0, 0.4)',
    paddingTop: 4,
    paddingLeft: 10,
    paddingBottom: 4,
    paddingRight: 5,
    borderRadius: 4,
  }
}))(TimePicker)

/**
 * @param {TimePickerProps} props 
 */
export default function XTimePicker({ label = '', error = '', ...props }) {
  const classes = useStyles()

  return (
    <MuiPickersUtilsProvider utils={MomentUtils}>
      <FormControl error={error.length > 0} className={classes.formControl}>
        {label && (
          <Typography variant="subtitle2" id={`${props.id}-label`} className={classes.label}>
            {label}
            {props.required && <span> *</span>}
          </Typography>
        )}

        <StyledTimePicker
          placeholder="08:00 AM"
          mask="__:__ _M"
          InputProps={{ disableUnderline: true }}
          {...props}
        />

        {error.length > 0 && <FormHelperText>{error}</FormHelperText>}
      </FormControl>
    </MuiPickersUtilsProvider>
  )
}

function TextField() {
  return <input />
}
