import React from 'react'
import { useHistory } from 'react-router-dom'

// Components
import Page from './components/Page'

// Store
import { useDispatch } from 'react-redux'

// Services
import ReminderLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'

export default function GenerateReminderLetter(props) {
  const history = useHistory()
  const dispatch = useDispatch()
  const letterNumber = props.match.params.letter_number

  async function handleSend() {
    try {
      LoadingService.loading(true)

      const response = await ReminderLetterServices.send(letterNumber)

      LoadingService.loading(false)

      dispatch(
        SnackbarService.set({
          open: true,
          title: 'Success!',
          message: response.data.data,
          severity: 'success'
        })
      )

      history.push('/')
    } catch (error) {
      console.log(`Error sending reminder letter: ${error}`)
      LoadingService.loading(true)
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance', 'generateReminderLetter'])
  }, [])

  return (
    <Page
      title="Generate Reminder Letter"
      letterNumber={letterNumber}
      onSend={handleSend}
      onCancel={() => history.push('/')}
    />
  )
}
