import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import TextFieldCurrency from '../../TextFieldCurrency/TextFieldCurrency'
import TextField from './../../TextField/CustomTextField'
// Style
import classes from '../style.module.scss'
import { UpdateField } from 'Pages/Protected/BillingAndCollection/Store/actions'

const ACDFGrid = props => {
    const {
        labels,
        data,
        _data,
        breakdownName,
        tax = null,
        disabled,
        UpdateField,
        UpdateFieldData,
        dispatch,
        handleOnSave = () => {} } = props

    const [debounce, setDebounce] = React.useState(null)
    const [adjustments, setAdjustments] = React.useState({})

    React.useEffect(() => {
      clearTimeout(debounce)
      setDebounce(setTimeout(() => { calculateFees() }, 500))
    }, [_data.adjustments_summary])

    const calculateFees = () => {
      const sub_total = _data.adjustments_summary.credit_memo + _data.adjustments_summary.commission + _data.adjustments_summary.deletion + _data.adjustments_summary.ewt
      const sub_total_tax = _data.adjustments_summary.credit_memo_tax + _data.adjustments_summary.commission_tax + _data.adjustments_summary.deletion_tax
      const grand_total = sub_total + sub_total_tax
      setAdjustments(i => ({ ...i, sub_total, sub_total_tax }))
      dispatch(UpdateField({
        value: {
          grand_total,
          sub_total,
          sub_total_tax
        },
        field: 'total_fees'
      }))
    }

    const handleTaxFields = (target, val) => {
      if (tax === null) return false
      dispatch(
        UpdateFieldData({
          value: val,
          field: target,
          target: 'adjustments_summary'
        })
      )
    }

    return (
        <>
          <Grid container spacing={4} className={`${classes['margin-top']} ${classes['bg-gray']}`}>
            <Grid item xs={12} lg={12}>
              <Typography variant='h5'>{breakdownName} Breakdown (Adjustments)</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container direction='row' justify='center'>
                <Grid item xs={11}>
                  <Grid container spacing={4}>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-credit-memo' label={'Credit Memo'} disabled={!!disabled} name='credit_memo' onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'credit_memo',
                              target: 'adjustments_summary'
                            }),
                            handleTaxFields('credit_memo_tax', taxAmount)
                          )
                        }}
                        value={_data.adjustments_summary ? _data.adjustments_summary.credit_memo : 0 }
                      />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-credit-memo-tax' label={'Credit Memo Tax'} disabled value={_data.adjustments_summary ? _data.adjustments_summary.credit_memo_tax : 0 } />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-deletion' label={'Deletion'} disabled={!!disabled} onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                              dispatch(
                                UpdateFieldData({
                                  value: e.target.value,
                                  field: 'deletion',
                                  target: 'adjustments_summary'
                                }),
                                handleTaxFields('deletion_tax', taxAmount)
                              )
                            }} value={_data.adjustments_summary ? _data.adjustments_summary.deletion : 0 } />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-deletion-tax' label={'Deletion Tax'} disabled value={_data.adjustments_summary ? _data.adjustments_summary.deletion_tax : 0 } />
                    </Grid>
                  </Grid>
                  <Grid container spacing={4} className={`${classes['bg-gray']}`}>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-ewt' label={'EWT'} disabled={!!disabled} onChange={e => {
                          if (!!disabled) return false
                          dispatch(
                                UpdateFieldData({
                                  value: e.target.value,
                                  field: 'ewt',
                                  target: 'adjustments_summary'
                                })
                              )
                            }} value={_data.adjustments_summary ? _data.adjustments_summary.ewt : 0 } />
                    </Grid>
                    <Grid item xs={3} />
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-commission' label={'Commission'} disabled={!!disabled} onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                              dispatch(
                                UpdateFieldData({
                                  value: e.target.value,
                                  field: 'commission',
                                  target: 'adjustments_summary'
                                }),
                                handleTaxFields('commission_tax', taxAmount)
                              )
                            }} value={_data.adjustments_summary ? _data.adjustments_summary.commission : 0 } />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-commission-tax' label={'Commission Tax'} disabled value={_data.adjustments_summary ? _data.adjustments_summary.commission_tax : 0 } />
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                      <TextField
                        constainerStyle={{ width: 'unset', flex: 1 }}
                        multiline
                        rows={3}
                        disabled={!!disabled}
                        id='breakdown-remarks'
                        label='REMARKS'
                        direction='row'
                        value={_data.adjustments_summary ? _data.adjustments_summary.remarks : '' }
                        onChange={ e => {
                          if (!!disabled) return false
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'remarks',
                              target: 'adjustments_summary'
                            })
                          )
                        }}
                      />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid
            container
            direction='row'
            justify='space-between'
            className={`${classes['no-margin']} ${classes['bg-gray']}`}
            spacing={4}
          >
            <Grid item xs={12}>
              <Typography variant='h5'>Sub Total (Adjustments)</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container justify='center' direction='row'>
                <Grid item xs={11}>
                  <Grid container direction='row' justify='space-between'>
                    <Grid item xs={4}>
                      <TextFieldCurrency id='breakdown-adjustments-sub-total' label='Sub Total Taxable Amount' disabled value={adjustments.sub_total || 0} />
                    </Grid>
                    <Grid item xs={4} alignContent='flex-end'>
                      <TextFieldCurrency id='breakdown-adjustments-sub-total-tax' label='Sub Total Tax Amount' disabled value={adjustments.sub_total_tax || 0} />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </>
    )
}

export default ACDFGrid