import React from 'react'
import Chart from 'react-apexcharts'
import Typography from '@material-ui/core/Typography'

// Tile
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Config
const filterMenu = {
  data: [
    { value: 'previous_month', text: 'Previous Month' },
    { value: 'this_month', text: 'This Month' },
    { value: 'next_month', text: 'Next Month' }
  ],
  default: 'this_month'
}

// Mock-Data
const mock = require('../_mock-data/actual-report.json')

const ActualReportTile = props => {
  const [chart, setChart] = React.useState({
    series: [],
    chart: {
      toolbar: {
        show: false
      },
      type: 'bar',
      height: 320
    },
    legend: {
      position: 'top',
      horizontalAlign: 'right'
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '40%'
        // endingShape: 'rounded'
      }
    },
    dataLabels: {
      enabled: false
    },
    stroke: {
      show: true,
      width: 2,
      colors: ['transparent']
    },
    xaxis: {
      categories: ['Week 1', 'Week 2', 'Week 3', 'Week 4']
    },
    // yaxis: {
    //   title: {
    //     text: '$ (thousands)'
    //   }
    // },
    fill: {
      opacity: 1
    },
    tooltip: {
      y: {
        formatter: function (val) {
          return `${val}M`
        }
      }
    }
  })

  React.useEffect(() => {
    // TODO: Change when end-point is created
    setChart(i => ({ ...i, series: mock }))
  }, [mock])

  // Methods
  /**
   * Handle View All Link Behavior
   * @param route Link to where the page will redirect
   */

  const handleOnViewAll = () => {}

  return (
    <React.Fragment>
      <DashboardTile
        id='aging-summary-tile'
        count={{ color: 20, value: 'April 2020 Target versus Actual Report', id: 'aging-summary-tile-count' }}
        sizes={{ sm: 12 }}
        title=''
        isFilterShown
        filterMenu={filterMenu}
        headerSize={'h6'}
        countSize={'h5'}
        viewList={
          <Typography
            id='aging-summary-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Chart options={{ ...chart }} series={chart.series} type='bar' width={'100%'} height={250} />
      </DashboardTile>
    </React.Fragment>
  )
}

ActualReportTile.displayName = 'DashboardTileComponent'

export default ActualReportTile
