import React, { useEffect, useState } from 'react'

// MATERIAL COMPONENTS
import {
  Grid, Typography, TableCell,
  TableRow
} from '@material-ui/core'

// SHARED COMPONENTS
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

/*
 * PAGE COMPONENTS 
 * 
 * Components customized for current page
*/
import ListViewOptions from './components/ListViewOptions'
import SideNav from './components/SideNav'
import DetailsModal from './components/DetailsModal'
import ListView from './components/ListView'

// API SERVICES
import AuditLogServices from '../../../../../Services/Api/AuditLogServices'

// STYLES
import style from './AuditLog.module.scss'

/*
 * MOCK DATA 
 * 
 * To be deleted on API integration
*/
import mockData from './mock/data.js'
import { config } from './config/ViewAllList.js'

export default function ActivityLog(props) {
  // LOCAL CONSTANTS
  const reportType = props.match.params.type
  const initialValue = {
    body: [],
    count: 0,
    filters: {},
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: '',
    reportType: 'activity'
  }

  // LOCAL STATES
  const [pageInfo, setPageInfo] = useState({ reportType: 'activity', breadCrumb: 'activityLogs' })
  const [list, setList] = useState(initialValue)
  const [showModal, setShowModal] = useState()
  const [params, setParams] = useState({})
  const [itemDetails, setItemDetails] = useState()

  // LOCAL METHODS 
  const onListViewFilterChange = (params) => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      reportType: pageInfo.reportType,
      ...params
    }
    setParams(_params)
  }
  const fetchAuditTrailLists = () => {
    if (params.reportType) {
      AuditLogServices.getAuditList(params).then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setList(i => ({ ...i, body: results, count, limit, page }))
      })
    }
  }
  const fetchItemDetails = ({ id, activityId }) => {

    switch (pageInfo.reportType) {
      case 'activity':
        AuditLogServices.getActivityDetails(activityId).then(({ data: res }) => {
          const { results } = res.data
          setItemDetails({
            ...results[0],
            fieldsData: results.map(item => ({ field_name: item.field_name, old_value: item.old_value, new_value: item.new_value }))
          })
        })
        break;
      case 'audit':
        AuditLogServices.getAuditDetails(id).then(({ data: res }) => {
          const { results } = res.data
          setItemDetails({
            ...results[0],
            fieldsData: results.map(item => ({ field_name: item.field_name, old_value: item.old_value, new_value: item.new_value }))
          })
        })
        break;
      default:
        break;
    }
  }

  /*
  * CONDITIONAL PAGE DATA 
  * 
  * Choose data to render based on activity log route
  */
  useEffect(() => {
    switch (reportType) {
      case 'activity':
        setPageInfo({ reportType: 'activity', breadCrumb: 'activityLogs' })
        setParams({ reportType: 'activity' })
        break;
      case 'audit':
        setPageInfo({ reportType: 'audit', breadCrumb: 'auditLogs' })
        setParams({ reportType: 'audit' })
        break;
      default:
        break
    }
  }, [reportType])

  // BREADCRUMB SERVICE
  useEffect(() => {
    pageInfo && BreadcrumbsService.setSelectedItems([
      pageInfo.breadCrumb,
    ])
  }, [pageInfo])

  // LIST VIEW INITIAL FETCH
  useEffect(() => {
    fetchAuditTrailLists()
  }, [])

  // LIST VIEW FETCH ON FILTER CHANGE
  useEffect(() => {
    fetchAuditTrailLists()
    console.log(params);

  }, [params])

  // RENDER
  return (
    <div>
      <Grid container spacing={5}>
        <Grid item xs={12}>
          <Grid container>
            <Grid item xs={8}>
              <Typography variant="h5">
                {pageInfo && pageInfo.user}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container>
            <Grid item xs={2} >
              <SideNav />
            </Grid>
            <Grid item xs={10}>
              <Grid container spacing={2} className={style.listview}>
                <Grid item xs={12}>
                  <ListViewOptions onListViewTopFilterChange={onListViewFilterChange} params={params} />
                </Grid>
                <Grid item xs={12}>
                  <ListView
                    id={'audit-log-view-all-list'}
                    config={config}
                    contents={list.body}
                    count={list.count}
                    list={list}
                    fetchItemDetails={fetchItemDetails}
                    onListViewFilterChange={onListViewFilterChange}
                    params={params}
                    setShowModal={setShowModal}
                    reportType={pageInfo.reportType}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <DetailsModal showModal={showModal} setShowModal={setShowModal} itemDetails={itemDetails} />
    </div>

  )
}
