import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import { green } from '@material-ui/core/colors'
import Radio from '@material-ui/core/Radio'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'

// Styles
import classes from './RadioGroup.module.scss'

// RadioButton Styles
const GreenRadio = withStyles({
  root: {
    color: '#212a71',
    '&$checked': {
      color: '#212a71'
    }
  },
  checked: {}
})((props) => <Radio {...props} />)

/**
 * Custom Radio Group
 * @param checked String value for default value of radio-group
 * @param direction String direction of label and row
 * @param handleOnChange callback() on select change
 * @param id String used for rendering ID for element
 * @param label String used as label for element group
 * @param optionDirection String direction for radio-button to appear
 * @param options Array used for radio-button { value, name }
 */
const CustomRadioGroup = props => {
  const {
    checked = '',
    direction = 'row',
    handleOnChange = () => {},
    id = 'radio-group-default',
    label = '',
    optionDirection = 'column',
    options = [],
    required = false
  } = props
  return (
    <Grid container className={classes.root} direction={'row'} justfiy={'flex-start'} >
      <Grid item className={classes['label-wrapper']}>
        <Typography className={classes.label}>{label} {required ? '*' : ''}</Typography>
      </Grid>
      <Grid item>
        <Grid container spacing={2}>
          <RadioGroup name={`${id}-radio-group`} value={checked} onChange={handleOnChange} className={classes[optionDirection]}>
            {options && options.map((item, index) => (
              <Grid key={`${id}-radio-group-item-${index}`} item xs={12} className={classes['radio-wrapper']}>
                <FormControlLabel
                  value={checked}
                  control={
                    <GreenRadio
                      value={item.value}
                    />
                  }
                  label={item.name}
                />
              </Grid>
            ))}
          </RadioGroup>
        </Grid>
      </Grid>
    </Grid>
  )
}

export default CustomRadioGroup
