import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import InputBase from '@material-ui/core/InputBase'
import MenuItem from '@material-ui/core/MenuItem'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import MomentUtils from '@date-io/moment'
import React from 'react'
import Select from '@material-ui/core/Select'
import { Typography } from '@material-ui/core'

// Style
import classes from './style.module.scss'

// Service
const numeral = require('numeral')

export default function TableInput (props) {
  const defaultFunction = function () {}
  const {
    disabled = false,
    errorMessage = '',
    inline = false,
    onInputChanged = defaultFunction,
    required = false,
    select = [],
    suffix = true,
    target = 'default_target',
    title = '',
    type = 'INPUT_TEXT',
    value = ''
  } = props
  const [isCalendarOpen, setIsCalendarOpen] = React.useState(false)
  const [focus, setFocus] = React.useState()
  const [inputValue, setInputValue] = React.useState('')
  const [errorValue, setErrorValue] = React.useState()

  React.useEffect(() => {
    setInputValue(value)
  }, [value])

  React.useEffect(() => {
    if (typeof errorMessage === 'string' && errorMessage.trim().length > 0) {
      setErrorValue(errorMessage)
    }
  }, [errorMessage])

  const handleOnClose = params => {
    setIsCalendarOpen(false)
  }

  const onCurrencyBlurr = params => {
    const { value, target } = params
    onInputChanged({ value: numeral(value).format('0,0.00'), target })
  }

  return (
    <Grid className={classes['input-root']} container direction='column'>
      {!inline && (
        <Grid className={classes['input-title-wrapper']} item xs={12}>
          <Typography className={classes['input-title']}>
            {title} {required ? '*' : ''}
          </Typography>
        </Grid>
      )}
      <Grid className={`${classes['input-component-wrapper']} ${inline ? classes['inline'] : ''}`} item xs={12}>
        {type === 'INPUT_TEXT' && (
          <InputBase
            className={`${classes['input-component']} ${focus ? classes['input-component-focus'] : ''} ${
              errorValue ? classes['input-component-error'] : ''
            }`}
            disabled={!!disabled}
            onChange={e => onInputChanged({ target, value: e.target.value })}
            onFocus={() => setFocus(true)}
            onBlur={() => setFocus(false)}
            autoComplete='off'
            value={inputValue}
            placeholder={`Enter ${title}`}
          />
        )}

        {type === 'SELECT' && (
          <Select
            className={`${classes['input-component']} ${focus ? classes['input-component-focus'] : ''} ${
              errorValue ? classes['input-component-error'] : ''
            } ${!inputValue || inputValue === '' || inputValue === null ? classes.placeholder : ''}`}
            disabled={!!disabled}
            disableUnderline
            displayEmpty
            onChange={e => onInputChanged({ target, value: e.target.value })}
            inputProps={{ 'aria-label': 'Without label' }}
            value={inputValue || ''}
          >
            {select.length === 0 && (
              <MenuItem value='' disabled>Select {title}</MenuItem>
            )}
            <MenuItem value='' disabled>
              Select {title}
            </MenuItem>
            {select.map((item, index) => (
              <MenuItem value={item.value}>{item.name}</MenuItem>
            ))}
          </Select>
        )}

        {type === 'DATE' && (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              TextFieldComponent={p => (
                <React.Fragment>
                  <Grid container direction='row'>
                    <Grid item xs>
                      <InputBase
                        disabled
                        value={p.value}
                        className={`${classes['input-component']} ${focus ? classes['input-component-focus'] : ''} ${
                          errorValue ? classes['input-component-error'] : ''
                        }`}
                        onClick={() => {
                          if (!!disabled) return false
                          setIsCalendarOpen(true)
                        }}
                        placeholder={`Select ${title}`}
                      />
                    </Grid>
                    <Grid item>
                      <IconButton size='small' disabled={!!disabled} onClick={() => setIsCalendarOpen(true)}>
                        <FontAwesomeIcon icon='calendar-day' />
                      </IconButton>
                    </Grid>
                  </Grid>
                </React.Fragment>
              )}
              clearable
              disabled={!!disabled}
              value={inputValue}
              format={'MM/DD/YYYY'}
              open={isCalendarOpen}
              onClose={handleOnClose}
              onChange={data => onInputChanged({ target, value: data ? data.toISOString() : null })}
            />
          </MuiPickersUtilsProvider>
        )}

        {type === 'INPUT_CURRENCY' && (
          <Grid className={classes['currency-field']} container direction='row'>
            <Grid className={classes.input} item xs>
              <InputBase
                className={`${classes['input-component']} ${focus ? classes['input-component-focus'] : ''} ${
                  errorValue ? classes['input-component-error'] : ''
                } ${classes.rtl}`}
                disabled={!!disabled}
                onChange={e => {
                  onInputChanged({ target, value: e.target.value })
                }}
                onFocus={() => setFocus(true)}
                onBlur={() => {
                  setFocus(false)
                  onCurrencyBlurr({ target, value: inputValue })
                }}
                autoComplete='off'
                value={inputValue}
                suffix={!!suffix}
                placeholder={`Enter ${title}`}
              />
            </Grid>
            {!inline && !!suffix && (
              <Grid className={`${classes.text} ${disabled ? classes.disabled : ''}`} item>
                <Grid container direction='column' justify='center' style={{ height: '100%' }}>
                  <Grid item>
                    <Typography>PHP</Typography>
                  </Grid>
                </Grid>
              </Grid>
            )}
            </Grid>
        )}
      </Grid>
      {errorValue && (
        <Grid className={classes['input-error-wrapper']} item xs={12}>
          <Typography className={classes['input-error']}> {errorValue} </Typography>
        </Grid>
      )}
    </Grid>
  )
}
