import React from 'react'

import {
  Typography
} from '@material-ui/core'

import { CurrencyFormatter } from 'Utilities/TextFormatter'

import style from './AgingSummaryBar.module.scss'

export default function AgingSummaryBar(props) {
  const { summaryBarData } = props
  
  const { ageSummary, total_collectibles } = summaryBarData
  return (
    <div className={style.totalBarWrapper}>
      <div className={style.totalAllWrapper}>
        <Typography variant="button" className={style.totalAllHeader}>Total Collectibles</Typography>
        <Typography variant="h6" className={style.totalCollectibleValue}>P {CurrencyFormatter(total_collectibles)}</Typography>
      </div>
      <div className={style.totalAgeWrapper}>
        {
          ageSummary.map(item => {
            return (
              <div className={style.totalAgeItem}>
                <div className={style.totalValueStatus}>{(item.name === "46 - 75 Days") ? 'OVERDUE' : ""}</div>
                  <Typography variant="h6" className={(item.name === "1 - 45 Days") ? style.totalValueFirst : style.totalValue}>P {CurrencyFormatter(item.total_amount_due)}</Typography>
                  <Typography variant="subtitle1" className={style.totalValueSubtitle}>{item.name}</Typography>
              </div>
            )
          })
        }
      </div>
    </div >
  )
}
