import React from 'react'

import ConfigController2 from 'Controllers/ConfigController2'

let data = {}

const _load = async () => {
  console.debug('LOADING RBAC .... ')
  const { rbac = [] } = await ConfigController2.getUserManagementData()

  for (let item of rbac) data[item.policy_id] = item
}

const _unload = () => {
  data = {}
}

const _hasPolicy = policy => {
  if (typeof policy === 'string' || typeof policy.policy_id === 'string') {
    return !!data[policy.policy_id || policy]
  }

  const { hasPolicy = {} } = policy

  const hasPolicyOnly = hasPolicy.only || policy.only
  const hasPolicyOr = hasPolicy.or || policy.or
  const hasPolicyAnd = hasPolicy.and || policy.and

  if (hasPolicyOnly) {
    return !!data[hasPolicyOnly]
  }

  if (hasPolicyOr) {
    for (let item of hasPolicyOr) if (data[item]) return true
    return
  }

  if (hasPolicyAnd) {
    for (let item of hasPolicyAnd) if (!data[item]) return
    return true
  }
}

const RBAC = props => {
  const { policy_id, hasPolicy, children } = props
  return <React.Fragment>{_hasPolicy({ policy_id, hasPolicy }) && children}</React.Fragment>
}

RBAC.load = _load
RBAC.unload = _unload
RBAC.hasPolicy = _hasPolicy

export default RBAC
