import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import TextFieldCurrency from '../TextFieldCurrency/TextFieldCurrency'
import TextField from './../TextField/CustomTextField'
// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField, UpdateFieldData } from 'Pages/Protected/BillingAndCollection/Store/actions'

// Style
import classes from './style.module.scss'

// GRID
import CDFGrid from './grids/CDFGrid'
import ACDFGrid from './grids/ACDFGrid'

const GroupOfCurrency = ({ labels, data = {}, disabled = false, breakdownName = "Collection", isAdvance = false, taxInfo, handleOnSave = () => {} }) => {
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [tax, setTax] = React.useState(null)
  

  React.useEffect( () => {
    const tax = taxInfo && taxInfo.tax_value ? taxInfo.tax_value.value : null
    setTax(tax)
  }, [taxInfo])

  const componentProps = {
    labels,
    data,
    _data,
    breakdownName,
    tax,
    disabled,
    UpdateField,
    UpdateFieldData,
    dispatch,
    handleOnSave
  }
  
  return (
    <Grid container className={classes.root}>
      {!isAdvance && <CDFGrid {...componentProps} />}

      {isAdvance && <ACDFGrid {...componentProps} />}
    </Grid>
  )
}

export default GroupOfCurrency
