import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
// const axios = instance('/c/al')
const axios = instance('/a/ab')

export default class AuditTrailServices {
  static getAuditList(params) {
    return axios({
      url: `/audit-trail/list/${params.reportType}`,
      method: 'GET',
      params
    })
  }

  static getActivityDetails(params) {
    return axios({
      url: `/audit-trail/detail/activity/${params}`,
      method: 'GET',
      params
    })
  }

  static getAuditDetails(params) {
    return axios({
      url: `/audit-trail/detail/audit/${params}`,
      method: 'GET',
      params
    })
  }

  static getModuleList(params) {
    return axios({
      url: `/audit-trail/module-list`,
      method: 'GET',
      params
    })
  }
}
