import React from 'react'
import Paper from '@material-ui/core/Paper'
import Submit from 'StandardComponents/Submit/Submit'
import { useHistory } from 'react-router-dom'

// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'
import Approve from 'StandardComponents/Approve/Approve'

// Service
import BillingSOAReportService from '../../Services/Api/BillingSOAReportService'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'
import BillingSOAServices from 'Services/Api/BillingSOAServices'
import classes from './Attachment.module.scss'

export default function Attachment (props) {
  const { soaNumber, submit = {} } = props
  const [file, setFile] = React.useState(null)
  const [number, setNumber] = React.useState(soaNumber)
  const [openDialog, setOpenDialog] = React.useState(true)
  const history = useHistory()
  const type = 'list'
  React.useEffect(() => {
    if (submit.soaType === 'member') {
      BillingSOAReportService.getMemberReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
        setNumber(soaNumber)
      })
    } else {
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
        setNumber(soaNumber)
      })
    }
  }, [type])
  const downloadServices = args => {
    const { type } = args
    BillingSOAReportService.getAttachment({ type: type, soaNumber: soaNumber }).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `SOA Attachment - ${soaNumber}.xlsx`)
      document.body.appendChild(link)
      link.click()
    })
  }

  const approveFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'approve' }).then(res => {
        console.group('Approved Member SOA')
        console.log(res)
        console.groupEnd()
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'approve' }).then(res => {
        console.group('Approved Member SOA')
        console.log(res)
        console.groupEnd()
      })
    }
    history.push('/')
  }

  const disapproveFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'disapprove' }).then(res => {
        console.group('Disapproved Member SOA')
        console.log(res)
        console.groupEnd()
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'disapprove' }).then(res => {
        console.group('Disapproved Member SOA')
        console.log(res)
        console.groupEnd()
      })
    }
    history.push('/')
  }

  const submitFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'submit' }).then(res => {
        console.group('Submit Member SOA')
        console.log(res)
        console.groupEnd()
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'submit' }).then(res => {
        console.group('Submit Client SOA')
        console.log(res)
        console.groupEnd()
      })
    }
    history.push('/')
  }

  return (
    <div className={classes.root}>
      {submit.type === 'supervisor' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'attachment'}
              file={file}
              download={() => downloadServices({ type: 'export-attachment', soaType: submit.soaType })}
              type={'list'}
              initialScale={1.5}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Approve
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() =>
                approveFunction({ soaNumber: number })
              }
              disapproveFunction={() =>
                disapproveFunction({ soaNumber: number })
              }
              setActiveStep={submit.setActiveStep}
            />
          )}
        </div>
      ) : (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'attachment'}
              file={file}
              download={() => downloadServices({ type: 'export-attachment', soaType: submit.soaType })}
              type={'list'}
              initialScale={1.5}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Submit
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() =>
                submitFunction({ soaNumber: number })
              }
              setActiveStep={submit.setActiveStep}
            />
          )}
        </div>
      )}
      {submit.type === 'validated' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'attachment'}
              file={file}
              download={() => downloadServices({ type: 'export-attachment', soaType: submit.soaType })}
              type={'list'}
              initialScale={1.5}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
      {submit.type === 'submitted' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'attachment'}
              file={file}
              download={() => downloadServices({ type: 'export-attachment', soaType: submit.soaType })}
              type={'list'}
              initialScale={1.5}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
      {submit.type === 'analyst-approved' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'attachment'}
              file={file}
              download={() => downloadServices({ type: 'export-attachment', soaType: submit.soaType })}
              type={'list'}
              initialScale={1.5}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}      
    </div>
  )
}
