import React from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import Button from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import Grid from '@material-ui/core/Grid'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import Typography from '@material-ui/core/Typography'
import AddIcon from '@material-ui/icons/Add'
import classes from './AddSOASelect.module.scss'

AddSOASelect.propTypes = {
  details: PropTypes.arrayOf(PropTypes.object)
}

const moment = require('moment')
const numeral = require('numeral')

export default function AddSOASelect ({ details = [], onAddClicked = () => {}, onSelect = () => {} }) {
  const [open, setOpen] = React.useState(false)

  const handleClose = () => {
    setOpen(false)
  }

  const handleOpen = () => {
    onAddClicked(true)
    setOpen(true)
  }

  return (
    <div className={classes.root}>
      <Button className={classes['btn']} onClick={handleOpen}>
        <AddIcon style={{ fontSize: '16px', marginRight: '5px' }} />
        <Typography>Add SOA</Typography>
      </Button>
      <FormControl className={classes['form-control']}>
        <Select
          labelId='demo-controlled-open-select-label'
          id='demo-controlled-open-select'
          open={open}
          onClose={handleClose}
          onOpen={handleOpen}
          value={''}
          onChange={e => onSelect(e.target.value)}
        >
          {details.map(data => (
            <MenuItem value={data.soa_number}>
              <div>
                <Typography className='bold'>{data.soa_number}</Typography>
                <Typography className='color-11'>
                  Balance: PHP {numeral(data.for_collection || 0).format('0,0.00')}
                </Typography>
                <Typography className='color-11'>
                  SOA Amount: PHP {numeral(data.summary?.sub_total || 0).format('0,0.00')}
                </Typography>
                <Typography className='color-11'>
                  Period Covered: {moment(data.period?.start).format('ll')} - {moment(data.period?.end).format('ll')}
                </Typography>
                <Typography className='color-11'>Due: {moment(data.due_date || null).format('ll')}</Typography>
              </div>
            </MenuItem>
          ))}
          {details.length === 0 && (
            <MenuItem value='' disabled>
              <Typography>No SOA Available</Typography>
            </MenuItem>
          )}
        </Select>
      </FormControl>
    </div>
  )
}
