    stepCount = 0,
    isAssign = false,
    isControl = false,
    isView = false,
    isEdit = false,
    isPrint = false,
    isVoid = true,
    handleStepChange = functionPlaceholder,
    handleOnCloseClicked = functionPlaceholder,
    handleOnSubmitClicked = functionPlaceholder,
    handleOnCancelClicked = functionPlaceholder,
    handleOnDisapproveClicked = functionPlaceholder,
    handleOnSaveClicked = functionPlaceholder,
    handleOnVoidClicked = functionPlaceholder,
    handOnEditClicked = functionPlaceholder,
    handleOnPrintClicked = functionPlaceholder,
    submitEndorsedForPenalty = false,
    submitEndorsedForPenaltyControl = functionPlaceholder,
    onOk = functionPlaceholder,
    cancelEndorsementForPenalty = false,
    handleChangeCancelEndorsementForPenalty = functionPlaceholder,
    submitConfirmationPenalty = false,
    handleChangeConfirmationPenalty = functionPlaceholder,
    submitCollectionDistribution = false,
    handleChangeSubmitCollection = functionPlaceholder,
    cancelCollectionDistribution = false,
    handleChangeCancelCollectionDistribution = functionPlaceholder,
    submittedCollectionDistribution = false,
