    stepNumberToShowListBtn = 2,
    showCDToValidateBtn = false,
    onClickDisapprove = functionPlaceholder,
    onClickApprove = functionPlaceholder,
    onClickVoid = functionPlaceholder,
    stepNumberCDToShowListBtn = 3,
    stepNumberPenaltyToShowListBtn = 3,
    stepNumberValidatePenaltyToShowListBtn = 2,
    stepNumberSuspensionBtn = 3,
    showCollectionDistributionBtn = false,
    showSuspensionListBtn = false,
    showValidateAccountForSuspensionBtn = false,
    stepNumberValidateAccountBtn = 2,
    showEndorsedAccountPenaltyBtn = false,
