    handleSubmitSuspensionEndorsement = functionPlaceholder,
    handleSubmitSuspensionEndorsementConfirmed = functionPlaceholder,
    handleSubmitSuspensionEndorsementCancelled = functionPlaceholder,
    cancelSuspensionEndorsement = false,
    handleCancelSuspensionEndorsement = functionPlaceholder,
    handleCancelSuspensionEndorsementConfirmed = functionPlaceholder,
    handleCancelSuspensionEndorsementCancelled = functionPlaceholder,
    confirmSuspensionEndorsement = false,
    handleConfirmSuspensionEndorsementConfirmed = functionPlaceholder,

    voidAccountSuspension = false,
    handleVoidAccountSuspension = functionPlaceholder,
    handleVoidAccountSuspensionCancellation = functionPlaceholder,
    handleVoidAccountSuspensionConfirmation = functionPlaceholder,

    disapproveAccountSuspension = false,
    handleDisapproveAccountSuspension = functionPlaceholder,
    handleDisapproveAccountSuspensionCancellation = functionPlaceholder,
    handleDisapproveAccountSuspensionConfirmation = functionPlaceholder,

    approveAccountSuspension = false,
    handleApproveAccountSuspension = functionPlaceholder,
    handleApproveAccountSuspensionCancellation = functionPlaceholder,
    handleApproveAccountSuspensionConfirmation = functionPlaceholder,

    handleCreateLetterTemplate = functionPlaceholder,

    publishLetterTemplate = false,
    handleSaveLetterTemplate = functionPlaceholder,
    handlePublishLetterTemplate = functionPlaceholder,
    handlePublishLetterTemplateConfirmation = functionPlaceholder,
    handlePublishLetterTemplateCancellation = functionPlaceholder
