import Grid from '@material-ui/core/Grid'
import React, { useRef } from 'react'
import { Typography, Paper } from '@material-ui/core'

// Components
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import DatePicker from './../../../../Components/FormComponents/DatePicker2/DatePicker2'
import { InputTable } from './../../../../Components/CustomTable'
import Checkbox from './../../../../Components/FormComponents/Checkbox/Checkbox'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'

// TODO: Services

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Styles
import classes from './CollectionDetailsComponent.module.scss'

// Config
import { inputTableConfig, disablers as disablersConfig } from './config/input-table'

const numeral = require('numeral')
const moment = require('moment')

export default function CollectionDetailsComponent (props) {
  const { disabled = false, data = { cdf_summary: {} }, isAdvance = false } = props
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [disablers, setDisablers] = React.useState(disablersConfig.DEFAULT)
  const refContainer = useRef()

  const handleDisableFields = _params => {
    setDisablers(i => ({ ...i, ...disablersConfig[_params] }))
  }

  React.useEffect(() => {
    refContainer.current.parentElement.parentElement.parentElement.parentElement.parentElement.parentElement.scrollTo(0,0)
  }, [])

  return (
    <Grid container className={classes.root} spacing={2} ref={refContainer}>
      <Grid item xs={12}>
        <Typography variant='h5' className={classes.bold}>Collection Details</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-taxable'}
                label={'total taxable amount'}
                text={numeral(isAdvance ? _data.total_fees ? _data.total_fees.sub_total : 0 : data.cdf_summary?.sub_total || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-tax'}
                label={'total tax (vat) amount'}
                text={numeral(isAdvance ? _data.total_fees ? _data.total_fees.sub_total_tax : 0 : data.cdf_summary?.sub_total_tax || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-amount'}
                label={'total amount'}
                text={numeral(isAdvance ? _data.total_fees ? _data.total_fees.sub_total : 0 : data.cdf_summary?.grand_total || data.cdf_summary?.sub_total || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item lg={12} style={{ marginTop: '1rem' }}>
        <Grid container spacing={4}>
          <Grid item lg={4}>
            <DatePicker
              id='collection-date'
              disabled={!!disabled}
              required
              label='Collection Date'
              value={_data.collection_date}
              onChange={event => {
                dispatch(UpdateField({ field: 'collection_date', value: event.toISOString() }))
              }}
              errorMessage='Please enter a Collection date'
              minDateMessage={disabled ? false : 'Date should be Today or Future'}
              disablePast
            />
            <Checkbox
              id='bir-form-2307'
              checked={_data.with_bir_form_2307 || false}
              disabled={!!disabled}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'with_bir_form_2307', value: e.target.checked }))
              }}
              label={'with BIR form 2307'}
            />
            <Checkbox
              id='net-of-commission'
              checked={_data.net_of_commission || false}
              disabled={!!disabled}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'net_of_commission', value: e.target.checked }))
              }}
              label={'Net of Commission'}
            />
            <Checkbox
              id='net-of-creditable-with-tax'
              checked={_data.net_of_creditable_with_tax || false}
              disabled={!!disabled}
              handleOnChange={e => {
                dispatch(UpdateField({ field: 'net_of_creditable_with_tax', value: e.target.checked }))
              }}
              label={'Net of Creditable w/ Tax'}
            />
          </Grid>
          <Grid item lg={6}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              disabled={!!disabled}
              multiline
              rows={7}
              id='remarks'
              label='Remarks'
              value={_data.remarks || ''}
              onChange={event => {
                dispatch(UpdateField({ field: 'remarks', value: event.target.value }))
              }}
            />
          </Grid>
          <Grid item lg={2}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              disabled={!!disabled}
              multiline
              rows={7}
              id='or-no'
              label='OR No.'
              value={_data.or_no || ''}
              onChange={event => {
                dispatch(UpdateField({ field: 'or_no', value: event.target.value }))
              }}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <InputTable
          data={{ header: inputTableConfig, data: _data.payment || [] }}
          disabled={!!disabled}
          disablers={disablers || disablersConfig.default}
          onAddLineItem={e => {
            const { amount } = e
            if (amount <= 0) return false
            const lineItems = _data.payment || []
            lineItems.push(e)
            dispatch(UpdateField({ field: 'payment', value: lineItems }))
          }}
          onAmountChange={e => {
            dispatch(UpdateField({ field: 'sub_total', value: e }))
          }}
          onRemoveLineItem={e => {
            const lineItems = _data.payment || []
            lineItems.splice(e, 1)
            dispatch(UpdateField({ field: 'payment', value: lineItems }))
          }}
          onSaveLineItem={e => {
            const { amount } = e
            if (amount <= 0) return false
            const lineItems = _data.payment || []
            lineItems[e.index] = e.item
            dispatch(UpdateField({ field: 'payment', value: lineItems }))
          }}
          handleDisabler={e => handleDisableFields(e)}
        />
        <Grid container style={{ justifyContent: 'flex-end', marginTop: '1rem', paddingRight: '5rem' }}>
          <Typography style={{ fontSize: '20px', fontWeight: 600 }}>
            TOTAL: {'PHP ' + (_data.sub_total || 0)}
          </Typography>
        </Grid>
      </Grid>
    </Grid>
  )
}
