import React from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment'

// MATERIAL UI
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'

// SHARED COMPONENTS
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// UTILITIES
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function ListView(props) {
  const {
    config,
    contents,
    count,
    list,
    onListViewFilterChange,
    params,
    setShowModal,
    fetchItemDetails,
    header,
    reportType
  } = props

  // LOCAL CONSTANTS
  const tableHeaders = config[reportType].header

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  // LOCAL METHODS
  const handleViewLogDetail = (params) => {
    setShowModal(true)
    fetchItemDetails(params)
  }

  const changeView = params => {
    if (onListViewFilterChange) {
      onListViewFilterChange(params)
    }
  }


  return (
    <CustomEnhancedTable
      count={count}
      changeView={changeView}
      tableHeaders={tableHeaders}
      pagination={pagination}
      search
      sort
      fetchData={onListViewFilterChange}
      params={params}
    >
      <TableBody>
        {contents && contents.map((row, contentsIndex) => (
          <StyledTableRow key={row._id}>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                {row.activity_id}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                {reportType === 'activity' ? row.object_name : row.user_name}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                <span style={{ textDecoration: 'underline', color: "#3ab77d", cursor: 'pointer' }} onClick={() => { handleViewLogDetail({ activityId: row.activity_id, id: row._id }) }}>{row.action}</span>
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                {reportType === 'activity' ? row.reference_id : row.ip_address}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                {DateFormatterByAbbreviation({ value: row.date_of_entry })}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='caption' align='left'>
                {row.date_timestamp}
              </Typography>
            </StyledTableCell>
          </StyledTableRow>
        ))}
      </TableBody>
    </CustomEnhancedTable>
  )
}
