import Grid from '@material-ui/core/Grid'
import React, { useRef } from 'react'
import clsx from 'clsx'
import { Paper, Typography, IconButton, MenuItem, Menu } from '@material-ui/core'

// Icons
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'

// Components
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import RadioGroup from './../../../../Components/FormComponents/RadioGroup/RadioGroup'
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import TextFieldCurrency from './../../../../Components/FormComponents/TextFieldCurrency/TextFieldCurrency'
import GroupOfCurrency from './../../../../Components/FormComponents/GroupOfCurrency/GroupOfCurrency'
import SubTotalTile from './../../../../Components/FormComponents/SubTotalTile/SubTotalTile'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Styles
import classes from './CollectionBreakDownComponent.module.scss'

// Mock
const mockLabels = require('./mock-data/group-of-labels.json')

const numeral = require('numeral')

const labelProps = {
  labels: mockLabels
}



export default function CollectionBreakDownComponent (props) {
  const { disabled = false, data = {}, handleOnSelectSoaTab = () => {}, isAdvance, taxInfo, handleOnSave = () => {} } = props
  const _data = useSelector(state => state.CollectionForm)
  const refContainer = useRef()

  React.useEffect(() => {
    refContainer.current.parentElement.parentElement.parentElement.parentElement.parentElement.parentElement.scrollTo(0,0)
  }, [])

  return (
    <Grid container spacing={2} className={classes.root} ref={refContainer}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h5' className={classes.bold}>Collection Reference Breakdown</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-taxable'}
                label={'total taxable amount'}
                text={numeral(_data.total_fees ? _data.total_fees.sub_total : 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-tax'}
                label={'total tax (vat) amount'}
                text={numeral(_data.total_fees ? _data.total_fees.sub_total_tax : 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-amount'}
                label={'total amount'}
                text={numeral(_data.total_fees ? _data.total_fees.grand_total : 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1rem' }}>
        <Grid container spacing={2}>
          <Grid item xs>
            <Grid container spacing={2}>
              <FormSlider
                showForCollection
                showSummary
                data={data}
                OnChanges={e => {
                  handleOnSelectSoaTab(e)
                }}
                isAdvance={isAdvance}
              />
            </Grid>
          </Grid>
        </Grid>
        {/* GROUP OF CURRENCY */}
        <Grid item xs={12} style={{ marginTop: '1.5rem' }}>
          <GroupOfCurrency {...labelProps} data={data} disabled={disabled} isAdvance={isAdvance} taxInfo={taxInfo} handleOnSave={handleOnSave} />
        </Grid>
      </Grid>
    </Grid>
  )
}
