import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cd')

export default class CollectionCDFServices {
  static approve ({ data }) {
    return axios({
      url: `/collection-distribution/client/approve`,
      method: 'PUT',
      data
    })
  }

  static disapprove ({ data }) {
    return axios({
      url: `/collection-distribution/client/disapprove`,
      method: 'PUT',
      data
    })
  }

  static pregenerateCDF ({ data }) {
    return axios({
      url: `/collection-distribution/form`,
      method: 'PUT',
      data
    })
  }

  static submit ({ data }) {
    return axios({
      url: `/collection-distribution/client/submit`,
      method: 'PUT',
      data
    })
  }

  static getAvailableSoa (params) {
    return axios({
      url: '/collection-distribution/soa/available',
      method: 'GET',
      params
    })
  }

  static getCDFFees (params) {
    return axios({
      url: '/collection-distribution/soa/fees',
      method: 'GET',
      params
    })
  }

  static getCDFMember (params) {
    return axios({
      url: '/collection-distribution/soa/member',
      method: 'GET',
      params
    })
  }

  static getCDF (params) {
    return axios({
      url: `collection-distribution/list/${params.data_id}`,
      method: 'GET',
      params
    })
  }

  static getTaxValue (taxType) {
    return axios({
      url: `collection-distribution/tax/${taxType}`,
      method: 'GET'
    })
  }

  static getMany (params) {
    return axios({
      url: `/collection-distribution/list`,
      method: 'GET',
      params
    })
  }

  static getReport ({ cdfNumber }) {
    return axios({
      url: `/collection-distribution/reports?cdfNumber=${cdfNumber}`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static addSoa ({ data }) {
    return axios({
      url: `/collection-distribution/soa/update/${data.cdfNumber}`,
      method: 'PUT',
      data
    })
  }

  static removeMember ({ data, params }) {
    return axios({
      url: `/collection-distribution/soa/remove-member/${params}`,
      method: 'PATCH',
      data
    })
  }

  static updateCDF (data) {
    return axios({
      url: `/collection-distribution/form/update`,
      method: 'POST',
      data
    })
  }

  static void ({ data }) {
    return axios({
      url: `/collection-distribution/client/void`,
      method: 'PUT',
      data
    })
  }
}
