import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import noop from 'lodash/noop'

import CustomDialog from 'SharedComponents/CustomDialog/CustomDialog'

SendDialog.propTypes = {
  open: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
}

export default function SendDialog({ open = false, onCancel = noop, onSubmit = noop, ...props }) {
  return (
    <CustomDialog
      open={open}
      title="Send Reminder Letter"
      content={
        <div style={{ marginBottom: 20 }}>
          <Typography>Are you sure you want to send the reminder letter to Client's email?</Typography>

          <Typography style={{ marginTop: 20 }}>
            This reminder letter will also be sent to Online Portal for the Client to view online.
          </Typography>
        </div>
      }
      onClose={onCancel}
      onOk={onSubmit}
      {...props}
    />
  )
}
