import { Link, useHistory } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Services
import CollectionSharedServices from 'Services/Api/CollectionSharedServices'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this-week', text: 'This Week' },
    { value: 'this-month', text: 'This Month' }
  ],
  default: 'this-month'
}

const columnConfig = [
  { id: 'validated-transaction-tile-table-header', name: 'Account Name' },
  { id: 'validated-transaction-tile-table-header', name: 'Transaction Type' },
  { id: 'validated-transaction-tile-table-header', name: '' }
]

const ValidatedTransactionsTile = props => {
  // Constants
  const { service = () => {} } = props

  const history = useHistory()
  const [tile, setTile] = React.useState({
    category: 'SM',
    count: 0,
    data: [],
    filter: {},
    filterMenu,
    limit: 10,
    order: true,
    orderBy: 'account_name',
    page: 1,
    sort: '',
    title: 'Validated Transactions',
    user_owned: true
  })

  React.useEffect(() => {
    Promise.all([
      CollectionSharedServices.getMany({
        category: tile.category,
        limit: 5,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        target_range: 'date_updated',
        user_owned: tile.user_owned
      })
    ]).then(res => {
      const [CDFService] = res
      const { count, results } = CDFService.data.data
      setTile(i => ({ ...i, count, data: results }))
    })
  }, [tile.filterMenu.default])

  const handleOnView = ({ status, transaction_type, transaction_number }) => {
    if (!transaction_type || !transaction_number) return false
    switch (transaction_type) {
      case 'Advance Collection Distribution':
        const acdPath = {
          create: `/collection/collection-distribution/client/${transaction_number}?isAdvance=true`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}?isAdvance=true`
        }
        history.push(acdPath.view)
        break
      case 'Collection Distribution':
        const path = {
          create: `/collection/collection-distribution/client/${transaction_number}`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}`
        }
        history.push(path.view)
        break
      case 'Suspension':
        history.push(`/collection/suspension/endorse/client/${transaction_number}`)
        break
      case 'Penalty':
        history.push(`/collection/penalty/client/${transaction_number}`)
        break
      default:
        return false
        break
    }
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='validated-transactions-tile'
        count={{ color: 3, value: tile.count, id: 'validated-transactions-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        viewList={
          <Typography
            id='validated-transactions-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => history.push('/collection/validated/lists')}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`validated-transactions-tile-table-row-${index}`}
                id={`validated-transactions-tile-table-row-${index}`}
              >
                <TableCell id={`validated-transactions-tile-table-cell-account-name-${index}`}>
                  {item.contract?.client?.account_name || ''}
                </TableCell>
                <TableCell id={`validated-transactions-tile-table-cell-date-submitted-${index}`}>
                  {item.transaction_type || ''}
                </TableCell>
                <TableCell id={`validated-transactions-tile-table-cell-action-${index}`}>
                  <Typography
                    variant='caption'
                    className='text-link underline'
                    id={`validated-transactions-tile-view-${index}`}
                    onClick={() =>
                      handleOnView({
                        status: item?.status || null,
                        transaction_type: item?.transaction_type || null,
                        transaction_number: item?.cdf_number || item?.penalty_number || item?.spn_number || null
                      })
                    }
                  >
                    View
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

ValidatedTransactionsTile.displayName = 'DashboardTileComponent'

export default ValidatedTransactionsTile
