const ActionTabs = props => {
  const {
    headCells,
    users,
    clientInfo,
    onShowViewMore,
    viewMore,
    setViewMore,
    pagination,
    params,
    fetchData,
    changeView,
    changeViewForViewMore,
    getUserAssignedClients,
    paramsAssigned,
    reAssign
  } = props
  const [reset, setReset] = React.useState(false)

  const cancelAssigning = params => {
    setReset(true)
  }

  const cancelReset = params => {
    setReset(false)
  }

  return (
    // TODO permissions (if necessary) and Tab Components
    <CustomTabs defaultIndex={0}>
      <CustomTab id='billing-analyst-tab' label='BILLING ANALYST'>
        <BillingAnalystTab
          headCells={headCells}
          users={users}
          onShowViewMore={onShowViewMore}
          viewMore={viewMore}
          setViewMore={setViewMore}
          clientInfo={clientInfo}
          pagination={pagination}
          params={params}
          fetchData={fetchData}
          changeView={changeView}
          changeViewForViewMore={changeViewForViewMore}
          getUserAssignedClients={getUserAssignedClients}
          paramsAssigned={paramsAssigned}
          cancelAssigning={cancelAssigning}
          cancelReset={cancelReset}
          reset={reset}
          reAssign={reAssign}
        />
      </CustomTab>
      {/* <CustomTab id='billing-cycle-tab' label='BILLING CYCLE'>
        <BillingCycleTab />
      </CustomTab> */}
      <CustomTab id='billing-instructions-tab' label='BILLING INSTRUCTIONS'>
        <BillingInstructionsTab contract={clientInfo.contract} />
      </CustomTab>
      <CustomTab id='billing-ternms-and-conditions-tab' label='BILLING TERMS & CONDITIONS'>
        <AnalystTermsAndConditionsTab contract={clientInfo.contract} />
      </CustomTab>
    </CustomTabs>
  )
}

export default function AssignClientPage (props) {
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [viewMore, setViewMore] = React.useState(initialValue)
  const [clientContract, setClientContract] = React.useState({ data: 0 })
  const [paramsAssigned, setParamsAssigned] = React.useState({})
  const { reAssign } = queryString.parse(props.location.search)

  const handleOnShowViewMore = analyst => () => {
    const title = analyst.first_name + ' ' + analyst.last_name
    const count = analyst.assignedCount
    const subtitle = `Billing Analyst for ${count} Accounts`
    // TODO API CALL
    getUserAssignedClients({ show: true, user_id: analyst._id, title: title, count: count, subtitle: subtitle })
  }

  const getUserAssignedClients = params => {
    const _params = {
      limit: viewMore.limit,
      page: viewMore.page,
      sort: `${viewMore.orderBy}|${viewMore.order ? 'asc' : 'desc'}`,
      filters: viewMore.filters,
      ...params
    }
    setParamsAssigned(_params)

    BillingSupervisorServices.getUserAssignedClients(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewMore(state => ({
        ...state,
        data: results,
        count,
        limit,
        page,
        sort: _params.sort,
        orderBy: _params.orderBy ? _params.orderBy : list.orderBy,
        show: _params.show,
        title: _params.title,
        subtitle: _params.subtitle
      }))
    })
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})
  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 0,
    page: 1
  })
  const [analysts, setAnalysts] = React.useState([])

  React.useEffect(() => {
    const { _id, account_number } = props.match.params

    PageTitleService('Assign Billing Analyst')
    BreadcrumbsService.setSelectedItems(['dashboard', 'clients', 'assign', `assignId:${account_number}`])
    /* Client Info */
    Promise.all([BillingSupervisorServices.getClientLedgerToSetup({ _id })]).then(values => {
      const client_contract = values[0].data.data
      setClientContract(prevState => ({
        ...prevState,
        data: client_contract
      }))
    })

    fetchData({})
  }, [])

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      role: 'BILLING_ANALYST',
      ...params
    }
    setParams(_params)

    BillingSupervisorServices.getUsersByRoleV1(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  React.useEffect(() => {
    setPagination({
      count: list.count,
      rowsPerPage: list.limit,
      page: list.page
    })
  }, [list])

  const changeView = params => {
    fetchData(params)
  }

  const changeViewForViewMore = params => {
    console.log({ ...params, ...paramsAssigned })
    getUserAssignedClients({ ...paramsAssigned, ...params })
  }

  return (
    <>
      <ClientInformation clientInfo={clientContract.data} classes={classes} />
      <ActionTabs
        headCells={header_items}
        users={list}
        clientInfo={clientContract.data}
        onShowViewMore={handleOnShowViewMore}
        viewMore={viewMore}
        setViewMore={setViewMore}
        pagination={pagination}
        params={params}
        fetchData={fetchData}
        changeView={changeView}
        changeViewForViewMore={changeViewForViewMore}
        getUserAssignedClients={getUserAssignedClients}
        paramsAssigned={paramsAssigned}
        reAssign={reAssign}
      />
    </>
  )
}
