import React, { useEffect, useState } from 'react'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography'
import DoneIcon from '@material-ui/icons/Done'
import Grid from '@material-ui/core/Grid'
import Checkbox from '@material-ui/core/Checkbox';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import FieldType from 'Constant/TermsAndConditions'

import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './BillingTermsAndConditionsTab.module.scss'

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: "#f6f7fa",
    color: theme.palette.common.white
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(even)': {
      backgroundColor: theme.palette.action.hover
    }
  }
}))(TableRow)

export default function BillingTermsAndConditionsTab (props) {
  const { contract } = props
  const sectionToDisplay = ['Other Fees', 'Extra Billings', 'Pre-Termination Refund Computation', 'Premium Refund', 'Refund Computation', 'Experience Refund', 'Escalation Clause']
  const singleColumns = ['EXPERIENCE REFUND', 'PREMIUM REFUND', 'PRE-TERMINATION REFUND COMPUTATION']
  
  const [refundTable, setRefundTable] = useState([])
  const [sections, setSections] = useState([])

  const getSections = () => {
    if (contract) {
      const sec = sectionToDisplay.map(sectionName => {
        let secObj = {}
        let result = contract.terms_and_conditions.filter(item => {
          return item.field_section === sectionName
        })
  
        return secObj[sectionName] = result[0]
      })
      
      setSections(sec)
    }
  }

  const getRefundTable = () => {
    if (contract) {
      let premiumRefundSection = contract.terms_and_conditions.filter(item => {
        return item.field_section.toUpperCase() === 'PREMIUM REFUND'
      })

      premiumRefundSection = premiumRefundSection.length ? premiumRefundSection[0].field_values : null
      if (premiumRefundSection) {
        let refundAnnual = premiumRefundSection.filter(item => {
          return item.code === 'refund_rates_anual'
        })
        let refundSemiAnnual = premiumRefundSection.filter(item => {
          return item.code === 'refund_rates_semi-anual'
        })

        let refundNames = refundAnnual.map(item => {
          return item.name
        })

        let table = refundNames.map(name => {
          let annualValue = refundAnnual.filter(item => item.name === name)[0].values
          let semiAnnualValue = refundSemiAnnual.filter(item => item.name === name)[0].values
          return {
            name: name,
            annual : annualValue,
            semi_annual: semiAnnualValue
          }
        })

        setRefundTable(table)
      }
    }
  }

  useEffect(() => {
    getSections()
    getRefundTable()
  }, [contract])
  
  return (
    <React.Fragment>
      { contract && (
        <div className={classes.root}>
          { contract.terms_and_conditions &&
            sections.map((item, indexSection) => (
              <div>
                <br />
                {item !== undefined ? 
                <>
                {singleColumns.includes(item.field_section.toUpperCase()) ? (
                    <Grid container spacing={2}>
                      <Grid item xs={12}>
                        <Typography id={`field-section-${indexSection}`} color='textSecondary' variant='caption'>
                          {item.field_section.toUpperCase()}
                        </Typography>
                        { item.field_values ?
                          item.field_values.map((v, indexItem) => (
                            <React.Fragment>
                              {v.code !== 'refund_rates_anual' && v.code !== 'refund_rates_semi-anual' ?
                                <div>
                                <Typography
                                  id={`field-name-${indexSection}-${indexItem}`}
                                  color='textSecondary'
                                  variant='caption'
                                >
                                  {v.name}
                                </Typography>
                                { indexItem === 0 ? (
                                  <div>
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold' }}
                                      variant='caption'
                                    >
                                      
                                      {v.values && v.values.length ? v.values.map((val, index) => (
                                        <span>
                                          <DoneIcon style={{ fontSize: 'smaller' }} />&nbsp;
                                          {val}
                                        </span>
                                      )) : "- no data"}
                                    </Typography>
                                  </div>
                                ) : (
                                  <div>
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold'}}
                                      variant='caption'
                                    >
                                      { v.values && v.values.length ? v.values.map((val, index) => 
                                        v.type === FieldType.PERCENTAGE ?  val + "%" : val ) : "- no data"
                                      }
                                    </Typography>
                                  </div>
                                )}
                              </div>
                              : null}
                            </React.Fragment>
                          )) : null 
                        }
                      </Grid>
                    </Grid>
                  ) : (
                    <Grid container spacing={1}>
                      { item.field_values ?
                        item.field_values.map((v, indexItem) => (
                          sectionToDisplay.includes(item.field_section) ? (
                            <Grid item xs={12} md={6}>
                              <Typography id={`field-section-${indexSection}-${indexItem}`} color='textSecondary' variant='caption'>
                                {indexItem === 0 ? item.field_section.toUpperCase() : indexItem === 1 ? <br/> : '' }
                              </Typography>
                              <div>
                                <Typography
                                  id={`field-name-${indexSection}-${indexItem}`}
                                  color='textSecondary'
                                  variant='caption'
                                >
                                  {v.name}
                                </Typography>
                                <br />
                                { v.type === FieldType.PERCENTAGE ? (
                                  <div>
                                    { v.name === 'Loss Ratio Range' && v.values ? (
                                      <div>
                                        <Typography
                                          id={`field-name-${indexSection}-${indexItem}-from`}
                                          style={{ marginRight:'30px' }}
                                          color='textSecondary'
                                          variant='caption'
                                        >
                                          From
                                        </Typography>
                                        <Typography
                                          id={`field-value-${indexSection}-${indexItem}-from`}
                                          style={{ fontWeight: 'bold', marginRight:'20px' }}
                                          variant='caption'
                                        >
                                          {v.values[0] + " %"}
                                        </Typography>
                                        <Typography
                                          id={`field-name-${indexSection}-${indexItem}-to`}
                                          style={{ marginRight:'30px' }}
                                          color='textSecondary'
                                          variant='caption'
                                        >
                                          To
                                        </Typography>
                                        <Typography
                                          id={`field-value-${indexSection}-${indexItem}-to`}
                                          style={{ fontWeight: 'bold', marginRight:'20px' }}
                                          variant='caption'
                                        >
                                          {v.values[1] + " %"}
                                        </Typography>
                                      </div>
                                    ): 
                                      <Typography
                                        id={`field-value-${indexSection}-${indexItem}`}
                                        style={{ fontWeight: 'bold' }}
                                        variant='caption'
                                      >
                                        {v.values && v.values.length ? v.values.map((val, index) => val + '%') : "- no data"}
                                      </Typography>
                                    }
                                  </div>
                                ) : v.type === FieldType.NUMBER ? (
                                  <div>
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold' }}
                                      variant='caption'
                                    >
                                      {v.values && v.values.length ? v.values.map((val, index) => val ): "- no data"}
                                    </Typography>
                                  </div>
                                ) : v.type === FieldType.CURRENCY ? (
                                  <div>
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold' }}
                                      variant='caption'
                                    >
                                      {v.values && v.values.length ? v.values.map((val, index) => 'PHP ' + CurrencyFormatter(val)) : "- no data"}
                                    </Typography>
                                  </div>
                                ) : (
                                  <div>
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold' }}
                                      variant='caption'
                                    >
                                      
                                      {v.values && v.values.length ? v.values.map((val, index) => (
                                        <span>
                                          <DoneIcon style={{ fontSize: 'smaller' }} />&nbsp;
                                          {val}
                                        </span>
                                      )) : "- no data"}
                                    </Typography>
                                  </div>
                                )}
                              </div>
                            </Grid>
                          ) : null 
                        )) : null 
                      }
                    </Grid>
                  )}

                  {item.field_section.toUpperCase() === 'PREMIUM REFUND' && refundTable && refundTable.length? 
                    <div style={{padding: "20px 50px"}}>
                    <TableContainer component={Paper}>
                      <Table className={classes.table} aria-label="simple table" size={"small"}>
                        <TableHead style={{backgroundColor: "#dddfe3"}}>
                          <TableRow style={{borderBottom: "none"}}>
                            <TableCell style={{borderBottom: "none"}}></TableCell>
                            <TableCell colSpan={2} align={'center'} style={{borderBottom: "none", paddingBottom: "0px"}}>Annual Mode</TableCell>
                            <TableCell colSpan={2} align={'center'} style={{borderBottom: "none", paddingBottom: "0px"}}>Semi-Annual Mode</TableCell>
                          </TableRow>
                          <TableRow class="table-sub-header">
                            <TableCell></TableCell>
                            <TableCell align={'center'} > Refund?</TableCell>
                            <TableCell align={'center'} > Refund Percentage</TableCell>
                            <TableCell align={'center'} > Refund?</TableCell>
                            <TableCell align={'center'} > Refund Percentage</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {refundTable.map((row, index) => (
                            <StyledTableRow key={row.name}>
                              <StyledTableCell component='th' width={350}>
                                {row.name.charAt(0).toUpperCase() + row.name.slice(1)}
                              </StyledTableCell >
                              <StyledTableCell align={'center'} component='th'>
                                <Checkbox
                                  color="primary"
                                  style={{padding: "0px"}}
                                  checked={row.annual && row.annual.length > 0}
                                  inputProps={{ 'aria-label': 'primary checkbox' }}
                                />
                              </StyledTableCell >
                              <StyledTableCell component='th' align={'center'}>
                                {row.annual && row.annual.length ? row.annual[0] + '%' : ''}
                              </StyledTableCell >
                              <StyledTableCell component='th' align={'center'}>
                                <Checkbox
                                  color="primary"
                                  style={{padding: "0px"}}
                                  checked={row.semi_annual && row.semi_annual.length > 0}
                                  inputProps={{ 'aria-label': 'primary checkbox' }}
                                />
                              </StyledTableCell >
                            <StyledTableCell component='th' align={'center'}>
                              {row.semi_annual && row.semi_annual.length ? row.semi_annual[0] + '%' : ''}
                            </StyledTableCell >
                          </StyledTableRow >
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                  </div>: null
                }
                 </>
                : null}
              </div>

            ))
          }
        </div>
      )}
    </React.Fragment>
  )
}
