import React from 'react'
import CancelIcon from '@material-ui/icons/Cancel'
import Checkbox from '@material-ui/core/Checkbox'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import CreateIcon from '@material-ui/icons/Create'
import CropSquareIcon from '@material-ui/icons/CropSquare'
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import IndeterminateCheckBoxIcon from '@material-ui/icons/IndeterminateCheckBox'
import Link from '@material-ui/core/Link'
import StopIcon from '@material-ui/icons/Stop'
import { default as MaterialTable } from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import { Typography } from '@material-ui/core'
import Paper from '@material-ui/core/Paper'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSortUp, faSortDown } from '@fortawesome/free-solid-svg-icons'
// Components
import Table from 'SharedComponents/Table/Table'
import InputFields from './components'

// Style
import classes from './CustomTable.module.scss'

// Config
import { defaultFieldValues } from './config/fields'

const moment = require('moment')
const numeral = require('numeral')

export function CustomTable (props) {
  const defaultFunction = function () {}
  const {
    data = {},
    handleSelectAll = defaultFunction,
    hasLink = false,
    setData = defaultFunction,
    showCheckBox = false,
    onDataListChange = defaultFunction,
    showTextField,
    body
  } = props

  const [debounce, setDebounce] = React.useState(null)
  const [open, setOpen] = React.useState(false)

  const handleOnSort = orderBy => e => {
    setData(i => ({ ...i, orderBy, order: !data.order }))
    onDataListChange({ sort: `${orderBy}|${data.order ? 'asc' : 'desc'}` })
  }

  const handleColumnFiltering = args => {
    const { field, value, match } = args
    setData(p => ({ ...p, filter: { ...p.filter, [field]: { value, match } } }))
    onDataListChange({ filter: { ...data.filter, [field]: { value, match } }, page: 1 })
  }

  return (
    <div className={classes.root}>
      <Table
        TableContainerProps={{
          style: { height: '100%', overflow: 'auto', position: 'relative' }
        }}
        TableProps={{ stickyHeader: true }}
        head={
          <TableRow>
            {showCheckBox && (
              <TableCell className={classes.checkbox} padding='checkbox'>
                <Checkbox color='inherit' onClick={event => handleSelectAll(event)} iconStyle={{ fill: 'white' }} />
              </TableCell>
            )}

            {data.header &&
              data.header.map(headCell => (
                <TableCell key={headCell.sortField || 'select-label'} id={headCell.id}>
                  {headCell.sortField && (
                    <Grid container className={classes['sort-field']} direction='row' onClick={handleOnSort(headCell.sortField)}>
                      <Grid item className={classes['sort-name']} xs={11}>{headCell.name}</Grid>
                      <Grid item className={classes['sort-wrapper']} xs={1}>
                        <Grid container direction='column'>
                          <Grid item className={`${classes['sort-container']} ${data.orderBy === headCell.sortField && data.order ? classes['selected'] : ''}`}>
                            <FontAwesomeIcon className={classes['sort-icon']} icon={faSortUp} />
                          </Grid>
                          <Grid item className={`${classes['sort-container']} ${data.orderBy === headCell.sortField && !data.order ? classes['selected'] : ''}`}>
                            <FontAwesomeIcon className={classes['sort-icon']} icon={faSortDown} />
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  )}
                  {!headCell.sortField && headCell.name}

                  {showTextField && headCell.sortField && (
                    <TextField
                      onChange={event => {
                        const search = event.target.value
                        clearTimeout(debounce)
                        setDebounce(
                          setTimeout(() => {
                            handleColumnFiltering({
                              field: headCell.sortField,
                              value: search,
                              match: headCell.match
                            })
                          }, 500)
                        )
                      }}
                    />
                  )}
                </TableCell>
              ))}

            {hasLink && <TableCell align='center' />}
          </TableRow>
        }
        body={body(data.body)}
        count={data.count || 0}
        page={data.page || 1}
        limit={data.limit || 10}
        rowsPerPageOptions={[10, 15, 20]}
        onChange={onDataListChange}
        pagination={data.pagination}
      />
    </div>
  )
}

export function InputTable (props) {
  const {
    data = {},
    disabled = false,
    onAddLineItem = () => {},
    onAmountChange = () => {},
    onRemoveLineItem = () => {},
    onSaveLineItem = () => {},
    disablers = {},
    handleDisabler = () => {}
  } = props
  const [newData, setNewData] = React.useState({})
  const [editData, setEditData] = React.useState({})
  const [isEdit, setIsEdit] = React.useState()
  const [lineItems, setLineItems] = React.useState([])

  React.useEffect(() => {
    const { header } = data
    setNewData(initializeNewData(header))
  }, [])

  React.useEffect(() => {
    setLineItems(data.data)
    computeTotal()
  }, [data.data])

  const initializeNewData = _params => {
    const newObject = {}
    _params.map(item => {
      newObject[item.target] = defaultFieldValues[item.type]
    })
    return newObject
  }

  const handleInputChanges = _params => {
    const { header } = data
    const { target, value } = _params
    if ( target === 'means_of_payment') {
      handleDisabler(value)
      setNewData(initializeNewData(header))
    }
    setNewData(i => ({ ...i, [target]: value }))
  }

  const handleEditChanges = _params => {
    const { header } = data
    const { target, value } = _params
    if ( target === 'means_of_payment') {
      handleDisabler(value)
      setEditData(initializeNewData(header))
    }
    setEditData(i => ({ ...i, [target]: value }))
  }

  const handleOnAddLineItem = () => {
    const { header } = data
    onAddLineItem(newData)
    setNewData(initializeNewData(header))
    computeTotal()
  }

  const computeTotal = () => {
    const amountData = data.data.reduce((amount, item) => {
      return amount + numeral(item.amount).value()
    }, 0)
    onAmountChange(numeral(amountData).format('0,0.00'))
  }

  return (
    <TableContainer className={classes['root-table-input']} component={Paper}>
      <MaterialTable className={classes.table}>
        <TableHead className={classes['table-head']}>
          <TableRow className={classes['table-head-row']}>
            <React.Fragment>
              {data.header &&
                data.header.map((item, index) => (
                  <TableCell className={classes['table-head-cell']}>
                    <InputFields
                      {...item}
                      disabled={disabled ? disabled : !!disablers[item.target]}
                      value={!!disablers[item.target] ? null : newData[item.target]}
                      onInputChanged={e => handleInputChanges(e)}
                    />
                  </TableCell>
                ))}
              {!disabled && (
                <TableCell className={classes['table-head-cell']}>
                  <Link component='button' variant='body2' onClick={() => handleOnAddLineItem()}>
                    + Add
                  </Link>
                </TableCell>
              )}
            </React.Fragment>
          </TableRow>
        </TableHead>
        <TableBody className={classes['table-body']}>
          {lineItems &&
            lineItems.map((item, index) => (
              <TableRow className={classes['table-body-row']} key={`table-row-${index}`}>
                {isEdit !== index && Object.keys(item).map((key, itemIndex) => (
                  <React.Fragment>
                    {isEdit !== index && (
                      <React.Fragment>
                        {key !== '_id' && (
                          <TableCell>{key !== 'check_date' ? item[key] : item[key] ? moment(item[key]).format('ll') : ''}</TableCell>
                        )}
                      </React.Fragment>
                    )}
                  </React.Fragment>
                ))}
                {isEdit === index && data.header &&
                  data.header.map((item, index) => (
                    <TableCell className={classes['table-head-cell']}>
                      <InputFields
                        {...item}
                        inline
                        disabled={disabled ? disabled : !!disablers[item.target]}
                        value={editData[item.target]}
                        onInputChanged={e => handleEditChanges(e)}
                      />
                    </TableCell>
                ))}
                <React.Fragment>
                  {isEdit === index && (
                    <React.Fragment>
                      <IconButton onClick={() => {
                        onSaveLineItem({ item: editData, index: isEdit })
                        setEditData()
                        setIsEdit()
                        computeTotal()
                      }}>
                        <CheckCircleIcon fontSize='small' />
                      </IconButton>
                      <IconButton onClick={() => {
                        setIsEdit()
                        setEditData()
                      }}>
                        <CancelIcon fontSize='small' />
                      </IconButton>
                    </React.Fragment>
                  )}
                  {isEdit !== index && !disabled && (
                    <React.Fragment>
                      <IconButton onClick={() => {
                        setEditData(i => ({...i, ...lineItems[index]}))
                        setIsEdit(index)
                      }}>
                        <CreateIcon fontSize='small' />
                      </IconButton>
                      <IconButton onClick={() => {
                        onRemoveLineItem(index)
                        computeTotal()
                      }}>
                        <IndeterminateCheckBoxIcon fontSize='small' />
                      </IconButton>
                    </React.Fragment>
                  )}
                </React.Fragment>
              </TableRow>
            ))}
        </TableBody>
      </MaterialTable>
    </TableContainer>
  )
}
