import React from 'react';

// MATERIAL COMPONENTS
import { makeStyles } from '@material-ui/core/styles';
import { FormLabel, FormControl, FormGroup, FormControlLabel, Checkbox } from '@material-ui/core';

// STYLES
import style from '../AuditLog.module.scss'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  formControl: {
    margin: theme.spacing(3),
  },
}));

export default function DownloadCheckboxGroup() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    gilad: true,
    jason: false,
    antoine: false,
  });

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  const { gilad, jason, antoine } = state;
  const error = [gilad, jason, antoine].filter((v) => v).length !== 2;

  return (
    <div>
      <FormControl component="fieldset">
        <FormLabel >
          <div className={style.downloadGroupLabel}>
            Choose Format
          </div>
        </FormLabel>
        <FormGroup>
          <FormControlLabel style={{ margin: 0, padding: 0 }}
            control={<Checkbox style={{ color: '#555', margin: 0, paddingTop: 2, paddingBottom: 2, paddingLeft: 0, fontWeight: 400 }}
              // checked={gilad} 
              onChange={handleChange} name="pdf" />}
            label="PDF"
          />
          <FormControlLabel style={{ margin: 0, padding: 0 }}
            control={<Checkbox style={{ color: '#555', margin: 0, paddingTop: 2, paddingBottom: 2, paddingLeft: 0, fontWeight: 400 }}
              // checked={jason} 
              onChange={handleChange} name="xls" />}
            label="XLS"
          />
          <FormControlLabel style={{ margin: 0, padding: 0 }}
            control={<Checkbox style={{ color: '#555', margin: 0, paddingTop: 2, paddingBottom: 2, paddingLeft: 0, fontWeight: 400 }}
              // checked={antoine}
              onChange={handleChange} name="xml" />}
            label="XML"
          />
        </FormGroup>
      </FormControl>
    </div>
  );
}
