import React, { useState } from 'react'
import { Link } from 'react-router-dom'

// Material Components
import { Grid, Typography, Paper, Tabs, Tab } from '@material-ui/core'

// Components
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import FirstStepComponent from './components/CollectionBreakdownComponent/CollectionBreakdownComponent'
import SecondStepComponent from './components/CollectionDetailsComponent/CollectionDetailsComponent'
import PageFooter from './../../Components/PageFooter/PageFooter'
import CollectionHeader from './../../Components/AdvanceCollectionComponents/CollectionHeader/CollectionHeader'

// Styles
import classes from './ValidateAdvanceCollectionDistributionPage.module.scss'

// JSON Data
import { COLLECTION_DATA } from './CollectionData.js'

export default function ValidateAdvanceCollectionDistributionPage (props) {
  const [sections] = useState(COLLECTION_DATA)
  const { data_id = null, data_type = '' } = props.match.params

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Collection Breakdown', 'Collection Details', 'Submit'],
    stepNumber: 1
  })

  const [pageTitle, setPageTitle] = React.useState('Advance Collection Distribution to Validate')

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    switch (stepper.stepNumber) {
      case 1:
        return <FirstStepComponent {...componentProp} />
      case 2:
        return <SecondStepComponent {...componentProp} />
      case 3:
        return <h1>Step 3</h1>
      default:
        return ''
    }
  }

  const pageProps = {
    isStepper: true,
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    showValidateAdvanceListButtons: true
  }

  return (
    <>
      <div style={{ display: 'flex' }}>
        <Grid container spacing={5}>
          <Grid item xs={12}>
            <Grid container>
              <Grid item xs={12}>
                <Grid container>
                  <Grid item xs={8}>
                    <Typography id='collection-distribution-page-header' variant='h5'>
                      {pageTitle}
                    </Typography>
                  </Grid>
                  <Grid item xs={4} style={{ textAlign: 'right' }}>
                    <FormStepper {...stepper} />
                  </Grid>
                </Grid>
              </Grid>
              {sections.map(({ ...otherCollectionProps }) => (
                <CollectionHeader key={'collection-header'} {...otherCollectionProps} />
              ))}
            </Grid>
            {showComponent()}
          </Grid>
          <Grid item xs={12}>
            <PageFooter {...pageProps} />
          </Grid>
        </Grid>
      </div>
    </>
  )
}
