import React from 'react'
import { useHistory } from 'react-router-dom'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'


import { makeStyles } from '@material-ui/core/styles'

// Styles
import classes from './ConfirmDialog.module.scss'
import { Typography, DialogContent, Button, Grid } from '@material-ui/core'

// Services
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

const useStyles = makeStyles({
  size: {
    fontSize: '14px'
  }
})

const SuccessDialog = props => {
  const { openSuccessDialog, setOpenSuccessDialog } = props
  const history = useHistory()

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
    history.push('/')
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '40%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes['success-close-button']}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id='assign-title' className={classes.title}>
            Account Successfully Assigned
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        Account was successfully assigned to Billing Analyst and sent for billing cycle setup.
        <div className={classes.button}>
          <Button
            variant='contained'
            id='ok-button'
            className={classes['button-positive']}
            onClick={handleOnCloseSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { assign, setAssign, onCancel, reAssign } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const muiClasses = useStyles()

  const handleOnClose = () => {
    setAssign(state => ({ ...state, show: false }))
  }

  const onShowSuccessDialog = () => {
    setAssign(state => ({ ...state, show: false }))
    BillingSupervisorServices.assignBillingAnalyst({
      analystId: assign.analyst,
      data: { clientId: assign.clientId, reAssign }
    }).then(res => {
      setOpenSuccessDialog(true)
    })
  }

  return (
    <>
      <Dialog
        maxWidth='xs'
        open={assign.show}
        className={classes.root}
        PaperProps={{ style: { display: 'flex' } }}
        onClose={handleOnClose}
      >
        <DialogTitle disableTypography style={{ padding: '10px 10px 0px 20px', borderBottom: 'none' }}>
          <Grid container>
            <Grid item xs={9} sm={9} md={9} lg={9} style={{ alignSelf: 'center' }}>
              <Typography style={{ fontSize: '19px' }}>{assign.title}</Typography>
            </Grid>
            <Grid item xs={3} sm={3} md={3} lg={3} style={{ textAlign: '-webkit-right' }}>
              <IconButton aria-label='close' onClick={onCancel}>
                <CloseIcon />
              </IconButton>
            </Grid>
          </Grid>
        </DialogTitle>
        <DialogContent style={{ padding: '10px 20px' }}>
          <Typography class={muiClasses.size}>{assign.text}</Typography>
          <Typography class={muiClasses.size}>{assign.text2}</Typography>
          <div className={classes.button}>
            <Button variant='outlined' className={classes['button-negative']} onClick={onCancel}>
              Cancel
            </Button>
            <Button variant='contained' className={classes['button-positive']} onClick={onShowSuccessDialog}>
              Assign
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <SuccessDialog openSuccessDialog={openSuccessDialog} setOpenSuccessDialog={setOpenSuccessDialog} />
    </>
  )
}
