import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/css')

export default class CollectionCDFServices {
  static getMany (params) {
    return axios({
      url: `/collection/transactions`,
      method: 'GET',
      params
    })
  }

  static getManyWithPagination (params) {
    return axios({
      url: `/collection/transactions-pagination`,
      method: 'GET',
      params
    })
  }
}
