import { Link, useHistory } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Services
import RemindersLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    {value: 'today', text: 'Today'},
    {value: 'week', text: 'This Week'},
    {value: 'month', text: 'This Month'}
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(1, 'weeks').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(1, 'months').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const columnConfig = [
  { id: 'reminder-letter-tile-table-header', name: 'Account Name' },
  { id: 'reminder-letter-tile-table-header', name: 'Due Date' },
  { id: 'reminder-letter-tile-table-header', name: '' }
]

// Mock data
const mock = require('./mock-data/collection-distribution.json')

const ReminderLetterTile = props => {
  // Constants
  const history = useHistory()
  const { services, sizes = { sm: 12 } } = props

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    range: [],
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tileList, setTileList] = React.useState(initialValue)

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const fetchTileData = (params) => {
    RemindersLetterServices.index(params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setTileList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value] })
  }

  const handleOnViewAll = () => {
    history.push('/collection/lists/reminder-letter')
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Reminder Letters Tile is not on viewport')}
      />
      <DashboardTile
        id='reminder-letter-tile'
        count={{ color: 15, value: tileList.count, id: 'reminder-letter-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Reminder Letters to Send'}
        isFilterShown
        onFilterChange={onFilterChange}
        filterMenu={filterMenu}
        viewList={
          <Typography
            id='reminder-letter-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tileList.body && tileList.body.map((item, index) => (<TableRow key={`reminder-letter-tile-table-row-${index}`} id={`reminder-letter-tile-table-row-${index}`}>
              <TableCell id={`reminder-letter-tile-table-cell-account-name-${index}`}>{(((item.client.account_name).substring(17,-3)) + '...') || ''}</TableCell>
              <TableCell id={`reminder-letter-tile-table-cell-date-due-${index}`}>{moment(item.soa_due_date).format('ll') || ''}</TableCell>
              <TableCell id={`reminder-letter-tile-table-cell-action-${index}`}>
                {/* TODO: Change link when end-point and data is available */}
                <Link to={`/`}>
                  <Typography variant='caption' className='text-link underline' id={`reminder-letter-tile-view-${index}`}>
                      View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

ReminderLetterTile.displayName = 'DashboardTileComponent'

export default ReminderLetterTile
