import React from 'react'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'
import { Link, useHistory } from 'react-router-dom'
import { withStyles, makeStyles } from '@material-ui/core/styles'

import _ from 'lodash'
import moment from 'moment'

// Components
import SearchTextComponent from '../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'
import PageFooter from '../../../Components/PageFooter/PageFooter'
import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'

// Styles
import classes from './ViewAllPage.module.scss'

// Util
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

// Config
import { ValidatedListTransactionHeaders } from '../config/config'
import { config } from '../../../Components/Tiles/ViewAllConfig'

export default function ViewAllPage (props) {
  const {
    config,
    tile,
    type,
    title,
    contents,
    count,
    placeholder,
    fetchData,
    params,
    accessors,
    list,
    isFetching
  } = props

  const history = useHistory()

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const tableHeaders = config[tile].headers
  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  const mainSearchChange = (value) => {
    value = value.trim()
    const params = {
      'search': value,
      'accessors': accessors,
      page: 1,
    }
    changeView(params)
  }

  // For Display Settings
  var today = moment().format('YYYY-MM-DD')
  const [fromDate, setFromDate] = React.useState(today)
  const [toDate, setToDate] = React.useState(today)
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined
  const handleOnApply = (event) => {
    let fdate = new Date(fromDate)
    let tdate = new Date(toDate)
    const params = {
      'range': {
        'start': fdate.toISOString(),
        'end': tdate.toISOString()
      }
    }
    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError('Please enter a valid effectivity start date')
      setToDateError('Please enter a valid effectivity end date')
    } else if (fdate.valueOf() > tdate.valueOf()) {
      setToDateError('Please enter a valid effectivity end date that is not later than the current date')
    } else {
      setFromDateError('')
      setToDateError('')
      if (fetchData) {
        fetchData(params)
      }
    }
  }

  // Methods
  const onDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleOnCloseClicked = () => {
    history.push('/')
  }

  const handleOnView = ({ status, transaction_type, transaction_number }) => {
    if (!transaction_type || !transaction_number) return false
    switch (transaction_type) {
      case 'Advance Collection Distribution':
        history.push(`/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}?isAdvance=true`)
        break
      case 'Collection Distribution':
        history.push(`/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}`)
        break
      case 'Suspension':
        history.push(`/collection/suspension/endorse/client/${transaction_number}`)
        break
      case 'Penalty':
        history.push(`/collection/penalty/client/${transaction_number}`)
        break
      default:
        return false
        break
    }
  }

  const pageProps = {
    isControl: true,
    handleOnCloseClicked,
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography id='collection-distribution-page-header' variant='h5'>
                {title}
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder={placeholder}
                displaySettings
                handleOnDisplaySettingsClicked={onDisplaySettingsClicked}
                onChange={mainSearchChange}
                exportFiles
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs>
            <CustomEnhancedTable
              count={count}
              changeView={changeView}
              tableHeaders={tableHeaders}
              pagination={pagination}
              search
              sort
              fetchData={fetchData}
              params={params}
            >
              {isFetching && (
                <StyledTableRow>
                  <StyledTableCell component='th' colspan={10}>
                    <CircularLoading />
                  </StyledTableCell>
                </StyledTableRow>
              )}

              {!isFetching && contents &&
                contents.map((row, contentsIndex) => (
                  <StyledTableRow key={contentsIndex}>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        { type === 'Validated Transactions' ? row.category : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        { type === 'Validated Transactions' ? row.transaction_type : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        { type === 'Validated Transactions' ? row.cdf_number : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        { type === 'Validated Transactions' ? row.contract?.client?.account_name : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      { type === 'Validated Transactions' ? row.status : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                      { type === 'Validated Transactions' ? CurrencyFormatter(row.summary.grand_total) : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                      { type === 'Validated Transactions' && row.status === 'Approved' ? DateFormatterByAbbreviation(row.date.approved) : ''}
                      { type === 'Validated Transactions' && row.status === 'Disapproved' ? DateFormatterByAbbreviation(row.date.disapproved) : ''}
                      { type === 'Validated Transactions' && row.status === 'Void' ? DateFormatterByAbbreviation(row.date.void) : ''}
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'  align='right'>
                      <Typography variant='caption' className='text-link underline' onClick={() => handleOnView({
                        status: row.status,
                        transaction_type: row.transaction_type,
                        transaction_number: row.transaction_number
                      })}>
                        View
                      </Typography>
                    </StyledTableCell>
                  </StyledTableRow>
                ))
              }
            </CustomEnhancedTable>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <PageFooter {...pageProps} />
        </Grid>
      </Grid>
    </div>
  )
      
}
