import React from 'react'
import { Grid, Typography, Button } from '@material-ui/core'

// Shared
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Components
import { CustomDatePicker as DatePicker } from '../FormComponents/CustomDatePicker/CustomDatePicker'
import { CustomSelect as Select } from '../FormComponents/CustomSelect/CustomSelect'
import ControlList from '../FormComponents/ControlList/ControlList'

// Services
import BillingSOAReportService from 'Services/Api/BillingSOAReportService'

// Standard Components
import Attachment from 'StandardComponents/Attachment/Attachment'

// style
import classes from './GenerateReports.module.scss'

// MuiStyles
import { withStyles } from '@material-ui/core/styles'

// config
import { typeOfReportConfig, categoryConfig } from './config/generate-config'

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    padding: '0 30px',
    marginBottom: '.5rem'
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

export default function GenerateReports () {
  const [reports, setReports] = React.useState({
    title: 'Generate Reports',
    categoryData: [],
    typeOfReportData: [],
    soaNumber: 'PRE-SOA060000001',
    type: 'supervisor'
  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionDistributionDashboard', 'generateReports'])
    setReports(prevState => ({ ...prevState, categoryData: categoryConfig, typeOfReportData: typeOfReportConfig }))
  }, [])

  const handleOnGenerateReportsPrintClicked = args => {
    console.log('args', args)
    let { type, soaNumber } = args
    if (!type) return
    if (type === 'preview') {
      type = 'print'
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'for-print.pdf')
        document.body.appendChild(link)
        link.click()
      })
    } else {
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'for-print.pdf')
        document.body.appendChild(link)
        link.click()
      })
    }
  }

  const controlProps = {
    handleOnGenerateReportsPrintClicked: handleOnGenerateReportsPrintClicked,
    downloadGenerateReports: true,
    printGenerateReports: true,
    soaNumber: reports.soaNumber,
    type: reports.type
  }

  console.log('reports', reports)
  console.log('controlProps', controlProps)

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography id='generate-reports-header' variant='h5'>
                {reports.title}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} style={{ marginTop: '1rem' }}>
          <Grid container spacing={2} direction={'row'}>
            <Grid item lg={2}>
              <Select data={{ ...reports.categoryData }} />
            </Grid>
            <Grid item lg={4} style={{ paddingRight: '1rem' }}>
              <Select data={{ ...reports.typeOfReportData }} />
            </Grid>
            <Grid item lg={2} style={{ paddingTop: '20px' }}>
              <DatePicker label={'Date Range'} noMargin required noElevation />
            </Grid>
            <Grid item lg={2} style={{ paddingTop: '45px' }}>
              <DatePicker noMargin noElevation />
            </Grid>
            <Grid item lg={2} style={{ alignSelf: 'flex-end' }}>
              <StyledButton>Generate</StyledButton>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} style={{ marginTop: '1rem' }}>
          <Grid container spacing={2}>
            <ControlList {...controlProps} />
          </Grid>
        </Grid>
        <Grid item xs={12} style={{ marginTop: '1rem' }}>
          <Attachment submit={{ type: reports.type }} soaNumber={reports.soaNumber} />
        </Grid>
      </Grid>
    </div>
  )
}
