import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'

// import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingClientInvoicesServices from 'Services/Api/BillingClientInvoicesServices'
import BillingCreditMemoServices from 'Services/Api/BillingCreditMemoServices'
import BillingDebitMemoServices from 'Services/Api/BillingDebitMemoServices'

import classes from './ValidatedTransactionsPage.module.scss'
import BillingClientCreditMemosServices from '../../../../../Services/Api/BillingCreditMemoServices'
import BillingClientDebitMemosServices from '../../../../../Services/Api/BillingDebitMemoServices'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Billing Code & Type *',
    center: true,
    search: true,
    sort: true
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member ID',
    center: true,
    search: true,
    sort: true
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Name',
    center: true,
    search: true,
    sort: true
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description *',
    center: true,
    search: true,
    sort: true
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Enter Taxable Amount *',
    center: true,
    search: true,
    sort: true
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function InvoiceToBeRevised (props = {}) {
  PageTitleService()
  const history = useHistory()

  const transaction_number = props.match.params.transaction_number
  const type = props.match.params.type

  const [transaction, setTransaction] = React.useState({
    data: []
  })
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})
  const [disabledSave, setDisabledSave] = React.useState(false)

  const [transactionDetail, setTransactionDetail] = React.useState({})
  React.useEffect(() => {
    BreadcrumbsService.setManualBreadCrumbs([
      {
        text: 'DASHBOARD',
        to: '/'
      },
      {
        text: 'LIST OF VALIDATED TRANSACTIONS',
        to: `/`
      },
      {
        text: 'VALIDATED TRANSACTIONS',
        to: '/'
      }
    ])
    if (transaction_number.includes('INV')) {
      Promise.all([BillingClientInvoicesServices.getTobeRevisedInvoice({ transaction_number })]).then(values => {
        setTransaction(prevState => ({
          ...prevState,
          data: values[0].data.data
        }))
      })
    } else if (transaction_number.includes('CM')) {
      Promise.all([BillingCreditMemoServices.getTobeRevisedCreditMemo({ transaction_number })]).then(values => {
        setTransaction(prevState => ({
          ...prevState,
          data: values[0].data.data
        }))
      })
    } else {
      Promise.all([BillingDebitMemoServices.getTobeRevisedDebitMemo({ transaction_number })]).then(values => {
        setTransaction(prevState => ({
          ...prevState,
          data: values[0].data.data
        }))
      })
    }

    // TODO API CALL FOR CURRENT TRANSACTIONS
    fetchData({})
  }, [])
  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)
    if (transaction_number.includes('INV')) {
      BillingClientInvoicesServices.getInvoiceDetails(_params).then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setList(i => ({
          ...i,
          body: results,
          count,
          limit,
          page,
          sort: _params.sort,
          orderBy: _params.orderBy ? _params.orderBy : list.orderBy
        }))
      })
    } else if (transaction_number.includes('CM')) {
      BillingClientCreditMemosServices.getCreditMemoDetails(_params).then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setList(i => ({
          ...i,
          body: results,
          count,
          limit,
          page,
          sort: _params.sort,
          orderBy: _params.orderBy ? _params.orderBy : list.orderBy
        }))
      })
    } else {
      BillingClientDebitMemosServices.getDebitMemoDetails(_params).then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setList(i => ({
          ...i,
          body: results,
          count,
          limit,
          page,
          sort: _params.sort,
          orderBy: _params.orderBy ? _params.orderBy : list.orderBy
        }))
      })
    }
  }
  const transactionInfo = transaction.data
  const clientInfo = transaction.data.client
  const billingInfo = transaction.data.billing
  const contractInfo = transaction.data.contract
  const summaryInfo = transaction.data.summary
  const dateInfo = transaction.data.date
  const invoiceInfo = transactionDetail.data
  function handleClose () {
    history.push('/')
  }

  // BreadcrumbsService.setSelectedItems([
  //   'dashboard',
  //   'invoice',
  //   `invoiceID:${clientInfo && clientInfo.account_name + ' - Invoice to be Revised'}`
  // ])

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }

  return (
    <div>
      <HeaderDetails>
        <Typography variant='h5'>Validated Transaction</Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>{clientInfo && clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>{clientInfo && clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                {billingInfo && billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                CORPORATE TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>{clientInfo && clientInfo.tin}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                {moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                {moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>
            <Link id='export-link' to={`/`} className={classes['export-link']}>
              <ReplyIcon id='export-icon' className={classes['export-icon']} />
              Export
            </Link>

            <Link id='view-billing-link' to={`/`} className={classes['view-link']}>
              <VisibilityIcon id='export-icon' className={classes['export-icon']} />
              View Bill Run Inclusion
            </Link>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                REFERENCE NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(dateInfo && dateInfo.created).format('MMM DD, YYYY')}
              </Typography>
              <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {moment(transactionInfo.transaction && transactionInfo.transaction.period.start).format('MMM DD, YYYY')}{' '}
                - {moment(transactionInfo.transaction && transactionInfo.transaction.period.end).format('MMM DD, YYYY')}
              </Typography>
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
            </div>
            <div className={classes.status}>
              <Typography id='content-details-status' style={{ fontSize: '14px', marginTop: '8px', fontWeight: '500' }}>
                {transactionInfo.transaction && transactionInfo.transaction.status}
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  />
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <div className={classes['item-right-side']}>
            <Button
              id='close-button'
              variant='contained'
              color='primary'
              className={classes['button-positive']}
              onClick={e => handleClose()}
            >
              Close
            </Button>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
