import React from 'react'
import { Typography, Select, MenuItem, FormControl } from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

import style from './CustomSelect.module.scss'

// MuiStyles
import { MuiThemeProvider, createMuiTheme, withStyles, makeStyles } from '@material-ui/core/styles'

const theme = createMuiTheme({
  overrides: {
    MuiFilledInput: {
      root: {
        backgroundColor: 'rgba(255,255,255,0.8)',
        '&:hover': {
          backgroundColor: 'rgba(255,255,255,1)'
        },
        '&.Mui-focused': {
          backgroundColor: 'rgba(255,255,255,1)'
        }
      }
    }
  }
})

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  },
  label: {
    padding: '5px 0rem',
    fontSize: '14px',
    fontWeight: '500'
  }
}))

export function CustomSelect (props) {
  const { data = {} } = props
  const [selectData, setSelectData] = React.useState({})

  React.useEffect(() => {
    setSelectData(prevState => ({
      ...prevState,
      id: data && data[0] && data[0].id,
      title: data && data[0] && data[0].title,
      arrayOfData: data && data[0] && data[0].select,
      required: data && data[0] && data[0].required
    }))
  }, [data])

  console.log('selectData', selectData.arrayOfData)

  const classes = useStyles()
  return (
    <MuiThemeProvider theme={theme}>
      <div className={style.wrapper}>
        <FormControl fullWidth disableRipple size='small' variant='outlined' className={classes.formControl}>
          <Typography className={classes.label} id={`label-${selectData.id}-id`}>
            {selectData.title} {selectData.required ? '*' : ''}
          </Typography>
          <Select
            style={{ background: '#fff' }}
            disableRipple
            IconComponent={ExpandMoreIcon}
            labelId='demo-simple-select-label'
            id='demo-simple-select'
            value={''}
            onChange={() => {}}
            disableUnderline
            elevation={1}
            InputProps={{
              classes: {
                notchedOutline: classes.notchedOutline
              }
            }}
          >
            <MenuItem value='' disabled>
              Select {selectData.title}
            </MenuItem>
            {selectData &&
              selectData.arrayOfData &&
              selectData.arrayOfData.map((item, index) => <MenuItem value={item.value}>{item.name}</MenuItem>)}
          </Select>
        </FormControl>
      </div>
    </MuiThemeProvider>
  )
}
