import React from 'react'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import get from 'lodash/get'
import noop from 'lodash/noop'

import CustomDialog from 'SharedComponents/CustomDialog/CustomDialog'
import Select from '../../../../Components/FormComponents/Select/Select'

GenerateDialog.propTypes = {
  open: PropTypes.bool,
  contracts: PropTypes.array.isRequired,
  form: PropTypes.shape({
    values: PropTypes.object.isRequired,
    errors: PropTypes.object,
  }).isRequired,
  onSubmit: PropTypes.func.isRequired
}

export default function GenerateDialog({ open = false, contracts = [], form, onSubmit = noop, ...props }) {
  return (
    <CustomDialog
      open={open}
      title="Select Account"
      cancelBtn={false}
      submitLabel="OK"
      content={
        <Grid container>
          <Grid item>
            <Typography>Select the Account you wish to generate the reminder letter</Typography>
          </Grid>

          <Grid item xs={12} style={{ marginTop: 25, marginBottom: 25 }}>
            <Select
              id="contract-id"
              label="Select Account"
              name="contract_id"
              value={form.contract_id}
              onChange={form.onChange}
              defaultItemText="Select Account"
              data={contracts.map(contract => ({
                value: contract.contract.id,
                text: contract.client.account_name
              }))}
              error={get(form.errors, 'contract_id')}
            />
          </Grid>
        </Grid>
      }
      onOk={onSubmit}
      {...props}
    />
  )
}
