import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import FormHelperText from '@material-ui/core/FormHelperText'
import Grid from '@material-ui/core/Grid'

import classes from './CustomTextField.module.scss'
export default function CustomTextField (props) {
  const {
    label = '',
    required,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    type,
    noMargin = false,
    placeholder = '',
    TextFieldStyle = {},
    direction='column',
    ...other
  } = props
  const { disabled } = { ...other }
  const { value = '' } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value])

  const handleOnChange = e => {
    setError()
    if (typeof onChange === 'function') onChange(e)
  }

  return (
    <Grid container className={classes.root} style={constainerStyle} direction={direction}>
      <Grid item>
        <Typography variant='subtitle2' className={`${classes.label} ${disabled ? classes.disabled : ''}`}>
          {label}
          {required ? ' *' : ''}
        </Typography>
      </Grid>
      <Grid item xs className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['text-field']} ${noMargin ? classes['no-margin'] : ''} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
              style={TextFieldStyle}
              onBlur={() => setFocus(false)}
            >
              <InputBase
                className={classes.input}
                onChange={handleOnChange}
                type={type}
                {...other}
                onFocus={() => setFocus(true)}
                autoComplete='off'
                placeholder={placeholder}
              />
            </Paper>

            {_error && (
              <FormHelperText className={classes.error}>{_error}</FormHelperText>
            )}
          </React.Fragment>
        )}
      </Grid>
    </Grid>
  )
}
