import React from 'react'
import moment from 'moment'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Collapse from '@material-ui/core/Collapse'

import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import IconButton from '@material-ui/core/IconButton'
import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function NormalRow (props) {
  const {
    index,
    table_columns,
    data,
    id,
    to,
    param,
    actionTo,
    style,
    show,
    monetary_columns,
    emphasis_columns,
    multipleActions
  } = props

  return (
    <React.Fragment>
      <TableRow key={index}>
        <TableCell id={`account-id-${index}`} className={classes.rowCollapseButton} />

        {table_columns.map((column, iteration) => (
          <TableCell
            className={classes.contentCell}
            style={emphasis_columns.includes(column) === true ? { 'font-weight': 'bold' } : { 'font-weight': 'normal' }}
          >
            {monetary_columns.includes(column) === true
              ? 'Php ' + CurrencyFormatter(data[column])
              : moment(data[column], moment.ISO_8601).isValid()
              ? DateFormatterByAbbreviation(data[column])
              : data[column]}
          </TableCell>
        ))}
        {show && (
          <TableCell className={classes.contentCell}>
            <Link id to={`/clients/assign/${data._id}/${data.corporate_id}?reAssign=true`} className={classes.link}>
              Billing Analyst
            </Link>
            <Link
              id
              to={`/collection/assign/${data._id}/${data.corporate_id}?reAssign=true`}
              className={classes.link}
              style={{ marginLeft: '10px' }}
            >
              Collection Analyst
            </Link>
          </TableCell>
        )}
        {actionTo ? (
          <TableCell className={classes.contentCell}>
            <Link id to={param == null ? actionTo : actionTo.concat('/', data[param])} className={classes.link}>
              View
            </Link>
          </TableCell>
        ) : null}
        {multipleActions ? (
          <TableCell className={classes.contentCell}>
            {multipleActions.map((action, iteration) => (
              <Link
                id
                onClick={() => action.handleClick(data)}
                className={classes.link}
                style={{ textDecoration: 'underline', marginRight: '10px' }}
              >
                {action.label}
              </Link>
            ))}
          </TableCell>
        ) : null}
      </TableRow>
    </React.Fragment>
  )
}
