import { Link, useHistory } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'
import AccountToSetupViewAll from '../../../../CollectionPages/CollectionAccountSetupPage/AccountToSetup/AccountToSetup'

// Utilities
import moment from 'moment'

// Services
import CollectionClientLedgerServices from 'Services/Api/CollectionClientLedgerServices'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this-week', text: 'This Week' },
    { value: 'this-month', text: 'This Month' }
  ],
  default: 'this-month'
}

const AccountSetupTile = props => {
  let history = useHistory()
  // Constants
  const { service = () => {} } = props
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filter: {},
    filterMenu,
    limit: 6,
    order: true,
    orderBy: 'date_created',
    page: 1,
    sort: '',
    title: 'Accounts to Setup'
  })

  // Initialize / Watch
  React.useEffect(() => {
    Promise.all([
      CollectionClientLedgerServices.getClientLedgersToSetup({
        limit: tile.limit,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        rangeTarget: 'date_created'
      })
    ])
      .then(res => {
        const [clientsToSetup] = res
        const { results, count } = clientsToSetup.data.data
        setTile(i => ({ ...i, count, data: results }))
      })
      .catch(e => console.error(`Error fetching data: ${e}`))
  }, [tile.filterMenu.default])

  // Method
  const handleOnViewAll = () => {
    LoadingService.loading(true)
    history.push('/collection/lists/account-setup')
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='account-setup-tile'
        count={{ color: 3, value: tile.count, id: 'account-setup-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        viewList={
          <Typography
            id='account-setup-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableBody>
            {tile &&
              tile.data.map((item, index) => (
                <TableRow
                  key={`account-setup-tile-table-row-${index}`}
                  id={`account-setup-tile-tile-table-row-${index}`}
                >
                  <TableCell id={`account-setup-tile-table-cell-account-name-${index}`}>
                    {item.account_name || ''}
                  </TableCell>
                  <TableCell id={`account-setup-tile-table-cell-action-${index}`}>
                    <Link to={`/collection/assign/${item._id}/${item.account_number}`}>
                      <Typography
                        variant='caption'
                        className='text-link underline'
                        id={`account-setup-tile-view-${index}`}
                      >
                        Setup
                      </Typography>
                    </Link>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

AccountSetupTile.displayName = 'DashboardTileComponent'

export default AccountSetupTile
