import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import BillingCreditMemoServices from 'Services/Api/BillingCreditMemoServices'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from '../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll4/ViewAll'
import CreditMemoService from "../../../../../../Services/Api/BillingAndCollection/CreditMemoService";

// Style
import classes from './ClientCreditMemosToValidateTile.module.scss'

// Utilities
import moment from 'moment'

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

// Mock data
const mock = require('./../mock-data/collection-ticketList.json')

const ClientCreditMemosToValidateTile = props => {
  // Constants
  const { service = () => {} } = props

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})
  const [statuses, setStatuses] = React.useState(
  {
    forApproval: 0,
    approved: 0,
    disapproved: 0,
    void: 0
  })

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = (params) => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      entityType: 'Client',
      status: 'forApproval',
      ...params
    }
    setParams(_params)

    CreditMemoService.getCreditMemos(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    const _params = {
      isTile: true,
      entityType: 'Client',
      status: 'forApproval',
      ...params
    }

    CreditMemoService.getCreditMemos(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setTileList(i => ({ ...i, body: results, count , limit, page }))
    })
  }

  const fetchStatuses = () => {
    CreditMemoService.getCreditMemosStatuses().then(({ data: res }) => {
      setStatuses(res.data)
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)



  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Client Credit Memos to Validate Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Client Credit Memos To Validate'}
        isFilterShown
        filterMenu={filterMenu }
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}); fetchStatuses() }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body && tileList.body.map((item, index) => (
              <TableRow key={index}>
                <TableCell style={{ paddingLeft: '6px' }}>{item.billing_client_contract[0] ? TruncateString(item.billing_client_contract[0].client.account_name, 30) : null}</TableCell>
                <TableCell>{item.billing_client_contract[0] ? item.billing_client_contract[0].billing.cycle.code : null}</TableCell>
                <TableCell>
                  <Link
                    to={`billing/client/credit/validate/${item.transaction.number}`}
                  >
                    <Typography variant='caption' className='text-link underline'>
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-credit-memos-to-validate-modal'}
        config={config}
        tile={'clientCreditMemosToValidate'}
        type='Client Credit Memos To Validate'
        title='List of Client Credit Memos To Validate'
        placeholder='Search List of Client Credit Memos To Validate'
        statuses={statuses}
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['transaction.number', 'transaction.type', 'billing_client_contract.client.account_name', 'transaction.date|isDate', 'summary.sub_total|isNumber']}
      />
    </React.Fragment>
  )
}

ClientCreditMemosToValidateTile.displayName = 'DashboardTileComponent'

export default ClientCreditMemosToValidateTile
