import React, { useRef } from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import queryString from 'query-string';

// Client
import FirstStepComponent from './../components/CollectionBreakDownComponent/CollectionBreakDownComponent'
import SecondStepComponent from './../components/CollectionDetailsComponent/CollectionDetailsComponent'

// MEMBER
// import MemberFirstStep from './_members/MemberAnalyzeDetailsComponent/MemberAnalyzeDetailsComponent'
// import MemberSecondStep from './_members/MemberBreakdownComponent/MemberBreakdownComponent'
// import MemberThirdStep from './_members/MemberMeansOfPaymentComponent/MemberMeansOfPaymentComponent'

// Components
import CollectionHeader from './../../../Components/CollectionDistribution/CollectionHeader/CollectionHeader'
import PageFooter from './../../../Components/PageFooter/PageFooter'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import { ApproveDialog, DisApproveDialog, VoidDialog } from './../components/ActionDialog'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetFields, UpdateField } from './../../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Styles
import classes from './style.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Mock-data
const mock = require('./../mock-data/collection-distribution.json')

export default function ValidateCollectionDistributionPage (props) {
  // send data in redux store
  const dispatch = useDispatch()

  // Used to reference element
  const refContainer = useRef()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '' } = props.match.params
  const history = useHistory()
  const _formData = useSelector(state => state.CollectionForm)

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Collection Breakdown', 'Collection Details', 'Approve'],
    stepNumber: 1
  })

  const [actionDialog, setActionDialog] = React.useState({ clientDisapprove: false, clientSubmit: false, clientVoid: false })
  const [pageData, setPageData] = React.useState({
    cdf: {},
    client: {},
    selectedFees: {}
  })
  const { isAdvance } = queryString.parse(props.location.search)
  const [advance, setAdvance] = React.useState(isAdvance === 'true')
  const [taxInfo, setTaxInfo] = React.useState({})

  React.useEffect(() => {
    // Scrolls the element to top
    refContainer.current.parentElement.scrollTo(0, 0)
    PageTitleService(null)
    handleGetData()
    dispatch(SetFields({ ...mock }))
  }, [])

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    if ((data_type && data_type) === 'client') {
      switch (stepper.stepNumber) {
        case 1:
          return <FirstStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
        case 2:
          return <SecondStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
        case 3:
          return <SecondStepComponent {...componentProp} isAdvance={advance} taxInfo={taxInfo} />
        default:
          return ''
      }
    } else if ((data_type && data_type) === 'member') {
      // switch (stepper.stepNumber) {
      //   case 1:
      //     return <MemberFirstStep {...componentProp} />
      //   case 2:
      //     return <MemberSecondStep {...componentProp} />
      //   case 3:
      //     return <MemberThirdStep {...componentProp} />
      //   case 4:
      //     return <h4>Step 4</h4>
      //   default:
      //     return ''
      // }
    }
  }

  const handleGetData = () => {
    Promise.all([CollectionCDFServices.getCDF({ data_id, isAdvance: advance })])
      .then(res => {
        const [cdfServiceRes] = res
        const { client = {}, cdf_fees = {}, form = {}, ref = {}, tax_type, adjustments, summary = {} } = cdfServiceRes.data.data
        setPageData(i => ({
          ...i,
          ...cdfServiceRes.data.data,
          reference: ref
        }))
        dispatch(SetFields({ ...form, total_fees: { ...summary } }))

        BreadcrumbsService.setSelectedItemsWithoutHome([
          'collectionDistributionDashboard',
          `listOfClientCDToValidate:${'CLIENT'}`,
          'collectionDistributionToValidate'
        ])

        CollectionCDFServices.getTaxValue(tax_type).then( res => {
          const { data } = res.data
          setTaxInfo(data[0])
        })
      })
      .catch(err => console.error(`Error Fetching CDF: ${err}`))
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handleCDFApprove = _params => {
    LoadingService.loading(true)
    CollectionCDFServices.approve({ data: { cdf_number: pageData.cdf_number, remarks: _params } })
      .then(res => {
        LoadingService.loading(false)
        history.push('/')
      })
      .catch(e => {
        console.error(`Error Approving CDF: ${e}`)
        LoadingService.loading(false)
      })
  }

  const handleCDFDisapprove = _params => {
    LoadingService.loading(true)
    CollectionCDFServices.disapprove({ data: { cdf_number: pageData.cdf_number, remarks: _params } })
      .then(res => {
        LoadingService.loading(false)
        history.push('/')
      })
      .catch(e => {
        console.error(`Error Disapproving CDF: ${e}`)
        LoadingService.loading(false)
      })
  }

  const handleCDFVoid = _params => {
    LoadingService.loading(true)
    CollectionCDFServices.void({ data: { cdf_number: pageData.cdf_number, remarks: _params } })
      .then(res => {
        LoadingService.loading(false)
        history.push('/')
      })
      .catch(e => {
        console.error(`Error Voiding CDF: ${e}`)
        LoadingService.loading(false)
      })
  }

  const handleOnSelectSoaTab = params => {
    let _data = null
    if (!params.data) {
      _data = pageData?.cdf_fees?.cdf_summary || {}
      dispatch(
        UpdateField({
          value: {
            ..._data,
          },
          field: 'formData'
        })
      )
      return false
    }
    _data = pageData?.cdf_fees?.cdf_soa || []
    dispatch(
      UpdateField({
        value: {
          ..._data[params.selected] || {},
        },
        field: 'formData'
      })
    )
  }

  const handleOnCancelClicked = () => {
    history.push(`/`)
  }

  const pageProps = {
    isStepper: true,
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    showCDToValidateBtn: true
  }

  return (
    <div className={classes.root} ref={refContainer}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5' className={classes.bold}>
                Collection Distribution to Validate
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} disabled />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({
                data: pageData.cdf_fees,
                disabled: true,
                handleOnSelectSoaTab
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            {...pageProps}
            handleOnSubmitClicked={() => {
              handleStepChange(stepper.stepNumber + 1)
              setActionDialog(i => ({ ...i, clientSubmit: true }))
            }}
            handleOnDisapproveClicked={() => {
              handleStepChange(stepper.stepNumber + 1)
              setActionDialog(i => ({ ...i, clientDisapprove: true }))
            }}
            handleOnVoidClicked={() => {
              handleStepChange(stepper.stepNumber + 1)
              setActionDialog(i => ({ ...i, clientVoid: true }))
            }}
            handleOnCancelClicked={() => handleOnCancelClicked()}
          />
        </Grid>
      </Grid>
      <ApproveDialog
        open={actionDialog.clientSubmit}
        handleOnApprove={e => handleCDFApprove(e)}
        handleOnClose={e => {
          handleStepChange(stepper.stepNumber - 1)
          setActionDialog(i => ({ ...i, clientSubmit: e }))
        }}
        category={pageData.category}
      />

      <DisApproveDialog
        open={actionDialog.clientDisapprove}
        handleOnDisapprove={e => handleCDFDisapprove(e)}
        handleOnClose={e => {
          handleStepChange(stepper.stepNumber - 1)
          setActionDialog(i => ({ ...i, clientDisapprove: e }))
        }}
      />

      <VoidDialog
        open={actionDialog.clientVoid}
        handleOnVoid={e => handleCDFVoid(e)}
        handleOnClose={e => {
          handleStepChange(stepper.stepNumber - 1)
          setActionDialog(i => ({ ...i, clientVoid: e }))
        }}
      />

    </div>
  )
}
