import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import TextFieldCurrency from '../../TextFieldCurrency/TextFieldCurrency'
import TextField from './../../TextField/CustomTextField'
// Style
import classes from '../style.module.scss'

const CDFGrid = props => {
    const {
      labels,
      data,
      _data,
      breakdownName,
      tax = null,
      disabled,
      UpdateField,
      UpdateFieldData,
      dispatch,
      handleOnSave = () => {} } = props

      const [debounce, setDebounce] = React.useState()
      const [adjustments, setAdjustments] = React.useState({})

      React.useEffect(() => {
        dispatch(
          UpdateField({
            value: {
              ..._data.formData?.adjustments || {},
              soa_number: _data.formData?.soa_number
            },
            field: 'adjustments'
          })
        )
      }, [_data.formData])

      React.useEffect(() => {
        clearTimeout(debounce)
        setDebounce(setTimeout(() => { calculateFees() }, 500))
      }, [_data.adjustments])

      const calculateFees = () => {
        const sub_total = _data.adjustments.credit_memo + _data.adjustments.commission + _data.adjustments.deletion + _data.adjustments.ewt
        const sub_total_tax = _data.adjustments.credit_memo_tax + _data.adjustments.commission_tax + _data.adjustments.deletion_tax
        setAdjustments(i => ({ ...i, sub_total, sub_total_tax }))
      }

      const handleRemarks = _params => {
        const newArr = []
        if (_params && typeof _params !== 'string') {
          _params.map((i, index) => {
            if (i.length > 0) newArr.push(i)
          })
          return `${newArr.length > 0 ? '* ' : ''}${newArr.join('\n* ')}`
        }
        return _params
      }

      const handleTaxFields = (target, val) => {
        if (tax === null) return false
        dispatch(
          UpdateFieldData({
            value: val,
            field: target,
            target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
          })
        )
      }

    return (
      <>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-premium' label={labels.premium} disabled value={_data.formData?.premium || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-premium-tax' label={labels.premium_tax} disabled value={_data.formData?.premium_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-processing-fee' label={labels.processing_fee} disabled value={_data.formData?.processing_fee || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-processing-fee-tax' label={labels.processing_fee_tax} disabled value={_data.formData?.processing_fee_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-dental-rider' label={labels.dental_rider} disabled value={_data.formData?.dental_rider || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-dental-rider-tax' label={labels.dental_rider_tax} disabled value={_data.formData?.dental_rider_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-maternity-rider' label={labels.maternity_rider} disabled value={_data.formData?.maternity_rider || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-maternity-rider-tax' label={labels.maternity_rider_tax} disabled value={_data.formData?.maternity_rider_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-medicare-rider' label={labels.medicare_rider} disabled value={_data.formData?.medicare_rider || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-medicare-rider-tax' label={labels.medicare_rider_tax} disabled value={_data.formData?.medicare_rider_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-other-fees' label={labels.other_fees} disabled value={_data.formData?.other_fees || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-other-fees-tax' label={labels.other_fees_tax} disabled value={_data.formData?.other_fees_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-dns' label={labels.doctor_and_nurses} disabled value={_data.formData?.doctor_and_nurses || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-dns-tax' label={labels.doctor_and_nurses_tax} disabled value={_data.formData?.doctor_and_nurses_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-revolving-fund' label={labels.revolving_fund} disabled value={_data.formData?.revolving_fund || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-revolving-fund-tax' label={labels.revolving_fund_tax} disabled value={_data.formData?.revolving_fund_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-rsd' label={labels.rsd} disabled value={_data.formData?.rsd || 0} />
            </Grid>
            <Grid item xs={3} />
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-naf' label={labels.naf} disabled value={_data.formData?.naf || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-naf-tax' label={labels.naf_tax} disabled value={_data.formData?.naf_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-admin-fee' label={labels.admin_fee} disabled value={_data.formData?.admin_fee || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-admin-fee-tax' label={labels.admin_fee_tax} disabled value={_data.formData?.admin_fee_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-accounts-receivable' label={labels.account_receivable} disabled value={_data.formData?.account_receivable || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-accounts-receivable-tax' label={labels.account_receivable_tax} disabled value={_data.formData?.account_receivable_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-lost-id' label={labels.lost_id} disabled value={_data.formData?.lost_id || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-lost-id-tax' label={labels.lost_id_tax} disabled value={_data.formData?.lost_id_tax || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-philhealth-refund' label={labels.philhealth_refund} disabled value={_data.formData?.philhealth_refund || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-philhealth-refund-tax' label={labels.philhealth_refund_tax} disabled value={_data.formData?.philhealth_refund_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={classes['margin-top']}>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-over-payment' label={labels.over_payment} disabled value={_data.formData?.over_payment || 0} />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency id='breakdown-over-payment-tax' label={labels.over_payment_tax} disabled value={_data.formData?.over_payment_tax || 0} />
            </Grid>
            <Grid item xs={3} />
            <Grid item xs={3} />
          </Grid>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={12}>
              <Typography variant='h5'>Sub Total</Typography>
            </Grid>
            <Grid item xs={4} className={classes['currency-center']}>
              <TextFieldCurrency id='breakdown-sub-total' label='Sub Total Taxable Amount' disabled value={_data.formData?.raw_sub_total || 0} />
            </Grid>
            <Grid item xs={4} className={classes['currency-center']} alignContent='flex-end'>
              <TextFieldCurrency id='breakdown-sub-total-tax' label='Sub Total Tax Amount' disabled value={_data.formData?.raw_sub_total_tax || 0} />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={`${classes['margin-top']} ${classes['bg-gray']}`}>
            <Grid item xs={12} lg={12} style={{ paddingBottom: 0 }}>
              <Typography variant='h5'>{breakdownName} Breakdown (Adjustments)</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container direction='row' justify='center'>
                <Grid item xs={11}>
                  <Grid container spacing={4} className={`${classes['bg-gray']}`}>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-credit-memo' label={'Credit Memo'} disabled={!!(!!disabled || !_data.formData?.soa_number)}
                        onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'credit_memo',
                              target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
                            }),
                            handleTaxFields('credit_memo_tax', taxAmount)
                          )
                        }} 
                        value={_data.adjustments ? _data.adjustments.credit_memo : 0 }
                      />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-credit-memo-tax' label={'Credit Memo Tax'} disabled value={_data.adjustments ? _data.adjustments.credit_memo_tax : 0} />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-deletion' label={'Deletion'} disabled={!!(!!disabled || !_data.formData?.soa_number)}
                        onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'deletion',
                              target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
                            }),
                            handleTaxFields('deletion_tax', taxAmount)
                          )
                        }} 
                        value={_data.adjustments ? _data.adjustments.deletion : 0} 
                      />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-deletion-tax' label={'Deletion Tax'} disabled value={_data.adjustments ? _data.adjustments.deletion_tax : 0 } />
                    </Grid>
                  </Grid>
                  <Grid container spacing={4} className={`${classes['bg-gray']}`}>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-ewt' label={'EWT'} disabled={!!(!!disabled || !_data.formData?.soa_number)}
                        onChange={e => {
                          if (!!disabled) return false
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'ewt',
                              target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
                            })
                          )
                        }}
                        value={_data.adjustments ? _data.adjustments.ewt : 0 }
                      />
                    </Grid>
                    <Grid item xs={3} />
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-commission' label={'Commission'} disabled={!!(!!disabled || !_data.formData?.soa_number)}
                        onChange={e => {
                          if (!!disabled) return false
                          const taxAmount = e.target.value * tax
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'commission',
                              target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
                            }),
                            handleTaxFields('commission_tax', taxAmount)
                          )
                        }}
                        value={_data.adjustments ? _data.adjustments.commission : 0 }
                      />
                    </Grid>
                    <Grid item xs={3}>
                      <TextFieldCurrency id='breakdown-commission-tax' label={'Commission Tax'} disabled value={_data.adjustments ? _data.adjustments.commission_tax : 0} />
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                      <TextField
                        constainerStyle={{ width: 'unset', flex: 1 }}
                        multiline
                        rows={3}
                        direction='row'
                        disabled={!!(!!disabled || !_data.formData?.soa_number)}
                        id='breakdown-remarks'
                        label='REMARKS'
                        value={_data.adjustments ? handleRemarks(_data.adjustments.remarks) : ''}
                        onChange={e => {
                          if (!!disabled) return false
                          dispatch(
                            UpdateFieldData({
                              value: e.target.value,
                              field: 'remarks',
                              target: _data.formData?.soa_number ? 'adjustments' : 'adjustments_summary'
                            })
                          )
                        }}
                      />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid
            container
            direction='row'
            justify='space-between'
            className={`${classes['no-margin']} ${classes['bg-gray']}`}
            spacing={4}
            style={{ paddingBottom: 0 }}
          >
            <Grid item xs={12}>
              <Typography variant='h5'>Sub Total (Adjustments)</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container direction='row' justify='center'>
                <Grid item xs={11}>
                  <Grid container direction='row' justify='space-between'>
                    <Grid item xs={4}>
                      <TextFieldCurrency id='breakdown-adjustments-sub-total' label='Sub Total Taxable Amount' disabled value={adjustments.sub_total || 0} />
                    </Grid>
                    <Grid item xs={4} alignContent='flex-end'>
                      <TextFieldCurrency id='breakdown-adjustments-sub-total-tax' label='Sub Total Tax Amount' disabled value={adjustments.sub_total_tax || 0} />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </>
    )
}

export default CDFGrid