import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cc')
const axios_root = instance('/')

export default class CollectionClientContractServices {
  static getClientContract (params) {
    return axios({
      url: `/client-contract/${params.accountNumber}`,
      method: 'GET',
      params
    })
  }
}
