import React from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import GroupOfCurrency from './../../../../Components/FormComponents/GroupOfCurrency/GroupOfCurrency'

// Utilities
import noop from 'lodash/noop'
import numeral from 'numeral'

// Mock
const mockLabels = require('./mock-data/group-of-labels.json')

export default function SuspensionBreakdown (props) {
  const { soa = [], disabled = false, totalFees = {}, selectedFees = {}, handleOnSelectSoaTab = noop } = props

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h5'>Suspension Reference Breakdown</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-taxable'}
                label={'total taxable amount'}
                text={`PHP ${numeral(totalFees.sub_total).format('0,0.00')}`}
              />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-tax'}
                label={'total tax (vat) amount'}
                text={`PHP ${numeral(totalFees.sub_total_tax).format('0,0.00')}`}
              />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-amount'}
                label={'total amount'}
                text={`PHP ${numeral(totalFees.grand_total || totalFees.sub_total || 0).format('0,0.00')}`}
              />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1rem' }}>
        <Grid container spacing={2}>
          <Grid item xs>
            <Grid container spacing={2}>
              <FormSlider
                showForCollection
                showSummary
                details={soa}
                data={totalFees}
                OnChanges={e => handleOnSelectSoaTab(e.data)}
              />
            </Grid>
          </Grid>
        </Grid>
        {/* GROUP OF CURRENCY */}
        <Grid item xs={12} style={{ marginTop: '1.5rem' }}>
          <GroupOfCurrency labels={mockLabels} data={selectedFees} disabled={disabled} />
        </Grid>
      </Grid>
    </Grid>
  )
}
