import React from 'react'
import { useHistory } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Components
import Void from '../Void/Void'

// styles
import classes from './ContentPageComponent.module.scss'

export default function ContentPage (props) {
  const { shift, soaPage, getStepContent, activeStep, nextStep, stepBack, content, voided, setActiveStep } = props
  const history = useHistory()

  const [openDialog, setOpenDialog] = React.useState(false)

  const handleVoid = () => {
    setOpenDialog(true)
  }

  const handleSave = () => {
    history.push('/')
  }

  return (
    <div className={`${classes.root} ${shift ? classes.contentShift : ''}`}>
      {soaPage ? (
        <div>
          <div>
            {activeStep === 4 ? (
              <Typography className={classes.instructions}>{getStepContent({ stepIndex: 3, activeStep })}</Typography>
            ) : (
              <Typography className={classes.instructions}>{getStepContent({ stepIndex: activeStep })}</Typography>
            )}
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${activeStep}-prev-button`}
                onClick={stepBack}
                className={classes['button-negative']}
              >
                {activeStep === 0 ? 'Cancel' : 'Back'}
              </Button>
              <div className={classes['item-right-side']}>
                {activeStep === 3 || activeStep === 4 ? (
                  <Button
                    variant='contained'
                    id={'void-button'}
                    onClick={handleVoid}
                    className={classes['button-void']}
                  >
                    Void
                  </Button>
                ) : (
                  <Button
                    variant='contained'
                    id={'save-button'}
                    onClick={handleSave}
                    className={classes['button-positive']}
                  >
                    Save
                  </Button>
                )}
                &emsp;&emsp;
                <Button
                  variant='contained'
                  id={`${activeStep}-next-button`}
                  className={classes['button-positive']}
                  onClick={nextStep}
                >
                  {activeStep === 3 || activeStep === 4 ? 'Approve' : 'Next'}
                </Button>
              </div>
            </div>
          </div>
        </div>
      ) : content != null ? (
        content
      ) : (
        'Test'
      )}
      {openDialog === true ? (
        <Void
          page={'soa'}
          openDialog={openDialog}
          setOpenDialog={setOpenDialog}
          setActiveStep={setActiveStep}
          submitFunction={voided}
        />
      ) : (
        ''
      )}
    </div>
  )
}
