import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Store 
import { useDispatch } from 'react-redux'

//  Hooks
import useForm from 'Hooks/useForm'

// Components
import PageFooter, { ActionButton } from 'SharedComponents/PageFooter'
import Checkbox from './../../../Components/FormComponents/Checkbox/Checkbox'
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'
import GenerateDialog from './components/GenerateDialog'

// Services
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import LetterTemplateServices from 'Services/Api/CollectionMaintenance/LetterTemplateServices'
import ReminderLetterServices from 'Services/Api/CollectionMaintenance/ReminderLetterServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'

// Utilities
import pick from 'lodash/pick'
import upperFirst from 'lodash/upperFirst'
import truncate from 'lodash/truncate'
import moment from 'moment'

// Styles
import classes from './LetterTemplate.module.scss'

export default function LetterTemplate() {
  const dispatch = useDispatch()
  const history = useHistory()

  const generateForm = useForm({
    contract_id: ''
  })

  const [data, setData] = React.useState({
    header: HEADERS,
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: 'date_updated',
    page: 1,
    sort: '',
    search: ''
  })

  const [contracts, setContracts] = React.useState([])
  const [activeTemplate, setActiveTemplate] = React.useState(null)
  const [generating, setGenerating] = React.useState(false)

  function handleGenerateClose() {
    setGenerating(false)
  }

  function handleGenerateSubmit() {
    LoadingService.loading(true)

    ReminderLetterServices.store({
      ...generateForm.values,
      letter_template_id: activeTemplate
    })
      .then(response => {
        LoadingService.loading(false)

        dispatch(
          SnackbarService.set({
            open: true,
            title: 'Success!',
            message: 'Reminder letter generated',
            severity: 'success'
          })
        )

        const reminderLetter = response.data.data
        history.push(`/collection/generate-reminder-letter/${reminderLetter.letter_number}`)
      })
      .catch(error => {
        const { data } = error.response

        if (data.message) {
          generateForm.setErrors(data.message)
        }

        LoadingService.loading(false)
      })
  }

  function handleGenerateClicked(templateId) {
    LoadingService.loading(true)

    BillingAnalystServices.getAllClientContract()
      .then(response => {
        setContracts(response.data.data)
        setActiveTemplate(templateId)
        setGenerating(true)

        LoadingService.loading(false)
      })
      .catch(error => {
        LoadingService.loading(false)
      })
  }

  function handleSearchChanged(search) {
    fetchData({ search })
  }

  function fetchData(params = {}) {
    LoadingService.loading(true)

    LetterTemplateServices.index({
      ...pick(data, ['limit', 'page', 'range', 'rangeTarget', 'filter']),
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      ...params
    })
      .then(res => {
        const { count, limit, page, results } = res.data.data
        setData(prevState => ({
          ...prevState,
          count,
          body: results,
          limit,
          page,
        }))
        LoadingService.loading(false)
      })
      .catch(err => {
        console.log(`Error fetching letter template list: ${err}`)
        LoadingService.loading(false)
      })
  }

  React.useEffect(() => {
    fetchData()
  }, [])

  return (
    <React.Fragment>
      <Grid item xs={12}>
        <Grid container className={classes.search}>
          <Grid item xs>
            <SearchTextComponent
              placeholder='Search from the list of letter templates'
              displaySettings={false}
              value={data.search}
              onChange={handleSearchChanged}
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <Grid container>
          <Grid item xs>
            <Table
              hasLink
              data={data}
              onDataListChange={fetchData}
              setData={setData}
              showTextField
              body={template =>
                template.map((template, index) => (
                  <TableRow key={`template-letter-table-row-${index}`}>
                    <TableCell id={`template-letter-table-cell-letter-type-${index}`}>
                      {upperFirst(template.letter_type)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-template-name-${index}`}>
                      {template.name}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-entity-type-${index}`}>
                      {upperFirst(template.entity_type)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-description-${index}`}>
                      {truncate(template.description)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-author-${index}`}>
                      {`${template.author ? template.author?.first_name + ' ' + template.author?.last_name : ''}`}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-last-updated-${index}`}>
                      {moment(template.date?.updated).format('ll')}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-published-${index}`}>
                      <Checkbox checked={template.date?.published} />
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-action-${index}`}>
                      <Link onClick={() => handleGenerateClicked(template._id)}>
                        <Typography
                          id={`template-letter-view-${index}`}
                          variant='caption'
                          className='text-link underline'
                        >
                          Generate
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <PageFooter
          onCancel={() => history.push('/')}
          actions={[
            <ActionButton onClick={() => history.push('/collection/maintenance/create-letter-template')}>
              Create Letter Template
            </ActionButton>
          ]}
        />
      </Grid>

      <GenerateDialog
        open={generating}
        contracts={contracts}
        form={generateForm}
        onSubmit={handleGenerateSubmit}
        onClose={handleGenerateClose}
      />
    </React.Fragment>
  )
}

const HEADERS = [
  {
    id: 'as-letter-type',
    numeric: false,
    disablePadding: false,
    name: 'Letter Type',
    sortField: 'letter_type',
    match: 'any'
  },
  {
    id: 'as-name',
    numeric: false,
    disablePadding: false,
    name: 'Template Name',
    sortField: 'name',
    match: 'any'
  },
  {
    id: 'as-entity-type',
    numeric: false,
    disablePadding: false,
    name: 'Category',
    sortField: 'entity_type',
    match: 'any'
  },
  {
    id: 'as-description',
    numeric: false,
    disablePadding: false,
    name: 'Description',
    sortField: 'description',
    match: 'any'
  },
  {
    id: 'as-author',
    numeric: false,
    disablePadding: false,
    name: 'Author',
    sortField: 'author',
    match: 'any'
  },
  {
    id: 'as-last-update',
    numeric: false,
    disablePadding: false,
    name: 'Last Update',
    sortField: 'date_updated',
    match: 'any'
  },
  {
    id: 'as-published',
    numeric: false,
    disablePadding: false,
    name: 'Published',
    sortField: 'published',
    match: 'any'
  },
  {}
]
