import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'

import {
  Grid, Typography, TableCell,
  TableRow
} from '@material-ui/core'
import _ from 'lodash'
import moment from 'moment'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import SearchTextComponent from '../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import AgingSummaryBar from '../../Components/AgingSummaryBar/AgingSummaryBar'
import SOAModal from '../../Components/SOAModal/SOAModal'

import ListView from './components/ListView'
import CreateOptions from './components/CreateOptions'

import AgingSummaryServices from '../../../../../Services/Api/AgingSummaryServices'
import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'
import CollectionClientContractServices from 'Services/Api/CollectionClientContractServices'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import CollectionPenaltyServices from 'Services/Api/CollectionPenaltyServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

import RBAC from 'Security/RBAC'
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import style from './AgingSummaryClientPage.module.scss'

import { ViewAllListConfig } from './config/config.js'

export default function AgingSummaryClientPage(props) {
  const { accountNumber = '7409-5462-2115' } = props.match.params
  const history = useHistory()

  const initialValue = {
    category: '',
    count: 0,
    search: '',
    limit: 10,
    body: [],
    page: 1,
    filters: null,
    data: [],
    orderBy: 'soa_number',
    order: false,
    sort: '',
    range: ''
  }

  const initalSummaryBarValue = {
    total_collectibles: 0,
    ageSummary: [
      { name: "1 - 45 Days", total_amount_due:  0 },
      { name: "46 - 75 Days", total_amount_due: 0 }, 
      { name: "76 - 105 Days", total_amount_due: 0 }, 
      { name: "106 - 136 Days", total_amount_due: 0 }, 
      { name: "137 - 166 Days", total_amount_due: 0 }, 
      { name: "167 - 196 Days", total_amount_due: 0 }, 
      { name: "Over 197 Days", total_amount_due: 0 }, 
    ]
  }

  const [soaList, setSoaList] = useState(initialValue)
  const [soaDetails, setSoaDetails] = useState({details: [], data: [], soaNumber:''})
  const [showModal, setShowModal] = useState(false)
  const [selectedSoa, setSelectedSoa] = useState({})
  const [summaryBarData, setSummaryBarData] = useState(initalSummaryBarValue)
  const [params, setParams] = useState({})
  const [showCreateOptions, setShowCreateOptions] = useState(false)
  const [noOfSelectedSoa, setNoOfSelectedSoa] = useState(0)
  const [checkedList, setCheckedList] = useState({})

  const handlePregenerateSuspensionEndorsement = () => {
    if (selectedSoa.length === 0) {
      return
    }

    LoadingService.loading(true)

    CollectionSuspensionServices.store({ accountNumber, soas: Object.keys(selectedSoa) })
      .then(response => {
        LoadingService.loading(false)
        history.push(`/collection/suspension/endorse/client/${response.data.data}`)
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error(error)
      })
  }

  const handlePregeneratePenaltyEndorsement = () => {
    if (selectedSoa.length === 0) {
      return
    }

    LoadingService.loading(true)

    CollectionPenaltyServices.store({ accountNumber, soa: Object.keys(selectedSoa) })
      .then(response => {
        LoadingService.loading(false)
        history.push(`/collection/penalty/client/${response.data.data}`)
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error(error)
      })
  }

  const handlePregenerateCDF = () => {
    LoadingService.loading(true)
    const _soa = []
    Object.keys(selectedSoa).map(i => {
      if (selectedSoa[i]) {
        _soa.push(i)
      }
    })
    // Will not proceed if there's no soa selected
    if (_soa.length === 0) {
      LoadingService.loading(false)
      return false
    }

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: _soa } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const handleAdvancePregenerateCDF = () => {
    LoadingService.loading(true)

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, transactionType: 'Advance Collection Distribution' } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}?isAdvance=true`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const handleInlinePregenerateCDF = params => {
    LoadingService.loading(true)

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: [params] } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const onCheckboxChanged = params => {
    const { soa_number, value } = params
    setSelectedSoa(i => ({ ...i, [soa_number]: value }))
  }


  const handleGroupSelect = (allSelected) =>{
    const selectAllSoa = {}
    
    soaList.body.map(soa=>selectAllSoa[soa.soa_number] = true)

    if (allSelected) {
      setCheckedList(selectAllSoa); 
      setSelectedSoa(selectAllSoa);
    }
    else{
      setCheckedList({})
      setSelectedSoa({})
    }

  }

  const fetchData = (params) => {

    const _params = {
      limit: soaList.limit,
      page: soaList.page,
      sort: `${soaList.orderBy}|${soaList.order ? 'asc' : 'desc'}`,
      filters: soaList.filters,
      search: soaList.search,
      range: soaList.range,
      ...params,
    }
    setParams(_params)

    Promise.all([
      AgingSummaryServices.getClientSOAList({ accountNumber, params: _params }),
      CollectionClientContractServices.getClientContract({ accountNumber }),
      AgingSummaryServices.getSummaryBarData({ accountNumber, type: 'client', params: _params })])
      .then(res => {
        const [data, contract, summary] = res
        let { results, count, limit, page } = data.data.data
        const { client } = contract.data.data
        const summaryData = summary.data.data

        results = results.length > 0 ? results : []
        count = count > 0 ? count : 0

        PageTitleService(`${client.account_name || ''} - Aging Summary`)

        BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'listOfAccountsWithAgingSummary', `clientAgingSummary:${client.account_name || ''}`])

        setSoaList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: _params.orderBy ? _params.orderBy : soaList.orderBy, search: _params.search, filters: _params.filters, range: _params.range }))

        setSummaryBarData(summaryData)

      })
      .catch(error => {
        console.error(`Error Fetching SOA(s): ${error}`)
      })
  }

  const fetchSoaDetails = ({soa_number}) => {
    Promise.all([AgingSummaryServices.getSOADetails({soaNumber: soa_number})])
    .then(res => {
      const [ data ] = res

      const total_amount_due = data.data.data.map(row => row.total_amount).reduce((sum, totalAmount) => sum + totalAmount, 0)

      const period_start = data.data.data[0].contract_effectivity
      const period_end = data.data.data[0].contract_expiration

      setSoaDetails({ details: [
        {
          label: 'Account No:',
          value: accountNumber
        },
        {
          label: 'Period Covered',
          value: `${DateFormatterByAbbreviation(period_start)} - ${DateFormatterByAbbreviation(period_end)}`
        },
        {
          label: 'Amount Due',
          value: `PHP ${CurrencyFormatter(total_amount_due)}`
        }
      ], 
      data: data.data.data,
      soaNumber: soa_number
    })

    })
    .catch(error => {
      console.error(`Error Fetching SOA Details: ${error}`)
    })

    setShowModal(true)
  }

  const handleModalClose = () => {
    setShowModal(false)
  }

  useEffect(() => {
    fetchData()
  }, [])

  const onSearchChange = _.debounce((value) => {
    const params = {
      search: value,
      accessors: ['soa_date|isDate', 'soa_number', 'due_date|isDate', 'days_overdue', 'total_amount_due'],
      page: 1,
    }
    fetchData(params)
  }, 300)

  const onDisplaySettingsChange = (value) => {
    fetchData({ range: value })
  }
  
  useEffect(()=>{
    const soaSelected = Object.values(selectedSoa).some((value)=>value)
    const noOfSelected = Object.values(selectedSoa).filter((value)=>value)
    
    setNoOfSelectedSoa(noOfSelected.length)

    if (soaSelected) setShowCreateOptions(true)
    else setShowCreateOptions(false)
    
  }, [selectedSoa])

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <SearchTextComponent
              id='aging-summary-search'
              placeholder="Search List of Accounts with Aging Summary"
              displaySettingsTitle='Due Date Range *Date Range*'
              startDaterErrorMessage = 'Please enter a valid due start date'
              endDaterErrorMessage = 'Please enter a valid due end date'
              validEndDaterErrorMessage = 'Please enter a valid due end date that is not later than the current date'
              errorMessage={{date_start: ' ', date_end: ' '}}
              exportFiles={true}
              displaySettings={true}
              onChange={onSearchChange}
              handleOnDisplaySettingsApplyClicked={onDisplaySettingsChange}
            />
          </Grid>
          <Grid item xs={12} >
            <Grid container>
              <Grid item xs={12}>
                <AgingSummaryBar summaryBarData={summaryBarData} />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12} >
            <Grid container>
            {RBAC.hasPolicy('BC-068') && (
              <Grid item xs={12} style={{minHeight: 15}}>
                {showCreateOptions && (
                  <CreateOptions 
                    handlePregenerateCDF={handlePregenerateCDF} 
                    handlePregenerateSuspensionEndorsement={handlePregenerateSuspensionEndorsement}
                    handlePregeneratePenaltyEndorsement={handlePregeneratePenaltyEndorsement}
                  />)
                }
                {!showCreateOptions && (
                  <CreateOptions 
                    handleAdvancePregenerateCDF={handleAdvancePregenerateCDF}
                  />
                )}
              </Grid>
            )}
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={12} style={{ width: 'fit-content', overflow: 'auto', boxShadow: "1px 2px 5px #ddd" }}>
            <ListView
              id={'audit-log-view-all-list'}
              config={ViewAllListConfig}
              contents={soaList.body}
              count={soaList.count}
              list={soaList}
              checkedList={checkedList}
              onCheckboxChanged={onCheckboxChanged}
              numberOfItemsSelected={noOfSelectedSoa}
              fetchData={fetchData}
              handleShowSoaDetails={fetchSoaDetails}
              handlePregenerateCDF={handlePregenerateCDF}
              handleInlinePregenerateCDF={handleInlinePregenerateCDF}
              handleGroupSelect={handleGroupSelect}
              params={params}
            />
          </Grid>
        </Grid>
      </Grid>
      <SOAModal handleClose={handleModalClose} open={showModal} soa={soaDetails}/>
    </Grid>
  )
}
