import React from 'react';

/**
 * @param {Object.<string, any>} defaultValues
 * @return {{ values: Object.<string, any>, setValues: Function, updateValue: Function, errors: Object.<string, any>, setErrors: Function, onChange: Function, reset: Function }}
 */
export default function useForm(defaultValues) {
  const [values, setValues] = React.useState(defaultValues);
  const [errors, setErrors] = React.useState(null)

  function reset() {
    setValues(defaultValues)
  }

  function forgetError(key) {
    setErrors(prevErrors => {
      if (prevErrors === null) {
        return prevErrors
      }

      if (prevErrors.hasOwnProperty(key)) {
        delete prevErrors[key]
      }

      return prevErrors
    })
  }

  function updateValue(key, value) {
    setValues(values => ({
      ...values,
      [key]: value,
    }));
  }

  function handleChange(event) {
    event.persist();

    updateValue(
      event.target.name,
      event.target.type === 'checkbox' ? event.target.checked : event.target.value
    );

    forgetError(event.target.name)
  }

  return { values, setValues, updateValue, onChange: handleChange, errors, setErrors, reset };
}