import React, { useState, useEffect } from 'react'

import { 
    Grid, 
    Typography 
} from '@material-ui/core'

import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import FirstStepComponent from './components/StepAnalyzeDetails'
import SecondStepComponent from './components/StepPenaltyBreakdown'
import ThirdStepComponent from './components/StepPenaltyDetails'
import PageFooter from './../../Components/PageFooter/PageFooter'

import CollectionPenaltyServices from 'Services/Api/CollectionPenaltyServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'

import classes from './PenaltyEndorsementPage.module.scss'

export default function PenaltyEndorsementPage(props) {
    const { penalty_number = null, data_type = '' } = props.match.params

    const [pageTitle, setPageTitle] = useState('Endorse Account for Penalty')

    const [stepper, setStepper] = useState({
        showActiveLabel: true,
        stepDetails: ['Analyze Details', 'Penalty Breakdown', 'Penalty Details', 'Submit'],
        stepNumber: 1
    })

    const [pageData, setPageData] = useState({
        client: {},
        penalty_number: null,
        penalty_soa: [],
        availableSoa: [],
        penalty_fees: {},
        selectedFees: []
    })

    const showComponent = componentProp => {
        // TODO
        switch (stepper.stepNumber) {
          case 1:
            return <FirstStepComponent {...componentProp} />
          case 2:
            return <SecondStepComponent {...componentProp} />
          case 3:
            return <ThirdStepComponent {...componentProp} />
          case 4:
            return <h4>Step 4</h4>
          default:
            return ''
        }
    }

    const getData = () => {
        LoadingService.loading(true)

        Promise.all([CollectionPenaltyServices.show(penalty_number, {}, data_type)])
          .then(response => {
    
            LoadingService.loading(false)
            
            const [penaltyEndorsementResponse] = response
            const { client = {}, penalty_fees = {}, status } = penaltyEndorsementResponse.data.data

            // if (status !== 'pending') {
            //   dispatch(
            //     SnackbarService.set({ 
            //       open: true, 
            //       message: 'Invalid penalty status',
            //       severity: 'error'
            //     })
            //   )
    
            //   history.push('/')
            // }
    
            setPageData(data => ({ 
              ...data, 
              ...penaltyEndorsementResponse.data.data,
              selectedFees: penalty_fees?.penalty_summary || {}
            }))
    
            BreadcrumbsService.setSelectedItems([
              'collectionDistributionDashboard',
              'listOfAccountsWithAgingSummary',
              `clientAgingSummary:${client.account_name || ''}`,
              'endorseAccountForPenalty'
            ])
          })
          .catch(error => {
            LoadingService.loading(false)
            console.error('Error fetching Penalty: ', error)
        })
    }

    const handleOnSelectSoaTab = (soaNumber = null) => {
      LoadingService.loading(true)
  
      CollectionPenaltyServices.getFees(pageData.penalty_number, {
        soa_number: soaNumber
      })
      .then(res => {
        LoadingService.loading(false)
        const selectedFees = res.data.data.length > 0 ? res.data.data.pop() : {}

        setPageData(prevState => ({ ...prevState, selectedFees: selectedFees.penalty_summary || {} }))
      })
      .catch(err => {
        console.error(`Error fetching SOA Fees: ${err}`)
        LoadingService.loading(false)
      })
    }
      
    const handlePageTitleChange = pageTitle => {
        setPageTitle(pageTitle)
    }
        
    const handleStepChange = stepNumber => {
        setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
    }
    
    const pageProps = {
        handleStepChange: handleStepChange,
        stepNumber: stepper.stepNumber,
        stepCount: stepper.stepDetails.length,
        showEndorsedAccountPenaltyBtn: true
    }

    const handleOnAddSOAClicked = params => {
        CollectionPenaltyServices.getAvailableSoa(penalty_number,{
          penalty_number: pageData.penalty_number
        })
          .then(res => {
            const { count, results } = res.data.data

            // setPageData(i => ({ ...i, availableSoa: results }))
          })
          .catch(err => console.error(`Error Fetching Available SOA(s): ${err}`))
    }

    const handleOnRemoveSoa = e =>{
        console.log(e)
    }
        
    useEffect(() => {
        getData()
    }, [])

    return (
        <div className={classes.root}>
            <Grid
                container
                className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
                spacing={2}
                direction='column'
                justify='space-between'
            >
                <Grid item xs={12}>
                <Grid container>
                    <Grid item className={classes['grid-item']}>
                    <Typography id='collection-distribution-page-header' variant='h5'>
                        {pageTitle}
                    </Typography>
                    </Grid>
                    <Grid item className={classes['grid-item']}>
                        <FormStepper {...stepper} />
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item xs={12}>
                    {showComponent({ 
                        handlePageTitleChange,
                        handleOnRemoveSoa, 
                        handleOnAddSOAClicked,
                        handleOnSelectSoaTab,
                        soa: pageData.penalty_soa,
                        members: pageData.penalty_soa.map(item=>item.member)[0],
                        client: pageData.client,
                        penalty_number: pageData.penalty_number,
                        availableSoa: pageData.availableSoa,
                        totalFees: pageData.penalty_fees?.penalty_summary || {},
                        selectedFees: pageData.selectedFees,
                    })}
                    </Grid>
                </Grid>
                </Grid>

                <Grid item xs={12}>
                <PageFooter {...pageProps} />
                </Grid>
            </Grid>
        </div>
    )
}
