import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import ViewAll from '../../ViewAll2/ViewAll'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'

// Style
import classes from './BillingCycleSetupTile.module.scss'

// Utilities
import moment from 'moment'

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    {value: 'today', text: 'Today'},
    {value: 'week', text: 'This Week'},
    {value: 'month', text: 'This Month'}
  ],
  default: 'month'
}
const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const BillingCycleSetupTile = props => {
  const { services, sizes } = props

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [isFetching, setIsFetching] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  // Initialize
  React.useEffect(() => {
    fetchTileData({ range: rangeDate['month']})
  }, [])

  const fetchData = (params) => {
    setIsFetching(true)
    const currentUserId = localStorage.getItem('user_id')
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    setParams(_params)

    services.getClientsAssigned({..._params, id: currentUserId}).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetching(false)
    })
  }

  const fetchTileData = (params) => {
    const currentUserId = localStorage.getItem('user_id')
    services.getClientsAssignedTile({...params, id: currentUserId}).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setTileList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }
  
  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Assign Billing Cycle Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Client Transaction Journals To Setup'}
        subTitle={'(Assign Billing Cycle)'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableBody className={classes.root}>
            {tileList.body ? tileList.body.map((item, index) => (<TableRow>
              <TableCell style={{paddingLeft: '6px'}}>{TruncateString(item.client.account_name, 50)}</TableCell>
              <TableCell align={'right'} style={{paddingRight: '0'}}>
                <Link to={`/clients/analyze/${item._id}/${item.client.account_number}`}>
                  <Typography variant='caption' className='text-link underline'>Setup</Typography>
                </Link>
              </TableCell>
            </TableRow>
            )) : null}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-transaction-ledgers-to-setup-modal'}
        config={config}
        tile={'clientTransactions'}
        type='Setup Billing Cycle'
        title='List of Client Transaction Journals to Setup'
        placeholder='Search List of Client Transaction Journals to Setup'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        accessors={['client.account_number', 'client.account_name', 'contract.type']}
        isFetching={isFetching}
      />
    </React.Fragment>
  )
}

BillingCycleSetupTile.displayName = 'DashboardTileComponent'

export default BillingCycleSetupTile
