import React from 'react'

import { Link } from 'react-router-dom'
import { Typography, TextField } from '@material-ui/core'

import Button from '@material-ui/core/Button'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import * as moment from 'moment'

import FormDrawer from '../../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../../Components/EnhancedSearchComponent/EnhancedSearchComponent'
import classes from '../InvoicePendingPage.module.scss'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

export default function SelectClientContract (props) {
  const { accountNumber, clientId, handleEntitySearch, handleShowEntity, errorMessage, selectedEntity, setSelectedEntity } = props

  const [clientContractList, setClientContractList] = React.useState([])

  React.useEffect(() => {
    Promise.all([BillingAnalystServices.getAllClientContract()]).then(values => {
      let tempClientInfoList = []
      for (const clientContract of values[0].data.data) {
        if (clientContract.billing.cycle.code) {
          let date = moment(clientContract.contract.effectivity).format('MMM DD, YYYY') + ' to ' + moment(clientContract.contract.expiration).format('MMM DD, YYYY')
          let clientInfo = {
            client_info_no: clientContract.client.account_number,
            name: clientContract.client.account_name,
            date: date
          }
          tempClientInfoList.push(clientInfo)
        }
      }
      setClientContractList(tempClientInfoList)
    })
  }, [])

  return (
    <div>
      <HeaderDetails>
        <Typography className={classes['header-details-create-transaction']} variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'595px'}
        drawerHeaderContent={
          <div>
            <Typography id='drawer-header-search'
              style={{'font-weight': 'bolder'}}
            >SEARCH ID</Typography>
          </div>
        }
        drawerContent={
          <div>
            {/* <TextField className={classes['search-field']} id='outlined-basic' variant='outlined'
              onChange={e => handleEntitySearch(e)}
            />
            <Button id='search-button' variant='contained' className={classes['search-button']}
              onClick={() => handleShowEntity()}
            >
              Search
            </Button> */}
            <EnhancedSearchComponent
              handleEntitySearch={handleEntitySearch}
              handleShowEntity={handleShowEntity}
              setSelectedEntity={setSelectedEntity}
              clientContractList={clientContractList}
              width={'290px'} />
            <Typography style={{color: 'red', 'text-align': 'right'}}>
              {errorMessage}
            </Typography>
            <Link
              id='advanced-search-link'
              className={classes['advanced-search-link']}
              // to='/'
              color='primary'
              size='medium'
            >
            Advanced Search
            </Link>
          </div>
        }
      >

        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='cancel-link' to={`/`} className={classes['cancel-link']}>
              <Button id='cancel-button' variant='contained' className={classes['cancel-button']}>
              Cancel
              </Button>
            </Link>
          </div>
        </div>
      </FormDrawer>

      {/* <div className={classes['button-wrapper']}>
      <div className={classes.button}>
        <Link id='close-link' to={`/`} className={classes['close-link']}>
          <Button id='close-button' variant='contained' className={classes['close-button']}>
            Close
          </Button>
        </Link>
      </div>
    </div> */}
    </div>
  )
}
