import 'date-fns'
import React from 'react'
import MomentUtils from '@date-io/moment'
import Typography from '@material-ui/core/Typography'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'

import classes from './DatePicker.module.scss'

const moment = require('moment')

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardDatePicker
 */
export default function DatePicker (props) {
  const {
    id,
    label,
    required,
    value = null,
    format = null,
    disableComponents = false,
    readOnly,
    onChange,
    orientation = '',
    errorMessage = null,
    placeholder = null,
    ...other
  } = props

  const theme = createMuiTheme({
    overrides: {
      MuiInput: {
        underline: {
          '&&&&:hover:before': {
            borderBottom: 'none'
          },
          '&&&&:before': {
            borderBottom: 'none'
          },
          '&&&&:after': {
            borderBottom: 'solid 1px rgba(0,0,0,.2)'
          },
          '&&&&:focus:before': {
            borderBottom: 'none'
          },
          '&&&&:focus:after': {
            borderBottom: 'none'
          }
        }
      }
    }
  })

  const handleDateChange = date => {
    if (typeof onChange === 'function') onChange(date)
  }

  return (
    <div className={`${classes.root} ${classes[orientation]}`}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {DateFormatter(props.value)}
          </Typography>
        ) : (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <MuiThemeProvider theme={theme}>
              <KeyboardDatePicker
                className={classes.container}
                clearable
                disabled={!!disableComponents}
                value={value}
                placeholder={placeholder || 'New Date'}
                onChange={date => handleDateChange(date)}
                format={format || 'MM/DD/YYYY'}
                invalidDateMessage={errorMessage || 'Invalid Date Format'}
                inputVariant='standard'
                {...other}
              />
            </MuiThemeProvider>
          </MuiPickersUtilsProvider>
        )}
      </div>
    </div>
  )
}
