import React, { useEffect, useState } from 'react'
import classes from './FormStepper.module.scss'

/**
 * Form Stepper
 * @param showActiveLabel Boolean: Highlight label of active step
 * @param stepDetails Array: Array of steps in string
 * @param stepNumber Int: Active step number
 * @param onStepChanged Flag: Initiate re-render of component
 */
const FormStepper = ({ showActiveLabel = false, stepDetails, stepNumber, onStepChanged = 0 }) => {
  let booleanTrue = true
  const [steps, setSteps] = useState([])

  useEffect(() => {
    const stepState = stepDetails.map((itemStep, index) => {
      const stepObj = {}
      stepObj.details = itemStep
      stepObj.completed = false
      stepObj.bold = index === 0 ? booleanTrue : false
      stepObj.selectedStep = index === 0 ? booleanTrue : false
      return stepObj
    })
    const currentSteps = updateCurrentSteps(stepState, stepNumber - 1)
    setSteps(currentSteps)
  }, [onStepChanged])

  const updateCurrentSteps = (stepState, stepNumber) => {
    const newStepNumber = [...stepState]
    let counter = 0
    while (counter < newStepNumber.length) {
      if (counter === stepNumber) {
        newStepNumber[counter] = {
          ...newStepNumber[counter],
          bold: true,
          selectedStep: true,
          completed: false
        }
        counter++
      } else if (counter < stepNumber) {
        newStepNumber[counter] = {
          ...newStepNumber[counter],
          bold: false,
          selectedStep: true,
          completed: true
        }
        counter++
      } else {
        newStepNumber[counter] = {
          ...newStepNumber[counter],
          bold: false,
          selectedStep: false,
          completed: true
        }
        counter++
      }
    }
    return newStepNumber
  }

  return (
    <div className={classes.formContainer}>
      <div className={classes['stepper-container']}>
        {steps.map((items, index) => {
          return (
            <React.Fragment key={index}>
              <div className={classes['step-wrapper-container']}>
                <div className={`${items.selectedStep ? classes.stepNumberActive : classes.stepNumber} ${showActiveLabel ? classes.highlight : ''}`} />
                <div className={`${items.bold ? classes.stepDetailsActive : classes.stepDetails} ${showActiveLabel ? classes.highlight : ''}`}>{items.details}</div>
                {index !== stepDetails.length - 1 ? <div className={`${classes['step-divider']} ${showActiveLabel && index < (stepNumber - 1) ? classes.highlight : ''}`} /> : ''}
              </div>
            </React.Fragment>
          )
        })}
      </div>
    </div>
  )
}

export default FormStepper
