export default function AnalyzeDetails(props) {
    const { 
        soa,
        members,
        client,
        penalty_number,
        availableSoa,
        handleOnAddNewSOA = noop,
        handleOnAddSOAClicked = noop, 
        handlePageTitleChange = noop,
        handleOnRemoveSoa = noop,
    } = props

    const muiClasses = useStyles()

    const initialValue = {
      body: [],
      count: 0,
      filters: {},
      header: [],
      limit: 10,
      order: true,
      orderBy: 'hc',
      page: 1,
      sort: '',
    }

    const [penaltyMembersList, setPenaltyMembersList] = useState(initialValue)
    const [slider, setSlider] = useState({ count: 0, cut: 3, index: 0, selected: 0 })
    const [anchorEl, setAnchorEl] = useState(null)
    const [currentMembers, setCurrentMembers] = useState([])
    const [params, setParams] = useState({})


    const handleOnFetchMembers = e =>{
        const currentSOAMembers = (soa.filter(item=>item.soa_number === e)[0] || {member: []}).member
        setCurrentMembers(currentSOAMembers)
    }

    const handleOnChanges = params => {
        setSlider(prevState => ({ ...prevState, ...params }))
    }

    const onClickBtnSOA = e => {
        setAnchorEl(e.currentTarget)
        handleOnAddSOAClicked()
    }

    const onCloseBtnSOA = e => {
        setAnchorEl(null)
    }

    const onListViewFilterChange = () => {

    }
    
    return (
      <Grid container spacing={2}>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'pn-no'} item>
              <DetailTile id={'pn-no'} label={'pn no.'} text={penalty_number} />
            </Grid>
            <Grid key={'account-name'} item>
              <DetailTile label={'account name'} text={client.account_name} />
            </Grid>
            <Grid key={'account-no'} item>
              <DetailTile label={'account no.'} text={client.account_number} />
            </Grid>
            <Grid key={'account-tin'} item>
              <DetailTile label={'account tin'} text={client.tin} />
            </Grid>
            <Grid key={'broker'} item>
              <DetailTile label={'broker'} text={client.broker} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Typography variant='h5'>Penalty Reference</Typography>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={12} md={10} lg={10}>
              <Grid container>
                  <FormSlider 
                      activeSlider={0} 
                      details={soa} 
                      OnChanges={e => handleOnChanges(e)} 
                      OnRemove={e => handleOnRemoveSoa(e)} 
                  />
              </Grid>
            </Grid>
            <Grid item xs={12} sm={12} md={2} lg={2}>
              <Grid container justify='flex-end' style={{ height: '100%' }}>
                <Grid item>
                  <SoaMenu
                    details={availableSoa || []}
                    onAddClicked={e => {
                      handleOnAddSOAClicked(e)
                    }}
                    onSelect={e => {
                      handleOnAddNewSOA(e)
                    }}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} className={`${classes['list-view']}`}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <ListView
                id={'penalty-endorsement-members-list'}
                config={ViewAllListConfig}
                contents={members}
                count={10}
                list={penaltyMembersList}
                onListViewFilterChange={onListViewFilterChange}
                params={params}
              />
            </Grid>
          </Grid>
      </Grid>
    </Grid> 
    )
}
