import React from 'react'
import PropTypes from 'prop-types'

// Slate
import { useSlate } from 'slate-react'

// Components
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import InsertPhotoIcon from '@material-ui/icons/InsertPhoto'

// Utilities
import first from 'lodash/first'
import noop from 'lodash/noop'
import pick from 'lodash/pick'
import { insertImage } from '../imagery'

// Styles
import { makeStyles } from '@material-ui/core/styles'
import greyColor from '@material-ui/core/colors/grey'

const useStyles = makeStyles(() => ({
  root: {
    display: 'inline-block'
  },

  hidden: {
    display: 'none'
  }
}))

export default function InsertImageButton({ upload = null, onInsert = noop }) {
  const editor = useSlate()
  const classes = useStyles()
  const [imageFile, setImageFile] = React.useState(null)
  const inputRef = React.useRef(null)

  function handleClick() {
    if (inputRef.current) {
      inputRef.current.click()
    }
  }

  function loadImage() {
    const fileReader = new FileReader()

    fileReader.onload = () => {
      const image = new Image()

      image.onload = () => {
        insertImage(editor, URL.createObjectURL(imageFile), {
          dimensions: pick(image, ['width', 'height']),
          url: `${process.env.REACT_APP_SERVER_API}/uploads/${upload.path}`
        })
      }

      image.src = fileReader.result
    }

    fileReader.readAsDataURL(imageFile)
  }

  React.useEffect(() => {
    if (!imageFile) {
      return
    }

    if (upload === null) {
      onInsert(imageFile)
      return
    }

    loadImage()
    setImageFile(null)
  }, [upload, imageFile])

  React.useEffect(() => {
    const inputElement = inputRef.current

    if (!inputElement) {
      return
    }

    const eventListener = inputElement.addEventListener('change', event => {
      const insertedImageFile = first(event.target.files)

      if (insertedImageFile) {
        setImageFile(insertedImageFile)
      }

      event.target.value = null
    })

    return () => {
      inputElement.removeEventListener('change', eventListener)
    }
  }, [])

  return (
    <div className={classes.root}>
      <Tooltip title="Insert an image" placement="top">
        <IconButton onClick={handleClick}>
          <InsertPhotoIcon style={{ color: greyColor[500] }} />
        </IconButton>
      </Tooltip>

      <input ref={inputRef} type="file" accept=".gif,.jpg,.jpeg,.png" className={classes.hidden} />
    </div>
  )
}

InsertImageButton.propTypes = {
  upload: PropTypes.object,
  onInsert: PropTypes.func
}