import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import RadioButton from '@material-ui/core/Radio/Radio'
import Button from '@material-ui/core/Button'

import { Grid } from '@material-ui/core'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Popover from '@material-ui/core/Popover'
import Paper from '@material-ui/core/Paper'

import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'

import Table from 'SharedComponents/Table/Table'

// Components
import { EllipsisFormatter } from 'Utilities/TextLengthFormatter'
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import ViewMore from '../ViewMore/ViewMore'
// import ViewMorePopOver from '../ViewMorePopOver/ViewMorePopOver'
import ConfirmDialog from '../AssignAnalystConfirmDialogue/ConfirmDialog'

// Styles
import classes from './BillingAnalystTab.module.scss'
import CustomEnhancedTable from "../CustomEnhancedTable/CustomEnhancedTable";

const AssignClientPageFooter = props => {
  const history = useHistory()
  const { analyst, clientInfo, setAnalyst, cancelAssigning, fetchData, reAssign } = props

  const [assign, setAssign] = React.useState({ show: false, title: '', text: '', text2: '' })

  const handleOnClickAssign = () => {
    const title = <span style={{ fontWeight: 'bold' }}>Assign Client to Billing Analyst</span>
    const text = 'Are you sure you want to assign the Account to the selected Billing Analyst?'
    const text2 = 'The account will be sent to the assigned Billing Analyst for processing and setup of billing cycle.'

    setAssign(state => ({ ...state, show: true, title, text, text2, analyst: analyst, clientId: clientInfo._id }))
  }

  const handleOnCLickCancel = () => {
    setAssign(state => ({ ...state, show: false }))
    setAnalyst(null)
  }

  const handleOnCLickCancelAssign = () => {
    if (history.location.hash.replace(/[#/]/g, '') === 'from_modal') {
      history.push('/#Assign-Billing-Analyst')
    } else {
      history.push('/clients/assign/5f3623f3d543ed3ef4f64ca6/7409-5462-2115')
    }
    cancelAssigning()
    fetchData()
    handleOnCLickCancel()
  }

  // const capitalizeWord = text => {
  //   return text.toLowerCase().split(' ').map(name => name.charAt(0).toUpperCase() + name.substring(1).join(' ')
  // }

  return (
    // <React.Fragment>
    <div className={classes.button}>
      <Button variant='contained' className={classes['button-negative']} onClick={handleOnCLickCancelAssign}>
        Cancel
      </Button>
      <Button variant='contained' className={classes['button-positive']} onClick={handleOnClickAssign}>
        Assign
      </Button>
      <ConfirmDialog assign={assign} setAssign={setAssign} onCancel={handleOnCLickCancel} reAssign={reAssign} />
    </div>
    // </React.Fragment>
  )
}

export default function BillingAnalystTab (props) {
  const { reset, cancelReset , cancelAssigning, headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore, pagination, params, fetchData, changeView, changeViewForViewMore, getUserAssignedClients, paramsAssigned, reAssign } = props
  const [selectedAnalyst, setSelectedAnalyst] = React.useState(null)
  const handleOnChangeAnalyst = e => {
    setSelectedAnalyst(e.target.value)
  }

  return (
    <>
      <CustomEnhancedTable
        count={users.count}
        changeView={changeView}
        tableHeaders={headCells}
        pagination={pagination}
        search
        sort
        fetchData={fetchData}
        params={params}
        reset={reset}
        cancelReset={cancelReset}
      >
        <TableBody>
          {users.body
            ? users.body.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <RadioButton
                      id={`analyst-radio-${item._id}`}
                      color='primary'
                      checked={selectedAnalyst === item._id}
                      onChange={handleOnChangeAnalyst}
                      value={item._id}
                    />
                  </TableCell>
                  <TableCell id={`analyst-name-${index}`}>{item.first_name + ' ' + item.last_name}</TableCell>
                  <TableCell id={`analyst-status-${index}`}>{item.status}</TableCell>
                  <TableCell id={`analyst-assigned-count-${index}`} align='center'>
                    {item.assignedCount}
                  </TableCell>
                  <TableCell id={`analyst-clients-${index}`}>
                    {EllipsisFormatter(
                      item.clients
                        .map(account => {
                          return account.client.account_name + ''
                        })
                        .join(),
                      50
                    )}
                  </TableCell>
                  <TableCell id={`analyst-view-more-${index}`}>
                    {/*<Link className={classes['view-more']} onClick={onShowViewMore(item)}>
                      View More
                      </Link>*/}
                    <Link className={classes['view-more']} onClick={onShowViewMore(item)}>
                      View More
                    </Link>
                  </TableCell>
                </TableRow>
              ))
            : 'No Data'}
          <div>
            {/*<ViewMore viewMore={viewMore} setViewMore={setViewMore} />*/}
            <ViewMore viewMore={viewMore} setViewMore={setViewMore} fetchData={getUserAssignedClients} params={paramsAssigned} changeViewForViewMore={changeViewForViewMore} />
          </div>
        </TableBody>
      </CustomEnhancedTable>
      <AssignClientPageFooter setSelectedAnalyst={setSelectedAnalyst} fetchData={fetchData} cancelAssigning={cancelAssigning} analyst={selectedAnalyst} setAnalyst={setSelectedAnalyst} clientInfo={clientInfo} reAssign={reAssign}/>
    </>
  )
}
