import { Link } from 'react-router-dom'
import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Utilities
import moment from 'moment'

// Services
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this-week', text: 'This Week' },
    { value: 'this-month', text: 'This Month' }
  ],
  default: 'this-month'
}

const columnConfig = [
  { id: 'validate-collection-distribution-tile-table-header', name: 'Account Name' },
  { id: 'validate-collection-distribution-tile-table-header', name: 'Date Submitted' },
  { id: 'validate-collection-distribution-tile-table-header', name: '' }
]

const ValidateDistributionTile = props => {
  // Constants
  const { service = () => {} } = props
  const history = useHistory()
  const [tile, setTile] = React.useState({
    status: 'For Approval',
    count: 5,
    data: [],
    filter: {},
    filterMenu,
    limit: 10,
    order: true,
    orderBy: 'account_name',
    page: 1,
    sort: '',
    title: 'Collection Distribution To Validate',
    userOwned: false
  })

  // Initialize / Watch
  React.useEffect(() => {
    Promise.all([
      CollectionCDFServices.getMany({
        status: tile.status,
        limit: 5,
        sort: `${tile.orderBy}|${tile.order ? 'asc' : 'desc'}`,
        filter: tile.filter,
        range: tile.filterMenu.default,
        rangeTarget: 'date_submitted'
      })
    ]).then(res => {
      const [CDFService] = res
      const { count, results } = CDFService.data.data
      setTile(i => ({ ...i, count, data: results }))
    })
  }, [tile.filterMenu.default])

  return (
    <React.Fragment>
      <DashboardTile
        id='validate-collection-distribution-tile'
        count={{ color: 3, value: tile.count, id: 'validate-collection-distribution-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        headerSize={'h6'}
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        viewList={
          <Typography
            id='validate-collection-distribution-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => history.push('/collection/lists/collection-distribution/client/submitted')}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              {columnConfig.map((item, index) => (
                <TableCell key={`${item.id}-${index}`} id={`${item.id}-${index}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {tile.data.map((item, index) => (
              <TableRow
                key={`validate-collection-distribution-tile-table-row-${index}`}
                id={`validate-collection-distribution-tile-table-row-${index}`}
              >
                <TableCell id={`validate-collection-distribution-tile-table-cell-account-name-${index}`}>
                  {item.contract?.client?.account_name || ''}
                </TableCell>
                <TableCell id={`validate-collection-distribution-tile-table-cell-date-approved-${index}`}>
                  {moment(item.date_submitted).format('ll') || ''}
                </TableCell>
                <TableCell id={`validate-collection-distribution-tile-table-cell-action-${index}`}>
                  {/* TODO: Change link when end-point and data is available */}
                  <Link to={item.transaction_type.includes('Advance') ? `/collection/distribution/validate/client/${item.cdf_number}?isAdvance=true` : `/collection/distribution/validate/client/${item.cdf_number}`}>
                    <Typography
                      variant='caption'
                      className='text-link underline'
                      id={`validate-collection-distribution-tile-view-${index}`}
                    >
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
    </React.Fragment>
  )
}

ValidateDistributionTile.displayName = 'DashboardTileComponent'

export default ValidateDistributionTile
