import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment'

// MATERIAL UI
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'
import Checkbox from '@material-ui/core/Checkbox'
import noop from 'lodash/noop'

// SHARED COMPONENTS
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// UTILITIES
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import RBAC from 'Security/RBAC'

import style from '../AgingSummaryClientPage.module.scss'

export default function ListView(props) {
  const {
    config,
    contents,
    count,
    list,
    fetchData,
    params,
    onCheckboxChanged,
    handleShowSoaDetails,
    handleInlinePregenerateCDF,
    numberOfItemsSelected,
    handleGroupSelect,
    checkedList
  } = props

  const [checked, setChecked] = useState({})

  // LOCAL CONSTANTS
  const tableHeaders = config.header

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const pagination = {
    count: count || 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  // LOCAL METHODS
  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  React.useEffect(()=>{
    setChecked(checkedList);
  }, [checkedList])

  // Quick Fix
  let hasCheckbox = true
  if (RBAC.hasPolicy('BC-069')) hasCheckbox = false

  return (
    <CustomEnhancedTable
      count={count}
      changeView={changeView}
      tableHeaders={tableHeaders}
      pagination={pagination}
      textBoxHeight={40}
      search
      sort
      fetchData={fetchData}
      params={params}
      hasLink
      hasCheckbox={hasCheckbox}
      numberOfListItems={contents.length}
      numberOfItemsSelected={numberOfItemsSelected}
      handleGroupSelect={handleGroupSelect}

    >
      <TableBody>
        {contents && contents.map((row, contentsIndex) => (
          <StyledTableRow key={row._id}>
            {RBAC.hasPolicy('BC-068') && (
              <StyledTableCell component='th'>
                  <Checkbox
                    id={`soa-checkbox-${contentsIndex}`}
                    key={row.soa_number}
                    onChange={(e) => {
                      const selected = { soa_number: row.soa_number, value: e.target.checked }
                      setChecked(i => ({ ...i, [row.soa_number]: e.target.checked }))
                      onCheckboxChanged(selected)
                    }}
                    checked={checked[row.soa_number]}
                    style={{ color: "rgba(0, 0, 0, 0.54)" }} />
              </StyledTableCell>
            )}
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {DateFormatterByAbbreviation(row.soa_date)}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'
                className={style.linkTypeText} onClick={()=>{handleShowSoaDetails({ soa_number: row.soa_number })}}>
                {row.soa_number}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {DateFormatterByAbbreviation(row.due_date)}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='center'>
                {row.days_overdue}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='right' style={{ marginRight: 20}}>
                {CurrencyFormatter(row.total_amount_due)}
              </Typography>
            </StyledTableCell>
            {RBAC.hasPolicy('BC-068') && (
              <StyledTableCell component='th'>
                <Typography id={`create-cd-${contentsIndex}`} variant='subtitle2' align='left'
                  onClick={i => {
                    handleInlinePregenerateCDF(row.soa_number)
                  }}
                  className={style.linkTypeText}>
                  Create CD
                </Typography>
              </StyledTableCell>
            )}
            {RBAC.hasPolicy('BC-069') && (
              <StyledTableCell component='th'>
                <Typography id={`soa-view-${contentsIndex}`} variant='subtitle2' align='left'
                  onClick={i => {}}
                  className={style.linkTypeText}>
                  View
                </Typography>
              </StyledTableCell>
            )}
          </StyledTableRow>
        ))}
      </TableBody>
    </CustomEnhancedTable>
  )
}
