import React, { useRef } from 'react'
import { Link, useHistory } from 'react-router-dom'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Alert from 'SharedComponents/Alert/Alert'
// import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'

import HeaderDetails from 'StandardComponents/HeaderDetails/HeaderDetails'
import FormDrawer from 'StandardComponents/FormDrawer/FormDrawer'
import FormDrawerSupervisor from '../../Components/SoaSupervisorFormDrawer/FormDrawer'
import BIR from 'StandardComponents/BIR/BIR'
import Corporate from 'StandardComponents/Corporate/Corporate'
import Attachment from 'StandardComponents/Attachment/Attachment'
import Submit from 'StandardComponents/Submit/Submit'
import Analyze from 'StandardComponents/Analyze/Analyze'
import TabPanel from '../../Components/TabPanelComponent/TabPanelComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

import BillingSOAServices from 'Services/Api/BillingSOAServices'

// CSS
import classes from './SOAPage.module.scss'
import { Divider } from '@material-ui/core'

function a11yProps (index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB'
  },
  tabs: {
    fontSize: '0.65rem'
  },
  indicator: {
    backgroundColor: '#3AB77D'
  },
  // button: {
  selected: {
    backgroundColor: 'yellow'
  }
  // }
}))

export default function SOAPage (props) {
  const refContainer = useRef()
  const { soaNumber, type } = props.match.params
  const [ledger, setLedger] = React.useState({})
  const [soaPage, setSOAPage] = React.useState(false)
  const tabClasses = useStyles()
  const [openDialog, setOpenDialog] = React.useState(false)
  const [activeStep, setActiveStep] = React.useState(0)
  const [value, setValue] = React.useState(0)
  const history = useHistory()

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }

  PageTitleService()

  React.useEffect(() => {
    handleGetData()
  }, [])

  if (ledger.details) {
    BreadcrumbsService.setSelectedItems(['ledgers', `ledgerId:${ledger.details.account_name.toUpperCase()}`])
  }

  const [params, setParams] = React.useState({})

  const handleGetData = params => {
    const _params = {
      soaNumber: soaNumber,
      ...params
    }
    setParams(_params)

    Promise.all([BillingAnalystServices.getClientSOA(_params)])
      .then(values => {
        let data
        if (values[0].data.data[0] !== null) {
          data = values[0].data.data[0]
        } else {
          data = []
        }
        setLedger(state => ({ ...state, details: data }))
        setSOAPage(true)
        console.log(values[0])
      })
      .catch(err => console.error(`Error Fetching SOAs: ${err}`))
  }

  const voidFunction = async soaNumber => {
    await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
    BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'void' }).then(res => {
      console.group('Void SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const getStepContent = props => {
    const { stepIndex, activeStep } = props
    if (type === 'analyst-approved') {
      switch (stepIndex) {
        case 0:
          return <BIR soaNumber={soaNumber} />
        case 1:
          return <Corporate soaNumber={soaNumber} />
        case 2:
          return activeStep ? (
            <Attachment submit={{ activeStep: activeStep, type: type, setActiveStep: setActiveStep }} />
          ) : (
            <Attachment soaNumber={soaNumber} />
          )
      }
    } else {
      switch (stepIndex) {
        case 0:
          return (
            <Analyze
              values={{ soa: ledger.details, type: type }}
              handleOnRemoveLineItem={handleOnRemoveLineItem}
              fetchData={handleGetData}
              params={params}
              refContainer={refContainer}
            />
          )
        case 1:
          return <BIR soaNumber={soaNumber} />
        case 2:
          return <Corporate soaNumber={soaNumber} />
        case 3:
          return activeStep ? (
            <Attachment submit={{ activeStep: activeStep, type: type, setActiveStep: setActiveStep }} />
          ) : (
            <Attachment soaNumber={soaNumber} />
          )
      }
    }
  }

  const handleNext = () => {
    if (type === 'analyst-approved') {
      setActiveStep(prevActiveStep => prevActiveStep + 1)
      if (activeStep === 3) history.push('/')
    } else {
      setActiveStep(prevActiveStep => prevActiveStep + 1)
    }
  }

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1)
    if (activeStep === 0) history.push('/')
  }

  const handleReset = () => {
    setActiveStep(0)
  }

  const handleOnRemoveLineItem = params => {
    console.log('For Deletion, disabled', params)
    
    Promise.all([BillingSOAServices.removeClientLineItems({ soaNumber: soaNumber, data: { memberObjIDs: params } })])
      .then(res => {
        console.log('Successfully Deleted Line Items')
        handleGetData()
      })
      .catch(err => {
        console.error(err)
      })
    
  }
  // for saving
  const save = async soaNumber => {
    await BillingAnalystServices.assignClientSOA({ soaNumber: soaNumber })
    BillingAnalystServices.routeSOA({ soaNumber: soaNumber, action: 'save' }).then(res => {
      console.group('Save SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  /** WORKAROUND ONLY */
  const role = localStorage.getItem('group')
  console.log(`current role=${role}`)
  let isAnalyst = false
  let isSupervisor = false

  const group = localStorage.getItem('group')
  if (group) {
    console.log(`current group=${group.name}`)
    isAnalyst = /ANALYST/.test(group.name)
    isSupervisor = /SUPERVISOR/.test(group.name)
  }

  const getSteps = () => {
    if (type === 'analyst-approved') {
      if (group === 'BILLING_SUPERVISOR' || group === 'BILLING_MANAGER') {
        return ['Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Approve']
      } else {
        return ['Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Submit']
      }
    } else {
      if (group === 'BILLING_SUPERVISOR' || group === 'BILLING_MANAGER') {
        return ['Validate Statement', 'Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Approve']
      } else {
        return ['Analyze Statement', 'Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Submit']
      }
    }
  }
  const steps = getSteps()

  console.log(`isAnalyst=${isAnalyst} isSupervisor=${isSupervisor}`)
  return (
    <div className={classes.root} ref={refContainer}>
      {console.log('container ', refContainer.current)}
      <React.Fragment>
        <HeaderDetails ledger={ledger.details} activeStep={activeStep} steps={steps} type={'analyst'} />
        {(group === 'BILLING_ANALYST' || isAnalyst) && (
          <FormDrawer
            voided={() => voidFunction(soaNumber)}
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            save={() => save(soaNumber)}
            nextStep={handleNext}
            stepBack={handleBack}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={ledger.details} value={value} />}
          />
        )}

        {(group === 'BILLING_SUPERVISOR' || group === 'BILLING_MANAGER') && (
          <FormDrawerSupervisor
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            setActiveStep={setActiveStep}
            openDialog={openDialog}
            setOpenDialog={setOpenDialog}
            nextStep={handleNext}
            stepBack={handleBack}
            voided={() => voidFunction(soaNumber)}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={ledger.details} value={value} />}
          />
        )}
      </React.Fragment>
      {/* <React.Fragment>
        <HeaderDetails ledger={ledger.details} activeStep={activeStep} steps={steps} type={'analyst'} />
        <FormDrawer
          voidFunction={() => voidFunction(soaNumber)}
          type={type}
          soaPage={soaPage}
          getStepContent={getStepContent}
          activeStep={activeStep}
          steps={steps}
          save={() => save(soaNumber)}
          nextStep={handleNext}
          stepBack={handleBack}
          reset={handleReset}
          drawerHeight={'570px'}
          drawerHeaderContent={
            <div className={tabClasses.root}>
              <Tabs
                value={value}
                onChange={handleChange}
                aria-label='simple tabs example'
                classes={{ indicator: tabClasses.indicator }}
              >
                <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
              </Tabs>
            </div>
          }
        />
      </React.Fragment> */}
    </div>
  )
}
