import instance from '../instance'
const axios = instance('/c/cm')

export default class LetterTemplateServices {
  static async index(params) {
    return axios({
      url: '/collection/letter-templates',
      method: 'GET',
      params
    })
  }

  static async store(data) {
    return axios({
      url: '/collection/letter-templates',
      method: 'POST',
      data
    })
  }

  static async storeImages(data) {
    return axios({
      url: '/collection/letter-templates/images',
      method: 'POST',
      data
    })
  }
}