import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
// TODO: Implement if Viewlist otherwise, remove
import ViewAll from '../../ViewAll3/ViewAll'
import { DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

// Style
import classes from './ApprovedPreSOAs.module.scss'

// Utilities
import moment from 'moment'
import BillingDebitMemoServices from "../../../../../../Services/Api/BillingDebitMemoServices";
import SOAService from "../../../../../../Services/Api/BillingAndCollection/SOAService";

import { TruncateString } from 'Utilities/TextFormatter'

// Config
const filterMenu = {
  data: [
    {value: 'today', text: 'Today'},
    {value: 'week', text: 'This Week'},
    {value: 'month', text: 'This Month'}
  ],
  default: 'month'
}
const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const ApprovedPreSOAs = props => {
  // Constants
  const { services = () => {} } = props
  const current_user_id = localStorage.getItem('user_id')

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    );
  };

  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      entityType: 'Client',
      status: 'approved',
      currentUserId: current_user_id,
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUser'] = localStorage.getItem('username')
    }
    setParams(_params)

    SOAService.getSoaList(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    const _params = {
      entityType: 'Client',
      isTile: true,
      status: 'approved',
      currentUserId: current_user_id,
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUser'] = localStorage.getItem('username')
    }
    SOAService.getSoaList(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => viewPortGetData()} onLeaveViewport={() => console.log('Approved Pre-SOAs is not on viewport')} />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Approved Pre-SOAs'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) } }
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>

              <TableCell style={{paddingLeft: '6px'}}>Account Name</TableCell>
              <TableCell>Date Approved</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body && tileList.body.map((item, index) => (
            <TableRow>
                <TableCell style={{padding: '0'}}>{TruncateString(item.account_name, 30)}</TableCell>
            <TableCell>{DateFormatterByAbbreviation(item.date && item.date.created)}</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                    <Link to={`/billing/ledgers/${item.soa_number}/${'analyst-approved'}`}>
                        <Typography variant='caption' className='text-link underline'>View</Typography>
                    </Link>
                </TableCell>
            </TableRow>
            ))}

          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'approved-pre-soa-modal'}
        config={config}
        tile={'approvedPreSoa'}
        type='Approved Pre-SOAs'
        title='List of Approved Pre SOAs'
        placeholder='Search List of Approved Pre SOAs'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['account_number', 'account_name', 'billing_cycle', 'date.created|isDate']}
      />
    </React.Fragment>
  )
}

ApprovedPreSOAs.displayName = 'DashboardTileComponent'

export default ApprovedPreSOAs
