import React from 'react'

import { Link } from 'react-router-dom'
import { Typography, TextField } from '@material-ui/core'

import Button from '@material-ui/core/Button'
import * as moment from 'moment'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import FormDrawer from '../../../Components/FormDrawer/FormDrawer'
import HeaderDetails from '../../../Components/HeaderDetails/HeaderDetails'
import EnhancedSearchComponent from '../../../Components/EnhancedSearchComponent/EnhancedSearchComponent'
import classes from '../DebitMemoPendingPage.module.scss'

import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

import { withStyles } from '@material-ui/core/styles'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faAngleDown,
  faFileInvoice,
  faMoneyBillWaveAlt,
  faFileAlt,
  faFileSignature
} from '@fortawesome/free-solid-svg-icons'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

// temp
import EnhacedSearchReferenceNo from '../EnhacedSearchReferenceNo'

export default function SearchClientContract (props) {
  const { clientContract, setStep, handlBackToSelectClientContract } = props
  const clientInfo = clientContract.data.client
  const billingInfo = clientContract.data.billing
  const contractInfo = clientContract.data.contract

  const location =
    contractInfo.soa_send_to_address.unit +
    ' ' +
    contractInfo.soa_send_to_address.floor +
    'F ' +
    contractInfo.soa_send_to_address.bldg_no +
    ' ' +
    contractInfo.soa_send_to_address.bldg_name +
    ' Bldg. ' +
    contractInfo.soa_send_to_address.street +
    ', ' +
    ' Brgy. ' +
    contractInfo.soa_send_to_address.brgy +
    ', ' +
    contractInfo.soa_send_to_address.region +
    ' ' +
    contractInfo.soa_send_to_address.zip_code

  const [transactionType, setTransactionType] = React.useState('none')

  const StyledMenu = withStyles({
    paper: {
      // border: '1px solid #d3d4d5'
    }
  })(props => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right'
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right'
      }}
      {...props}
    />
  ))

  const StyledMenuItem = withStyles(theme => ({
    root: {
      '& .MuiListItemText-primary': {
        fontSize: '13px'
      }
    }
  }))(MenuItem)

  const [dropDownAnchor, setDropDownAnchor] = React.useState(null)
  const [currentSelected, setCurrentSelected] = React.useState(1)

  const handleDropDownClick = event => {
    setDropDownAnchor(event.currentTarget)
  }

  const handleDropDownClose = () => {
    setDropDownAnchor(null)
  }

  const selectedItem = item => {
    switch (item) {
      case 1:
        return (
          <React.Fragment>
            <ListItemText secondary='Select transaction type' />
          </React.Fragment>
        )
      case 2:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
            </ListItemIcon>
            <ListItemText primary='Invoice' />
          </React.Fragment>
        )
      case 3:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon
                className={classes['transaction-type-item-icon-debit-memo']}
                icon={faMoneyBillWaveAlt}
              />
            </ListItemIcon>
            <ListItemText primary='Debit Memo' />
          </React.Fragment>
        )
      case 4:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
            </ListItemIcon>
            <ListItemText primary='Debit Memo' />
          </React.Fragment>
        )
    }
  }

  if (transactionType === 'invoice') {
    setStep('invoice')
  } else if (transactionType === 'debit') {
    setStep('debit')
  } else if (transactionType === 'debit') {
    setStep('debit')
  }

  console.log('REACHED')

  return (
    <div>
      <HeaderDetails>
        <Typography className={classes['header-details-create-transaction']} variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>
              {clientInfo.account_name}
              <RemoveCircleIcon
                onClick={() => handlBackToSelectClientContract()}
                color='secondary'
                className={classes['remove-icon']}
              />
            </Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>{clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>{clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>{clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>{clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                {billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}
              </Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>{contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                {moment(contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                {moment(contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>{location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['main-content-wrapper']}>
          <div className={classes['transaction-type-container']}>
            <div className={classes['transaction-type-field']}>
              <Typography id='transaction-type-field' color='textSecondary'>
                Transaction Type
              </Typography>
            </div>

            <div className={classes['transaction-type-value']}>
              <Button
                id='transaction-type-button'
                className={classes['transaction-type-button']}
                aria-controls='customized-menu'
                aria-haspopup='true'
                onClick={handleDropDownClick}
                endIcon={<FontAwesomeIcon className={classes['transaction-type-button-icon']} icon={faAngleDown} />}
              >
                {selectedItem(currentSelected)}
              </Button>
              <StyledMenu
                id='transaction-type-items'
                anchorEl={dropDownAnchor}
                keepMounted
                open={Boolean(dropDownAnchor)}
                onClose={handleDropDownClose}
              >
                <StyledMenuItem
                  id='transaction-type-item-1'
                  onClick={e => {
                    setCurrentSelected(1)
                    handleDropDownClose()
                    setTransactionType('none')
                  }}
                >
                  <ListItemText primary='' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-2'
                  onClick={e => {
                    setCurrentSelected(2)
                    handleDropDownClose()
                    setTransactionType('invoice')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
                  </ListItemIcon>
                  <ListItemText primary='Invoice' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-3'
                  onClick={e => {
                    setCurrentSelected(3)
                    handleDropDownClose()
                    setTransactionType('debit')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon
                      className={classes['transaction-type-item-icon-debit-memo']}
                      icon={faMoneyBillWaveAlt}
                    />
                  </ListItemIcon>
                  <ListItemText primary='Debit Memo' />
                </StyledMenuItem>
                <StyledMenuItem
                  id='transaction-type-item-4'
                  onClick={e => {
                    setCurrentSelected(4)
                    handleDropDownClose()
                    setTransactionType('debit')
                  }}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
                  </ListItemIcon>
                  <ListItemText primary='Debit Memo' />
                </StyledMenuItem>
              </StyledMenu>
            </div>
          </div>

          <div className={classes['reference-number-container']}>
            <div className={classes['reference-number-field']}>
              <Typography id='reference-number-field' color='textSecondary'>
                Reference No.
              </Typography>
            </div>
            <div className={classes['reference-number-value']}>
              <EnhacedSearchReferenceNo width={'450px'} />
            </div>
          </div>

          <Link
            id='advanced-search-link'
            className={classes['advanced-search-link']}
            // to='/'
            color='primary'
            size='medium'
          >
            Advanced Search
          </Link>
        </div>

        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='cancel-link' to={`/`} className={classes['cancel-link']}>
              <Button id='cancel-button' variant='contained' className={classes['cancel-button']}>
                Cancel
              </Button>
            </Link>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
