import React from 'react'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'

// Components
import SearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from 'Pages/Protected/BillingAndCollection/Components/CustomTable'
import PageFooter from 'Pages/Protected/BillingAndCollection/Components/PageFooter/PageFooter'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionSharedServices from 'Services/Api/CollectionSharedServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { useHistory } from 'react-router-dom'

// Styles
import classes from './style.module.scss'

// Utilities
import moment from 'moment'

// Config
import { PendingTransactionsHeaders } from './../config/config'

// Library
const numeral = require('numeral')

export default function PendingTransactionsAnalystPage (props) {
  const history = useHistory()
  const [pageState, setPageState] = React.useState({
    body: [],
    status: 'Approved',
    count: 0,
    filter: {},
    header: PendingTransactionsHeaders,
    limit: 10,
    order: true,
    orderBy: 'date_updated',
    page: 1,
    sort: '',
    status: 'All',
    search: '',
    user_owned: true
  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'pendingTransactionsList'])
    handleOnFetchData()
  }, [])

  // Methods
  const handleOnFetchData = params => {
    LoadingService.loading(true)

    const _params = {
      status: pageState.status,
      limit: pageState.limit,
      page: pageState.page,
      sort: `${pageState.orderBy}|${pageState.order ? 'asc' : 'desc'}`,
      filter: pageState.filter,
      search: pageState.search,
      user_owned: pageState.user_owned,
      ...params
    }

    Promise.all([CollectionSharedServices.getMany(_params)])
      .then(res => {
        const [CDFService] = res
        const { count, limit, page, results } = CDFService.data.data
        setPageState(i => ({ ...i, count, body: results, limit, page }))
        LoadingService.loading(false)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(`Error fetching Data: ${err}`)
      })
  }

  const onDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleOnCloseClicked = () => {
    console.log('closed clicked')
  }

  const handleOnSearchChanged = _data => {
    console.log(_data)
  }

  const handleOnDisplaySettingsApplied = params => {
    if (!params) return false

    handleOnFetchData({
      date_range_target: 'date_updated',
      date_range: { ...params }
    })
  }

  const handleOnView = ({ status, transaction_type, transaction_number }) => {
    if (!transaction_type || !transaction_number) return false
    switch (transaction_type) {
      case 'Advance Collection Distribution':
        const acdPath = {
          create: `/collection/collection-distribution/client/${transaction_number}?isAdvance=true`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}?isAdvance=true`
        }
        history.push(acdPath.view)
        break
      case 'Collection Distribution':
        const path = {
          create: `/collection/collection-distribution/client/${transaction_number}`,
          view: `/collection/distribution/view/${status.toLowerCase()}/client/${transaction_number}`
        }
        history.push(path.view)
        break
      case 'Suspension':
        history.push(`/collection/suspension/endorse/client/${transaction_number}`)
        break
      case 'Penalty':
        history.push(`/collection/penalty/client/${transaction_number}`)
        break
      default:
        return false
        break
    }
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography id='pending-transactions-page-header' variant='h5'>
                List of Pending Transactions
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder='Search List of Pending Transactions'
                displaySettings
                displaySettingsTitle='Last Modified Date Range *'
                handleOnDisplaySettingsClicked={onDisplaySettingsClicked}
                handleOnDisplaySettingsApplyClicked={handleOnDisplaySettingsApplied}
                onChange={e => {
                  setPageState(i => ({ ...i, search: e }))
                  handleOnFetchData({ search: e })
                }}
                errorMessage={{
                  date_start: 'Please enter a valid Last Modified start date',
                  date_end: 'Please enter a valid Last Modified end date'
                }}
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs>
              <Table
                hasLink
                data={pageState}
                onDataListChange={e => handleOnFetchData(e)}
                setData={setPageState}
                showTextField
                body={_data =>
                  _data.map(({ category, transaction_type, cdf_number, penalty_number, spn_number, contract, status, date, summary }, index) => (
                    <TableRow key={`pending-list-item-${index}`}>
                      <TableCell id={`pending-item-category-${index}`}>{category || ''}</TableCell>
                      <TableCell id={`pending-item-transaction-type-${index}`}>{transaction_type || ''}</TableCell>
                      <TableCell id={`pending-item-transaction-number-${index}`}>
                        {cdf_number || penalty_number || spn_number || ''}
                      </TableCell>
                      <TableCell id={`pending-item-account-name-${index}`}>
                        {contract?.client?.account_name || ''}
                      </TableCell>
                    <TableCell id={`pending-item-transaction-status-${index}`}>{status || ''}</TableCell>
                    <TableCell className={classes.currency} id={`pending-item-amount-${index}`}>{numeral(summary?.grand_total || 0).format('0,0.00')}</TableCell>
                    <TableCell id={`pending-item-date-modified-${index}`}>
                      {date?.updated ? moment(date.updated).format('ll') : ''}
                    </TableCell>
                      <TableCell id={`pending-item-view-${index}`}>
                        <Typography
                          id={`pending-item-action-${index}`}
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => handleOnView({
                            status: status || null,
                            transaction_type: transaction_type || null,
                            transaction_number: cdf_number || penalty_number || spn_number || null
                          })}
                        >
                          View
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))
                }
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <PageFooter isControl handleOnCloseClicked={e => history.push('/')} />
        </Grid>
      </Grid>
    </div>
  )
}
