import instance from '../instance'

const axios = instance('/b/ccm')
export default class DebitMemoServices {
  static getCreditMemos (params) {
    return axios({
      url: `/billing/${params.entityType}/credit/${params.status}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getCreditMemosStatuses () {
    return axios({
      url: `/billing/credit/current-status`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 }
    })
  }

}
