export default function AccountListPage (props) {
  // const [accountList, setAccountList] = React.useState({ account_list: {} })
  const [collapseAll, setCollapseAll] = React.useState(false)
  const [header_collapsed, setHeaderState] = React.useState()
  const [headers, setHeaders] = React.useState(header_items)
  const [tableData, setTableData] = React.useState([])
  const [clientTransactionLedgerData, setTransactionData] = React.useState([])
  const [table_columns, setColumns] = React.useState(mainColumn)
  const [sub_columns, setSubColumns] = React.useState(subColumn)
  const [userList, setUsersList] = React.useState([])
  const [userInfo, setUserInfo] = React.useState({
    userId: '',
    userFirstName: '',
    userMiddleName: '',
    userLastName: '',
    userRole: ''
  })

  const initialValue = {
    show: false,
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    data: [],
    orderBy: '_id',
    order: true,
    sort: '',
    accessors: [
      'client.account_number',
      'client.account_name',
      'contract.status',
      'contract.type',
      'contract.effectivity|isDate',
      'contract.expiry|isDate'
    ]
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const handleTableCollapseAll = () => {
    console.log('ALL: ', collapseAll)
    setCollapseAll(!collapseAll)
    setHeaderState(!collapseAll)
    header_collapsed == true ? setHeaders(header_items) : setHeaders(header_items_collapsed)
    header_collapsed == true ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
    console.log('set to: ', collapseAll)
  }

  const handleHeaders = header_collapsed => {
    console.log('header_collapsed')
    header_collapsed ? setHeaders(header_items) : setHeaders(header_items_collapsed)
    header_collapsed ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  }

  // const handleTableCollapse = () => {
  //   collapseAll ? setHeaders(header_items_collapsed) : setHeaders(header_items)
  //   collapseAll ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  //   console.log('header_collapsed')
  // }

  React.useEffect(() => {
    try {
      const { getUserInfoFunction } = ConfigController2.getUserInfo().then(values => {
        const { data } = values
        setUserInfo(data)
      })
    } catch (error) {
      console.log(error)
    }
  }, [])

  React.useEffect(() => {
    fetchData(params)
  }, [])

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      userList: userList,
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUserId'] = localStorage.getItem('user_id')
    }

    setParams(_params)
    console.log(params)
    Promise.all([BillAnalystServices.getClientTransactionLedgerEntries(_params)]).then(values => {
      const [transactions] = values
      // console.log(transactions)
      const { results, count, limit, page } = transactions.data.data
      setList(i => ({
        ...i,
        body: results,
        count,
        limit,
        page,
        sort: _params.sort,
        orderBy: _params.orderBy ? _params.orderBy : list.orderBy,
        search: typeof _params.search === 'string' ? _params.search : list.search
      }))
      setIsFetchingState(false)
    })
  }

  const mainSearchChange = _.debounce(e => {
    console.log(e.target.value)
    const _params = {
      ...params,
      search: '.*' + e.target.value + '.*',
      page: 1
    }
    fetchData(_params)
  }, 300)

  React.useEffect(() => {
    setCollapseAll(false)
    PageTitleService(`List of Accounts`)
    BreadcrumbsService.setSelectedItems(['journal', 'accountList'])
  }, [])

  return (
    <React.Fragment>
      <div className={classes['search-container']}>
        <div id='modal-search-field' className={classes['search-field']}>
          <SearchTextComponent
            placeholder={'Search client or member'}
            onChange={e => {
              e.persist()
              mainSearchChange(e)
            }}
          />
        </div>
        <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
          <IconButton id='export-icon'>
            <ReplyIcon className={classes['export-icon']} />
            <Typography className='text-link underline'>Export</Typography>
          </IconButton>
        </Link>
        <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
          <IconButton id='display-settings-icon'>
            <ViewListRoundedIcon className={classes['display-settings-icon']} />
            <Typography className='text-link underline'>Display Settings</Typography>
          </IconButton>
        </Link>
      </div>
      <TreeViewTable
        headCells={headers}
        handleCollapseEvent={handleTableCollapseAll}
        collapseAllState={collapseAll}
        isViewTree
        sorter
        fetchData={fetchData}
        params={params}
        count={list.count}
        pagination={pagination}
      >
        <TableBody>
          {isFetching && (
            <StyledTableRow>
              <StyledTableCell component='th' colspan={10}>
                <CircularLoading />
              </StyledTableCell>
            </StyledTableRow>
          )}
          {!isFetching &&
            list &&
            list.body &&
            list.body.map((item, index) =>
              Array.isArray(item.members) & (item.members[0] !== null) & (item.members.length > 0) ? (
                <CollapsibleRow
                  index={index}
                  id={`account-id-${index}`}
                  data={item}
                  objectKey={'members'}
                  tableColumnOrder={table_columns}
                  subColumnOrder={sub_columns}
                  offset={3}
                  collapseAllState={collapseAll}
                  collapseFunction={handleHeaders}
                  actionTo={`/billing/journal/transactions`}
                  param='corporate_id'
                  actionText={'View'}
                  subActionTo={`/billing/journal/member`}
                  subParam='person_id'
                  subActionText={'View'}
                  monetary_columns={monetary_columns}
                  emphasis_columns={emphasis_columns}
                />
              ) : (
                <NormalRow
                  index={index}
                  table_columns={table_columns}
                  data={item}
                  id={`link-table-${index}`}
                  actionTo={`/billing/journal/transactions`}
                  param='corporate_id'
                  actionText={'View'}
                  style={{ textDecoration: 'none' }}
                  show={localStorage.getItem('isAnalyst') !== 'true'}
                  monetary_columns={monetary_columns}
                  emphasis_columns={emphasis_columns}
                />
              )
            )}
        </TableBody>
      </TreeViewTable>
    </React.Fragment>
  )
}
