import React from 'react'
import { Typography } from '@material-ui/core'

// Style
import classes from './SummaryTile.module.scss'

const SummaryTile = props => {
  const { id, label, orientation = 'column', text, prefix } = props
  return (
    <React.Fragment>
      <div id={`${id}-detail-tile`} className={classes.root}>
        <div className={classes[orientation]}>
          <Typography variant='subtitle1' className={classes.subtitle}>{label}</Typography>
          <div>
            <Typography variant='h4' className={classes.description}>{prefix || ''}<span id={`${id}-value`}>{text}</span></Typography>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default SummaryTile
