import instance from '../instance'
const axiosClient = instance('/client')
const axiosRoot = instance('/')

export default class UserManagementService {
  static getUsersByReportsToMock () {
    return axiosRoot({
      // url: `/api/users/list?filter={"where":{"group.name"%20:"BILLING_ANALYST"}}`,
      url: '/users/v1/BILLING_ANALYST',
      method: 'GET'
    })
  }

  static getUsersByReportsTo (currentUser) {
    return axiosClient({
      //   url: `/api/users/list?filter={"where":{"group.name"%20:"BILLING_ANALYST"}}`,
      url: `/api/users/filter={"where":{"reports_to.username":${currentUser.username}}}`,
      method: 'GET'
    })
  }
}
