import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Grid from '@material-ui/core/Grid'
import InputBase from '@material-ui/core/InputBase'
import IconButton from '@material-ui/core/IconButton'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import MomentUtils from '@date-io/moment'
import { Typography } from '@material-ui/core'

import classes from './DatePicker2.module.scss'

const moment = require('moment')

export default function DatePicker (props) {
  const {
    direction = 'row',
    disabled = false,
    label = '',
    required = false,
    value = '',
    onChange = () => {},
    minDateMessage = '',
    errorMessage = '',
    ...other
  } = props

  const [isCalendarOpen, setIsCalendarOpen] = React.useState(false)

  const handleDateChange = date => {
    if (typeof onChange === 'function') onChange(date)
  }

  return (
    <Grid container className={classes.root} direction={direction}>
      <Grid item>
        <Grid container direction='column' style={{ height: '100%' }} justify='center' >
          <Grid item>
            <Typography className={`${classes.label} ${disabled ? classes.disabled : ''}`}>
              {label} {required ? '*' : ''}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
        <Grid item className={`${classes['date-picker']} ${moment(value).isValid() && moment(value).startOf('day') < moment().startOf('day') && !disabled ? classes.error : ''}`} xs>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              TextFieldComponent={p => (
                <React.Fragment>
                  <Grid container direction='row'>
                    <Grid item xs>
                      <InputBase
                        disabled
                        value={new Date(p.value) === new Date('') ? 'Select Collection Date' : moment(p.value).format('ll')}
                        className={`${classes['input-component']}`}
                        onClick={() => {
                          if (!!disabled) return false
                          setIsCalendarOpen(true)
                        }}
                        placeholder='Select Collection Date'
                      />
                    </Grid>
                    <Grid item>
                      <IconButton size='small' disabled={!!disabled} onClick={() => setIsCalendarOpen(true)}>
                        <FontAwesomeIcon icon='calendar-day' />
                      </IconButton>
                    </Grid>
                  </Grid>
                </React.Fragment>
              )}
              clearable
              disabled={!!disabled}
              value={value || ''}
              format={'MM/DD/YYYY'}
              open={isCalendarOpen}
              onClose={() => setIsCalendarOpen(false)}
              onChange={date => handleDateChange(date)}
              {...other}
            />
          </MuiPickersUtilsProvider>
      </Grid>
      {moment(value).isValid() && moment(value).startOf('day') < moment().startOf('day') && !disabled && (
        <Grid item xs={12}>
          <Grid container direction='row' justify='flex-end'>
            <Grid item>
              <Typography className={classes['error-message']} variant='subtitle2'>{minDateMessage || 'Date should be Today or Future'}</Typography>
            </Grid>
          </Grid>
        </Grid>
      )}
      {!moment(value).isValid() && !disabled && (
        <Grid item xs={12}>
          <Grid container direction='row' justify='flex-end'>
            <Grid item>
              <Typography className={classes['error-message']} variant='subtitle2'>{errorMessage || 'Please enter date'}</Typography>
            </Grid>
          </Grid>
        </Grid>
      )}
    </Grid>
  )
}
